import { useState } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Spacing } from "../../style";
import asField from "./hoc/asField";
import EhMaskedInput from "./EhMaskedInput";
import { Validators } from "@evolent/roots";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";

const ZipInput = styled(EhMaskedInput)`
  &&&& {
    width: 8.5rem;
    margin-right: calc(${Spacing.spacingSmall} + 10rem);
  }
`;

const EhZipComposite = ({ name, mask, ...props }) => {
  return (
    <EhFlexBox>
      <ZipInput name={name} mask={mask} data-aq={`txtzip-${name}`} {...props} />
    </EhFlexBox>
  );
};

EhZipComposite.propTypes = {
  name: PropTypes.string.isRequired,
  mask: PropTypes.string.isRequired,
};

const EhZipCompositeField = asField(EhZipComposite);

const EhZipInput = ({ fieldLabel, name, required, ...props }) => {
  const [mask, setMask] = useState(
    props.value?.length === 9 ? "99999-9999" : "999999"
  );
  const validateZip = (value) => {
    if (required && !value) return "This field is required";
    value.length > 5 ? setMask("99999-9999") : setMask("999999");
    return Validators.zip(value);
  };
  return (
    <EhZipCompositeField
      fieldLabel={fieldLabel}
      name={name}
      required={required}
      validate={validateZip}
      mask={mask}
      {...props}
    />
  );
};

EhZipInput.propTypes = {
  fieldLabel: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  required: PropTypes.bool,
  value: PropTypes.string,
};

EhZipInput.displayName = "EhZip";

export default EhZipInput;
