import { createSelector } from "reselect";
import { APP_SETTINGS, MODULES, APP_TILES, USER_TYPES } from "../constants";
import {
  has,
  values,
  each,
  indexOf,
  find,
  isEmpty,
  get,
  flatten,
  filter,
  intersection,
  sortBy,
} from "lodash";

/* SELECTORS */

export const userAppClientsSelector = createSelector(
  (state) => getUserProfileSelector(state),
  (user) => {
    if (!user.clients) {
      return user;
    }

    const userAppClients = {};

    const moduleIds = values(MODULES);
    each(user.clients, function (client) {
      each(client.appModules, function (module) {
        if (module.isActive && indexOf(moduleIds, module.id) > -1) {
          const moduleRoles = [];
          each(module.appModuleRoles, function (role) {
            if (role.isActive) {
              moduleRoles.push({
                roleId: role.roleId,
                roleName: role.roleName,
              });
            }
          });

          if (moduleRoles.length) {
            userAppClients[module.id] = userAppClients[module.id] || [];
            userAppClients[module.id].push({
              name: client.name,
              id: client.id,
            });
          }
        }
      });
    });

    return userAppClients;
  }
);

const _hasAdminAppAndRolesConflict = (userProfile) => {
  const moduleId = APP_SETTINGS.moduleId;
  const { ADMIN_ID, GLOBAL_ADMIN_ID } = USER_TYPES;
  const ADMIN_ROLE_IDS = [ADMIN_ID, GLOBAL_ADMIN_ID];

  const userTypeId = userProfile?.userType?.id;
  return (
    (moduleId !== MODULES.admintools && ADMIN_ROLE_IDS.includes(userTypeId)) ||
    (moduleId === MODULES.admintools && !ADMIN_ROLE_IDS.includes(userTypeId))
  );
};

export const hasAccessSelector = createSelector(
  [(state) => getUserProfileSelector(state), userAppClientsSelector],
  (userProfile, userAppClients) => {
    const moduleId = APP_SETTINGS.moduleId;

    if (_hasAdminAppAndRolesConflict(userProfile)) {
      return false;
    }

    if (moduleId === MODULES.launcher || isEmpty(userAppClients)) {
      return true;
    }

    return _hasAccess(userAppClients, moduleId);
  }
);

export const getModulesToShowSelector = createSelector(
  userAppClientsSelector,
  (userAppClients) => {
    const modulesToShow = filter(APP_TILES, function (module) {
      const hasAccess = _hasAccess(userAppClients, module.id);
      return APP_SETTINGS.moduleId !== module.id && hasAccess;
    });
    return modulesToShow;
  }
);

export const getUserProfileSelector = (state) =>
  get(state, "app.auth.userProfile", null);

/* HELPER FUNCTIONS */
const _hasAccess = (userAppClients, moduleId) => {
  const moduleDependency = {
    [MODULES.cm]: _hasModule(userAppClients, MODULES.reports),
    [MODULES.learning]:
      _hasModule(userAppClients, MODULES.cm) ||
      _hasModule(userAppClients, MODULES.reports) ||
      _hasModule(userAppClients, MODULES.practice) ||
      _hasModule(userAppClients, MODULES.network) ||
      _hasModule(userAppClients, MODULES.practiceAuthorizationRequests),
    [MODULES.practice]: _hasModule(
      userAppClients,
      MODULES.practiceAuthorizationRequests
    ),
  };

  return !!(_hasModule(userAppClients, moduleId) || moduleDependency[moduleId]);
};

function _hasModule(userAppClients, moduleId, clientId) {
  const hasAppAccess = has(userAppClients, moduleId);

  if (!clientId) {
    return hasAppAccess;
  }

  return hasAppAccess && !!find(userAppClients[moduleId], { id: clientId });
}

export const getNormalizedUserClientData = (state) =>
  get(state, "app.auth.normalizedUserProfile");

export const userHasAppRoleSelector = (state, appId, roleNames) => {
  const modifiedRoleNames = roleNames.map((role) =>
    role.replace(/\s/g, "").toLowerCase()
  );
  const normalizedUserData = getNormalizedUserClientData(state);
  const values = Object.values(normalizedUserData?.roles?.[appId] || {});
  const flattened = flatten(values).map((role) =>
    role.roleName.replace(/\s/g, "").toLowerCase()
  );
  return !!intersection(flattened, modifiedRoleNames).length;
};

export const hasActiveFeature = createSelector(
  [
    (state) => getUserProfileSelector(state),
    userAppClientsSelector,
    (state, featureId) => featureId,
    (state, featureId, moduleId) => moduleId,
  ],
  (userProfile, userAppClients, featureId, moduleId) => {
    const feature = userProfile.clientFeatures?.filter(
      (clFeature) =>
        clFeature.featureId === featureId &&
        (moduleId
          ? !!find(userAppClients[moduleId], { id: clFeature.clientId })
          : true)
    );
    return !!feature?.filter((feature) => feature.isActive === true).length;
  }
);

function _hasModuleId(appModules, moduleId) {
  return appModules.find(
    (module) => module.id === moduleId && module?.appModuleRoles?.length > 0
  );
}

export const userAppModuleSelector = createSelector(
  (state) => getUserProfileSelector(state),
  (userProfile) => {
    if (!userProfile?.clients) {
      return userProfile;
    }
    const clients = [];
    userProfile.clients.map((client) => {
      if (_hasModuleId(client.appModules, APP_SETTINGS.moduleId)) {
        clients.push(client);
      }
    });
    const accessClientsUserProfile = Object.assign({}, userProfile, {
      clients,
    });
    return accessClientsUserProfile;
  }
);

export const userRoleSelector = createSelector(
  (state) => userAppModuleSelector(state),
  (userProfile) => {
    if (!userProfile?.clients) {
      return userProfile;
    }
    const moduleRoles = userProfile?.clients[0]?.appModules.find(
      (appModule) =>
        appModule.isActive && appModule.id === APP_SETTINGS.moduleId
    );
    const role = {
      ...moduleRoles?.appModuleRoles[0],
      ...{
        firstName: userProfile.firstName,
        lastName: userProfile.lastName,
        id: userProfile.id,
      },
    };
    return role;
  }
);
export const clientSelector = createSelector(
  (state) => userAppClientsSelector(state),
  (response) => {
    return response[APP_SETTINGS.moduleId]?.length > 0
      ? sortBy(
          response[APP_SETTINGS.moduleId]?.map((client) => {
            return {
              id: client.id,
              clientName: client.name,
              enterpriseId: client.enterpriseId,
            };
          }),
          "clientName"
        )
      : [];
  }
);
