import { useRef, Fragment } from "react";
import {
  MultiselectContainer,
  MultiselectHeader,
  CheckboxGroupContainer,
  SearchInput,
} from "./EhCheckboxGroup";
import { PropTypes } from "prop-types";
import { Icon, Loader } from "semantic-ui-react";
import EhCheckbox from "./EhCheckbox";
import { EhText, EhFormattedText } from "../typography";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";

const EhCheckboxGroupControlled = ({
  allowSelectAll = true,
  searchText,
  placeholderText = "Search",
  noResultsText = "No results found",
  scrollBuffer = 150,
  selectedAll = false,
  selectedOptionsMessage,
  error,
  height,
  loading,
  onSearchChange,
  onSearchFocus,
  onSelectAll,
  onSearchBlur,
  onScrollEvent,
  onClearSearch,
  children,
}) => {
  const scrollBoxRef = useRef();

  return (
    <Fragment>
      <MultiselectContainer error={error}>
        <MultiselectHeader error={error}>
          <EhFlexBox
            justifyContent={allowSelectAll ? "space-between" : "flex-end"}
            alignItems="baseline"
          >
            {allowSelectAll ? (
              <EhCheckbox
                data-aq="select-all"
                label="Select All"
                onClick={() => onSelectAll({ searchText })}
                value={selectedAll}
              />
            ) : null}
            <SearchInput
              value={searchText}
              onChange={(e) => {
                scrollBoxRef.current && scrollBoxRef.current.scrollTo(0, 0);
                onSearchChange(e.target.value);
              }}
              onFocus={onSearchFocus}
              onBlur={onSearchBlur}
              placeholder={placeholderText}
              icon={
                searchText ? (
                  <Icon
                    name="close"
                    color="blue"
                    link
                    onClick={onClearSearch}
                  />
                ) : (
                  <Icon name="filter" color="blue" />
                )
              }
            />
          </EhFlexBox>
        </MultiselectHeader>
        <CheckboxGroupContainer
          innerRef={scrollBoxRef}
          height={height}
          onScroll={(e) => {
            const element = e.target;
            if (
              element.scrollHeight - element.scrollTop <=
              element.clientHeight + scrollBuffer
            ) {
              onScrollEvent({
                type: "SCROLL_END",
              });
            }
          }}
        >
          {loading ? (
            <Loader active />
          ) : (
            <Fragment>
              {children}
              {children?.length === 0 ? (
                <EhText color="grey">
                  {noResultsText || "No Results Found"}
                </EhText>
              ) : null}
            </Fragment>
          )}
        </CheckboxGroupContainer>
      </MultiselectContainer>
      {selectedOptionsMessage ? (
        <EhFormattedText
          size="xsmall"
          textAlign="right"
          data-aq="selected-options-message"
          value={selectedOptionsMessage}
          type="substitutor"
        />
      ) : null}
    </Fragment>
  );
};

/* eslint-disable */
EhCheckboxGroupControlled.EhCheckboxGroupItem = ({
  keyProp, // key is not a valid prop as it is a special prop used by react
  label,
  value,
  disabled,
  onChange,
}) => (
  <div key={keyProp}>
    <EhCheckbox
      data-aq="checkbox-option"
      name={keyProp?.toString()}
      disabled={disabled}
      value={value}
      label={label}
      onChange={onChange}
    />
  </div>
);

EhCheckboxGroupControlled.EhCheckboxGroupItem.propTyes = {
  keyProp: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  value: PropTypes.bool.isRequired,
  disabled: PropTypes.bool,
  onChange: PropTypes.bool,
};

EhCheckboxGroupControlled.propTypes = {
  allowSelectAll: PropTypes.bool,
  onSearchChange: PropTypes.func.isRequired,
  onSearchFocus: PropTypes.func,
  onSearchBlur: PropTypes.func,
  searchText: PropTypes.string,
  placeholderText: PropTypes.string,
  onSelectAll: PropTypes.func,
  onClearSearch: PropTypes.func.isRequired,
  /** Used to power infinite scroll - pass event back to consumer to load more rows */
  onScrollEvent: PropTypes.func,
  noResultsText: PropTypes.string,
  selectedOptionsMessage: PropTypes.string,
  error: PropTypes.any,
  height: PropTypes.string.isRequired,
  /** Sets the amount of buffer before end of scroll bar to emit end scroll event */
  scrollBuffer: PropTypes.number,
  loading: PropTypes.bool,
  selectedAll: PropTypes.bool,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

export default EhCheckboxGroupControlled;
