import { PureComponent } from "react";
import { Message } from "semantic-ui-react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Colors, Elevation, Spacing, Typography } from "../style";

const StyledMessage = styled(Message)`
  &&&&&& {
    box-shadow: ${Elevation.boxShadowInsetThin};
    border-radius: ${Spacing.borderRadiusLarge};
    padding: ${Spacing.spacingMedium} ${Spacing.spacingLarge};

    &.compact {
      display: inline-flex;
    }

    &.success {
      background-color: ${Colors.green[25]};

      .icon {
        color: ${Colors.green[125]};
      }
    }

    &.warning {
      background-color: ${Colors.gold[25]};

      .icon {
        color: ${Colors.gold[100]};
      }
    }

    &.error {
      background-color: ${Colors.red[25]};
      .icon {
        color: ${Colors.red[100]};
      }
    }

    &.info {
      background-color: ${Colors.blue[25]};
      .icon {
        color: ${Colors.blue[100]};
      }
    }

    .header {
      color: ${Colors.grey[100]};
      font-size: ${Typography.fontSizeMedium};
      font-weight: ${Typography.fontWeightBold};

      & + p {
        margin-top: ${Spacing.spacingXxxSmall};
      }
    }

    .icon:not(.close) {
      font-size: ${Typography.fontSizeXLarge};
      margin-right: ${Spacing.spacingMedium};
    }

    .icon.close {
      top: ${Spacing.spacingXxSmall};
      right: ${Spacing.spacingXxSmall};
    }

    .content p {
      font-size: ${Typography.fontSizeSmall};
      color: ${Colors.grey[100]};
      font-weight: ${Typography.fontWeightRegular};
    }
  }
`;

export default class EhMessage extends PureComponent {
  // Removed importing PropTypes from Semantic Message due to overhead it was causing for documentation.
  static propTypes = {
    time: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]),
    /** Shorthand for primary content. */
    content: PropTypes.any,
    /** Shorthand for MessageHeader. */
    header: PropTypes.any,
    /** A message that the user can choose to hide. Called when the user clicks the "x" icon. This also adds the "x" icon.
    @param {SyntheticEvent} event - React's original SyntheticEvent. @param {object} data - All props. */
    onDismiss: PropTypes.any,
    /** A message can only take up the width of its content. */
    compact: PropTypes.bool,
    /** A message can float above content that it is related to. */
    floating: PropTypes.bool,
    /** A message can be hidden. */
    hidden: PropTypes.bool,
    /** A message can be set to visible to force itself to be shown. */
    visible: PropTypes.bool,
    /** A message may be formatted to display a negative message. */
    error: PropTypes.bool,
    /** A message may be formatted to display information. */
    info: PropTypes.bool,
    /** A message may be formatted to display a positive message.  */
    success: PropTypes.bool,
    /** A message may be formatted to display warning messages. */
    warning: PropTypes.bool,
  };

  state = {
    visible: true,
  };

  componentDidMount() {
    let time;
    if (this.props.time) {
      this.props.time instanceof Boolean
        ? (time = 10000)
        : (time = this.props.time);
    }
    this.props.time &&
      window.setTimeout(() => this.setState({ visible: false }), time);
  }

  render() {
    const { success, warning, info, error } = this.props;
    let icon;

    if (success) {
      icon = "check";
    } else if (warning) {
      icon = "exclamation triangle";
    } else if (info) {
      icon = "info circle";
    } else if (error) {
      icon = "exclamation circle";
    }

    return this.state.visible && <StyledMessage icon={icon} {...this.props} />;
  }
}
