import axios from "axios";
import { showModal } from "../componentsStd/EhGlobalModal";
import { EhAlertModalMat } from "../componentsStd";
import {
  EventEmitter,
  RequestInterceptor as DefaultRequestInterceptor,
} from "@evolent/roots";
import { AUTH_EVENTS } from "../constants";

const APP_ERROR_TITLE = "Sorry! Identifi had a problem.";
const APP_ERROR_MESSAGE =
  "You can try again, or copy the Problem ID (below) and send it to the support team with details of when/how the error occurred via the “Contact Support” link under your user profile.\r\n\r\n";
const NETWORK_ERROR_TITLE =
  "Sorry! Your connection to the internet was interrupted.";
const NETWORK_ERROR_MESSAGE =
  "Please check your network settings and try again. If the issue persists, contact your help desk for assistance.";
const NETWORK_CODE = Symbol("NETWORK_ERROR");

const notAuthenticated = () => EventEmitter.emit(AUTH_EVENTS.NOT_AUTHENTICATED);

/**
 * Map of HTTP response status codes -> methods for handling these responses.
 */
export const defaultStatusCodeHandlerMap = {
  401: notAuthenticated,
  419: notAuthenticated,
  403: () => EventEmitter.emit(AUTH_EVENTS.NOT_AUTHORIZED),
};

const errorModal = ({ message, title, type }) => {
  const closeModal = showModal(
    <EhAlertModalMat
      heading={title}
      buttons={[
        {
          text: "OK",
          onClick: () => closeModal(),
        },
      ]}
      message={message}
    />,
    type || NETWORK_CODE
  );
};

/**
 * Default behavior for handling server errors (500): Open alert modal with generic server error title, and message
 * and problem id from server
 * @param { HttpResponse } response the HTTP response object from the server
 */
export const defaultServerError = (response) => {
  errorModal({
    message: (
      <>
        {APP_ERROR_MESSAGE}
        <p>Problem ID:{response.headers.logid || "Not Available"}</p>
      </>
    ),
    title: APP_ERROR_TITLE,
  });
};

/**
 * Default behavior for handling non 200 status codes that are not 500s or specified in the response code map:
 * Open alert modal with generic error title and message and data from server
 * @param { HttpResponse } response the HTTP response object from the server
 */
export const defaultError = (response) => {
  const title = "Oops, an error occurred.";
  errorModal({
    title,
    message: (response && response.data && response.data.message) || title,
    type: response.status,
  });
};
/**
 * Default behavior for handing network/connectivity issues: Opens the alert modal with generic network error
 * message and title
 */
export const defaultNetworkError = () => {
  errorModal({
    title: NETWORK_ERROR_TITLE,
    message: NETWORK_ERROR_MESSAGE,
  });
};

export const defaultOnSuccess = (content) => {
  if (content?.status === 204) {
    return Promise.resolve({ data: null });
  } else {
    return Promise.resolve(content);
  }
};
/**
 * Factory method for building response interceptors. Client can specify custom behaviors for server errors,
 * network errors, and others.
 * @param { ResponseHandlerMap} handlers object containing the following properties:
 * onServerError: behavior for 500 status codes
 * onNetworkError: behavior for network connectivity issues
 * statusCodeHandlerMap: map of status code -> handler for that code
 * onDefaultError: behavior for all other errors (optional)
 */
export const interceptorFactory =
  ({
    onServerError,
    onDefaultError,
    onNetworkError,
    statusCodeHandlerMap,
    onSuccess,
  }) =>
  () => {
    return {
      onSuccess,
      onError: (error) => {
        if (!axios.isCancel(error)) {
          const response = error.response;
          if (response) {
            if (response.status >= 500) {
              onServerError(response);
            } else {
              const responseHandler =
                statusCodeHandlerMap[response.status] || onDefaultError;
              responseHandler && responseHandler(response);
            }
          } else {
            onNetworkError(response);
          }
        }

        return Promise.reject(error);
      },
    };
  };

const defaultInterceptor = interceptorFactory({
  onServerError: defaultServerError,
  onDefaultError: defaultError,
  onNetworkError: defaultNetworkError,
  onSuccess: defaultOnSuccess,
  statusCodeHandlerMap: defaultStatusCodeHandlerMap,
});

/**
 * Method for creating axios HTTP clients that will be injected into our redux thunks.  These clients are the
 * objects that allow us to make HTTP request to our Web APIs
 *
 * @param { string } baseUrl base of url for every request made using the resulting client.
 * @param { (HttpResponse) => Promise } ResponseInterceptor function that takes the HTTP Response and handles
 * it in some way
 * @param { (HttpRequest) => HttpRequest } RequestInterceptor function for manipulating outbound requests
 * (e.g. for authentication) before sending them out
 */
export default function createHttpClient(
  baseUrl,
  ResponseInterceptor,
  RequestInterceptor = DefaultRequestInterceptor
) {
  const axiosInstance = axios.create({
    baseURL: baseUrl,
    withCredentials: true,
  });

  if (RequestInterceptor) {
    axiosInstance.interceptors.request.use(RequestInterceptor);
  }

  let interceptor;
  if (ResponseInterceptor) {
    const resInterceptor = ResponseInterceptor();
    interceptor = {
      ...resInterceptor,
      onSuccess: defaultOnSuccess,
    };
    axiosInstance.interceptors.response.use(
      interceptor.onSuccess,
      interceptor.onError
    );
  } else {
    interceptor = defaultInterceptor();
  }
  const { onSuccess, onError } = interceptor;
  axiosInstance.interceptors.response.use(onSuccess, onError);

  return axiosInstance;
}
