import styled, { css } from "styled-components";
import PropTypes from "prop-types";
import {
  Spacing,
  Colors,
  Elevation,
  Typography,
  LineHeight,
} from "../../../style";
import { APP_SETTINGS } from "@evolent/roots";

const StyledAnchor = styled.a`
  margin: ${Spacing.spacingMedium} 0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const StyeldIcon = styled.div`
  min-width: calc(${Spacing.spacingLarge} + ${Spacing.spacingXxLarge});
  min-height: calc(${Spacing.spacingLarge} + ${Spacing.spacingXxLarge});
  border-radius: ${Spacing.borderRadiusLarge};
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: ${Elevation.boxShadowMedium};
  ${(props) => {
    const tile = props?.tile?.reactApp;
    if (!tile) return;
    const color = tile.shade
      ? Colors[tile.color][tile.shade]
      : Colors[tile.color];
    const bgURL = tile.iconUrl;
    const size = bgURL?.endsWith(".svg") ? "80% 80%" : "80%";
    return css`
      background-color: ${color};
      background-image: url(${bgURL});
      background-size: ${size};
    `;
  }}
`;

const StyledText = styled.p`
  font-size: ${Typography.fontSizeXXSmall};
  line-height: ${LineHeight.text.small};
`;

const AppTile = ({ tile, openInNewTab = false }) => {
  let appUrl = "";

  if (tile?.isSameDomain) {
    const baseUrl = APP_SETTINGS.launcherUrl
      .toLowerCase()
      .replace("/launcher/", "");
    const urlString = `${baseUrl}${tile?.url?.substring(
      tile.url.indexOf("/")
    )}`;
    appUrl = urlString;
  } else {
    appUrl = tile?.url;
  }

  return (
    <StyledAnchor href={appUrl} target={openInNewTab ? "_blank" : "_self"}>
      <StyeldIcon tile={tile} />
      <StyledText>{tile.name}</StyledText>
    </StyledAnchor>
  );
};

AppTile.propTypes = {
  tile: PropTypes.object,
  openInNewTab: PropTypes.bool,
};

export default AppTile;
