import { isEmpty } from "../js-utils/lodashUtil";
export const NUMBER = "number";
export const STRING = "string";

const email = (input) => {
  const regex =
    /^((([a-zA-Z0-9]+([^<>()[\]\\,;:\s@"]+)*)+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@(([[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return regex.test(input.toLowerCase())
    ? null
    : "Please enter a valid email address";
};

const phone = (input) => {
  const regex = /^[1-9]\d{9}$/;
  // both num and ext are empty but if not required -- No validation msg
  if (!input.num && !input.ext) return null;
  // ext only entered
  else if (!input.num && input.ext) return "Please enter a valid phone number";
  // phone number not valid
  else if (!(input.num && regex.test(input.num))) {
    return "Please enter a valid phone number";
  } else return null;
};

const zip = (input) => {
  const regex = /^\d{5}(\d{4})?$/;
  return regex.test(input) ? null : "Please enter a valid ZIP code";
};

const getValidationMessage = (inputValue, type, message) => {
  if (type === NUMBER && typeof inputValue === "number") {
    return message;
  } else if (type === STRING && typeof inputValue === "string") {
    return message;
  } else {
    return null;
  }
};

const minLength =
  (noOfCharacters, type = STRING) =>
  (inputValue) => {
    if (!noOfCharacters) {
      return null;
    }

    let message = null;
    if (`${inputValue}`.length >= noOfCharacters) {
      message = null;
    } else {
      message = `Value must be at least ${noOfCharacters} ${
        type === NUMBER ? "digits" : "characters"
      }`;
    }

    return getValidationMessage(inputValue, type, message);
  };

const maxLength =
  (noOfCharacters, type = STRING) =>
  (inputValue) => {
    if (!noOfCharacters) {
      return null;
    }

    let message = null;
    if (`${inputValue}`.length <= noOfCharacters) {
      message = null;
    } else {
      message = `Value must not be longer than ${noOfCharacters} ${
        type === NUMBER ? "digits" : "characters"
      }`;
    }

    return getValidationMessage(inputValue, type, message);
  };

const isLengthBetween =
  (minValue, maxValue, type = STRING) =>
  (inputValue) => {
    if (!minValue || !maxValue) {
      return null;
    }

    let message = null;
    if (
      `${inputValue}`.length < minValue ||
      `${inputValue}`.length > maxValue
    ) {
      message = `Value must be between ${minValue} - ${maxValue} ${
        type === NUMBER ? "digits long" : "characters long"
      }`;
    } else {
      message = null;
    }

    return getValidationMessage(inputValue, type, message);
  };

const isValueBetween = (minValue, maxValue) => (inputValue) => {
  if ((minValue || maxValue) === undefined) {
    return null;
  }

  if (
    typeof inputValue === "number" &&
    (inputValue < minValue || inputValue > maxValue)
  ) {
    return `Value must be between ${minValue} - ${maxValue}`;
  } else {
    return null;
  }
};

const exactLength =
  (noOfCharacters, type = STRING) =>
  (inputValue) => {
    if (!noOfCharacters) {
      return null;
    }

    let message = null;
    if (`${inputValue}`.length !== noOfCharacters) {
      message = `Value must be exactly ${noOfCharacters} ${
        type === NUMBER ? "digits" : "characters"
      }`;
    } else {
      message = null;
    }

    return getValidationMessage(inputValue, type, message);
  };

const maxItems = (max) => (input) => {
  return input.length <= max ? null : "Too many items selected";
};

const alphanumeric = (value, otherAllowedChars = "", errorMsg = "") => {
  const regex = new RegExp(`^[a-zA-Z0-9${otherAllowedChars}]*$`);
  const otherAllowedString = otherAllowedChars
    .split("")
    .join(",")
    .replace(" ", "spaces");
  if (!regex.test(value))
    if (!isEmpty(errorMsg)) return errorMsg;
    else
      return otherAllowedChars
        ? `Please enter combination of ${otherAllowedString} and alphanumeric chars.`
        : `Please remove spaces or special characters.`;
  else return null;
};

const noWhitespace = (value) => {
  const noWhitespaceRegEx = /\s+/;
  return noWhitespaceRegEx.test(value)
    ? "Please remove any whitespace characters."
    : null;
};

const startingWithAlphaNumeric = (str, otherAllowedChars = "") =>
  RegExp(`^[a-zA-Z0-9${otherAllowedChars}]`).test(str)
    ? null
    : "Field value cannot begin with special characters";

const ssn = (value) => {
  const ssnRegEx = /^\d{3}-?\d{2}-?\d{4}$/;
  return value && !RegExp(ssnRegEx).test(value)
    ? "Please enter a valid SSN"
    : null;
};

const maskedPhone = (input) => {
  const phoneNumberPattern = /^\(?(\d{3})\)?[- ]?(\d{3})[- ]?(\d{4})$/;
  if (!phoneNumberPattern.test(input)) {
    return "Please enter a valid phone number";
  }
  return null;
};

export {
  email,
  phone,
  zip,
  maxLength,
  minLength,
  maxItems,
  alphanumeric,
  noWhitespace,
  startingWithAlphaNumeric,
  ssn,
  isLengthBetween,
  exactLength,
  isValueBetween,
  maskedPhone,
};
