import { STORAGE_KEYS, IDP_EVENTS } from "../constants";
import ReturnToURLService from "./returnToURLService";
import { defer, throttle } from "lodash";
import AuthenticationService from "./authenticationService";
import { EventEmitter, JSUtils, Logger } from "../js-utils";
import DateHandler from "../js-utils/dateHandler";

let authenticationService;

let state = {
  initialCountdownTime: null,
  lastActiveTime: null,
  sessionExpiresAt: null,
  timeoutWarningTimerRunning: false,
  timeoutWarningTimeLeft: 0,
  timeoutWarningIntervalPromise: null,
  isListenToUserEventsInitialized: false,
  currentSession: null,
};

/**
 * Default settings for the session manager, these can be changed by the bootstrap call (during initialization)
 * and after the fact with addSettings(...)
 */
let settings = {
  /**
   * At what time (seconds) before the end of the session should the expiration warning modal appear
   */
  accessTokenExpiringNotificationTime: 300, // 5 Minutes

  /**
   * Minimum amount of time between session renewals
   */
  renewTokenCooldownTime: 60, // 1 Minute

  /**
   * How often (seconds) to poll local storage for changes to the session token which have come from other open windows/tabs
   */
  syncInterval: 5,

  /**
   * When the session manager initializes if the token is less than this old (seconds) then don't bother renewing it
   */
  startRenewDowntime: 20,

  /**
   * How long (seconds) do the tokens last for
   * This setting seems to be overriden by the TTL in the DB for the IDP server
   */
  sessionDuration: 1800, // 30 Minutes

  /**
   * How long (seconds) before the end of the session to consider it 'ended' to prevent issues around clocks not being in perfect sync
   */
  sessionEndOffset: 30,

  /**
   * How often (*milliseconds) session manager checks if a token renewal call has been made.
   * If no call has been made and the user was active within those 5 minutes, go ahead and renew the token
   */
  throttleWaitTime: 300000, // 5 Minutes
};

EventEmitter.addListener(IDP_EVENTS.NEW_ACCESS_TOKEN_RECEIVED, () => {
  checkAuthentication();
});

window.addEventListener("beforeunload", function () {
  state.currentSession = null;
  cancelTimeoutWarningTimer();
});

/**
 * Calculates the notification time that should be passed to OIDC using the offset.
 */
function calcExpirationNotificationTime(options = {}) {
  if (options.accessTokenExpiringNotificationTime) {
    options.accessTokenExpiringNotificationTime += settings.sessionEndOffset;
    Logger.info(
      `OIDC: Expiration Notification Time is ${options.accessTokenExpiringNotificationTime}`
    );
  }
}

/**
 * Instantiate the session, should be called in index.
 * @param {*} options: can override default settings and pass in lifecycle methods.
 */
function bootstrap(options = {}) {
  settings.accessTokenExpiringNotificationTime += settings.sessionEndOffset;
  addSettings(options);

  authenticationService = AuthenticationService.bootstrap({
    sessionDuration: settings.sessionDuration,
    accessTokenExpiringNotificationTime:
      settings.accessTokenExpiringNotificationTime,
    ...options,
    onAccessTokenExpiring: onAccessTokenExpiring,
  });
}

/**
 * Used to add settings after the bootstrap has already been called.
 */
function addSettings(options) {
  calcExpirationNotificationTime(options);
  settings = { ...settings, ...options };
}

async function setAuthorizationToken(token) {
  await JSUtils.safePromise(settings.setAuthorizationToken, token);
}

/**
 * Gets the authorization token from OIDC.
 */
function getAuthorizationToken() {
  return authenticationService.getAuthorizationToken();
}

// Gets called every time the page loads and when the token gets renewed from same tab
async function checkAuthentication() {
  // Get the session from local storage
  const session = (state.currentSession =
    await authenticationService.getSession());
  await setAuthorizationToken(authenticationService.getAuthorizationToken());

  if (!session) {
    // Send to IDP sign in if not authenticated
    Logger.info("Sending to IDP sign in if not authenticated");
    authenticationService.authenticate(true);
  } else {
    Logger.info("Updating session expiry");
    state.sessionExpiresAt = session.expires_at;
    state.initialCountdownTime = state.lastActiveTime =
      DateHandler.utcNow().valueOf();
    return session;
  }

  return null;
}

/**
 * Gets called when redirecting back to application after a successful sign in from IDP.
 */
async function handleReturnFromIDP() {
  Logger.info("OIDC: Handle Return from IDP");
  try {
    await authenticationService.completeAuthentication();

    // Gets called the first time a page is loading after sign in
    await JSUtils.safePromise(settings.onFirstSessionStart);

    ReturnToURLService.processRedirect(async () => {
      await JSUtils.safePromise(settings.onBeforeExternalRedirect);
    });
  } catch (error) {
    Logger.error(error);
    window.location.href = window.location.pathname;
  }
}

// Checks if the url search contains a return url
function checkForReturnURL() {
  const returnToUrl = JSUtils.searchToObject(
    window.location.search
  ).returnToUrl;
  if (returnToUrl) {
    ReturnToURLService.sessionSet(
      STORAGE_KEYS.RETURN_TO_URL_EXTERNAL,
      returnToUrl
    );
  }
}

/**
 *  creates a throttled function that invokes keepAlive
 */
const throttleKeepAlive = throttle(keepAlive, settings.throttleWaitTime, {
  trailing: true,
  leading: true,
});

/**
 *registers event listeners
 */
function listenToUserEvents() {
  state.isListenToUserEventsInitialized = true;
  const eventAction = (e) => {
    e.stopPropagation();
    throttleKeepAlive();
  };

  document.addEventListener("click", eventAction);
  document.addEventListener("mousemove", eventAction);
  document.addEventListener("keypress", eventAction);
}

/**
 * Gets called every time the page loads. Will send to IDP to sign in if not authenticated.
 */
async function start() {
  validateSettings();
  checkForReturnURL();
  !state.isListenToUserEventsInitialized && listenToUserEvents();

  if (await checkAuthentication()) {
    Logger.info("Session started");
    await renewToken();

    await JSUtils.safePromise(settings.onSessionStart);
    EventEmitter.emit(IDP_EVENTS.SESSION_STARTED);
    return true;
  }
}

/**
 * Will force end the session, perform some cleanup, call the expire api to destroy
 * the cookie when user logs out via timeout and redirect to the sign in page.
 */
async function end(hasTimedOut) {
  Logger.info("OIDC: End Session");
  await JSUtils.safePromise(settings.onBeforeSessionEnd, hasTimedOut);
  await destroy();
  await authenticationService.signout(hasTimedOut);
}

/**
 * Will close the timeout modal if open and renew the access token.
 */
async function extend(retryCountdownList = [15, 30, 60]) {
  // Close the application's timeout modal if open
  EventEmitter.emit(IDP_EVENTS.SESSION_WARNING);
  try {
    await authenticationService.renewAccessToken();
  } catch (err) {
    Logger.error(err);

    if (retryCountdownList.length > 0) {
      const retryCountdown = retryCountdownList.shift();
      setTimeout(() => extend(retryCountdownList), retryCountdown * 1000);
    } else if (retryCountdownList.length === 0) {
      Logger.error("Maximum number of retry calls reached");
    }
  }
  EventEmitter.emit(IDP_EVENTS.NEW_ACCESS_TOKEN_RECEIVED);

  // Stop the internal timeout countdown if running
  await toggleCountdownClock(false);
}

/**
 * Gets called by OIDC when the token enters the expiration window.
 */
async function onAccessTokenExpiring() {
  // We check if the timer is already running first because if getSession is called while
  // the token is in the expiration window, OIDC will refire the callback.
  if (!state.timeoutWarningTimerRunning && state.currentSession) {
    Logger.info("OIDC: TOKEN IN EXPIRATION WINDOW");
    // If there had been activity since the last renew, then renew the token
    if (state.initialCountdownTime < state.lastActiveTime) {
      Logger.info(
        "ON ACCESS TOKEN EXPIRING: TOKEN RENEWED (activity detected)"
      );
      await extend();
    } else {
      // Start the timeout countdown
      await toggleCountdownClock(true);
    }
  }
}

// Calculates what the timeout time should be based on expiration time and current time
function syncCountdownTime() {
  const currentTime = Math.round(DateHandler.utcNow().valueOf() / 1000);
  const timeDifference = state.sessionExpiresAt - currentTime;

  if (timeDifference < settings.accessTokenExpiringNotificationTime) {
    state.timeoutWarningTimeLeft = timeDifference;
  } else {
    state.timeoutWarningTimeLeft = settings.accessTokenExpiringNotificationTime;
  }
}

/**
 * Calculates the timeout time to display in the timeout modal.
 */
function getTimeoutTimeLeft() {
  return state.timeoutWarningTimeLeft - settings.sessionEndOffset;
}

// Used to open and close the timeout modal and internal countdown
async function toggleCountdownClock(show) {
  if (show && state.currentSession) {
    // When deciding to display the modal, first check if another tab has renewed
    if (await isTokenCurrent()) {
      syncCountdownTime();

      state.timeoutWarningTimerRunning = true;
      state.timeoutWarningIntervalPromise = setInterval(async () => {
        await expirationCountDown();
      }, 1000);
      Logger.info("TOGGLE COUNTDOWN CLOCK: (+) SHOW MODAL");
      EventEmitter.emit(IDP_EVENTS.SESSION_WARNING, {
        timeoutWarningTimeLeft: getTimeoutTimeLeft(),
        onSignout: end,
        onExtend: extend,
      });
    }
  } else {
    if (state.timeoutWarningTimerRunning) {
      Logger.info("TOGGLE COUNTDOWN CLOCK: (-) CLOSE MODAL");
      EventEmitter.emit(IDP_EVENTS.SESSION_WARNING);
    }
    cancelTimeoutWarningTimer();
  }
}

// Checks if another tab renewed the token, closes the timeout modal and returns false if it has
async function isTokenCurrent() {
  const session = await authenticationService.getSession();
  if (session) {
    // If the token is already expired, call end (fallsafe)
    if (DateHandler.utcNow().valueOf() / 1000 >= session.expires_at) {
      await end();
      return;
    }

    if (state.sessionExpiresAt < session.expires_at) {
      state.sessionExpiresAt = session.expires_at;
      await setAuthorizationToken(
        authenticationService.getAuthorizationToken()
      );
      Logger.info("IS TOKEN CURRENT: TOKEN NOT CURRENT");
      await toggleCountdownClock(false);
      return false;
    }
  }
  return true;
}

/**
 * Performs the necessary actions during the timeout countdown on second intervals.
 */
async function expirationCountDown() {
  state.timeoutWarningTimeLeft -= 1;
  const timeLeftDate = new Date(state.timeoutWarningTimeLeft * 1000);
  const seconds = timeLeftDate.getUTCSeconds();

  // Check if another tab renewed token at an interval
  if (seconds % settings.syncInterval === 0) {
    await isTokenCurrent();
    syncCountdownTime();
  }

  // End session if the timer runs its course
  if (state.timeoutWarningTimeLeft <= settings.sessionEndOffset) {
    if (await isTokenCurrent()) {
      Logger.info("EXPIRATION COUNTDOWN: CALLING END");
      await end(true);
    }
  }
}

/**
 * Should be called on user interaction with the application.
 */
async function keepAlive() {
  if (state.currentSession && !state.timeoutWarningTimerRunning) {
    await renewToken();
  }
}

// Only renew the token if it hasnt been renewed in the last 20 seconds
async function renewToken() {
  const secondsSinceRenew = await authenticationService.getSecondsSinceRenew();
  if (secondsSinceRenew > settings.startRenewDowntime) {
    Logger.info("renewal started");
    await extend();
  }
}

function cancelTimeoutWarningTimer() {
  clearInterval(state.timeoutWarningIntervalPromise);
  state.timeoutWarningTimerRunning = false;
}

function validateSettings() {
  const errors = [];
  if (
    settings.syncInterval <= 0 ||
    settings.syncInterval >= settings.accessTokenExpiringNotificationTime
  ) {
    errors.push(
      "'syncInterval' time must be between 0 and 'accessTokenExpiringNotificationTime' time."
    );
  }
  if (errors.length > 0) {
    defer(function () {
      throw new Error("[SessionManager]: " + errors.join(" | "));
    });
    return false;
  }
  return true;
}

function editStateForTesting(options) {
  state = { ...state, ...options };
}

/**
 * Performs cleanup on session end.
 */
const destroy = async () => {
  Logger.info("OIDC: Peforms Cleanup on Session End");
  state.currentSession = null;
  await toggleCountdownClock(false);
  throttleKeepAlive.cancel(); // cancels keepAlive invocation if session ends before
  window.onbeforeunload = null;
  await setAuthorizationToken(null);
};

const Session = {
  extend,
  start,
  end,
  bootstrap,
  addSettings,
  handleReturnFromIDP,
  keepAlive,
  throttleKeepAlive,
  listenToUserEvents,
  getAuthorizationToken,
  getTimeoutTimeLeft,
  onAccessTokenExpiring,
  destroy,
  expirationCountDown,
  editStateForTesting,
  viewSettings: () => {
    return { ...settings, ...state };
  },
};

export default Session;
