import { toast } from "react-toastify";
import PropTypes from "prop-types";
import EhMessage from "./EhMessage";
import EhText from "./typography/EhText";
import styled from "styled-components";
import { LineHeight } from "../style";

const StyledEhText = styled(EhText)`
  & {
    line-height: ${LineHeight.heading.small};
  }
`;

export const ToastMessage = ({
  type,
  header,
  content,
  closeToast,
  dataAq = "eh-toast-message",
  isCustomStyleRequired = true,
}) => {
  const customType = {
    success: type === "success",
    info: type === "info",
    warning: type === "warning",
    error: type === "error",
  };

  const getContent = () => (
    <EhText color="black" fontWeight="bold">
      {content}
    </EhText>
  );
  const getHeader = () => (
    <StyledEhText color="black" size="large" fontWeight="bold">
      {header}
    </StyledEhText>
  );

  return (
    <EhMessage
      data-aq={dataAq}
      header={isCustomStyleRequired ? getHeader() : header}
      content={isCustomStyleRequired ? getContent() : content}
      time={0}
      onDismiss={closeToast}
      visible
      {...customType}
    />
  );
};

ToastMessage.propTypes = {
  type: PropTypes.string,
  closeToast: PropTypes.func,
  header: PropTypes.string,
  content: PropTypes.string,
  dataAq: PropTypes.string,
  isCustomStyleRequired: PropTypes.bool,
};

const ehToastAction = (type, title, message, customOptions) => {
  toast[type](
    <ToastMessage
      type={type}
      header={title}
      content={message}
      dataAq={customOptions?.dataAq}
      isCustomStyleRequired={customOptions?.isCustomStyleRequired}
    />,
    {
      ...customOptions,
    }
  );
};

const ehToast = {
  success: (title, message, customOptions) =>
    ehToastAction("success", title, message, customOptions),
  error: (title, message, customOptions) =>
    ehToastAction("error", title, message, customOptions),
  warning: (title, message, customOptions) =>
    ehToastAction("warning", title, message, customOptions),
  info: (title, message, customOptions) =>
    ehToastAction("info", title, message, customOptions),
};

export default ehToast;
