import PropTypes from "prop-types";
import styled, { css } from "styled-components";
import { Icon } from "semantic-ui-react";
import { Spacing, Typography, Colors, Elevation } from "../../style";

const StyledMenu = styled.div`
  margin: ${Spacing.spacingXxxSmall} ${Spacing.spacingSmall};
  text-align: start;
  border-radius: ${Spacing.borderRadiusLarge};
  min-width: auto;
  height: ${Spacing.spacingXxLarge};
  font-size: ${Typography.fontSizeSmall};
  display: flex;
  cursor: pointer;
  padding: ${Spacing.spacingXxSmall} ${Spacing.spacingSmall}
    ${Spacing.spacingSmall};

  &:hover {
    background-color: ${Colors.midnight};
    box-shadow: ${Elevation.boxShadowInsetMedium};
  }

  .icon {
    margin: 0 ${Spacing.spacingSmall} 0 0;
    display: inline;
    min-width: ${Spacing.spacingLarge};
  }

  &:hover {
    background-color: ${Colors.indigo[75]};
  }
  background-color: ${(props) =>
    props.isActive ? Colors.midnight : undefined};
`;

const StyledLabel = styled.div`
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: ${Typography.fontWeightBold};
  color: ${Colors.white};
`;

const StyledSideBar = styled.div`
  background-color: ${Colors.indigo[100]};
  color: ${Colors.white};
  width: ${Typography.fontSizeXXXLarge};
  overflow: hidden;
  transition: width 0.15s 0s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: ${Spacing.spacingXLarge};
  border-radius: 0;
  &:hover {
    ${() => openMenuCSS}
  }
  ${(props) => props.keepOpen && openMenuCSS}
`;
const openMenuCSS = css`
  width: ${(props) => props.width};
  transition: width 0.15s 0.5s;
`;

const StyledHeader = styled.div`
  display: flex;
  line-height: ${Spacing.spacingXxLarge};
  margin: ${Spacing.spacingMedium} ${Spacing.spacingMedium}
    ${Spacing.spacingLarge} ${Spacing.spacingMedium};
`;

const StyledTopSegment = styled.div``;
const StyledBottomSegment = styled.div`
  margin-top: auto;
`;

export const EhMenuItem = ({ name = "", onClick, isActive = false, icon }) => (
  <StyledMenu onClick={onClick} isActive={isActive} data-aq={name}>
    <Icon name={icon} />
    {<StyledLabel>{name}</StyledLabel>}
  </StyledMenu>
);

EhMenuItem.propTypes = {
  name: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  isActive: PropTypes.bool.isRequired,
  icon: PropTypes.string.isRequired,
};

const EhSideBar = ({
  topMenuOptions,
  bottomMenuOptions,
  headerIcon,
  keepOpen = false,
  width,
}) => {
  return (
    <StyledSideBar data-aq="eh-side-bar" keepOpen={keepOpen} width={width}>
      <StyledHeader>{headerIcon}</StyledHeader>
      <StyledTopSegment>{topMenuOptions}</StyledTopSegment>
      <StyledBottomSegment>{bottomMenuOptions}</StyledBottomSegment>
    </StyledSideBar>
  );
};

EhSideBar.defaultProps = {
  width: "14.6875rem",
};
EhSideBar.propTypes = {
  topMenuOptions: PropTypes.element.isRequired,
  bottomMenuOptions: PropTypes.element,
  headerIcon: PropTypes.element.isRequired,
  keepOpen: PropTypes.bool,
  width: PropTypes.string,
};

export default EhSideBar;
