import { useEffect, useState } from "react";
import PropTypes from "prop-types";

import EhCircularLoader from "./layout/EhCircularLoader";

import EhPDFViewerMat from "./EhPDFViewerMat";

const EhDocument = ({
  className,
  logic,
  onLoadingChange,
  ...remainingProps
}) => {
  const [loading, setLoading] = useState(false);
  const [pdfUrl, setPdfUrl] = useState("");

  const updateLoading = (loading) => {
    setLoading(loading);
    onLoadingChange && onLoadingChange(loading);
  };

  useEffect(() => {
    (async function () {
      updateLoading(true);
      const response = await logic();
      updateLoading(false);
      const dataUrl = URL.createObjectURL(response.data);
      setPdfUrl(dataUrl);
      return () => URL.revokeObjectURL(dataUrl);
    })();
  }, []);

  return loading ? (
    <EhCircularLoader />
  ) : (
    <EhPDFViewerMat pdfurl={pdfUrl} className={className} {...remainingProps} />
  );
};

EhDocument.propTypes = {
  className: PropTypes.string,
  logic: PropTypes.func.isRequired,
  onLoadingChange: PropTypes.func,
};

export default EhDocument;
