import PropTypes from "prop-types";
import styled from "styled-components";
import InputLabel from "@mui/material/InputLabel";
import { Typography, Colors, LineHeight } from "../../style";
import { FONT_WEIGHTS } from "../../constants";

const fontSize = {
  small: Typography.fontSizeXSmall,
  medium: Typography.fontSizeSmall,
  "medium-condensed": Typography.fontSizeMedium,
  large: Typography.fontSizeLarge,
  standard: Typography.fontSizeStandard,
};

const getColor = (color) => {
  if (color === "white" || color === "black") {
    return Colors[color];
  } else {
    return Colors[color][100];
  }
};

const StyledInputLabel = styled(InputLabel)`
  &&&& {
    line-height: ${LineHeight.text.normal};
    font-family: ${Typography.fontFamily};
    font-size: ${({ size }) => fontSize[size]};
    font-weight: ${({ fontWeight }) => fontWeight};
    color: ${({ labelColor, size }) => {
      if (size === "standard") {
        return labelColor
          ? labelColor === "grey"
            ? Colors.grey[55]
            : getColor(labelColor)
          : "";
      } else {
        return labelColor
          ? labelColor === "grey"
            ? Colors.grey[40]
            : getColor(labelColor)
          : "";
      }
    }}};
  }
`;
export const EhLabelMat = ({ color, ...remainingProps }) => {
  return (
    <StyledInputLabel color="primary" labelColor={color} {...remainingProps} />
  );
};

EhLabelMat.propTypes = {
  color: PropTypes.string,
  fontWeight: PropTypes.string,
  size: PropTypes.oneOf([
    "small",
    "medium",
    "medium-condensed",
    "large",
    "standard",
  ]),
};

EhLabelMat.defaultProps = {
  fontWeight: FONT_WEIGHTS.BOLD,
  size: "medium",
  color: "grey",
};
