import styled from "styled-components";
import { Typography, Colors, LineHeight } from "../../style";
import PropTypes from "prop-types";
import { Component } from "react";

const sizeStyle = {
  small: `
  font-size: ${Typography.fontSizeXSmall}; 
`,
  medium: `
  font-size: ${Typography.fontSizeSmall};
`,
  "medium-condensed": `
  font-size: ${Typography.fontSizeMedium};
`,
  large: `
  font-size: ${Typography.fontSizeLarge};
`,
};

const Label = styled.label`
  line-height: ${LineHeight.text.small};
  color: ${(props) =>
    props.color
      ? props.color === "grey"
        ? Colors.grey[40]
        : Colors[props.color][100]
      : ""};
  ${(props) => sizeStyle[props.size]};
  font-weight: ${Typography.fontWeightBold};
`;

export default class EhLabel extends Component {
  static propTypes = {
    /** The size of the text. */
    size: PropTypes.oneOf(["small", "medium", "medium-condensed", "large"]),
    /** The color of the text */
    color: PropTypes.string,
  };

  static defaultProps = {
    size: "medium",
  };

  render() {
    return <Label {...this.props} />;
  }
}
