import { combineReducers } from "redux";
import { simpleReducer, createAction } from "../js-utils";

const IDP_USER_PROFILE = "IDP_USER_PROFILE";
const NORMALIZED_USER_PROFILE = "NORMALIZED_USER_PROFILE";

export default combineReducers({
  userProfile: simpleReducer(IDP_USER_PROFILE, {}),
  normalizedUserProfile: simpleReducer(NORMALIZED_USER_PROFILE, {}),
});

export const actions = {
  setIDPUserProfile: createAction(IDP_USER_PROFILE),
  setNormalizedUserProfile: createAction(NORMALIZED_USER_PROFILE),
};
