import { Children, PureComponent } from "react";
import EhModal from "./EhModal";
import { Icon } from "semantic-ui-react";
import styled, { css } from "styled-components";
import PropTypes from "prop-types";
import { Colors, Spacing } from "../../style";
import { omit } from "lodash";

const LEFT_ARROW_KEY = 37;
const RIGHT_ARROW_KEY = 39;

const StyledIcon = styled(({ position, ...rest }) => <Icon {...rest} />)`
  &&&& {
    position: absolute;
    ${({ position }) => css`
    ${position}: -3rem;
    padding-${position === "left" ? "right" : "left"}: 0.1875rem !important;
    `}
    top: calc(50% - ${Spacing.spacingLarge});
    background: ${Colors.white};
    color: ${Colors.blue[100]};
    margin: 0;
    width: ${Spacing.spacingXxLarge} !important;
    height: ${Spacing.spacingXxLarge} !important;
    font-size: 1.2rem !important;
    line-height: ${Spacing.spacingLarge} !important;
    cursor: pointer;
    box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2),
      1px 3px 15px 2px rgba(0, 0, 0, 0.2);
  }
`;

class CarouselFrame extends PureComponent {
  static propTypes = {
    children: PropTypes.any,
  };
  render() {
    return this.props.children;
  }
}

class EhCarouselModal extends PureComponent {
  static propTypes = {
    children: PropTypes.any,
    currentSlideIndex: PropTypes.number,
    closeModal: PropTypes.func,
    navigateModal: PropTypes.func,
    keyboardNavigation: PropTypes.func,
  };
  static Frame = CarouselFrame;
  constructor(props) {
    super(props);
    this.state = {
      currentSlide: this.props.currentSlideIndex || 0,
    };
  }

  onNext = () => {
    const currentSlideIndex = this.state.currentSlide;
    this.navigate(currentSlideIndex, currentSlideIndex + 1);
  };

  onPrevious = () => {
    const currentSlideIndex = this.state.currentSlide;
    this.navigate(currentSlideIndex, currentSlideIndex - 1);
  };

  navigate = async (currentSlideIndex, newSlideIndex) => {
    let flag = true;
    const { navigateModal } = this.props;
    navigateModal &&
      (flag = await navigateModal({ currentSlideIndex, newSlideIndex }));
    flag && this.setState({ currentSlide: newSlideIndex });
  };

  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyDown, false);
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyDown, false);
  }

  handleKeyDown = (evt) => {
    const { keyboardNavigation, children } = this.props;
    const keyboardEnabled = keyboardNavigation ? keyboardNavigation() : true;

    if (keyboardEnabled) {
      if (evt.keyCode === LEFT_ARROW_KEY) {
        this.state.currentSlide !== 0 && this.onPrevious();
      }

      if (evt.keyCode === RIGHT_ARROW_KEY) {
        this.state.currentSlide !== Children.toArray(children).length - 1 &&
          this.onNext();
      }
    }
  };

  render() {
    const { children, closeModal, ...restProps } = this.props;
    const currentSlide = this.state.currentSlide;
    return (
      <>
        <EhModal
          {...omit(restProps, [
            "currentSlideIndex",
            "navigateModal",
            "keyboardNavigation",
          ])}
          onClose={closeModal}
        >
          {currentSlide !== 0 && (
            <StyledIcon
              name="caret left"
              position="left"
              circular
              onClick={this.onPrevious}
              data-aq="carousel-prev"
            />
          )}
          {Children.toArray(children)[currentSlide]}
          {currentSlide !== Children.toArray(children).length - 1 && (
            <StyledIcon
              name="caret right"
              circular
              position="right"
              onClick={this.onNext}
              data-aq="carousel-next"
            />
          )}
        </EhModal>
      </>
    );
  }
}

export default EhCarouselModal;
