import { Children, PureComponent } from "react";
import { Segment, SegmentGroup } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled from "styled-components";

import EhHeader from "../typography/EhHeader";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";

import { Spacing } from "../../style";

const NoPaddingSegment = styled(Segment)`
  padding: 0 !important;
`;

const ButtonContainer = styled.div`
  float: right;
  margin-top: -${Spacing.spacingLarge};
  margin-bottom: -${Spacing.spacingLarge};
`;

class EhPanel extends PureComponent {
  static propTypes = {
    /** The element to render as the body of the panel. */
    children: PropTypes.any.isRequired,
    /** The element to render as the header of the panel. */
    header: PropTypes.any,
    /** Optional buttons to render in the upper right corner of the panel. */
    buttons: PropTypes.array,
  };

  render() {
    const { header, children, buttons } = this.props;

    if (
      !header &&
      Children.count(children) === 1 &&
      children.type.name === "EhTabs"
    ) {
      return (
        <SegmentGroup>
          <NoPaddingSegment>{children}</NoPaddingSegment>
        </SegmentGroup>
      );
    }
    return (
      <Segment.Group data-aq={`pnl-${header}`}>
        {header && (
          <Segment>
            <EhHeader size="large">
              <EhFlexBox justifyContent="space-between" alignItems="center">
                {header}
                <ButtonContainer>
                  {buttons &&
                    buttons.map((button, index) => ({ ...button, key: index }))}
                </ButtonContainer>
              </EhFlexBox>
            </EhHeader>
          </Segment>
        )}
        <Segment>{children}</Segment>
      </Segment.Group>
    );
  }
}

export default EhPanel;
