import { alertActions } from "../ducks";
import _ from "lodash";
import { STATUS_CODES } from "../constants";

export default function createLogic(logicFunc) {
  return (data) => {
    return async (dispatch, getState, injections) => {
      try {
        return await logicFunc(
          {
            dispatch,
            getState,
            ...injections,
          },
          data
        );
      } catch (err) {
        if (err.status === STATUS_CODES.PRE_CONDITION_FAILED) {
          const fallbackError = {
            title: "Error",
            text: _.get(err, "data.error.message"),
          };
          const businessError =
            _.find(Object.values(injections.BUSINESS_ERRORS), {
              code: _.get(err, "data.error.code"),
            }) || fallbackError;
          dispatch(
            alertActions.openAlertAction({
              title: businessError.title,
              text: businessError.text,
              type: "warning",
            })
          );
        }
        throw err;
      }
    };
  };
}
