import PropTypes from "prop-types";
import { Switch } from "@mui/material";
import { Colors, Spacing, Typography } from "../../style/variables";
import styled from "styled-components";

const StyledSwitch = styled(Switch)`
  &.MuiSwitch-root {
    width: calc(${Spacing.spacingXxxLarge} + ${Spacing.spacingXxSmall});
    height: calc(${Spacing.spacingXxLarge} + ${Spacing.spacingLarge});
    padding: ${Spacing.spacingNone};
  }

  & .MuiSwitch-switchBase {
    padding: 1px;
    &.Mui-checked {
      transform: translateX(36px);
      & + .MuiSwitch-track {
        background-color: ${Colors.blue[100]};
        opacity: 1;
      }
    }
  }

  & .MuiSwitch-thumb {
    width: calc(${Spacing.spacingXLarge} + ${Spacing.spacingXSmall});
    height: calc(${Spacing.spacingLarge} + ${Spacing.spacingMedium});
    background-color: ${Colors.white};
    border: ${Spacing.borderWidthThick} solid ${Colors.blue[100]};
    margin-top: -1px;
  }

  & .MuiSwitch-track {
    border-radius: calc(${Spacing.spacingXLarge} + ${Spacing.spacingXSmall});
    height: calc(${Spacing.spacingLarge} + ${Spacing.spacingMedium});
    opacity: 1;
    background-color: ${Colors.grey[25]};
    margin-top: ${Spacing.spacingNone};
    padding-top: ${Spacing.spacingXxxSmall};
    &:before {
      content: "Yes";
      color: ${Colors.white};
      margin-left: ${Spacing.spacingMedium};
      font-size: ${Typography.fontSizeXSmall};
    }
    &:after {
      content: "No";
      margin-left: ${Spacing.spacingSmall};
      font-size: ${Typography.fontSizeXSmall}};
    }
  }
`;

const EhToggleButtonMat = ({ value, ...remainingProps }) => {
  const onChange = (e, { checked }) => {
    this.props.onChange && this.props.onChange(checked);
  };

  return (
    <StyledSwitch
      value={value}
      checked={value || false}
      onChange={onChange}
      {...remainingProps}
    />
  );
};

EhToggleButtonMat.propTypes = {
  value: PropTypes.bool,
  onChange: PropTypes.func,
};

export default EhToggleButtonMat;
