import { PureComponent } from "react";
import PropTypes from "prop-types";

import EhRadio from "./EhRadio";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";
import { EhText } from "../typography";
import styled from "styled-components";
import { Spacing } from "../../style";

const StyledRadio = styled(EhRadio)`
  margin-right: ${Spacing.spacingXxLarge};
`;

class EhRadioGroup extends PureComponent {
  static propTypes = {
    /** The data used to render the radio group. */
    options: PropTypes.arrayOf(PropTypes.any).isRequired,
    name: PropTypes.any,
    /** The field for displaying in radio group. It also serves as fall back for keyField */
    display: PropTypes.string,
    /** The unique identifier for each row */
    keyField: PropTypes.string,
    /** Which field from options to use when selecting a radio. */
    select: PropTypes.string,
    value: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
      PropTypes.bool,
    ]),
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    /** The flag which determines if the component should be in the edit or display state. */
    editMode: PropTypes.bool,
    viewFormat: PropTypes.func,
    /** The flag which determines if the text should wrap when too long. */
    wrapText: PropTypes.bool,
    /** The direction of the radio items */
    orientation: PropTypes.oneOf(["horizontal", "vertical"]),
    disabled: PropTypes.bool,
    /** The flag which determines if the radio should be in radio button style. */
    choiceButton: PropTypes.bool,
    /** The flag which determines if the radio should apply the white background color. */
    applyBgColor: PropTypes.bool,
  };

  static defaultProps = {
    editMode: true,
    display: "label",
    orientation: "vertical",
    disabled: false,
    choiceButton: false,
    applyBgColor: false,
  };

  onChange = (value) => {
    const { keyField, options, select } = this.props;
    const val = select
      ? value
      : options.find((option) => option[keyField] === value);

    this.props.onChange(val);
  };

  render() {
    const {
      editMode,
      options,
      value,
      viewFormat,
      display,
      keyField,
      select,
      wrapText,
      orientation,
    } = this.props;

    const selectedVal = select ? value : value[keyField];
    let component;
    const flexOrientation =
      orientation && orientation === "horizontal" ? "row" : "column";
    if (editMode) {
      component = (
        <EhFlexBox flexDirection={flexOrientation}>
          {this.generateRadios(selectedVal)}
        </EhFlexBox>
      );
    } else if (viewFormat) {
      component = viewFormat(options, value, keyField, display);
    } else {
      component = (
        <EhText wrapText={wrapText}>{select ? value : value[display]}</EhText>
      );
    }

    return component;
  }

  generateRadios(selectedVal) {
    const { keyField, options, select } = this.props;
    return options.map((option) => (
      <div key={select ? option[select] : option[keyField]}>
        {this.generateRadio(option, selectedVal)}
      </div>
    ));
  }

  generateRadio(option, selectedVal) {
    const {
      name,
      display,
      keyField,
      select,
      disabled,
      choiceButton,
      applyBgColor,
      orientation,
    } = this.props;
    const optionVal = select ? option[select] : option[keyField];
    const idVal = name ? name + optionVal : optionVal;
    return (
      <StyledRadio
        id={idVal}
        name={name}
        label={option[display]}
        value={optionVal}
        checked={selectedVal === optionVal}
        onChange={this.onChange}
        disabled={disabled || option.disabled}
        dataAq={`rdb-${option[display]}`}
        choiceButton={choiceButton}
        applyBgColor={applyBgColor}
        orientation={orientation}
      />
    );
  }
}

export default EhRadioGroup;
