import PropTypes from "prop-types";
import asField from "./hoc/asField";
import EhMaskedInput from "./EhMaskedInput";
import { Validators } from "@evolent/roots";

const EhSsnComposite = ({ name, ...props }) => {
  const mask = "999-99-9999";
  return (
    <EhMaskedInput
      name={name}
      mask={mask}
      data-aq={`txtssn-${name}`}
      {...props}
    />
  );
};

EhSsnComposite.propTypes = {
  name: PropTypes.string.isRequired,
};

const EhSsnCompositeField = asField(EhSsnComposite);

const EhSsnInput = ({ fieldLabel, name, required, ...props }) => {
  const validateSsn = (value) => {
    if (required && !value) return "This field is required";
    return Validators.ssn(value);
  };
  return (
    <EhSsnCompositeField
      fieldLabel={fieldLabel}
      name={name}
      required={required}
      validate={validateSsn}
      {...props}
    />
  );
};

EhSsnInput.propTypes = {
  fieldLabel: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  required: PropTypes.bool,
  value: PropTypes.string,
};

EhSsnInput.displayName = "EhSsnInput";

export default EhSsnInput;
