import styled from "styled-components";
import PropTypes from "prop-types";
import { Typography, LineHeight, Spacing, Colors } from "../style";
import { Grid, Icon } from "semantic-ui-react";
import EhPopup from "./EhPopup";
import EhLabel from "./typography/EhLabel";
import EhText from "./typography/EhText";

const Segment = styled.div`
  display: flex;
  text-align: center;
  font-weight: ${Typography.fontWeightBold};
  font-size: ${Typography.fontSizeXSmall};
  height: 2.5rem;
  overflow: hidden;
`;

const StyledGrid = styled(Grid)`
  ${(props) =>
    props.total &&
    `:hover {
      border: 1px solid ${Colors.blue[75]};
      border-radius: 5px;
      cursor: pointer;}`}
`;
const Division = styled.div`
  ${(props) => `background-color: ${props.color}; width:${props.width}`};
  color: ${Colors.white};
  border-right: 1px solid ${Colors.white};
  line-height: ${LineHeight.heading.h2};
  overflow: hidden;
`;

const StyledColumn = styled(Grid.Column)`
  text-align: right;
  padding-top: ${Spacing.spacingSmall};
`;

const IndexRow = styled(Grid.Row)`
  &&& {
    justify-content: end;
    padding: ${Spacing.spacingSmall};
  }
`;
const IndexCol = styled.div`
  font-size: ${Typography.fontSizeXXSmall};
  margin: ${Spacing.spacingXSmall};
`;

const ColorDiv = styled.div`
  display: inline-block;
  height: ${Spacing.spacingMedium};
  width: ${Spacing.spacingMedium};
  background-color: ${(props) => props.color};
  margin-right: ${Spacing.spacingSmall};
`;

const StyledSpan = styled.span`
  & {
    cursor: pointer;
    padding-left: ${Spacing.spacingXxSmall};
  }
`;

const StyledText = styled(EhText)`
  & {
    color: ${Colors.grey[35]}!important;
    margin: ${Spacing.spacingSmall};
  }
`;

const StyledIndexGrid = styled(Grid)`
  position: absolute;
  bottom: 0;
  right: ${Spacing.spacingSmall};
`;

const EhMultiDataProgressBar = ({
  colorData,
  countData,
  onClick,
  minimumWidth,
  graphWidth = 9,
  zeroCountMessage = "",
  showLegend,
}) => {
  return (
    <>
      {countData.map((count, index) => {
        return (
          <StyledGrid
            key={index}
            onClick={() => onClick(count)}
            total={count.total}
          >
            <Grid.Row>
              <StyledColumn width={12 - graphWidth}>
                <EhLabel>{count.label}</EhLabel>
              </StyledColumn>
              {count.description && (
                <Grid.Column width={1}>
                  <EhPopup
                    basic
                    trigger={() => (
                      <StyledSpan>
                        <Icon
                          name="info circle"
                          color="blue"
                          data-aq="icnInfo"
                        />
                      </StyledSpan>
                    )}
                    content={<EhText>{count.description}</EhText>}
                    size="small"
                    position="top center"
                    wide
                    style={{
                      marginLeft: Spacing.spacingXxxLarge,
                    }}
                  />
                </Grid.Column>
              )}
              <Grid.Column width={graphWidth - 1}>
                {count.total ? (
                  <Segment>
                    {count.progressData.map((data, index) => {
                      const perct = (data / count.total) * 100;

                      return (
                        data > 0 && (
                          <EhPopup
                            basic
                            key={colorData[index].color}
                            trigger={() => (
                              <Division
                                width={
                                  (minimumWidth
                                    ? perct > minimumWidth
                                      ? perct
                                      : minimumWidth
                                    : perct) + "%"
                                }
                                color={colorData[index].color}
                              >
                                {data}
                              </Division>
                            )}
                            content={`${data} ${colorData[index].label} (${
                              perct.toFixed(2) % 1
                                ? `${perct.toFixed(2)}%`
                                : `${Math.round(perct)}%`
                            })`}
                            size="small"
                            position="bottom center"
                          />
                        )
                      );
                    })}
                  </Segment>
                ) : (
                  <Segment>
                    <StyledText size="small" fontWeight="normal">
                      {zeroCountMessage}
                    </StyledText>
                  </Segment>
                )}
              </Grid.Column>
            </Grid.Row>
          </StyledGrid>
        );
      })}
      {showLegend && (
        <StyledIndexGrid>
          <IndexRow>
            {colorData.map((data) => {
              return (
                <IndexCol key={data.label}>
                  <ColorDiv color={data.color} />
                  {data.label}
                  {data.description && (
                    <EhPopup
                      basic
                      trigger={() => (
                        <StyledSpan>
                          <Icon
                            name="info circle"
                            color="blue"
                            data-aq="icnInfo"
                          />
                        </StyledSpan>
                      )}
                      content={<EhText>{data.description}</EhText>}
                      size="small"
                      position="top center"
                      wide
                    />
                  )}
                </IndexCol>
              );
            })}
          </IndexRow>
        </StyledIndexGrid>
      )}
    </>
  );
};

EhMultiDataProgressBar.propTypes = {
  colorData: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      color: PropTypes.string,
    })
  ),
  countData: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      total: PropTypes.number,
      progressData: PropTypes.arrayOf(PropTypes.number),
      description: PropTypes.string,
    })
  ),
  onClick: PropTypes.func,
  minimumWidth: PropTypes.number,
  zeroCountMessage: PropTypes.string,
  graphWidth: PropTypes.number,
  showLegend: PropTypes.bool,
};

export default EhMultiDataProgressBar;
