import PropTypes from "prop-types";

import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/pro-solid-svg-icons";
import { far } from "@fortawesome/pro-regular-svg-icons";
import { Colors } from "../../style";

library.add(fas);
library.add(far);

const StyledFontAwesomeIcon = styled(FontAwesomeIcon)`
  &&&&& {
    color: ${(props) => {
      if (props.color && Colors[props.color]) {
        if (props.color === "white" || props.color === "black") {
          return Colors[props.color];
        } else {
          return Colors[props.color][props.shade];
        }
      } else if (props.color === "inherit") {
        return "inherit";
      } else {
        return Colors.black;
      }
    }};
  }
`;

export const EhIcon = ({
  iconSize,
  iconStyle,
  iconName,
  iconFamily,
  iconColor,
  dataAq,
  inverse,
  fixedWidth,
  iconShade,
}) => {
  return (
    <StyledFontAwesomeIcon
      icon={`fa-${iconStyle} fa-${iconName} fa-${iconFamily} `}
      size={iconSize === "md" ? null : iconSize}
      color={iconColor}
      data-aq={dataAq}
      inverse={inverse}
      fixedWidth={fixedWidth}
      shade={iconShade}
    />
  );
};

EhIcon.propTypes = {
  dataAq: PropTypes.string,
  iconSize: PropTypes.oneOf(["xs", "sm", "md", "lg", "xl", "2xl"]),
  iconStyle: PropTypes.oneOf(["solid", "regular"]),
  iconFamily: PropTypes.string,
  iconName: PropTypes.string,
  iconColor: PropTypes.string,
  inverse: PropTypes.bool,
  fixedWidth: PropTypes.bool,
  iconShade: PropTypes.number,
};

EhIcon.defaultProps = {
  iconSize: "sm",
  iconStyle: "solid",
  iconFamily: "classic",
  dataAq: "txEhIcon",
  iconColor: "black",
  iconShade: 100,
};
