import PropTypes from "prop-types";
import { Colors } from "../../style";
import EhText from "../../components/typography/EhText";
import Paper from "@mui/material/Paper";
import styled from "styled-components";

const StyledPaper = styled(Paper)`
  background-color: ${({ bgColor }) => bgColor};
  border: 1px solid ${Colors.grey[100]};
  height: 7.5rem;
  width: 7.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: ${({ onClick }) => (onClick ? "pointer" : "unset")};
`;

const Title = styled(EhText)`
  height: 2rem;
`;

const Content = styled(EhText)`
  height: 2.5rem;
`;

const Box = styled.div`
  border-radius: 85px;
  background-color: ${Colors.grey[5]};
  min-width: 70px;
  margin: 0 auto;
  text-align: center;
`;

const EhPaperMat = ({ title, content, count, bgColor, onClick }) => {
  return (
    <StyledPaper variant="outlined" bgColor={bgColor} onClick={onClick}>
      {title && (
        <Title size="small" color="white" textAlign="center">
          {title}
        </Title>
      )}
      {content && (
        <Content
          size="medium-condensed"
          color="white"
          fontWeight="bold"
          textAlign="center"
        >
          {content}
        </Content>
      )}
      <Box>
        <EhText size="large" fontWeight="black">
          {count}
        </EhText>
      </Box>
    </StyledPaper>
  );
};

EhPaperMat.propTypes = {
  title: PropTypes.string,
  content: PropTypes.string,
  count: PropTypes.number,
  bgColor: PropTypes.string,
  onClick: PropTypes.func,
};

export default EhPaperMat;
