import { isObject, isArray } from "lodash";

/**
 * @description removes the properties included in propsToRemove at any level of an object
 * @param {*} objectToRemoveProperties
 * @param {*} propsToRemove
 */
export const omitDeep = (objectToRemoveProperties = {}, propsToRemove = []) => {
  const propsToRemoveObject = propsToRemove.reduce((acc, next) => {
    acc[next] = true;
    return acc;
  }, {});

  const omitDeepHelper = (obj, props) => {
    if (Object.keys(obj).length === 0) return obj;
    return Object.keys(obj).reduce((acc, key) => {
      if (isArray(obj[key]) && !props[key]) {
        acc = {
          ...acc,
          [key]: obj[key].map((item) => {
            if (isObject(item)) {
              return omitDeepHelper(item, props);
            } else {
              return item;
            }
          }),
        };
      } else if (isObject(obj[key]) && !props[key]) {
        acc = { ...acc, [key]: { ...omitDeepHelper(obj[key], props) } };
      } else if (!props[key]) {
        acc = { ...acc, [key]: obj[key] };
      }
      return acc;
    }, {});
  };
  return omitDeepHelper(objectToRemoveProperties, propsToRemoveObject);
};
