import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";
import CardMedia from "@mui/material/CardMedia";
import PropTypes from "prop-types";
import { EhShowMore } from "./index";
import { Spacing, Colors, Typography } from "../../style/variables";
import { EhTheme } from "../../style/theme";
import Divider from "@mui/material/Divider";
import { isArray } from "lodash";
import styled, { css } from "styled-components";

const StyledCard = styled(Card)`
  &.MuiCard-root{
    border: ${Spacing.borderWidthThin} solid ${Colors.blue[25]};
    box-shadow:none,
    color: ${Colors.grey[100]};
    position: relative;
    background-color: ${({ disabled }) => disabled && Colors.grey[5]};
    margin: ${Spacing.spacingLarge} ${Spacing.spacingNone};
    &:hover{
      border-color:${({ disabled }) => !disabled && Colors.blue[100]};
      cursor: ${({ cursor, onClick }) => (onClick ? "pointer" : cursor)};
      transform: none;
    }
    width: ${({ fluid }) => (fluid ? "100%" : EhTheme.spacing(18.125))};
    &:first-child{
      margin-top: ${Spacing.spacingNone};
    }
    &:last-child{
      margin-bottom: ${Spacing.spacingNone};
    }
  }
`;

const StyledCardContent = styled(CardContent)`
  padding-top: ${({ header, headerActions, meta }) =>
    header || headerActions || meta ? EhTheme.spacing(0) : EhTheme.spacing(1)};
  &:last-child {
    padding-bottom: ${Spacing.spacingLarge};
  }
`;

const StyledCardHeader = styled(CardHeader)`
  padding-bottom: ${Spacing.spacingNone};
  & .MuiCardHeader-title {
    font-weight: ${Typography.fontWeightBold};
    font-size: ${Typography.fontSizeMedium};
    padding-bottom: ${Spacing.spacingSmall};
  }
  & .MuiCardHeader-subheader {
    padding-bottom: ${({ description }) =>
      description ? Spacing.spacingSmall : null};
    font-size: ${Typography.fontSizeSmall};
    font-weight: ${Typography.fontWeightRegular};
    color: ${Colors.grey[100]};
  }
  & .MuiCardHeader-action {
    margin-top: ${Spacing.spacingNone};
    margin-right: ${Spacing.spacingNone};
    position: absolute;
    right: ${Spacing.spacingLarge};
    top: ${Spacing.spacingLarge};
  }
  & .MuiTypography-root {
    font-family: ${Typography.fontFamily};
  }
  ${(props) => {
    return css`
      ${{ ...props.headerBackground }};
    `;
  }}
`;

const CardExtra = styled(CardContent)`
  padding: ${Spacing.spacingMedium} ${Spacing.spacingLarge};
  color: ${Colors.grey[40]};
  &:last-child {
    padding-bottom: ${Spacing.spacingMedium};
  }
`;

const StyledCardActions = styled(CardActions)`
  padding: ${Spacing.spacingMedium} ${Spacing.spacingLarge};
  color: ${Colors.grey[40]};
  &:last-child {
    padding-bottom: ${Spacing.spacingMedium};
  }
`;

const StyledCardmedia = styled(CardMedia)`
  margin-bottom: ${Spacing.spacingSmall};
  height: 12.125rem;
`;

const EhCardMat = ({
  header,
  headerActions,
  meta,
  description,
  truncateLength,
  disabled,
  onClick,
  cursor = "default",
  extra,
  fluid = false,
  cardActions,
  cardMedia,
  mediaType,
  headerBackground,
  ...remainingProps
}) => {
  return (
    <StyledCard
      disabled={disabled}
      cursor={cursor}
      onClick={onClick}
      fluid={fluid}
      {...remainingProps}
    >
      {(header || headerActions || meta) && (
        <StyledCardHeader
          description={description}
          action={headerActions}
          title={header}
          subheader={meta}
          headerBackground={headerBackground}
        />
      )}

      {cardMedia && (
        <StyledCardmedia src={cardMedia} component={mediaType} controls />
      )}

      {description && typeof description === "string" ? (
        <StyledCardContent
          header={header}
          headerActions={headerActions}
          meta={meta}
        >
          <EhShowMore
            description={description}
            truncateLength={truncateLength}
          />
        </StyledCardContent>
      ) : (
        description && (
          <StyledCardContent
            header={header}
            headerActions={headerActions}
            meta={meta}
          >
            {description}
          </StyledCardContent>
        )
      )}

      {extra && isArray(extra)
        ? extra.map((item) => {
            return (
              <>
                <Divider light />
                <CardExtra cdata-aq="card-extra">{item}</CardExtra>
              </>
            );
          })
        : // eslint-disable-line
          extra && (
            <>
              <Divider light />
              <CardExtra data-aq="card-extra">{extra}</CardExtra>
            </>
          )}

      {cardActions && (
        <StyledCardActions data-aq="card-actions">
          {cardActions}
        </StyledCardActions>
      )}
    </StyledCard>
  );
};

EhCardMat.propTypes = {
  /** A card can have a header section */
  header: PropTypes.node,
  /** */
  headerActions: PropTypes.node,
  /** A card can have a meta section */
  meta: PropTypes.node,
  /** A card can have a description text */
  description: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  /** Length can be provided to truncate the description and display show more button */
  truncateLength: PropTypes.number,
  /** A card can take up the width of its container. */
  fluid: PropTypes.bool,
  /** */
  onClick: PropTypes.func,
  /** A card can have disabled style */
  disabled: PropTypes.bool,
  /** Cursor style on hover of card */
  cursor: PropTypes.oneOf(["default", "pointer"]),
  /** A card can have an extra section */
  extra: PropTypes.node,
  /** A card can have an card media */
  cardMedia: PropTypes.string,
  /** A card can have an card action section */
  cardActions: PropTypes.node,
  /** Media type for card media */
  mediaType: PropTypes.oneOf(["img", "video", "audio", "picture", "iframe"]),
  /** A card can have header css */
  headerBackground: PropTypes.object,
};

export default EhCardMat;
