import { Component, Fragment } from "react";
import PropTypes from "prop-types";
import { uniqueId } from "lodash";
import EhLoader from "./EhLoader";
import EhForm from "../form/EhForm";
import EhFormCancelButton from "../EhFormCancelButton";
import EhButton from "../EhButton";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";
import { EhHeader } from "../typography";

import {
  startEditing,
  stopEditing,
} from "../../componentsStd/layout/hoc/editing";
import { withProfileContext } from "../../componentsStd/layout/hoc/ProfileContext";

const keyEditIsActive = (state, editKey) => state.editing.global === editKey;
const getGlobalEditState = (state) => state.editing.global;
class EhProfileSection extends Component {
  static propTypes = {
    /** No need to pass this prop.  */
    profileState: PropTypes.any,
    /** Heading for this component */
    heading: PropTypes.string,
    /**
     * It is a flag to hide/show the heading
     */
    showHeader: PropTypes.bool,
    /** isEditable is true if we want to make it editable */
    isEditable: PropTypes.bool,
    /**
     * It is callback function which will be executed on click of save button in edit mode.
     * It will have two params, formData & form
     * */
    beforeSave: PropTypes.func,
    /**
     * It is callback function which will be executed on click of save button in edit mode.
     * It will have two params, formData & form
     * */
    onSave: PropTypes.func,
    /**
     * It will be an object, we can provide the inital values to the form of this component
     * */
    isCustomCancel: PropTypes.bool,
    onCancel: PropTypes.func,
    initialValues: PropTypes.any,
    /**
     * This is a callback function which will be called when Edit button is clicked
     */
    startEditing: PropTypes.func,
    /**
     * We can add any component to the header of this component. It will array of object having three must have fields.
     * key: It will be used to uniquely identify the header component
     * addComponent: This is a render prop, which we use to add a component to the header
     * hideAction: This is a callback which returns boolean value, and used to decide whether to hide/show this header component
     */
    headerActions: PropTypes.arrayOf(
      PropTypes.shape({
        key: PropTypes.string.isRequired,
        addComponent: PropTypes.func.isRequired,
        hideAction: PropTypes.func.isRequired,
      })
    ),
    /**
     * We can add any component to the footer of this component. It will array of object having three must have fields.
     * key: It will be used to uniquely identify the footer component
     * addComponent: This is a render prop, which we use to add a component to the footer
     * hideAction: This is a callback which returns boolean value, and used to decide whether to hide/show this footer component
     */
    footerActions: PropTypes.arrayOf(
      PropTypes.shape({
        key: PropTypes.string.isRequired,
        addComponent: PropTypes.func.isRequired,
        hideAction: PropTypes.func.isRequired,
      })
    ),
    /**
     * This render prop is used to render components in view mode.
     */
    viewChild: PropTypes.func,
    /**
     * This render prop is used to render component in edit mode.
     */
    editChild: PropTypes.func,
    /**
     * This prop tells the component to either render it in view mode or edit mode
     */
    editMode: PropTypes.bool,
    loading: PropTypes.bool,
  };

  static defaultProps = {
    isEditable: false,
    loading: false,
    headerActions: [],
    footerActions: [],
    profileState: {},
    showHeader: true,
  };

  state = {
    _headerActions: [],
  };

  componentWillMount() {
    const { headerActions, isEditable, heading } = this.props;
    this.editKey = uniqueId(heading) + "_EDITING";
    const _headerActions = isEditable
      ? [
          {
            key: "edit",
            addComponent: () => (
              <EhButton data-aq="btnEdit" onClick={this._startEditing}>
                Edit
              </EhButton>
            ),
            hideAction: () =>
              !isEditable || !!getGlobalEditState(this.props.profileState),
          },
          ...headerActions,
        ]
      : headerActions;
    this.setState({
      _headerActions,
    });
  }

  componentWillUnmount() {
    this._stopEditing();
  }

  componentDidMount() {
    this.props.editMode && this._startEditing();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.editMode !== this.props.editMode) {
      this.props.editMode ? this._startEditing() : this._stopEditing();
    }
  }

  _stopEditing = () => {
    this.props.profileState.actionDispatcher(stopEditing(this.editKey));
  };

  _startEditing = () => {
    this.props.profileState.actionDispatcher(startEditing(this.editKey));
    this.props.startEditing && this.props.startEditing();
  };

  onCancel = () => {
    this._stopEditing();
  };

  onCustomCancel = (form) => {
    this.props.onCancel(form, this.onCancel);
  };

  onSave = async (formData, form) => {
    const { onSave, beforeSave } = this.props;
    beforeSave && (await beforeSave(formData, form));
    await onSave(formData, form);
    this._stopEditing();
  };

  renderActionComponents(actionComponents) {
    const { loading } = this.props;
    return (
      !loading && (
        <Fragment>
          {actionComponents.map(
            (action) =>
              !action.hideAction() && (
                <Fragment key={action.key}>{action.addComponent()}</Fragment>
              )
          )}
        </Fragment>
      )
    );
  }

  renderViewMode() {
    const { heading, viewChild, loading, showHeader, initialValues } =
      this.props;
    return loading ? (
      <EhLoader />
    ) : (
      <Fragment>
        {showHeader && (
          <EhFlexBox justifyContent="space-between">
            <EhHeader size="large">{heading}</EhHeader>
            <EhFlexBox>
              {this.renderActionComponents(this.state._headerActions)}
            </EhFlexBox>
          </EhFlexBox>
        )}
        <EhFlexBox>{viewChild && viewChild(initialValues)}</EhFlexBox>
      </Fragment>
    );
  }

  renderEditMode() {
    const { initialValues, heading, showHeader, editChild, isCustomCancel } =
      this.props;
    return (
      <EhForm
        data-aq="frmEdit"
        onSave={this.onSave}
        initialValues={initialValues}
      >
        {(form) => (
          <Fragment>
            {showHeader && <EhHeader size="large">Edit {heading}</EhHeader>}
            <EhFlexBox>{editChild && editChild(form)}</EhFlexBox>
            <EhFlexBox justifyContent="flex-end">
              {isCustomCancel ? (
                <EhButton onClick={() => this.onCustomCancel(form)}>
                  Cancel
                </EhButton>
              ) : (
                <EhFormCancelButton
                  navigateOnCancel={false}
                  showWarning={form.dirty}
                  onCancel={this.onCancel}
                />
              )}
              <EhButton primary type="submit">
                Save
              </EhButton>
            </EhFlexBox>
          </Fragment>
        )}
      </EhForm>
    );
  }

  render() {
    const { isEditable, footerActions, profileState } = this.props;
    const editMode = keyEditIsActive(profileState, this.editKey);
    return (
      <Fragment>
        {editMode && isEditable ? this.renderEditMode() : this.renderViewMode()}
        {this.renderActionComponents(footerActions)}
      </Fragment>
    );
  }
}

const EhProfileSectionContext = withProfileContext(EhProfileSection);
export default EhProfileSectionContext;
