import PropTypes from "prop-types";
import { EhIcon } from "../componentsStd/typography/EhIcon";
import LoadingButton from "@mui/lab/LoadingButton";
import styled from "styled-components";
import is, { isNot, match } from "styled-is";
import { Colors, Elevation, LineHeight, Spacing, Typography } from "../style";
import EhFlexBox from "./layout/EhFlexBox";

const smallStyle = () => match("size", "small")`
  font-size: ${Typography.fontSizeXSmall};
  line-height: ${LineHeight.text.small};
  border-radius: ${Spacing.borderRadiusSmall};
  height: ${Spacing.spacingXLarge};
  ${(props) =>
    props.icon && !props.text
      ? `padding: ${Spacing.spacingNone};
      width: ${Spacing.spacingXLarge};`
      : `padding: ${Spacing.spacingNone} ${Spacing.spacingSmall};
      min-width: 3rem;
      `}
`;

const mediumStyle = () => match("size", "medium")`
  font-size: ${Typography.fontSizeSmall};
  line-height: ${LineHeight.text.small};
  border-radius: ${Spacing.borderRadiusLarge};
  height: ${Spacing.spacingXxLarge};
  ${(props) =>
    props.icon && !props.text
      ? `padding: ${Spacing.spacingNone};
      width: ${Spacing.spacingXxLarge};`
      : `padding: ${Spacing.spacingNone} ${Spacing.spacingMedium};
      min-width: 4rem;
      `}
`;

const largeStyle = () => match("size", "large")`
  font-size: ${Typography.fontSizeMedium};
  line-height: ${LineHeight.text.normal};
  border-radius: ${Spacing.borderRadiusLarge};
  height: 3rem;
  ${(props) =>
    props.icon && !props.text
      ? `padding: ${Spacing.spacingNone};
      width: 3rem;`
      : `padding: ${Spacing.spacingNone} ${Spacing.spacingXLarge};
      min-width: 6rem;
      `}
`;

const headerButtonStyle = () => match("type", "headerButton")`
  padding: ${Spacing.spacingXxSmall} ${Spacing.spacingXSmall} !important;
  margin-left: ${Spacing.spacingSmall} !important;
`;

const borderStyle = () => isNot("borderless")`
  ${(props) => {
    const borderColor =
      !props.primary && props.borderColor
        ? `,0 0 0 1px ${Colors[props.borderColor][100]} inset`
        : "";
    const normalShadow = `${Elevation.boxShadowInsetThin},${Elevation.boxShadowXSmall}`;
    return `
      box-shadow: ${normalShadow}${borderColor} !important;
      :hover,:focus {
        box-shadow: ${normalShadow}${borderColor} !important;
      }
      :active {
        box-shadow: ${Elevation.boxShadowInsetThin},${Elevation.boxShadowInsetSmall}${borderColor} !important;
      }
    `;
  }}
`;

const borderlessStyle = () => is("borderless")`
  box-shadow: none !important;
  :hover,:focus {
    box-shadow: none !important
  }
`;

const loadingStyle = () => is("loading")`
  color: transparent !important;
  .MuiLoadingButton-loadingIndicator {
    top: 50%;
    transform: translate(-50%, -50%);
  }
`;

const basicStyle = () => isNot("primary")`
  color: ${Colors.blue[100]};
  background: ${Colors.white} !important;
  :hover,:focus {
    background: ${Colors.blue[15]} !important;
  }
  :active {
    background: ${Colors.blue[55]} !important;
    color: ${Colors.blue[110]} !important;
  }
  &&:disabled {
    color: ${Colors.grey[35]};
    background: ${Colors.white} !important;
    ${isNot("borderless")`
      box-shadow: 0 0 0 1px ${Colors.grey[35]} inset !important;
    `}
  }

  ${is("positive")`
    color: ${Colors.green[100]};
    :hover,:focus {
      background: ${Colors.green[15]} !important;
    }
    :active {
      background: ${Colors.green[60]} !important;
      color: ${Colors.green[140]} !important;
    }
  `}

  ${is("negative")`
    color: ${Colors.red[100]};
    :hover,:focus {
      background: ${Colors.red[30]} !important;
    }
    :active {
      background: ${Colors.red[60]} !important;
      color: ${Colors.red[135]} !important;
    }
  `}
`;

const primaryStyle = () => is("primary")`
  color:${Colors.white} !important;
  background:${Colors.blue[100]} !important;
  :hover,:focus {
    background: ${Colors.blue[115]} !important;
  }
  :active {
    background: ${Colors.blue[135]} !important;
  }
  &&:disabled {
    background: ${Colors.grey[15]} !important;
    box-shadow: none !important;
  }

  ${is("positive")`
    background:${Colors.green[135]} !important;
    :hover,:focus {
      background: ${Colors.green[130]} !important;
    }
    :active {
      background: ${Colors.green[80]} !important;
    }
  `}

  ${is("negative")`
    background:${Colors.red[115]} !important;
    :hover,:focus {
      background: ${Colors.red[125]} !important;
    }
    :active {
      background: ${Colors.magenta[85]} !important;
    }
  `}
`;

const MaterialButton = (props) => {
  const {
    text,
    icon,
    iconStyle,
    iconFamily,
    iconSize,
    iconColor,
    fluid,
    borderColor,
    negative,
    positive,
    primary,
    borderless,
    size,
    children,
    dataAq,
    loading,
    elementRef,
    ...otherProps
  } = props;

  return (
    <LoadingButton
      {...otherProps}
      ref={elementRef}
      loading={loading}
      fullWidth={fluid}
      data-aq={dataAq}
    >
      <EhFlexBox
        justifyContent="center"
        columnGap={
          size === "large" ? Spacing.spacingMedium : Spacing.spacingSmall
        }
        alignItems="center"
      >
        {icon && (
          <EhIcon
            iconName={icon}
            iconStyle={iconStyle}
            iconFamily={iconFamily}
            iconSize={iconSize}
            iconColor={loading ? "inherit" : iconColor}
          />
        )}
        {text}
        {children}
      </EhFlexBox>
    </LoadingButton>
  );
};

MaterialButton.propTypes = {
  text: PropTypes.string,
  size: PropTypes.oneOf(["small", "medium", "large"]),
  icon: PropTypes.string,
  iconStyle: PropTypes.string,
  iconFamily: PropTypes.string,
  iconSize: PropTypes.oneOf(["xs", "sm", "md", "lg", "xl", "2xl"]),
  iconColor: PropTypes.string,
  negative: PropTypes.bool,
  positive: PropTypes.bool,
  primary: PropTypes.bool,
  fluid: PropTypes.bool,
  borderless: PropTypes.bool,
  children: PropTypes.any,
  borderColor: PropTypes.string,
  dataAq: PropTypes.string,
  loading: PropTypes.bool,
  elementRef: PropTypes.any,
};

const StyledMaterialButton = styled(MaterialButton)`
  position: relative;
  cursor: pointer;
  margin-right: ${Spacing.spacingSmall};
  font-weight: ${Typography.fontWeightBold};
  min-width: unset;
  text-transform: none;
  vertical-align: middle;
  color: ${Colors.blue[100]};
  border: none;
  :disabled {
    cursor: default;
  }

  ${headerButtonStyle()}

  ${largeStyle()}

  ${mediumStyle()}

  ${smallStyle()}

  ${primaryStyle()}
  
  ${basicStyle()}
  
  ${borderStyle()}

  ${borderlessStyle()}

  ${loadingStyle()}
`;

export const EhButtonMat = ({ as, ...props }) => {
  const borderColor = props.positive
    ? "green"
    : props.negative
    ? "red"
    : "blue";
  const defaultIconSize = props.size === "large" ? "xl" : "lg";

  return (
    <StyledMaterialButton
      borderColor={borderColor}
      iconSize={defaultIconSize}
      forwardedAs={as}
      {...props}
    />
  );
};

EhButtonMat.propTypes = {
  as: PropTypes.string,
  /** Shorthand for primary content. */
  content: PropTypes.any,
  /** The text of the button. */
  text: PropTypes.string,
  /** The size of the button. */
  size: PropTypes.oneOf(["small", "medium", "large"]),
  /** Add an Icon by name */
  icon: PropTypes.string,
  /** Add an Icon style */
  iconStyle: PropTypes.string,
  /** Add an Icon family */
  iconFamily: PropTypes.string,
  /** The size of the icon */
  iconSize: PropTypes.oneOf(["xs", "sm", "md", "lg", "xl", "2xl"]),
  /** The color of the icon */
  iconColor: PropTypes.string,
  /** Called after user's click. @param {SyntheticEvent} event - React's original SyntheticEvent. @param {object} data - All props. */
  onClick: PropTypes.func,
  /** A button can hint towards a negative consequence. */
  negative: PropTypes.bool,
  /** A button can hint towards a positive consequence. */
  positive: PropTypes.bool,
  /** A button can be formatted to show different levels of emphasis. */
  primary: PropTypes.bool,
  /** A button can take the width of its container. */
  fluid: PropTypes.bool,
  /** A button can show it is currently unable to be interacted with. */
  disabled: PropTypes.bool,
  /** A button can show it is loading and currently unable to be interacted. */
  loading: PropTypes.bool,
  /** The flag which determines if the button has a border. */
  borderless: PropTypes.bool,
  /** Add any children to the component */
  children: PropTypes.any,
  /** Add an id for testing */
  dataAq: PropTypes.string,
  /** pass react refs to this and access button from code */
  elementRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]),
};

EhButtonMat.defaultProps = {
  type: "button",
  as: "button",
  size: "medium",
  iconColor: "inherit",
  dataAq: "txEhButtonMat",
};
