import { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import EhButton from "./EhButton";
import EhPrompt from "../componentsStd/EhPrompt";

import { showUnsavedChangeWarning, UNSAVED_MESSAGE } from "../util";

export default class EhFormCancelButton extends PureComponent {
  static propTypes = {
    navigateOnCancel: PropTypes.bool,
    showWarning: PropTypes.bool,
    onCancel: PropTypes.func,
    ...EhButton.propTypes,
  };

  onClick = () => {
    if (this.props.showWarning && !this.props.navigateOnCancel) {
      showUnsavedChangeWarning((discard) => {
        if (discard) {
          this.props.onCancel();
        }
      });
    } else {
      this.props.onCancel();
    }
  };

  render() {
    const { navigateOnCancel, showWarning, onCancel, ...buttonProps } =
      this.props;
    return (
      <Fragment>
        <EhPrompt when={this.props.showWarning} message={UNSAVED_MESSAGE} />
        <EhButton
          {...buttonProps}
          text={buttonProps.text || "Cancel"}
          onClick={this.onClick}
        />
      </Fragment>
    );
  }
}
