import { Children, cloneElement, PureComponent } from "react";
import { Form } from "semantic-ui-react";
import PropTypes from "prop-types";
import { Formik } from "formik";
import warning from "warning";

import { FormProvider } from "./hoc/connectForm";

export default class EhForm extends PureComponent {
  static propTypes = {
    children: PropTypes.any,
    onSubmit: PropTypes.any,
    editMode: PropTypes.bool,
    onAfterSave: PropTypes.func,
    onSave: PropTypes.func,
    multistep: PropTypes.bool,
    isLastStep: PropTypes.bool,
    loading: PropTypes.bool,
    noValidate: PropTypes.bool,
    innerRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]),
  };

  static defaultProps = {
    editMode: true,
    noValidate: true,
  };

  constructor(props) {
    super(props);
    warning(
      !props.onSubmit,
      "onSubmit is not supported for EhForm. Use onSave instead."
    );
  }

  onSubmit = async (values, formikBag) => {
    const { isLastStep, multistep, onAfterSave, onSave } = this.props;
    try {
      if (onSave) {
        formikBag.setSubmitting(true);
        const result = await onSave(values, formikBag);
        if (!multistep || isLastStep) {
          formikBag.resetForm(values);
          onAfterSave && (await onAfterSave(values, result));
        }
      }
    } finally {
      formikBag.setSubmitting(false);
    }
  };

  renderChildren(form) {
    const { loading } = this.props;
    return this.props.children instanceof Function
      ? this.props.children(form, loading)
      : Children.map(this.props.children, (child) =>
          cloneElement(child, { form, loading })
        );
  }

  renderForm = (form) => {
    return (
      <Form
        loading={form.isSubmitting}
        onSubmit={form.handleSubmit}
        noValidate={this.props.noValidate}
      >
        {this.renderChildren(form)}
      </Form>
    );
  };

  render() {
    const { children, editMode, loading, innerRef, ...props } = this.props;
    const providerValue = {
      editMode: editMode,
      formLoading: loading,
    };
    return (
      <FormProvider value={providerValue}>
        {loading ? (
          <Form>{this.renderChildren()}</Form>
        ) : (
          <Formik
            {...props}
            onSubmit={this.onSubmit}
            render={this.renderForm}
            validateOnChange={false}
            validateOnBlur
            ref={innerRef}
          />
        )}
      </FormProvider>
    );
  }
}
