import { cloneElement, Component, Fragment } from "react";
import PropTypes from "prop-types";
import cloneDeep from "lodash/cloneDeep";
import { FieldArray, getIn } from "formik";

import EhButton from "../EhButton";

export default class EhAdder extends Component {
  static propTypes = {
    /** The HTML input name. */
    name: PropTypes.string.isRequired,
    /** The default value passed to created children. */
    defaultValue: PropTypes.any.isRequired,
    /** The text displayed next to the add button. */
    addButtonText: PropTypes.string,
    /** The element that gets generated when the add button is clicked. */
    children: PropTypes.func.isRequired,
    /** The flag that determines if the last element remaining can be deleted. */
    required: PropTypes.bool,
    /** The flag which disables the add button. */
    disableAddButton: PropTypes.bool,
    /** The title for the button. */
    deleteTitle: PropTypes.string,
    /** The flag which hides the add button. */
    hideAddButton: PropTypes.bool,
    /** A render prop for providing a custom delete button */
    deleteButton: PropTypes.node,
  };

  static defaultProps = {
    defaultValue: {},
    deleteTitle: "Delete",
  };

  render() {
    return <FieldArray name={this.props.name} render={this.renderChildren} />;
  }

  renderChildren = ({ remove, push, form }) => {
    const {
      name,
      addButtonText,
      defaultValue,
      disableAddButton,
      hideAddButton,
    } = this.props;
    const arrayValue = getIn(form.values, name) || [];
    return (
      <Fragment>
        {arrayValue.map((item, index) =>
          this.renderTemplate(item, index, arrayValue, remove)
        )}
        {hideAddButton || (
          <EhButton
            borderless
            icon="plus circle"
            text={addButtonText}
            onClick={() => push(cloneDeep(defaultValue))}
            disabled={disableAddButton}
            data-aq="btnAdd"
          />
        )}
      </Fragment>
    );
  };

  renderTemplate(item, index, array, remove) {
    const onClick = () => remove(index);
    const defaultDeleteButton = (
      <EhButton
        borderless
        disabled={this.props.required && array.length === 1}
        icon="trash"
        title={this.props.deleteTitle}
        onClick={onClick}
        data-aq="btnDelete"
      />
    );
    const deleteButton = this.props.deleteButton
      ? cloneElement(this.props.deleteButton, { onClick })
      : defaultDeleteButton;
    return this.props.children(item, index, array, deleteButton);
  }
}
