import PropTypes from "prop-types";
import { Pagination, Icon } from "semantic-ui-react";
import styled from "styled-components";
import { Colors, Spacing, Typography } from "../style";

const StyledPagination = styled(Pagination)`
  &&&&&& {
    border-bottom: none;
    .item {
      color: ${Colors.blue[100]};
      padding: ${Spacing.spacingXxxSmall} ${Spacing.spacingSmall};
      font-size: ${Typography.fontSizeSmall};
      font-weight: ${Typography.fontWeightBold};
      text-align: center;
      min-width: initial;
      outline: none;
      line-height: 1;
      border-width: ${Spacing.borderWidthThin};
      &:hover,
      :focus {
        background-color: ${Colors.blue[25]};
      }
    }
    .item:not(.active) {
      border-radius: ${Spacing.borderRadiusLarge};
    
  }
`;
const EhTablePagination = (props) => {
  return (
    <StyledPagination
      ellipsisItem={{
        content: <Icon name="ellipsis horizontal" />,
        icon: true,
      }}
      firstItem={{ content: <Icon name="angle double left" />, icon: true }}
      lastItem={{ content: <Icon name="angle double right" />, icon: true }}
      prevItem={{ content: <Icon name="angle left" />, icon: true }}
      nextItem={{ content: <Icon name="angle right" />, icon: true }}
      totalPages={props.totalPages}
      activePage={props.activePage}
      onPageChange={props.onPageChange}
      secondary
      pointing
    />
  );
};

EhTablePagination.propTypes = {
  totalPages: PropTypes.number.isRequired,
  activePage: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
};

export default EhTablePagination;
