import { useRef } from "react";
import EhCircularLoader from "../../../componentsStd/layout/EhCircularLoader";
import { isEmpty } from "@evolent/roots";
import EhWithTracking from "../../../componentsStd/EhWithTracking";
import EhMessage from "../../EhMessage";

const EhLoadingHOC = (
  WrappedComponent,
  { dataProp, errorMessage = "Something went wrong", height = 400 }
) => {
  return (props) => {
    const data = props[dataProp];
    const componentName = WrappedComponent?.name;
    const firstRender = useRef(true);
    if (firstRender.current === true) {
      firstRender.current = false;
      if (data?.loading) {
        return <EhCircularLoader height={height} data-aq={`ldr${dataProp}`} />;
      } else {
        const WrappedComponentWithTracking = EhWithTracking(
          WrappedComponent,
          componentName
        );
        return <WrappedComponentWithTracking {...props} />;
      }
    } else {
      if (data?.loading) {
        return <EhCircularLoader height={height} data-aq={`ldr${dataProp}`} />;
      } else if (!isEmpty(data?.error)) {
        return (
          <EhMessage dataAq={dataProp} content={errorMessage} visible error />
        );
      } else {
        const WrappedComponentWithTracking = EhWithTracking(
          WrappedComponent,
          componentName
        );
        return <WrappedComponentWithTracking {...props} />;
      }
    }
  };
};

export default EhLoadingHOC;
