import { Fragment } from "react";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import PropTypes from "prop-types";
import { Spacing, Typography, Colors } from "../../style";
import styled from "styled-components";

const StyledDialog = styled(Dialog)`
  & .MuiDialog-paper {
    overflow-y: ${({ scrolling }) => (scrolling ? "auto" : "visible")};
  }
`;

const StyledTitle = styled(DialogTitle)`
  padding: ${Spacing.spacingMedium} ${Spacing.spacingLarge};
  border-bottom: ${Spacing.borderWidthThin} solid ${Colors.grey[10]};
  &.MuiTypography-h6 {
    font-size: ${Typography.fontSizeLarge};
    font-weight: ${Typography.fontWeightBlack};
    font-family: ${Typography.fontFamily};
  }
`;

const StyledFooter = styled(DialogActions)`
  border-top: ${Spacing.borderWidthThin} solid ${Colors.grey[10]};
`;

const StyledDialogContent = styled(DialogContent)`
  overflow-y: ${({ scrolling }) => (scrolling ? "auto" : "visible")};
  padding: ${({ noPadding }) =>
    noPadding
      ? Spacing.spacingNone
      : `${Spacing.spacingSmall} ${Spacing.spacingXLarge}`};
  &:first-child: {
    padding-top: ${({ noPadding }) =>
      noPadding
        ? Spacing.spacingNone
        : `calc(${Spacing.spacingLarge} + ${Spacing.spacingXxSmall})`};
  }
  & .muidialogcontent-root: {
    overflow-y: ${({ scrolling }) => (scrolling ? "auto" : "visible")};
  }
`;

const EhModalMat = ({
  children,
  renderHeader,
  renderFooter,
  loading,
  loaderContent,
  maxWidth = "xl",
  closeOnDimmerClick = false,
  scrolling = true,
  noPadding = false,
  onClose,
  ...modalProps
}) => {
  const handleHeader = () => <StyledTitle>{renderHeader()}</StyledTitle>;

  const handleFooter = () => <StyledFooter>{renderFooter()}</StyledFooter>;

  return (
    <Fragment>
      <StyledDialog
        scrolling={scrolling}
        open
        fullWidth
        maxWidth={maxWidth}
        onClose={(event, reason) => {
          if (reason !== "backdropClick" || closeOnDimmerClick) {
            onClose && onClose(event);
          }
        }}
        {...modalProps}
      >
        {renderHeader && handleHeader()}
        <StyledDialogContent noPadding={noPadding} scrolling={scrolling}>
          {children}
        </StyledDialogContent>
        {renderFooter && handleFooter()}
      </StyledDialog>
    </Fragment>
  );
};

EhModalMat.propTypes = {
  renderHeader: PropTypes.func,
  renderFooter: PropTypes.func,
  children: PropTypes.any,
  loading: PropTypes.bool,
  loaderContent: PropTypes.string,
  maxWidth: PropTypes.oneOf(["lg", "md", "sm", "xl", "xs", false]),
  closeOnDimmerClick: PropTypes.bool,
  scrolling: PropTypes.bool,
  noPadding: PropTypes.bool,
  onClose: PropTypes.func,
  TransitionProps: PropTypes.shape({
    //  Called when the portal is mounted on the DOM.
    onEntered: PropTypes.func,
    //  Called when the portal is unmounted from the DOM.
    onExited: PropTypes.func,
    //  Called when an open event happens.
    onEnter: PropTypes.func,
  }),
};

export default EhModalMat;
