import { PureComponent } from "react";
import { Radio } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled, { css } from "styled-components";
import { Colors, Spacing, Typography } from "../../style";

const StyledRadio = styled(
  ({ choiceButton, orientation, applyBgColor, ...rest }) => <Radio {...rest} />
)`
  &&&&&&&& {
    label:before {
      border-color: ${Colors.indigo[25]};
    }
    input:focus ~ label:before,
    input:hover ~ label:before {
      border-color: ${Colors.blue[130]};
      box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.5);
    }
  }

  ${({ choiceButton, orientation, applyBgColor }) =>
    choiceButton &&
    css`
      &&&&&&&.ui.checkbox {
        label {
          font-weight: ${Typography.fontWeightBold};
          padding: ${Spacing.spacingXSmall} ${Spacing.spacingMedium};
          border: ${Spacing.borderWidthThin} solid ${Colors.blue[100]};
          border-radius: ${Spacing.spacingLarge};
          color: ${Colors.blue[100]};
          margin-bottom: ${orientation === "horizontal"
            ? Spacing.spacingNone
            : Spacing.spacingLarge};
          font-size: ${Typography.fontSizeSmall};
          height: ${Spacing.spacingXxLarge};
          background-color: ${applyBgColor ? Colors.white : "transparent"};

          :after,
          :before {
            opacity: 0;
          }
        }

        input:checked + label {
          background-color: ${Colors.blue[100]};
          color: ${Colors.white};
        }

        input:checked:hover + label,
        input:checked:focus + label {
          background-color: ${Colors.blue[115]};
          color: ${Colors.white};
        }

        input:hover + label,
        input:focus + label {
          background-color: ${Colors.blue[70]};
          color: ${Colors.white};
        }
      }
    `}
`;

class EhRadio extends PureComponent {
  static propTypes = {
    error: PropTypes.bool,
    loading: PropTypes.bool,
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    renderButton: PropTypes.func,
    /** The flag which determines the selected state. */
    value: PropTypes.any,
    choiceButton: PropTypes.bool,
    /** The flag which determines if the radio should apply the white background color. */
    applyBgColor: PropTypes.bool,
    /** The direction of the radio items */
    orientation: PropTypes.oneOf(["horizontal", "vertical"]),
    /** Add an id for testing */
    dataAq: PropTypes.string,
  };

  static defaultProps = {
    error: false,
    loading: false,
    choiceButton: false,
    orientation: "vertical",
  };

  onChange = (evt, { value }) => {
    this.props.onChange(value);
  };

  render() {
    const {
      error,
      loading,
      renderButton,
      choiceButton,
      applyBgColor,
      orientation,
      dataAq,
      ...props
    } = this.props;

    return (
      <StyledRadio
        {...props}
        onChange={this.onChange}
        choiceButton={choiceButton}
        applyBgColor={applyBgColor || false}
        orientation={orientation}
        data-aq={dataAq}
      />
    );
  }
}

export default EhRadio;
