import { Fragment, useState } from "react";
import { withRouter } from "react-router";
import { EhMenuItem } from "../components";
import { showModal } from "../componentsStd/EhGlobalModal";
import { EhAlertModalMat } from "../componentsStd";
import { APP_SETTINGS, Session, handleContactSupport } from "@evolent/roots";

const EhStandardNavigationItems = withRouter((props) => {
  const routerState = props?.location?.pathname;
  const [signingOut, setSigningOut] = useState(false);
  const { keepOpen, setKeepOpen, userEmail, enableSupport } = props;

  return (
    <Fragment>
      <EhMenuItem
        name="Settings"
        onClick={() => {
          window.open(`${APP_SETTINGS.launcherUrl}#/profile`, "_blank");
        }}
        isActive={routerState.includes("settings")}
        icon={"setting"}
      />
      <EhMenuItem
        name={"Applications"}
        onClick={() => {
          setKeepOpen(true);
        }}
        isActive={keepOpen}
        icon={"th"}
      />
      {enableSupport && (
        <EhMenuItem
          name={"Contact Support"}
          onClick={async () => {
            handleContactSupport(userEmail);
          }}
          isActive={false}
          icon={"help"}
        />
      )}
      <EhMenuItem
        name={"Log Out"}
        onClick={() => {
          const closeModal = showModal(
            <EhAlertModalMat
              heading="Are you sure you want to log out?"
              message="You will lose any unsaved changes."
              data-aq="log-out-alert-modal"
              buttons={[
                {
                  text: "No, I'm still working",
                  onClick: () => {
                    if (!signingOut) {
                      closeModal();
                    }
                  },
                },
                {
                  text: "Yes, log me out",
                  primary: true,
                  onClick: async () => {
                    if (!signingOut) {
                      setSigningOut(true);
                      await Session.end();
                    }
                  },
                },
              ]}
            />
          );
        }}
        isActive={false}
        icon={"sign-out alternate"}
      />
    </Fragment>
  );
});

export default EhStandardNavigationItems;
