import { map, each, values, indexOf } from "lodash";
import { MODULES } from "../constants";

function _parseClients(clients) {
  const clientList = map(clients, function (client) {
    if (client.isActive) {
      return {
        name: client.name,
        id: client.id,
      };
    }
  });
  return clientList;
}

function _parseLobs(clients) {
  const clientLobs = [];
  each(clients, function (client) {
    each(client.clientLobs, function (lob) {
      clientLobs.push({
        clientId: client.id,
        lobId: lob.id,
        lobName: lob.name,
        clientName: client.name,
      });
    });
  });

  return clientLobs;
}

export function normalizeUserClientData(clients) {
  // parse app clients and roles
  const userAppClients = {};
  const appClients = [];
  const roles = {};

  const moduleIds = values(MODULES);
  each(clients, function (client) {
    each(client.appModules, function (module) {
      if (module.isActive && indexOf(moduleIds, module.id) > -1) {
        appClients.push({
          clientId: client.id,
          clientName: client.name,
          moduleId: module.id,
          moduleName: module.name,
        });

        const moduleRoles = [];
        each(module.appModuleRoles, function (role) {
          if (role.isActive) {
            moduleRoles.push({
              roleId: role.roleId,
              roleName: role.roleName,
            });
          }
        });

        if (moduleRoles.length) {
          userAppClients[module.id] = userAppClients[module.id] || [];
          roles[module.id] = roles[module.id] || {};

          userAppClients[module.id].push({
            name: client.name,
            id: client.id,
            edwClientId: client.edwClientId,
          });
          roles[module.id][client.id] = moduleRoles;
        }
      }
    });
  });

  // parse client lobs and client flat list
  const allClients = _parseClients(clients);
  const clientLobs = _parseLobs(clients);

  const userData = {
    clients: allClients,
    userAppClients: userAppClients,
    roles: roles,
    clientLobs: clientLobs,
    appClients: appClients,
  };

  return userData;
}
