import Logger from "./logger";
import StringMask from "string-mask";

const stringMask = (maskPattern, value) => {
  return new StringMask(maskPattern).apply(value);
};

const searchToObject = function (search) {
  return search
    .substring(1)
    .split("&")
    .reduce(function (result, value) {
      const parts = value.split("=");
      if (parts[0])
        result[decodeURIComponent(parts[0])] = decodeURIComponent(parts[1]);
      return result;
    }, {});
};

const safePromise = async (promise, param) => {
  try {
    await (promise && promise(param));
  } catch (error) {
    Logger.error(error);
  }
};

const safeStringify = (obj) => {
  let stringified = "";
  try {
    stringified = JSON.stringify(obj);
  } catch (e) {
    stringified = JSON.stringify({ message: e.message });
  }
  return stringified;
};

const joinBy = ((joinBy) =>
  Object.assign(joinBy, {
    space: joinBy(" "),
    comma: joinBy(", "),
  }))(
  (joinBy) =>
    (...values) =>
      values.filter((v) => v).join(joinBy)
);

export default {
  searchToObject,
  safePromise,
  stringMask,
  safeStringify,
  joinBy,
};
