import { Fragment } from "react";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import PropTypes from "prop-types";
import { Spacing, Colors } from "../../style";
import EhForm from "../../components/form/EhForm";
import EhModalMat from "./EhModalMat";
import styled from "styled-components";

const ModalFooter = styled(DialogActions)`
  border-top: ${Spacing.borderWidthThin} solid ${Colors.grey[10]};
`;

const EhFormModalMat = ({
  loading,
  onAfterSave,
  onSave,
  initialValues,
  renderContent,
  renderFooter,
  renderHeader,
  ...ehModalMatProps
}) => {
  return (
    <EhModalMat noPadding renderHeader={renderHeader} {...ehModalMatProps}>
      <EhForm
        onSave={onSave}
        onAfterSave={onAfterSave}
        initialValues={initialValues}
        loading={loading}
      >
        {(form) => (
          <Fragment>
            <DialogContent>{renderContent(form)}</DialogContent>
            {renderFooter && <ModalFooter>{renderFooter(form)}</ModalFooter>}
          </Fragment>
        )}
      </EhForm>
    </EhModalMat>
  );
};

EhFormModalMat.propTypes = {
  loading: PropTypes.bool,
  onAfterSave: PropTypes.func,
  onSave: PropTypes.func,
  initialValues: PropTypes.object,
  renderContent: PropTypes.func,
  renderFooter: PropTypes.func,
  renderHeader: PropTypes.func,
  scroll: PropTypes.oneOf(["body", "paper"]),
};

export default EhFormModalMat;
