import styled from "styled-components";
import { Spacing, Colors } from "../../style";
import TextField from "@mui/material/TextField";
import InputMask from "react-input-mask";

const StyledTextField = styled(TextField)`
  &&&&& input {
    padding:0.3125rem,
    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus,
    &:-webkit-autofill:active &&&&& {
      box-shadow: ${Spacing.spacingNone} ${Spacing.spacingNone}
        ${Spacing.spacingNone} ${Spacing.spacingXxLarge} ${Colors.white} inset;
    }
  }
`;

const EhPhoneMat = (props) => {
  return (
    <InputMask mask="(999) 999-9999" maskChar={null} {...props}>
      {(inputProps) => <StyledTextField {...inputProps} />}
    </InputMask>
  );
};

export default EhPhoneMat;
