import { Fragment } from "react";
import PropTypes from "prop-types";
import { Icon, Label } from "semantic-ui-react";
import { isArray, isEmpty } from "lodash";
import styled from "styled-components";

import { EhShowMore } from "../componentsStd/layout/index";
import EhFlexBox from "../componentsStd/layout/EhFlexBox";
import { EhText } from "./typography";
import { Spacing, Colors, Elevation, Typography, LineHeight } from "./../style";
import EhButton from "./EhButton";

export const InlineText = styled(EhText)`
  display: inline;
  padding-left: ${Spacing.spacingXxSmall};
`;

const StyledIcon = styled(Icon)`
  padding-right: ${Spacing.spacingSmall};
`;

const StyledLabel = styled(Label)`
  &&&&& {
    margin: 0rem;
    margin-right: ${Spacing.spacingSmall};
    box-shadow: ${Elevation.boxShadowInsetThin};
    line-height: ${LineHeight.text.small};
    padding: ${Spacing.spacingXxSmall} ${Spacing.spacingSmall}
      ${Spacing.spacingXxSmall} ${Spacing.spacingXxSmall};
    font-size: ${Typography.fontSizeSmall};
    height: ${Spacing.spacingXLarge};
    color: ${Colors.grey[100]};
    background-color: ${Colors.grey[10]};
    border-radius: ${Spacing.borderRadiusSmall};
    i.icon {
      padding: 0;
      width: ${Spacing.spacingLarge};
      height: ${Spacing.spacingLarge};
      margin: 0;
      font-size: ${Typography.fontSizeSmall};
      opacity: 1;
    }
  }
`;

const StyledEhFlexBox = styled(EhFlexBox)`
  flex-wrap: wrap;
  width: 100%;
  border: thin solid ${Colors.grey[25]};
  border-radius: ${Spacing.borderRadiusLarge};
  padding: ${Spacing.spacingSmall} 0 ${Spacing.spacingSmall}
    ${Spacing.spacingSmall};
  margin-top: ${Spacing.spacingLarge};
`;

const removeOne = (filters, setFilters, index) => {
  return () =>
    setFilters([...filters.slice(0, index), ...filters.slice(index + 1)]);
};

const getFilterValue = (filter) => {
  let filterValue;
  if (filter.renderValue) {
    filterValue = filter.renderValue(filter.value);
  } else if (isArray(filter.value)) {
    filterValue = filter.value.join(",");
  } else {
    filterValue = filter.value;
  }
  return filterValue;
};

const getFilterContent = (filter) => {
  return `${filter.header}: ${getFilterValue(filter)}`;
};

const EhFilterPills = ({
  clearAll,
  ellipsis,
  filters,
  onSave,
  saveBtnText = "Save",
  setFilters,
  showMore = false,
  truncateLength = 5,
}) => {
  const filtersContent = filters.map((filter, index) => (
    <Label.Group key={index}>
      <StyledLabel>
        <StyledIcon
          data-aq={`icnClose${index}`}
          name="close"
          onClick={removeOne(filters, setFilters, index)}
        />
        <InlineText ellipsis={ellipsis}>{getFilterContent(filter)}</InlineText>
      </StyledLabel>
    </Label.Group>
  ));

  return (
    <Fragment>
      {!isEmpty(filters) && (
        <StyledEhFlexBox
          alignItems="baseline"
          justifyContent="space-between"
          flexWrap="wrap"
        >
          <EhFlexBox flexDirection="row" flexWrap="wrap">
            {showMore ? (
              <EhShowMore
                description={filtersContent}
                truncateLength={truncateLength}
              />
            ) : (
              filtersContent
            )}
          </EhFlexBox>
          <EhFlexBox>
            {clearAll && (
              <EhButton
                size="small"
                text="Clear"
                onClick={() => setFilters([])}
              />
            )}
            {onSave && (
              <EhButton size="small" text={saveBtnText} onClick={onSave} />
            )}
          </EhFlexBox>
        </StyledEhFlexBox>
      )}
    </Fragment>
  );
};

EhFilterPills.defaultProps = {
  ellipsis: true,
  clearAll: true,
};

EhFilterPills.propTypes = {
  /** The flag which determines if the filter value should display ellipsis when too long. */
  ellipsis: PropTypes.bool,
  /** The flag which determines Clear All option should be available */
  clearAll: PropTypes.bool,
  /** The object of filters with their value
   * {[filters]:{
   *  elementName:{
   *    value:{filterValue},
   *    description: {column header}
   *   }
   * }}
   */
  filters: PropTypes.any,
  /** A function for an optional save button */
  onSave: PropTypes.func,
  /** Button text for the optional save button */
  saveBtnText: PropTypes.string,
  /** The setFilters function to remove any one/all of the filters from the filters object. */
  setFilters: PropTypes.func.isRequired,
  showMore: PropTypes.bool,
  truncateLength: PropTypes.number,
};

export default EhFilterPills;
