import { Component } from "react";
import { find } from "lodash";

const showModalCalls = [];

const defaultShow = (newModal, type) => {
  const closeFunction = null;
  const modal = { newModal, type, closeFunction };
  showModalCalls.push(modal);
  return () => {
    modal.closeFunction();
  };
};

export let showModal = defaultShow;
export let isModalOpen = false;

export class EhGlobalModal extends Component {
  state = { modals: [] };

  componentDidMount() {
    showModal = this.showModal.bind(this);
    while (showModalCalls.length) {
      const current = showModalCalls.shift();
      current.closeFunction = showModal(current.newModal, current.type);
    }
  }

  componentWillUnmount() {
    showModal = defaultShow;
  }

  showModal(newModal, type) {
    let item;
    this.setState((state) => {
      if (type) {
        if (!find(state.modals, (modal) => modal.type === type)) {
          item = { component: newModal, type };
          return {
            modals: [...state.modals, item],
          };
        }
      } else {
        item = { component: newModal };
        return {
          modals: [...state.modals, item],
        };
      }
    });

    return () => {
      this.setState((state) => {
        const itemIndex = state.modals.indexOf(item);
        return {
          modals: [
            ...state.modals.slice(0, itemIndex),
            ...state.modals.slice(itemIndex + 1),
          ],
        };
      });
    };
  }

  render() {
    isModalOpen = this.state.modals.length > 0;
    return this.state.modals.map((modal, index) => ({
      ...modal.component,
      key: index,
    }));
  }
}
