import { Component } from "react";
import { AppContainer } from "react-hot-loader";
import { Provider } from "react-redux";
import PropTypes from "prop-types";
import { Logger, LOGGER_ENTRY_SOURCES } from "@evolent/roots";

class EhAppRoot extends Component {
  static getDerivedStateFromError(/* err */) {
    // needed to supress runtime warnings about this not being implemented even though we don't need it
  }

  componentDidCatch(error, info) {
    Logger.error(
      {
        error: error,
        message: error.message,
        stackTrace: error.stack,
        info,
      },
      LOGGER_ENTRY_SOURCES.COMPONENT_DID_CATCH
    );
  }

  render() {
    return (
      <AppContainer>
        <Provider store={this.props.store}>{this.props.children}</Provider>
      </AppContainer>
    );
  }
}

EhAppRoot.propTypes = {
  store: PropTypes.object.isRequired,
  children: PropTypes.element.isRequired,
};

export default EhAppRoot;
