import { PureComponent } from "react";
import { Accordion, Icon } from "semantic-ui-react";
import { omit, isNil, isEqual } from "lodash";
import EhText from "../typography/EhText";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";
import PropTypes from "prop-types";
import styled, { css } from "styled-components";
import { Spacing, Colors } from "../../style";

const StyledTitle = styled(({ bordered, active, ...rest }) => (
  <Accordion.Title active={active} {...rest} />
))`
  &&&& {
    ${({ bordered, active }) =>
      bordered &&
      css`
        padding: ${Spacing.spacingXSmall} ${Spacing.spacingMedium}
          ${Spacing.spacingXSmall} ${Spacing.spacingLarge};
        margin-bottom: ${Spacing.spacingLarge};
        border: ${Spacing.borderWidthThin} solid ${Colors.blue[25]};
        border-radius: ${Spacing.borderRadiusLarge};

        ${active &&
        css`
          margin-bottom: 0;
          border-bottom: none;
          border-radius: ${Spacing.borderRadiusLarge}
            ${Spacing.borderRadiusLarge} 0 0;
        `}
      `}
  }
`;

const StyledDiv = styled(({ bordered, ...rest }) => <div {...rest} />)`
  padding-left: ${Spacing.spacingXxLarge};
  margin-top: -${Spacing.spacingLarge};

  ${({ bordered }) =>
    bordered &&
    css`
      padding: 0 ${Spacing.spacingLarge} ${Spacing.spacingLarge};
      margin: -${Spacing.spacingSmall} 0 0;
      border: ${Spacing.borderWidthThin} solid ${Colors.blue[25]};
      border-top: none;
      border-radius: 0 0 ${Spacing.borderRadiusLarge}
        ${Spacing.borderRadiusLarge};
    `}
`;

const StyledIcon = styled(Icon)`
  &&&& {
    margin: 0;
    line-height: 1;
  }
`;

export default class EhAccordion extends PureComponent {
  static propTypes = {
    items: PropTypes.arrayOf(
      PropTypes.shape({
        title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
        content: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
      })
    ),
    exclusive: PropTypes.bool,
    defaultActiveIndex: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.number),
      PropTypes.number,
    ]),
    onClick: PropTypes.func,
    iconPosition: PropTypes.oneOf(["left", "right"]),
    iconSize: PropTypes.oneOf([
      "mini",
      "tiny",
      "small",
      "default",
      "large",
      "big",
      "huge",
      "massive",
    ]),
    bordered: PropTypes.bool,
    // it will keep index open
    keepActive: PropTypes.bool,
  };

  static defaultProps = {
    exclusive: true,
    iconPosition: "left",
  };

  state = {
    activeItemIndexes: [],
  };

  getActiveItemIndexes = () => {
    const { defaultActiveIndex } = this.props;
    const activeItemIndexes = !isNil(defaultActiveIndex)
      ? [].concat(defaultActiveIndex)
      : [];

    this.setState({ activeItemIndexes });
  };
  componentDidMount() {
    this.getActiveItemIndexes();
  }

  componentDidUpdate(prevProps) {
    const { defaultActiveIndex } = this.props;
    if (!isEqual(defaultActiveIndex, prevProps.defaultActiveIndex)) {
      this.getActiveItemIndexes();
    }
  }

  handleClick = (e, { index }) => {
    const { onClick, exclusive, keepActive } = this.props;

    let activeItemIndexes = [...this.state.activeItemIndexes];
    if (!keepActive) {
      const ind = activeItemIndexes.indexOf(index);
      if (ind === -1) {
        exclusive
          ? (activeItemIndexes = [index])
          : activeItemIndexes.push(index);
      } else {
        exclusive ? (activeItemIndexes = []) : activeItemIndexes.splice(ind, 1);
      }
      this.setState({ activeItemIndexes });
    }
    // eslint-disable-next-line no-unused-expressions
    onClick?.(index, activeItemIndexes);
  };

  getItems = () => {
    const { items, iconPosition, bordered, iconSize } = this.props;
    const { activeItemIndexes } = this.state;
    const isIconPositionRight = iconPosition === "right";
    return (
      items &&
      items.map(({ title, content }, index) => ({
        key: `item-${index}`,
        title: (
          <StyledTitle
            data-aq={`${index}-accordion-title`}
            bordered={bordered}
            active={activeItemIndexes.includes(index)}
          >
            <EhFlexBox
              alignItems="center"
              justifyContent={isIconPositionRight ? "space-between" : "left"}
            >
              {!isIconPositionRight && <Icon name="dropdown" color="blue" />}
              {typeof title === "string" ? (
                <EhText fontWeight="bold" color="blue" size="medium">
                  {title}
                </EhText>
              ) : (
                title
              )}
              {isIconPositionRight && (
                <StyledIcon
                  name={`angle ${
                    activeItemIndexes.includes(index) ? "up" : "down"
                  }`}
                  color="blue"
                  size={iconSize}
                />
              )}
            </EhFlexBox>
          </StyledTitle>
        ),
        content: (
          <Accordion.Content active={activeItemIndexes.includes(index)}>
            <StyledDiv
              data-aq={`${index}-accordion-content`}
              bordered={bordered}
            >
              {content}
            </StyledDiv>
          </Accordion.Content>
        ),
      }))
    );
  };

  render() {
    return (
      <Accordion
        data-aq="EhAccordion"
        panels={this.getItems()}
        onTitleClick={this.handleClick}
        fluid
        {...omit(this.props, [
          "iconPosition",
          "iconSize",
          "bordered",
          "onClick",
        ])}
      />
    );
  }
}
