import styled, { css } from "styled-components";
import { Table } from "semantic-ui-react";
import { Typography, Spacing, Colors, LineHeight } from "../../style";
import get from "lodash/get";
import is from "styled-is";

const DIRECTION_PROPS = {
  left: { childSelector: "nth-child", border: "border-right" },
  right: { childSelector: "nth-last-child", border: "border-left" },
};

const stickyStyle = ({ position, widthString, zIndex }) =>
  `position: sticky;
  ${position}: calc(${widthString});
  background-color:${Colors.white};
  z-index: ${zIndex};
  `;
const applyStickyStyle = ({ position, direction, count }) =>
  (position === direction || position === "both") && count > 0;

const getStickyStyleString = ({
  columnsdata,
  direction,
  count,
  zIndex,
  footer,
  striped,
}) => {
  const { childSelector, border } = DIRECTION_PROPS[direction];
  let stickyStyleStr = ``;
  let prevWidth = `0px`;
  for (let i = 0; i < count; i++) {
    const fixedWidth = columnsdata[i - 1]?.fixedWidth || "0px";
    prevWidth = `${prevWidth} +  ${fixedWidth}`;
    const styles = stickyStyle({
      position: direction,
      widthString: prevWidth,
      zIndex,
    });
    stickyStyleStr = `${stickyStyleStr}
           tr td:${childSelector}(${i + 1}) {
            ${styles}
          }
          tr th:${childSelector}(${i + 1}) {
            ${styles}
            z-index: 10;          
          }`;
  }

  stickyStyleStr = `${stickyStyleStr}
      tr td:${childSelector}(${count}),tr th:${childSelector}(${count}) {
        ${border}: 2px solid rgb(221, 221, 223);
       }`;
  if (footer) {
    stickyStyleStr = `${stickyStyleStr}
    tbody {
       tr:last-child {
         td{
          position: sticky;
          bottom: 0;
         }
        background-color: ${striped ? "inherit" : Colors.white};
        z-index: 10;
      }
    }`;
  }

  return stickyStyleStr;
};

const stickyColumnsStyle = ({ columnsdata, stickyarea, striped }) => {
  if (!columnsdata || !stickyarea) return "";
  const headers = get(stickyarea, "headers", false);
  const columns = get(stickyarea, "columns", {});
  const position = get(columns, "position", "");
  const leftCount = get(columns, "leftCount", 0);
  const rightCount = get(columns, "rightCount", 0);
  const zIndex = get(stickyarea, "zIndex", "auto");
  const footer = get(stickyarea, "footer", false);
  let str = "";

  if (leftCount > columnsdata?.length || rightCount > columnsdata?.length)
    return "";

  if (headers) {
    str = `${str}
     th {
       position:sticky;
       top:0;
     } 
    `;
  }

  if (
    applyStickyStyle({
      position: position,
      direction: "left",
      count: leftCount,
    })
  ) {
    str = `${str} ${getStickyStyleString({
      columnsdata: columnsdata,
      direction: "left",
      count: leftCount,
      zIndex,
      footer,
      striped,
    })}`;
  }

  if (
    applyStickyStyle({
      position: position,
      direction: "right",
      count: rightCount,
    })
  ) {
    str = `${str} ${getStickyStyleString({
      columnsdata: columnsdata.reverse(),
      direction: "right",
      count: rightCount,
      zIndex,
      footer,
      striped,
    })}`;
  }
  return str;
};

const stripedTableStyle = ({ striped }) => {
  return striped
    ? ` tr:nth-child(odd) td {
    background: ${Colors.grey[5]};
  }
  tr:nth-child(even) td {
    background: ${Colors.white};
  }`
    : "";
};

export const EhTableHeaderCell = styled(Table.HeaderCell)`
  &&&&& {
    color: ${Colors.black};
    border-left: ${Spacing.spacingNone};
    padding-left: ${Spacing.spacingMedium};
    padding-top: ${Spacing.spacingSmall};
    padding-bottom: ${Spacing.spacingSmall};
    white-space: pre-wrap;
    text-overflow: clip;
    font-size: ${Typography.fontSizeSmall};
    .sortable.table > thead & {
      &:not(.ascending):not(.descending):not(.nosort):after {
        display: inline-block;
        content: "\\f0dc";
      }
      &:after {
        margin-left: ${Spacing.spacingSmall};
        font-size: ${Typography.fontSizeSmall};
        height: ${Spacing.spacingXLarge};
      }
      &.nosort {
        cursor: text;
      }
      &:last-child {
        padding-right: ${Spacing.spacingLarge};
      }
    }
    ${is("collapsed")`
      display:none;
    `}
    ${(props) =>
      props.fixedwidth &&
      css`
        width: ${props.fixedwidth};
      `}
    z-index: 8;
  }
`;

export const EhTableCell = styled(Table.Cell)`
  &&&& {
    vertical-align: center;
    font-size: ${Typography.fontSizeSmall};
    overflow: visible;
    word-break: break-all; /* IE doesn't support break-word*/
    word-break: break-word;
    height: 3.0625rem;
    padding-left: ${Spacing.spacingMedium};
    .textMat {
      font-size: ${Typography.fontSizeSmall};
      line-height: ${LineHeight.text.small};
    }
    ${is("collapsed")`
    display:none;
  `}
    &:last-child {
      padding-right: ${Spacing.spacingLarge};
    }
  }
`;

export const EhTable = styled(Table)`
  &&&&& {
    ${(props) => stickyColumnsStyle(props)}
    ${(props) => stripedTableStyle(props)}
  }
`;

EhTable.Cell = EhTableCell;
EhTable.HeaderCell = EhTableHeaderCell;
