import { PureComponent } from "react";
import PropTypes from "prop-types";
/**
 * This component is referring to
 * https://reactdatepicker.com/
 */
import DatePicker from "react-datepicker";
import styled from "styled-components";
import { pick, omit, trim } from "lodash";
import { DateHandler } from "@evolent/roots";
import EhInput from "./EhInput";
import { EhText } from "../typography";
import {
  Colors,
  Typography,
  Spacing,
  Elevation,
  LineHeight,
} from "../../style";

import "react-datepicker/dist/react-datepicker.css";
import StringMask from "string-mask";

const StyledContainer = styled.div`
  &&&& {
    .react-datepicker-wrapper {
      width: 100%;
    }

    .react-datepicker {
      border: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
      border-radius: ${Spacing.borderRadiusLarge};
      background-color: ${Colors.white};
      box-shadow: ${Elevation.boxShadowMedium};
      font-family: ${Typography.fontFamily};

      .react-datepicker__triangle {
        display: none;
      }

      .react-datepicker__navigation--previous {
        border-right-color: ${Colors.blue[100]};
        margin: ${Spacing.spacingMedium} 0 0 ${Spacing.spacingMedium};
      }
      .react-datepicker__navigation--next {
        border-left-color: ${Colors.blue[100]};
        margin: ${Spacing.spacingMedium} ${Spacing.spacingMedium} 0 0;
      }
      .react-datepicker__navigation--next--with-time:not(.react-datepicker__navigation--next--with-today-button) {
        right: 7.5rem;
      }

      .react-datepicker__header {
        background-color: ${Colors.white};
        padding: 0;
        border: none;

        .react-datepicker__current-month {
          padding: ${Spacing.spacingLarge} 0 ${Spacing.spacingSmall};
          font-size: ${Typography.fontSizeMedium};
          color: ${Colors.grey[100]};
          font-weight: ${Typography.fontWeightBold};
        }

        .react-datepicker__day-names {
          padding-top: ${Spacing.spacingLarge};
          margin-top: ${Spacing.spacingSmall};
          border-top: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
        }
      }

      .react-datepicker__month {
        margin: 0 ${Spacing.spacingXLarge} ${Spacing.spacingLarge};
      }

      .react-datepicker__day-name {
        font-size: ${Typography.fontSizeSmall};
        color: ${Colors.grey[100]};
        font-weight: ${Typography.fontWeightRegular};
        height: ${Spacing.spacingXxLarge};
        width: ${Spacing.spacingXxLarge};
        line-height: ${Spacing.spacingLarge};
        padding: ${Spacing.spacingSmall};
        margin: 0;
      }

      .react-datepicker__day {
        font-size: ${Typography.fontSizeXSmall};
        color: ${Colors.blue[100]};
        font-weight: ${Typography.fontWeightBold};
        height: ${Spacing.spacingXxLarge};
        width: ${Spacing.spacingXxLarge};
        line-height: ${Spacing.spacingLarge};
        padding: ${Spacing.spacingSmall};
        margin: 0;
      }

      .react-datepicker__navigation--previous:focus,
      .react-datepicker__navigation--next:focus,
      .react-datepicker__day:focus {
        outline: none;
      }

      .react-datepicker__time-list-item:hover,
      .react-datepicker__day:hover {
        border-radius: unset;
        background-color: ${Colors.grey[5]};
      }

      .react-datepicker__day.react-datepicker__day--outside-month {
        font-weight: ${Typography.fontWeightRegular};
      }

      .react-datepicker__day--today {
        background-color: ${Colors.white};
        border: ${Spacing.borderWidthThin} solid ${Colors.blue[100]};
        border-radius: unset;
      }

      .react-datepicker__time-list-item.react-datepicker__time-list-item--selected,
      .react-datepicker__day.react-datepicker__day--selected {
        background-color: ${Colors.blue[25]};
        color: ${Colors.grey[100]};
        border-radius: unset;
      }

      .react-datepicker__time-list-item.react-datepicker__time-list-item--disabled,
      .react-datepicker__day.react-datepicker__day--disabled {
        color: ${Colors.grey[55]};
      }

      .react-datepicker__time-list-item.react-datepicker__time-list-item--disabled:hover,
      .react-datepicker__day.react-datepicker__day--disabled:hover {
        background-color: ${Colors.white};
      }

      .react-datepicker__today-button {
        border: none;
        background-color: ${Colors.white};
        color: ${Colors.blue[100]};
        margin: 0 ${Spacing.spacingXLarge} ${Spacing.spacingXLarge};
        font-size: ${Typography.fontSizeXSmall};
        line-height: ${LineHeight.text.small};
        border-radius: ${Spacing.borderRadiusSmall};
        padding: ${Spacing.spacingXxSmall} ${Spacing.spacingXSmall};
        min-width: 3rem;
        box-shadow: ${Elevation.boxShadowInsetThin},
          ${Elevation.boxShadowXSmall}, 0 0 0 1px ${Colors.blue[100]} inset !important;
      }

      .react-datepicker__today-button:hover {
        background-color: ${Colors.grey[5]};
      }

      .react-datepicker__time-container,
      .react-datepicker__time-container--with-today-button {
        right: -6.875rem;
        width: 6.875rem;
        top: -1px;
        border-radius: ${Spacing.borderRadiusLarge};
        border-left: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
        box-shadow: 3px 4px 6px 0px rgba(0, 0, 0, 0.16);
      }

      .react-datepicker__time-container--with-today-button {
        .react-datepicker__time-list {
          height: 17.0625rem !important;
        }
      }
      .react-datepicker__time-container
        .react-datepicker__time
        .react-datepicker__time-box {
        width: 6.875rem;
      }

      .react-datepicker-time__header {
        font-size: ${Typography.fontSizeMedium};
        color: ${Colors.grey[100]};
        font-weight: ${Typography.fontWeightBold};
        padding: 1.125rem ${Spacing.spacingLarge};
        line-height: ${LineHeight.heading.small};
        border-bottom: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
      }

      .react-datepicker__time-list-item {
        font-size: ${Typography.fontSizeXSmall};
        color: ${Colors.blue[100]};
        font-weight: ${Typography.fontWeightBold};
        line-height: ${Spacing.spacingXxLarge};
      }

      .react-datepicker__day--keyboard-selected,
      .react-datepicker__month-text--keyboard-selected,
      .react-datepicker__quarter-text--keyboard-selected {
        border-radius: inherit;
        background-color: inherit;
      }
    }
    .react-datepicker-popper {
      z-index: 20;
    }
    .react-datepicker-popper[data-placement^="bottom"] {
      margin-top: ${Spacing.spacingXxSmall};
    }
    .react-datepicker-popper[data-placement^="top"] {
      margin-bottom: ${Spacing.spacingXxSmall};
    }
  }
`;
const PRIMARY_DATE_FORMAT = "MM/DD/YYYY";

class EhDateTimePicker extends PureComponent {
  state = {
    openPicker: false,
    manualDateEntry: false,
  };

  static propTypes = {
    /** Format for Date */
    dateFormat: PropTypes.string,
    /** Format for Time */
    timeFormat: PropTypes.string,
    /** DateTime will be fetched as per the valueFormat */
    valueFormat: PropTypes.string,
    /** The value displayed when not in editMode */
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    name: PropTypes.any,
    /** The flag which determines if the component should be in the edit or display state. */
    editMode: PropTypes.bool,
    /** The flag which determines if the text should wrap when too long. */
    wrapText: PropTypes.bool,
    /** The flag which governs react date picker to have strictParsing. */
    strictParsing: PropTypes.bool,
    /** The flag which will set the component in the error state when attempting to move forward without completion. */
    required: PropTypes.bool,
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    /** User cannot select a date higher than the provided maxDate */
    maxDate: PropTypes.any,
    /** User cannot select a date lower than the provided minDate */
    minDate: PropTypes.any,
    /** This will give suggestion */
    autoComplete: PropTypes.string,
    /** It will show Time selctor if set to true */
    showTimeSelect: PropTypes.bool,
    /** Label for Time in the DateTimePicker */
    timeCaption: PropTypes.string,
    /** Time Intervals in date time picker */
    timeIntervals: PropTypes.number,
    /** If provided string, it will dispay today button with this text */
    todayButton: PropTypes.string,
    "data-aq": PropTypes.string,
    showYearDropdown: PropTypes.bool,
    /** The flag which determines whether to display year DropDown. */
    showMonthDropdown: PropTypes.bool,
    /** The flag which determines  to display month DropDown. */
    placeholderText: PropTypes.string,
    /** The function to execute on focus of date field */
    onFocus: PropTypes.func,
    /** The function to execute on blur of date field */
    onBlur: PropTypes.func,
  };

  static defaultProps = {
    value: "",
    editMode: true,
    wrapText: true,
    strictParsing: true,
    dateFormat: "MM/dd/yyyy",
    timeFormat: "hh:mm:ss a",
    timeCaption: "Time",
    timeIntervals: 15,
    valueFormat: "MM/DD/YYYY",
    todayButton: "Today's Date",
    autoComplete: "off",
    showYearDropdown: false,
    showMonthDropdown: false,
    minDate: new Date("1900-01-01"),
  };

  getFormatedValue = () => {
    return DateHandler.formatDate(this.props.value, this.props.valueFormat);
  };
  handleChange = (date) => {
    this.props.onChange(DateHandler.formatDate(date, this.props.valueFormat));
  };

  clearDateField = (e) => {
    e.target.value = null;
    e.preventDefault();
    this.setState({ openPicker: true });
  };
  handlePaste = (e) => {
    if (this.props.valueFormat === PRIMARY_DATE_FORMAT) {
      const copiedValue = e.clipboardData.getData("Text");
      const existingText = e.target.value;
      const selectStart = e.target.selectionStart;
      const selectEnd = e.target.selectionEnd;
      const unSelectedTextStart = existingText.substring(0, selectStart);
      const unSelectedTextEnd = existingText.substring(
        selectEnd,
        existingText.length
      );
      const valueToCompare = trim(
        `${unSelectedTextStart}${copiedValue}${unSelectedTextEnd}`
      );
      if (!DateHandler.isValid(valueToCompare, PRIMARY_DATE_FORMAT)) {
        e.preventDefault();
      } else if (
        DateHandler.isBefore(valueToCompare, this.props.minDate) ||
        DateHandler.isAfter(valueToCompare, this.props.maxDate)
      ) {
        e.preventDefault();
      }
    }
  };

  maskDate = (e) => {
    this.setState({ manualDateEntry: true });
    if (this.props.valueFormat === "MM/DD/YYYY") {
      const value = e.target.value?.replaceAll("/", "");
      const PRIMARY_DATE_FORMAT = "MM/DD/YYYY";
      const keysToInclude = [
        "Delete",
        "Backspace",
        "Enter",
        "Tab",
        "ArrowLeft",
        "ArrowRight",
      ];
      const charCode = String.fromCharCode(e.which).toLowerCase();
      if (
        (e.ctrlKey || e.metaKey) &&
        ["x", "a", "c", "z", "v"].includes(charCode)
      )
        return;

      if (!(/[0-9]/.test(e.key) || keysToInclude.includes(e.key))) {
        e.preventDefault();
      } else {
        let enteredDate;
        if (/[0-9]/.test(e.key)) {
          if (value.length < 2) {
            e.target.value = new StringMask("99").apply(value);
          } else if (value.length === 2) {
            e.target.value = new StringMask("99/99").apply(value);
          } else if (value.length === 4) {
            e.target.value = new StringMask("99/99/9999").apply(value);
          } else if (value?.length === 7) {
            enteredDate = new StringMask("99/99/9999").apply(value + e.key);
            if (!DateHandler.isValid(enteredDate, PRIMARY_DATE_FORMAT)) {
              this.clearDateField(e);
            } else if (
              DateHandler.isBefore(enteredDate, this.props.minDate) ||
              DateHandler.isAfter(enteredDate, this.props.maxDate)
            ) {
              this.clearDateField(e);
            }
          } else if (value?.length === 8) {
            e.target.value = e.key;
            e.preventDefault();
          }
        } else if (e.key === "Tab") {
          enteredDate = new StringMask("99/99/9999").apply(value + e.key);
          if (!DateHandler.isValid(enteredDate, PRIMARY_DATE_FORMAT))
            e.target.value = null;
          this.setState({ openPicker: false });
        }
      }
    }
  };

  render() {
    const {
      required,
      editMode,
      strictParsing,
      wrapText,
      value,
      onFocus,
      onBlur,
      ...props
    } = omit(this.props, ["onChange", "fluid", "data-aq"]);
    if (editMode) {
      return (
        <StyledContainer data-aq={this.props["data-aq"]}>
          <DatePicker
            customInput={
              <EhInput
                {...pick(this.props, ["value", "placeholder", "fluid"])}
                icon="calendar alternate"
                onPaste={this.handlePaste}
              />
            }
            selected={value ? new Date(value) : value}
            onChange={this.handleChange}
            strictParsing={strictParsing}
            popperPlacement="bottom-start"
            onKeyDown={(e) => this.maskDate(e)}
            onFocus={() => {
              onFocus && onFocus();
              this.setState({ openPicker: true });
            }}
            onClickOutside={() => this.setState({ openPicker: false })}
            onSelect={(value) => {
              if (value !== null) {
                this.state.manualDateEntry
                  ? this.setState({ manualDateEntry: false })
                  : this.setState({ openPicker: false });
              } else {
                this.setState({ manualDateEntry: false });
              }
            }}
            onBlur={(e) => {
              onBlur && onBlur(e);
              this.setState({ openPicker: false });
            }}
            open={this.state.openPicker}
            showTimeInput={!this.state.openPicker}
            {...props}
          />
        </StyledContainer>
      );
    } else {
      return (
        <EhText wrapText={this.props.wrapText}>
          {this.getFormatedValue()}
        </EhText>
      );
    }
  }
}

export default EhDateTimePicker;
