import PropTypes from "prop-types";
import { EhPopup } from "../../components";
import { Colors, LineHeight, Spacing, Typography } from "../../style";
import styled from "styled-components";

const sizeStyle = {
  large: {
    fontSize: Typography.fontSizeLarge,
    lineHeight: LineHeight.text.large,
  },
  medium: {
    fontSize: Typography.fontSizeMedium,
    lineHeight: LineHeight.text.normal,
  },
  "medium-condensed": {
    fontSize: Typography.fontSizeMedium,
    lineHeight: LineHeight.text.small,
  },
  small: {
    fontSize: Typography.fontSizeSmall,
    lineHeight: LineHeight.text.small,
  },
  xsmall: {
    fontSize: Typography.fontSizeXSmall,
    lineHeight: LineHeight.text.small,
  },
  standard: {
    fontSize: Typography.fontSizeStandard,
    lineHeight: LineHeight.text.small,
  },
};

const fontWeightStyle = {
  normal: Typography.fontWeightRegular,
  bold: Typography.fontWeightBold,
  black: Typography.fontWeightBlack,
};
/* eslint-disable */
const Div = ({ "data-aq": dataAq, children, className, onClick, title }) => (
  <div className={className} onClick={onClick} data-aq={dataAq} title={title}>
    {children}
  </div>
);
/* eslint-enable */

const lineCountPropValidation = (props, propName, componentName) => {
  componentName = componentName || "ANONYMOUS";
  if (props[propName] && !props.ellipsis) {
    return new Error(
      `ellipsis prop should be passed if ${propName} prop is passed in ${componentName}`
    );
  }
  return null;
};

const getColor = (props) => {
  const { color, shade } = props;
  let colorVal = "inherit";
  if (color && color !== "inherit") {
    if (color === "black" || color === "white") {
      colorVal = Colors[color];
    } else if (shade) {
      colorVal = Colors[color][shade];
    }
  }
  return `${colorVal} !important`;
};

const wrapText = (props) =>
  props.wrapText ? { wordWrap: "break-word" } : { whiteSpace: "nowrap" };

const ellipsis = (props) =>
  props.ellipsis ? { overflow: "hidden", textOverflow: "ellipsis" } : {};

const lineCount = (props) =>
  props.lineCount && props.ellipsis
    ? {
        whiteSpace: "inherit",
        display: "-webkit-box",
        WebkitBoxOrient: "vertical",
        WebkitLineClamp: props.lineCount,
      }
    : {};

const Text = styled((props) => <Div {...props} />)((props) => ({
  display: props.display,
  color: getColor(props),
  ...sizeStyle[props.size],
  textAlign: props.textAlign,
  fontWeight: fontWeightStyle[props.fontWeight],
  fontStyle: props.fontStyle,
  ...wrapText(props),
  ...ellipsis(props),
  ...lineCount(props),
  wordBreak: props.wordBreak,
  textTransform: props.textTransform || "none",
  "&:not(:first-child)": {
    marginTop: Spacing.spacingXxSmall,
  },
}));

const getClassName = (className) => (className ? ` ${className}` : "");

export const EhTextMat = (props) => {
  const { popUpHeader, popUpData } = props;
  const getTitle = () => {
    const { ellipsis, showTitle, children } = props;
    return ellipsis && showTitle ? children : undefined;
  };

  const renderData = () => (
    <Text
      {...props}
      className={`textMat${getClassName(props.className)}`}
      title={getTitle()}
      wrapText={!props.ellipsis && props.wrapText}
    />
  );

  const triggerMethod = () => <div>{renderData()}</div>;
  return popUpData ? (
    <EhPopup
      trigger={triggerMethod}
      size="small"
      content={popUpData}
      header={popUpHeader}
    />
  ) : (
    renderData()
  );
};

/* eslint-disable */
EhTextMat.propTypes = {
  /** The size of the text. */
  size: PropTypes.oneOf([
    "large",
    "medium",
    "medium-condensed",
    "small",
    "xsmall",
    "standard",
  ]),
  /** The alignment of the text. */
  textAlign: PropTypes.oneOf(["left", "center", "right", "justify"]),
  /** The weight of the text. */
  fontWeight: PropTypes.oneOf(["normal", "bold", "black"]),
  /** The style of the text. */
  fontStyle: PropTypes.oneOf(["italic", "normal"]),
  /**  sets how the line breaks should appear. */
  wordBreak: PropTypes.oneOf(["normal", "break-all", "keep-all", "break-word"]),
  /** The flag which determines if the text should wrap when too long. */
  wrapText: PropTypes.bool,
  /** The flag which determines if the text should display ellipsis when too long. */
  ellipsis: PropTypes.bool,
  /** The number of lines after which ellipsis should be displayed */
  lineCount: lineCountPropValidation,
  /** The color of the text. */
  color: PropTypes.string,
  /** The value of the text */
  children: PropTypes.any,
  /** The value of the text displaying on popUp */
  popUpData: PropTypes.any,
  /** The value of the text displaying on popUp header */
  popUpHeader: PropTypes.string,
  /** The flag which determines if the title on ellipsis should be shown or not */
  showTitle: PropTypes.bool,
  /** a number value indicating the shade of the color to use (available shades vary by color) */
  shade: PropTypes.number,
  /** display inline with another element */
  display: PropTypes.oneOf(["none", "inline", "block", "inline-block"]),
  /** The flag which determines if the text should wrap when too long. */
  textTransform: PropTypes.oneOf(["capitalize", "lowercase", "uppercase"]),
  /** This Prop is needed if we want to style EhTextMat component  */
  className: PropTypes.string,
};
/* eslint-enable */

EhTextMat.defaultProps = {
  showTitle: true,
  wrapText: true,
  size: "medium",
  color: "grey",
  shade: 100,
};
