import { Component } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { flowRight } from "lodash";

import { Spacing } from "../../style";
import asField from "./hoc/asField";
import EhMaskedInput from "./EhMaskedInput";
import { Validators } from "@evolent/roots";

const PhoneInput = flowRight(asField)(styled(EhMaskedInput)`
  width: 8.5rem !important;
  margin-right: calc(${Spacing.spacingSmall} + 10rem);
`);

const PhoneExtInput = flowRight(asField)(styled(EhMaskedInput)`
  width: 5rem !important;
  margin-left: -10rem;
`);

const PhoneInputContainer = styled.div`
  display: flex;
`;

class EhPhoneComposite extends Component {
  static propTypes = {
    name: PropTypes.string,
    hideExt: PropTypes.bool,
  };

  render() {
    const { name, hideExt } = this.props;
    return (
      <PhoneInputContainer>
        <PhoneInput
          name={`${name}.num`}
          mask="(999) 999-9999"
          data-aq={`txtPh-${name}`}
        />
        {!hideExt && (
          <PhoneExtInput
            hideNotAvailable
            name={`${name}.ext`}
            mask="x999999"
            data-aq={`txtExt-${name}`}
          />
        )}
      </PhoneInputContainer>
    );
  }
}

const EhPhoneCompositeField = asField(EhPhoneComposite);

const EhPhoneInput = ({ fieldLabel, name, required, hideExt }) => {
  const validatePhone = (value) => {
    if (required && !value.num) return "This field is required";
    return Validators.phone(value);
  };
  return (
    <EhPhoneCompositeField
      fieldLabel={fieldLabel}
      name={name}
      required={required}
      validate={validatePhone}
      hideExt={hideExt}
    />
  );
};

EhPhoneInput.propTypes = {
  fieldLabel: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  required: PropTypes.bool,
  hideExt: PropTypes.bool,
};

EhPhoneInput.displayName = "EhPhone";

export default EhPhoneInput;
