// ------------------------------------
// Constants
// ------------------------------------
const OPEN_ALERT = "OPEN_ALERT";
const RESET_ALERT = "RESET_ALERT";

// ------------------------------------
// Reducer
// ------------------------------------

const ACTION_HANDLERS = {
  [OPEN_ALERT]: openAlert,
  [RESET_ALERT]: resetAlert,
};
const initialState = { alertData: null };
export default function createNotifyReducer(state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type];
  return handler ? handler(state, action) : state;
}

function openAlert(state, action) {
  return {
    ...state,
    alertData: { ...action.data },
  };
}

function resetAlert() {
  return initialState;
}
// ------------------------------------
// Action Creators
// ------------------------------------

export function openAlertAction(data) {
  return {
    type: OPEN_ALERT,
    data,
  };
}

export function resetAlertAction() {
  return { type: RESET_ALERT };
}
