/* eslint-disable no-unused-expressions */
import {
  ApplicationInsights,
  SeverityLevel,
} from "@microsoft/applicationinsights-web";
import DateHandler from "./js-utils/dateHandler";
import { MODULES, APP_SETTINGS } from "./constants";

let appInsights;

const captureMemory = () => {
  const memory = window?.performance?.memory;
  return {
    jsHeapSizeLimit: (memory.jsHeapSizeLimit / (1024 * 1024)).toFixed(2),
    totalJSHeapSize: (memory.totalJSHeapSize / (1024 * 1024)).toFixed(2),
    usedJSHeapSize: (memory.usedJSHeapSize / (1024 * 1024)).toFixed(2),
  };
};

const setUpAppInsights = ({
  history,
  reactPlugin,
  config = {},
  getTelemetryItemProps,
}) => {
  if (APP_SETTINGS.applicationInsightInstrumentationId) {
    const SPA_ID = Math.floor(Math.random() * 90000000) + 10000000;
    const SPA_INITIALIZED = DateHandler.now();

    const reactExtConfig = reactPlugin
      ? {
          extensions: [reactPlugin],
          extensionConfig: {
            [reactPlugin.identifier]: { history },
          },
        }
      : {};
    appInsights = new ApplicationInsights({
      config: {
        instrumentationKey: APP_SETTINGS.applicationInsightInstrumentationId,
        ...reactExtConfig,
        ...config,
      },
    });
    appInsights.loadAppInsights();
    appInsights.addTelemetryInitializer((envelope) => {
      // condition added to prevent default page view track
      if (envelope.baseType === "PageviewData") {
        return false;
      }

      const telemetryItemProps = getTelemetryItemProps?.() || {};

      const telemetryItem = envelope.baseData;
      telemetryItem.properties = {
        ...(telemetryItem.properties || {}),
        Application: APP_SETTINGS.moduleId,
        spaInstanceId: SPA_ID,
        memory: captureMemory(),
        secondsSinceInit: DateHandler.diff(
          DateHandler.now(),
          SPA_INITIALIZED,
          DateHandler.DATE_UNIT.second
        ),
        usedHeapSize: window?.performance?.memory?.usedJSHeapSize,
        "Render Time (ms)": JSON.stringify(window.renderMetrics),
        ...telemetryItemProps,
      };

      envelope.tags["ai.operation.name"] = Object.keys(MODULES).find(
        (key) => MODULES[key] === APP_SETTINGS.moduleId
      );

      window.renderMetrics = {};
    });
  }
};

const setAuthId = (authId) => {
  appInsights?.setAuthenticatedUserContext(authId, "", true);
};

const clearAuthId = () => {
  appInsights?.clearAuthenticatedUserContext();
};

const logMetric = (
  name,
  average,
  sampleCount,
  min,
  max,
  additionalProperties
) => {
  appInsights?.trackMetric({
    name,
    average,
    sampleCount,
    min,
    max,
    ...additionalProperties,
  });
};

const logPageView = (name) => {
  appInsights?.trackPageView({ name });
};

const logEvent = ({ name, properties, measurements = "Not Specified" }) => {
  appInsights?.trackEvent({
    name,
    properties: { properties, measurements },
  });
};

const logTrace = (message, properties, severityLevel) => {
  appInsights?.trackTrace({
    message,
    properties,
    severityLevel,
  });
};

const logException = (exception) => {
  let error = exception;
  if (!(exception instanceof Error)) {
    try {
      error = new Error(
        JSON.stringify(error.response ? error.response.data : error)
      );
    } catch (err) {
      error = new Error(error.toString());
    }
  }

  appInsights?.trackException({
    error: error,
    severityLevel: SeverityLevel.Error,
  });
};

export const AppInsightsService = {
  setUpAppInsights,
  logMetric,
  logPageView,
  logEvent,
  logTrace,
  logException,
  setAuthId,
  clearAuthId,
};
