import { createContext } from "react";

export const { Provider: FormProvider, Consumer: FormConsumer } =
  createContext();

export default function connectForm(Component) {
  const Wrapper = ({ editMode: inputEditMode, ...props }) => (
    <FormConsumer>
      {({ editMode: formEditMode, formLoading }) => (
        <Component
          {...props}
          editMode={formEditMode && inputEditMode}
          formLoading={formLoading}
        />
      )}
    </FormConsumer>
  );
  Wrapper.displayName = `connectForm(${
    Component.displayName || Component.name
  })`;
  Wrapper.propTypes = Component.propTypes;
  Wrapper.defaultProps = Component.defaultProps;

  return Wrapper;
}
