import PropTypes from "prop-types";
import { Progress } from "semantic-ui-react";
import styled from "styled-components";
import {
  Colors,
  Elevation,
  LineHeight,
  Spacing,
  Typography,
} from "../style/variables";

const StyledProgress = styled(Progress)`
  &&&& {
    background-color: ${Colors.grey[5]};
    box-shadow: ${Elevation.boxShadowInsetSmall};
    border-radius: 0.75rem;
    .bar {
      background-color: ${Colors.indigo[50]};
      height: ${LineHeight.text.normal};
      border-radius: 0.75rem;
    }
    .label {
      text-align: right;
      font-weight: ${Typography.fontWeightRegular};
      font-size: ${Typography.fontSizeSmall};
      margin-top: ${Spacing.spacingSmall};
    }
  }
`;

const EhProgressBar = ({ header, label, total, value }) => {
  return (
    <div className="eh-progress-bar">
      {header}
      <StyledProgress total={total} value={value} label={label} />
    </div>
  );
};

EhProgressBar.propTypes = {
  header: PropTypes.node,
  label: PropTypes.node,
  total: PropTypes.number,
  value: PropTypes.number,
};

export default EhProgressBar;
