import { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import { Dimmer, Icon, Loader, Modal } from "semantic-ui-react";
import styled from "styled-components";

import { Colors, Spacing } from "../style";
import { EhHeader } from "./typography";
import EhFlexBox from "../componentsStd/layout/EhFlexBox";
import EhButton from "./EhButton";

const Content = styled.p`
  margin-bottom: ${Spacing.spacingXxLarge};
  color: ${Colors.grey[100]};
`;

const ButtonContainer = styled.div`
  margin-right: -${Spacing.spacingLarge};
`;

const StyledIcon = styled(Icon)`
  margin-right: 0.5rem !important;
  font-size: 1.5rem !important;
  margin-top: -5px !important;
`;

export default class EhAlertModal extends PureComponent {
  static propTypes = {
    /** The heading text displayed at the top in bold. */
    heading: PropTypes.string,
    /** The name of the icon to display besides the heading. */
    iconName: PropTypes.string,
    /** The color of the icon displayed next to the icon. */
    iconColor: PropTypes.string,
    /** The buttons to be rendered at the bottom of the modal. */
    buttons: PropTypes.arrayOf(
      PropTypes.shape({
        ...EhButton.propTypes,
        afterClick: PropTypes.func,
        text: PropTypes.string.isRequired,
      }).isRequired
    ),
    /** The message to display between the header and the buttons. */
    message: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    /** The flag which determines if the modal is open. */
    open: PropTypes.bool,
  };

  static defaultProps = {
    iconName: "exclamation triangle",
    iconColor: "brown",
  };

  componentDidMount() {
    this.hasMounted = true;
  }

  componentWillUnmount() {
    this.hasMounted = false;
  }

  state = { loading: false };

  wrapOnClick = (onClick, afterClick) => {
    return async (event) => {
      this.setState({ loading: true });
      await onClick(event);
      this.hasMounted && this.setState({ loading: false });
      if (afterClick) {
        afterClick(event);
      }
    };
  };

  renderButton = ({ onClick, afterClick, ...config }) => {
    return (
      <EhButton
        size="large"
        key={config.text}
        onClick={this.wrapOnClick(onClick, afterClick)}
        {...config}
      />
    );
  };

  render() {
    const { iconName, iconColor, heading, buttons, message, ...semanticProps } =
      this.props;
    const { loading } = this.state;

    return (
      <Modal dimmer="blurring" basic open {...semanticProps}>
        <EhFlexBox
          alignItems="center"
          justifyContent="center"
          flexDirection="column"
        >
          {loading ? (
            <Dimmer active inverted>
              <Loader />
            </Dimmer>
          ) : (
            <Fragment>
              <EhHeader size="large">
                {iconName && <StyledIcon name={iconName} color={iconColor} />}
                {heading}
              </EhHeader>
              <Content>{message}</Content>
              <ButtonContainer>
                {buttons && buttons.map(this.renderButton)}
              </ButtonContainer>
            </Fragment>
          )}
        </EhFlexBox>
      </Modal>
    );
  }
}
