import { Icon, Dropdown } from "semantic-ui-react";
import { PureComponent } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

import { Colors, Spacing, Typography, Elevation } from "../style";
import EhButton from "./EhButton";

const SemanticDropdown = (props) => {
  const { text, ...otherProps } = props; // eslint-disable-line react/prop-types
  return <Dropdown {...otherProps} />;
};

const MenuDropdown = styled(({ menuColor, size, ...otherProps }) => (
  <SemanticDropdown {...otherProps} />
))`
  &&&&&&&&&&&& {
    padding: 0;
    background: none;

    button.ui.button {
      margin-right: 0;
      padding-right: 0.125rem;
      .up {
        display: none;
      }
      .down {
        display: inline-block;
      }
    }
    &.active {
      button.ui.button {
        .up {
          display: inline-block;
        }
        .down {
          display: none;
        }
      }
    }

    .menu {
      border-color: ${(props) => Colors[props.menuColor][100]};
      color: ${Colors.white};
      background-color: ${(props) => Colors[props.menuColor][100]};
      min-width: 9rem;

      .item {
        color: ${(props) =>
          props.menuColor === "white" ? Colors.grey[100] : Colors.white};
        padding: ${(props) =>
          props.size === "small" &&
          `${Spacing.spacingXxSmall} ${Spacing.spacingLarge} !important`};
        font-weight: ${(props) =>
          props.size === "small" && Typography.fontWeightRegular};

        &:hover {
          background-color: ${(props) =>
            props.menuColor === "white"
              ? Colors.blue[25]
              : Colors[props.menuColor][50]};
        }
      }
    }
    &&&.floating {
      .menu {
        margin-top: ${Spacing.spacingXxSmall} !important;
        box-shadow: ${Elevation.boxShadowMedium} !important;
      }
    }

    &&& > .dropdown.icon {
      display: none;
    }
  }
`;

export default class EhMenuButton extends PureComponent {
  static propTypes = {
    icon: PropTypes.string,
    text: PropTypes.string,
    options: PropTypes.arrayOf(
      PropTypes.shape({
        text: PropTypes.string,
        onClick: PropTypes.func,
        icon: PropTypes.string,
      })
    ),
    /** The size of the menu button */
    size: PropTypes.string,
    /** The background color of the option items */
    menuColor: PropTypes.string,
    /** Menu button props */
    menuButtonProps: PropTypes.shape({
      ...EhButton.propTypes,
    }),
  };

  static defaultProps = {
    size: "medium",
    menuColor: "green",
  };

  render() {
    const { icon, text, size, menuButtonProps, ...remainingProps } = this.props;
    const trigger = (
      <EhButton size={size} {...menuButtonProps}>
        {icon && <Icon name={icon} />}
        {text}
        <Icon name="caret down" />
        <Icon name="caret up" />
      </EhButton>
    );
    return (
      <MenuDropdown
        button
        inline
        trigger={trigger}
        size={size}
        {...remainingProps}
      />
    );
  }
}
