import styled from "styled-components";
import PropTypes from "prop-types";
import { Colors, Spacing } from "../../style";
import CircularProgress from "@mui/material/CircularProgress";

const Loading = styled.div`
  width: 100%;
  height: ${(props) => props.height}rem;
  line-height: ${(props) => props.height}rem;
  text-align: center;
  border: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
  border-radius: ${Spacing.borderRadiusLarge};
`;

const EhCircularLoader = ({ height, dataAq }) => {
  return (
    <Loading height={height} data-aq={dataAq}>
      <CircularProgress />
    </Loading>
  );
};

EhCircularLoader.propTypes = {
  height: PropTypes.number,
  dataAq: PropTypes.string,
};
export default EhCircularLoader;
