import { PureComponent, Fragment } from "react";
import { Input } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled from "styled-components";

import { EhText, EhFormattedText } from "../typography";
import { Colors } from "../../style";

const PrismInput = styled(Input)`
  &&&&& {
    input::placeholder {
      color: ${Colors.grey[40]} !important;
    }

    &.disabled input {
      background-color: ${Colors.grey[5]};
      border-color: ${Colors.grey[25]};
      color: ${Colors.grey[25]};
    }
  }
`;

class EhInput extends PureComponent {
  static propTypes = {
    /** The value displayed when not in editMode */
    value: PropTypes.any,
    /** The flag which determines if the component should be in the edit or display state. */
    editMode: PropTypes.bool,
    /** The flag which determines if the text should wrap when too long. */
    wrapText: PropTypes.bool,
    renderButton: PropTypes.func,
    /** The flag which will set the component in the error state when attempting to move forward without completion. */
    required: PropTypes.bool,
    showNumCharsRemaining: PropTypes.bool,
    elementRef: PropTypes.any,
    ellipsis: PropTypes.bool,
    /** Object of props that should be sent to EhText in non editable mode */
    textProps: PropTypes.object,
  };

  static defaultProps = {
    value: "",
    editMode: true,
    wrapText: true,
    ellipsis: false,
  };

  render() {
    const {
      required,
      editMode,
      wrapText,
      renderButton,
      showNumCharsRemaining,
      elementRef,
      ellipsis,
      textProps,
      ...props
    } = this.props;
    if (editMode) {
      return (
        <Fragment>
          <PrismInput {...props} ref={elementRef} />
          {props.maxLength && showNumCharsRemaining && (
            <EhFormattedText
              size="xsmall"
              textAlign="right"
              type="substitutor"
              value={`Remaining Characters: <<${
                props.maxLength - (this.props.value?.length || 0)
              }|number>>`}
            />
          )}
        </Fragment>
      );
    } else {
      return (
        <EhText
          wrapText={this.props.wrapText}
          ellipsis={ellipsis}
          {...textProps}
        >
          {this.props.value}
        </EhText>
      );
    }
  }
}

export default EhInput;
