import { useState } from "react";
import { omit, isNil } from "lodash";
import PropTypes from "prop-types";
import { Accordion, AccordionSummary, AccordionDetails } from "@mui/material";

import { EhTextMat } from "../typography/EhTextMat";
import { Spacing, Colors, Typography } from "../../style";
import { EhIcon } from "../typography/EhIcon";
import styled from "styled-components";
import is from "styled-is";

const StyledAccordion = styled(Accordion)`
  background-color: transparent;
  box-shadow: none;
  border-radius: ${Spacing.borderRadiusLarge};
  margin-bottom: ${Spacing.spacingLarge};
  &.Mui-expanded {
    margin: ${Spacing.spacingNone};
  }
  &:not(:last-child) {
    margin-bottom: ${Spacing.spacingLarge};
  }
  &:before {
    content: none;
  }
  border: ${Spacing.spacingNone};
  ${is("bordered")`
 border:${Spacing.borderWidthThin} solid ${Colors.blue[25]};
`}
`;

const StyledSummary = styled(AccordionSummary)`
  &.MuiAccordionSummary-root {
    margin: ${Spacing.spacingNone};
    min-height: auto;
    padding: ${Spacing.spacingXSmall} ${Spacing.spacingLarge};
    flex-direction: ${({ iconPosition }) =>
      iconPosition === "left" ? "row-reverse" : "row"};
    background-color: ${({ applyBgColor }) =>
      applyBgColor ? Colors.backgroundColor : "transparent"};
  }
  & .Mui-expanded {
    min-height: auto;
    margin: ${Spacing.spacingNone};
  }
  & .MuiAccordionSummary-content {
    margin: ${Spacing.spacingNone};
    &.radio > label {
      margin-bottom: ${Spacing.spacingNone} !important;
    }
  }
  & .MuiAccordionSummary-expandIconWrapper {
    font-size: ${Typography.fontSizeStandard};
    color: ${Colors.blue[100]};
    margin: ${Spacing.spacingNone};
    padding: ${Spacing.spacingMedium} ${Spacing.spacingXSmall};
    &.Mui-expanded {
      transform: ${({ iconPosition }) =>
        iconPosition === "left" ? "rotate(90deg)" : ""};
    }
  }
`;
const StyledAccordionDetails = styled(AccordionDetails)`
  padding: ${({ iconPosition }) =>
    iconPosition === "left"
      ? `${Spacing.spacingXSmall} ${Spacing.spacingXxLarge}`
      : `${Spacing.spacingXSmall} ${Spacing.spacingLarge}`};
  background-color: ${({ applyBgColor }) =>
    applyBgColor ? Colors.white : "transparent"};
  border-top: ${({ contentSeparator }) =>
    contentSeparator
      ? `${Spacing.borderWidthThin} solid ${Colors.blue[25]}`
      : Spacing.spacingNone};
`;

export const EhAccordionMat = (props) => {
  const {
    items,
    exclusive,
    defaultActiveIndex,
    onChange,
    iconPosition,
    iconSize,
    bordered,
    contentSeparator,
    applyBgColor,
  } = props;

  const [activeItemIndexes, setActiveItemIndexes] = useState(
    !isNil(defaultActiveIndex) ? [].concat(defaultActiveIndex) : []
  );
  // can only have 1 default expanded index with exclusive true
  const [expanded, setExpanded] = useState(
    defaultActiveIndex && exclusive ? activeItemIndexes[0] : false
  );

  const handleChange = (panelIndex) => (event, expandStatus) => {
    setExpanded(expandStatus ? panelIndex : false);
    let activeIndexesData = [...activeItemIndexes];

    const ind = activeIndexesData.indexOf(panelIndex);
    if (ind === -1) {
      exclusive
        ? (activeIndexesData = [panelIndex])
        : activeIndexesData.push(panelIndex);
    } else {
      exclusive ? (activeIndexesData = []) : activeIndexesData.splice(ind, 1);
    }
    setActiveItemIndexes(activeIndexesData);
    // eslint-disable-next-line no-unused-expressions
    onChange?.(panelIndex, activeIndexesData);
  };

  return (
    items &&
    items.map(({ title, content }, key) => {
      return (
        <StyledAccordion
          bordered={bordered}
          key={key}
          data-aq={`EhAccordionMat-${key}`}
          defaultExpanded={activeItemIndexes.includes(key)}
          expanded={exclusive ? expanded === key : undefined}
          onChange={exclusive || onChange ? handleChange(key) : undefined}
          {...omit(props, [
            "iconPosition",
            "iconSize",
            "bordered",
            "applyBgColor",
            "contentSeparator",
            "onChange",
          ])}
        >
          <StyledSummary
            iconPosition={iconPosition}
            applyBgColor={applyBgColor}
            contentSeparator={contentSeparator}
            data-aq={`accordion-title-${key}`}
            expandIcon={
              iconPosition === "right" ? (
                <EhIcon
                  iconColor="blue"
                  iconStyle="solid"
                  iconName="chevron-down"
                  iconSize={iconSize}
                  dataAq={`accordion-expand-icon-${key}`}
                />
              ) : (
                <EhIcon
                  iconColor="blue"
                  iconStyle="solid"
                  iconName="caret-right"
                  iconSize={iconSize}
                  dataAq={`accordion-expand-icon-${key}`}
                />
              )
            }
            aria-controls={`panel-${key}-content`}
            aria-label="Expand"
            id={`panel-${key}-header`}
          >
            {typeof title === "string" ? (
              <EhTextMat fontWeight="bold" color="blue">
                {title}
              </EhTextMat>
            ) : (
              title
            )}
          </StyledSummary>

          <StyledAccordionDetails
            iconPosition={iconPosition}
            contentSeparator={contentSeparator}
            applyBgColor={applyBgColor}
            bordered={bordered}
            data-aq={`accordion-content-${key}`}
            id={`panel-${key}-content`}
          >
            {content}
          </StyledAccordionDetails>
        </StyledAccordion>
      );
    })
  );
};

EhAccordionMat.defaultProps = {
  exclusive: true,
  iconPosition: "left",
  iconSize: "sm",
  bordered: false,
  contentSeparator: false,
  applyBgColor: false,
};

EhAccordionMat.propTypes = {
  /* Prop Items with Title and Contant of Accordion Panel */
  items: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
      content: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    })
  ),
  /* Exclusively opening the panels */
  exclusive: PropTypes.bool,
  /* Accordion panels that need to be open by default on load */
  defaultActiveIndex: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.number),
    PropTypes.number,
  ]),
  /* Function that handles the click action of the expand option */
  onChange: PropTypes.func,
  /* Positioning of the expand icon */
  iconPosition: PropTypes.oneOf(["left", "right"]),
  /* expand icon choices that are available as per our UX */
  iconSize: PropTypes.oneOf(["sm", "lg", "2x"]),
  /* Bordering the panel */
  bordered: PropTypes.bool,
  /* Border between accordion header and content area */
  contentSeparator: PropTypes.bool,
  /* Custom background color for accordion header/summary section */
  applyBgColor: PropTypes.bool,
};

export default EhAccordionMat;
