import { orderBy, isEmpty } from "lodash";
import { sortsByType } from "./sorts";
import eventEmitter from "./eventEmitter";
import { localStorageUtil } from "./browserStorageUtil";

class EmitApiResponse {
  constructor(apiUrl, timeInterval, callback, eventName, localStoragekey) {
    Object.assign(this, {
      apiUrl,
      timeInterval,
      callback,
      eventName,
      localStoragekey,
    });
  }
  _sort(data) {
    return orderBy(
      data,
      [sortsByType.date("endDate"), sortsByType.date("createDate")],
      ["asc", "desc"]
    );
  }

  _filterDismissed(records) {
    const disMissedRecords = localStorageUtil.getEntry(this.localStoragekey);
    const updatedEntries = {};
    const data = isEmpty(disMissedRecords)
      ? records
      : records.filter(({ notificationId }) =>
          disMissedRecords[notificationId]
            ? ((updatedEntries[notificationId] = true), false)
            : true
        );
    isEmpty(updatedEntries)
      ? localStorageUtil.clearEntry(this.localStoragekey)
      : localStorageUtil.setEntry(this.localStoragekey, updatedEntries);
    return data;
  }

  _getAPICall = async (baseClient) => {
    try {
      const { data } = await baseClient.get(this.apiUrl);
      const filteredData = this._filterDismissed(data.data);
      const sortedData = this._sort(filteredData);
      eventEmitter.emit(this.eventName, sortedData);
    } catch (error) {}
  };

  subscribe(baseClient) {
    eventEmitter.addListener(this.eventName, this.callback);
    this._getAPICall(baseClient);
    const intervalId = setInterval(
      this._getAPICall,
      this.timeInterval,
      baseClient
    );
    const removeSubscriptions = () => {
      eventEmitter.removeListener(this.eventName, this.callback);
      clearInterval(intervalId);
    };
    return removeSubscriptions;
  }
}

export default EmitApiResponse;
