import * as React from "react";
import { isArray, isEmpty, isObject } from "lodash";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Form } from "semantic-ui-react";
import { Field, getIn } from "formik";

import { Colors, Spacing, Typography } from "../../../style";
import EhLoadingBar from "../../../componentsStd/EhLoadingBar";
import { EhLabel, EhText } from "../../typography";
import EhFlexBox from "../../../componentsStd/layout/EhFlexBox";
import connectForm from "./connectForm";

const FieldWrapper = styled(({ noRightMargin, ...rest }) => (
  <Form.Field {...rest} />
))`
  &&& {
    margin-right: ${({ noRightMargin }) =>
      noRightMargin ? "0" : Spacing.spacingSmall};
    margin-bottom: 0;

    &.required label:after {
    }
  }
`;

const StyledLabel = styled(EhLabel)`
  ${(props) => {
    return (
      props.required &&
      `&:after {
        margin: 0 0 0 ${Spacing.spacingXxSmall};
        content: "(Required)";
        color: ${Colors.grey[70]};
        font-weight: ${Typography.fontWeightRegular};
      }`
    );
  }};
`;

const FieldLabelRow = styled(EhFlexBox)`
  margin-bottom: ${Spacing.spacingSmall};
`;

const REQUIRED_MESSAGE = "This field is required";

class EventWrapper extends React.Component {
  static propTypes = {
    name: PropTypes.string,
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    form: PropTypes.shape({
      setFieldValue: PropTypes.func,
      setFieldError: PropTypes.func,
      setFieldTouched: PropTypes.func,
      validateForm: PropTypes.func,
    }),
    children: PropTypes.element.isRequired,
    componentOnChange: PropTypes.func,
    componentOnBlur: PropTypes.func,
  };

  onChange = (evtOrValue) => {
    if (
      evtOrValue &&
      evtOrValue.nativeEvent &&
      (evtOrValue.target.name || evtOrValue.target.id)
    ) {
      this.props.onChange(evtOrValue);
    } else {
      this.props.form.setFieldValue(this.props.name, evtOrValue); // trigger validaton on change
      this.props.form.setFieldTouched(this.props.name, true); // trigger validation on blur
    }

    this.props.componentOnChange && this.props.componentOnChange(evtOrValue);
  };

  onBlur = (evtOrValue) => {
    if (
      evtOrValue &&
      evtOrValue.nativeEvent &&
      (evtOrValue.target.name || evtOrValue.target.id)
    ) {
      this.props.onBlur(evtOrValue);
    } else {
      this.props.form.setFieldTouched(this.props.name, true); // trigger validation on blur
    }
    this.props.componentOnBlur && this.props.componentOnBlur(evtOrValue);
  };

  render() {
    const child = React.Children.only(this.props.children);
    return React.createElement(child.type, {
      ...child.props,
      onChange: this.onChange,
      onBlur: this.onBlur,
    });
  }
}

export default function asField(Component) {
  class EhField extends React.Component {
    static propTypes = {
      name: PropTypes.string.isRequired,
      validate: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.func),
        PropTypes.func,
      ]),
      editMode: PropTypes.bool,
      fieldLabel: PropTypes.string,
      required: PropTypes.bool,
      hideRequired: PropTypes.bool,
      format: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
      message: PropTypes.any,
      hideNotAvailable: PropTypes.bool,
      formLoading: PropTypes.bool,
      wrapText: PropTypes.bool,
      renderButton: PropTypes.func,
      onChange: PropTypes.func,
      onBlur: PropTypes.func,
      disabled: PropTypes.bool,
      noRightMargin: PropTypes.bool,
    };
    static defaultProps = { editMode: true, wrapText: true };
    static displayName = `${Component.displayName || Component.name}Field`;

    state = {
      loading: false,
      renderButton: null,
    };

    validate = async (value) => {
      if (!this.props.disabled) {
        value = value && value.trim ? value.trim() : value;

        if (
          this.props.validate &&
          (value || value === false) &&
          this.props.editMode
        ) {
          const validators = isArray(this.props.validate)
            ? this.props.validate
            : [this.props.validate];
          this.setState({ loading: true });
          for (const v of validators) {
            let error;
            try {
              error = await v(value);
            } catch (err) {
              console.error(err);
            }
            if (error) {
              this.setState({ loading: false });
              throw error;
            }
          }
          this.setState({ loading: false });
        }

        if (
          this.props.required &&
          ((!value && value !== 0) || (isObject(value) && isEmpty(value)))
        ) {
          throw REQUIRED_MESSAGE;
        }
      }
    };

    renderButton = (renderFunc) => {
      this.setState({ renderButton: renderFunc });
    };

    renderComponent = ({ field, form }) => {
      const {
        fieldLabel,
        editMode,
        hideRequired,
        format,
        validate,
        hideNotAvailable,
        wrapText,
        renderButton,
        onChange,
        onBlur,
        formLoading,
        message,
        noRightMargin,
        ...componentProps
      } = this.props;

      const fieldError =
        (getIn(form.touched, field.name) || !!form.submitCount) &&
        getIn(form.errors, field.name);

      return editMode ? (
        <FieldWrapper
          required={this.props.required && !this.props.hideRequired}
          error={!this.state.loading && !!fieldError}
          noRightMargin={this.props.noRightMargin}
        >
          {this.props.fieldLabel && (
            <FieldLabelRow justifyContent="space-between">
              <StyledLabel required={this.props.required && !hideRequired}>
                {fieldLabel}
              </StyledLabel>
              {this.state.renderButton && this.state.renderButton()}
            </FieldLabelRow>
          )}
          <div>
            <EventWrapper
              {...field}
              form={form}
              componentOnChange={onChange}
              componentOnBlur={onBlur}
            >
              <Component
                loading={this.state.loading}
                error={!!fieldError}
                {...componentProps}
                {...field}
                renderButton={this.renderButton}
              />
            </EventWrapper>
            {message ? (
              <EhText wrapText={wrapText} size="xsmall">
                {message}
              </EhText>
            ) : null}
            {fieldError && !this.state.loading ? (
              <EhText wrapText={wrapText} size="small" color="red">
                {fieldError}
              </EhText>
            ) : null}
          </div>
        </FieldWrapper>
      ) : (
        <FieldWrapper>
          {fieldLabel && <label>{fieldLabel}</label>}
          {formLoading ? (
            <EhLoadingBar />
          ) : field.value === "" || field.value == null ? (
            hideNotAvailable ? null : (
              <EhText color="grey">Not Available</EhText>
            )
          ) : (
            <Component
              {...componentProps}
              {...field}
              wrapText={wrapText}
              editMode={false}
            />
          )}
        </FieldWrapper>
      );
    };

    render() {
      const { formLoading } = this.props;
      return formLoading ? (
        <FieldWrapper>
          <label>{this.props.fieldLabel}</label>
          <EhLoadingBar />
        </FieldWrapper>
      ) : (
        <Field
          name={this.props.name}
          validate={this.validate}
          render={this.renderComponent}
        />
      );
    }
  }

  return connectForm(EhField);
}
