import { PureComponent, createContext, forwardRef } from "react";
import { Dropdown } from "semantic-ui-react";
import { omit, isNil } from "lodash";
import PropTypes from "prop-types";
import styled from "styled-components";
import {
  Colors,
  Elevation,
  LineHeight,
  Spacing,
  Typography,
} from "../../style";
import { EhText } from "../typography";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import { FixedSizeList as List } from "react-window";
import is, { isNot } from "styled-is";

const ellipsis = (props) =>
  props.ellipsis
    ? `overflow: hidden;text-overflow: ellipsis;white-space: nowrap;`
    : null;

const StyledAutoComplete = styled(Autocomplete)`
  && .MuiAutocomplete-inputRoot {
    padding: ${Spacing.spacingNone};
  }
  && .MuiSvgIcon-root {
    color: ${Colors.blue[100]};
    font-size: 1.2rem;
  }
  && .MuiAutocomplete-endAdornment {
    top: calc(50% - 11px);
  }
`;

const StyledTextField = styled(TextField)`
  &&&&& input {
    &::placeholder {
      color: ${Colors.black};
      opacity: 1;
    }
  }

  & .MuiOutlinedInput-root {
    ${isNot("isError")`
  & fieldset{
    border-color:${Colors.indigo[25]};
  }
  &:hover fieldset{
    border-color:${Colors.indigo[25]};
  }
  &.Mui-focused fieldset{
    border:1px solid ${Colors.blue[100]};
    box-shadow:${Elevation.boxShadowInsetMedium};
  }
  `}
    ${is("isError")`
  & fieldset{
    border:none;
  }
`}
  }
`;

export const StyledDropdown = styled(Dropdown)`
  &&&.floating {
    .menu {
      margin-top: ${Spacing.spacingXxSmall} !important;
    }
  }
  &&&&& {
    line-height: ${(props) =>
      props.size === "large" ? LineHeight.text.large : LineHeight.text.normal};
    max-width: ${(props) => props.widthCollapsed};
    border-color: ${Colors.indigo[25]};
    border-radius: ${Spacing.borderRadiusLarge} !important;

    &.search {
      border-bottom-left-radius: ${Spacing.borderRadiusLarge} !important;
      border-bottom-right-radius: ${Spacing.borderRadiusLarge} !important;
      border-top-left-radius: ${Spacing.borderRadiusLarge} !important;
      border-top-right-radius: ${Spacing.borderRadiusLarge} !important;
      border-color: ${Colors.indigo[25]};
      box-shadow: none;

      &.active {
        box-shadow: ${Elevation.boxShadowInsetMedium};
        border-color: ${Colors.blue[100]};
      }

      &.upward {
        .menu {
          margin-bottom: ${Spacing.spacingXSmall};
        }
      }

      &:not(.upward) {
        .menu {
          margin-top: ${Spacing.spacingXSmall};
        }
      }

      .menu {
        border-radius: ${Spacing.borderRadiusLarge};
      }
    }

    .menu {
      border-top-width: 1px !important;
      border-bottom-width: 1px !important;
      border-color: ${Colors.indigo[25]};

      .item,
      &.menu .item.selected {
        background-color: ${Colors.white};
        .text {
          background-color: ${Colors.white};
        }
        &:hover {
          background-color: ${Colors.grey[5]};
          .text {
            background-color: ${Colors.grey[5]};
          }
        }
      }

      .item {
        border-left: ${Spacing.borderWidthThick} solid transparent;
        font-weight: ${Typography.fontWeightRegular};
        padding: ${Spacing.spacingXxSmall} ${Spacing.spacingMedium} !important;
        &.selected {
          border-left: ${Spacing.borderWidthThick} solid ${Colors.indigo[100]};
        }
      }
      :focus {
        .icon:before {
          content: "\f0d8";
        }
      }
      .search {
        line-height: ${(props) =>
          props.size === "large"
            ? LineHeight.text.large
            : LineHeight.text.normal} !important;
        z-index: 1000;
      }
    }

    .icon {
      color: ${Colors.blue[100]};
    }

    &.active .default.text {
      color: ${Colors.grey[40]};
    }

    &&&&.disabled {
      background-color: ${Colors.grey[5]};
      border-color: ${Colors.grey[25]} !important;
      color: ${Colors.grey[25]};
      box-shadow: none;

      .text {
        color: ${Colors.grey[25]};
        background-color: ${Colors.grey[5]};
      }
      .icon {
        color: ${Colors.grey[25]};
      }
    }

    .visible.menu.transition {
      min-width: ${(props) => props.widthExpanded};
    }

    .text {
      display: block;
      min-height: 1.5rem;
      line-height: 1.125rem;
      padding: 0.188rem 0;
      ${ellipsis}
    }

    .disabled.item {
      position: sticky;
      bottom: 0;
      background: ${Colors.white};
      z-index: 100;
      border-top: ${Spacing.borderWidthThin} solid ${Colors.grey[25]};
      font-size: ${Typography.fontSizeSmall};
    }
  }
`;
const OuterElementContext = createContext({});
export default class EhDropdown extends PureComponent {
  // Removed importing PropTypes from Semantic Dropdown due to overhead it was causing for documentation.
  static propTypes = {
    /** The options avilable for the dropdown. */
    options: PropTypes.any,
    /** The field which displays in dropdown; it also serves as fall back for keyField. */
    display: PropTypes.string,
    /** The dropdown will return select field instead whole option object if present. */
    select: PropTypes.string,
    /** The unique identifier for each row. */
    keyField: PropTypes.string,
    /** The selected value. If select is defined, it should be value of select field;
     * otherwise, it should be object containing matching keyField. */
    value: PropTypes.any,
    /** The value to be shown as tooltip on hovered options */
    title: PropTypes.any,
    forbidden: PropTypes.arrayOf(PropTypes.any),
    /** The flag which determines if multiple selections can be made. */
    multiple: PropTypes.bool,
    /** The flag which determines if the dropdown should take the maximum width. */
    fluid: PropTypes.bool,
    /** The function which gets called when the dropdown loses focus. */
    onBlur: PropTypes.func,
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    /** The flag which determines if the component should be in the edit or display state. */
    editMode: PropTypes.bool,
    /** The flag which determines if the text should wrap when too long. */
    wrapText: PropTypes.bool,
    renderButton: PropTypes.func,
    /** Mapped over the active items and returns shorthand for the active item Labels. Only applies to multiple Dropdowns.
     * @param {object} item - A currently active dropdown item.
     * @param {number} index - The current index.
     * @param {object} defaultLabelProps - The default props for an active item Label.
     * @returns {*} Shorthand for a Label.
     */
    renderLabel: PropTypes.func,
    /** Placeholder text. */
    placeholder: PropTypes.any,
    /** A dropdown menu can contain a header. */
    header: PropTypes.any,
    /** A compact dropdown has no minimum width. */
    compact: PropTypes.bool,
    /** Using the clearable setting will let users remove their selection from a dropdown. */
    clearable: PropTypes.bool,
    /** Whether or not the menu should close when the dropdown is blurred. */
    closeOnBlur: PropTypes.bool,
    /** Whether or not the menu should close when a value is selected from the dropdown.
     * By default, multiple selection dropdowns will remain open on change,
     * while single selection dropdowns will close on change.
     */
    closeOnChange: PropTypes.bool,
    /** A disabled dropdown menu or item does not allow user interaction. */
    disabled: PropTypes.bool,
    /** An errored dropdown can alert a user to a problem. */
    error: PropTypes.bool,
    /** A dropdown menu can contain floated content. */
    floating: PropTypes.bool,
    /** Controls whether the dropdown will open upward. */
    upward: PropTypes.bool,
    /** A dropdown can show that it is currently loading data. */
    loading: PropTypes.bool,
    /** A dropdown can have a specified width when it is in collapsed state */
    widthCollapsed: PropTypes.string,
    /** A dropdown can have a specified width when it is in expanded state */
    widthExpanded: PropTypes.string,
    /** A dropdown menu can contain a footer. */
    renderFooter: PropTypes.any,
    /** If is ture Autocomplete compoenet geting loaded. */
    async: PropTypes.bool,
    elementRef: PropTypes.any,
    ellipsis: PropTypes.bool,
  };

  static defaultProps = {
    renderLabel: ({ text }) => text,
    additionLabel: "Add ",
    additionPosition: "top",
    closeOnBlur: true,
    deburr: false,
    icon: "dropdown",
    minCharacters: 1,
    noResultsMessage: "No results found.",
    openOnFocus: true,
    searchInput: "text",
    selectOnNavigation: true,
    wrapSelection: true,
    selection: true,
    fluid: true,
    editMode: true,
    selectOnBlur: false,
    wrapText: true,
    display: "description",
    filterOptions: () => {},
    async: false,
    ellipsis: false,
  };

  state = {};

  static getDerivedStateFromProps(props) {
    return {
      keyField: props.select || props.keyField || props.display,
    };
  }

  onChange = (e, { value }) => {
    const val = this.props.select
      ? value
      : this.props.multiple
      ? value
      : this.props.options.find(
          (option) => option[this.state.keyField] === value
        );
    this.props.onChange(val);
  };

  render() {
    const {
      select,
      display,
      fluid,
      value,
      keyField,
      editMode,
      options,
      wrapText,
      renderButton,
      error,
      loading,
      async,
      onChange,
      elementRef,
      ellipsis,
      ...props
    } = this.props;

    let selectedVal = null;

    if (value) {
      selectedVal =
        select || props.multiple ? value : value[this.state.keyField];
    }

    if (async) {
      return (
        <AsyncSelect {...this.props} onChange={(e, val) => onChange(val)} />
      );
    }

    if (editMode) {
      return (
        <StyledDropdown
          loading={!isNil(loading) ? loading : options === undefined}
          {...omit(props, ["renderFooter"])}
          value={selectedVal}
          fluid={fluid}
          options={options && this.getOptions()}
          onChange={this.onChange}
          ref={elementRef}
          ellipsis={ellipsis}
        />
      );
    } else {
      const selectedOption = select
        ? options.find((option) => option[select] === value)
        : value;
      return selectedOption ? (
        <EhText data-aq={props["data-aq"]}>{selectedOption[display]}</EhText>
      ) : null;
    }
  }

  getOptions() {
    const { display, renderFooter } = this.props;
    const dropdownOptions = this.removeForbidden().map((item) => ({
      value: item[this.state.keyField],
      text: item[display],
      title: item[display],
    }));
    if (renderFooter) {
      const footerOption = { value: -1, content: renderFooter, disabled: true };
      dropdownOptions.push(footerOption);
    }
    return dropdownOptions;
  }

  removeForbidden() {
    const { options, value, select, forbidden } = this.props;
    if (!options) {
      return [];
    }
    const keyField = this.state.keyField;
    if (forbidden && forbidden.length) {
      const uniqueForbidden = new Set(
        select ? forbidden : forbidden.map((badVal) => badVal[keyField])
      );
      value && uniqueForbidden.delete(select ? value : value[keyField]);
      return options.filter((item) => !uniqueForbidden.has(item[keyField]));
    }
    return options;
  }
}

const AsyncSelect = (props) => {
  const {
    options,
    filterOptions,
    keyField,
    name,
    display,
    getOptionLabel,
    callback,
    onInputChange,
    error,
    value,
    ...remainingProps
  } = props;

  const OuterElementType = forwardRef((props, ref) => (
    <OuterElementContext.Consumer>
      {(value = {}) => {
        return <div ref={ref} {...props} {...value} />;
      }}
    </OuterElementContext.Consumer>
  ));

  const AutoCompleteOption = (props) => {
    const { data, index, ...others } = props;
    const [optionProps, option] = data[index];
    return (
      <div {...optionProps} {...others}>
        {callback ? callback(option) : option[display]}
      </div>
    );
  };
  AutoCompleteOption.propTypes = {
    data: PropTypes.array,
    index: PropTypes.number,
  };

  const ListboxComponent = forwardRef((props, ref) => {
    const { children, ...other } = props;
    const itemData = [];
    children.forEach((item) => {
      itemData.push(item);
      itemData.push(...(item.children || []));
    });
    const itemCount = itemData.length;
    const itemSize = 36;
    const getHeight = () => {
      if (itemCount > 8) {
        return 8 * itemSize;
      }
      return itemCount * itemSize;
    };
    return (
      <div ref={ref}>
        <OuterElementContext.Provider value={other}>
          <List
            itemData={itemData}
            height={getHeight() + 2 * 3}
            width="100%"
            itemSize={36}
            itemCount={itemCount}
            outerElementType={OuterElementType}
          >
            {AutoCompleteOption}
          </List>
        </OuterElementContext.Provider>
      </div>
    );
  });

  ListboxComponent.propTypes = {
    children: PropTypes.array,
  };

  return (
    <StyledAutoComplete
      getOptionLabel={(option) =>
        getOptionLabel ? getOptionLabel(option) : option[display]
      }
      name={name}
      onInputChange={onInputChange}
      isOptionEqualToValue={(option, value) =>
        option[keyField] === value[keyField]
      }
      disableClearable
      ListboxComponent={ListboxComponent}
      clearOnBlur
      freeSolo
      forcePopupIcon
      options={options}
      filterOptions={filterOptions}
      value={value}
      renderOption={(props, option) => [props, option]}
      noOptionsText="No results found."
      {...remainingProps}
      renderInput={(params) => (
        <StyledTextField
          placeholder="---Select---"
          isError={error}
          {...params}
          variant="outlined"
          fullWidth
        />
      )}
    />
  );
};

AsyncSelect.propTypes = {
  onChange: PropTypes.func,
  onInputChange: PropTypes.func,
  onBlur: PropTypes.func,
  options: PropTypes.array,
  display: PropTypes.string,
  keyField: PropTypes.string,
  value: PropTypes.any,
  name: PropTypes.string,
  filterOptions: PropTypes.func,
  getOptionLabel: PropTypes.func,
  callback: PropTypes.func,
  error: PropTypes.boolean,
};
