import { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

const defaultShow = (newView) => {
  const closeFunction = null;
  const view = { newView, closeFunction };

  return () => {
    view.closeFunction();
  };
};

const defaultCloseOpenSplitScreen = () => {};

export let showSplitScreen = defaultShow;
export let closeOpenSplitScreen = defaultCloseOpenSplitScreen;
export let isSplitScreenOpen = false;

export function EhGlobalSplitScreen() {
  const [view, setView] = useState(null);
  const [removeOnRouteChange, setRemoveOnRouteChange] = useState(true);
  const location = useLocation();

  const showSplitScreenC = (modal, clearOnRouteChange = true) => {
    setView(modal);
    setRemoveOnRouteChange(clearOnRouteChange);
    return () => {
      setView(null);
    };
  };

  const closeOpenSplitScreenC = () => {
    setView(null);
  };

  useEffect(() => {
    showSplitScreen = showSplitScreenC;
    closeOpenSplitScreen = closeOpenSplitScreenC;
    return () => {
      showSplitScreen = defaultShow;
    };
  }, []);

  useEffect(() => {
    if (removeOnRouteChange) {
      setView(null);
    }
  }, [location.pathname]);

  isSplitScreenOpen = view !== null;
  return <>{view}</>;
}
