import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import ReactDOM from "react-dom";
import useSizeListener from "./useSizeListener";

const useIsWithinScrollWidth = (ref) => {
  const [isWithinScrollWidth, setIsWithinScrollWidth] = useState(false);
  const { innerWidth, innerHeight } = useSizeListener();

  const resizeEvent = () => {
    if (
      ref.current &&
      ReactDOM.findDOMNode(ref.current).offsetWidth <
        ReactDOM.findDOMNode(ref.current).scrollWidth
    ) {
      setIsWithinScrollWidth(true);
    } else {
      setIsWithinScrollWidth(false);
    }
  };

  useEffect(() => {
    resizeEvent();
  }, [innerWidth, innerHeight]);

  return isWithinScrollWidth;
};

useIsWithinScrollWidth.propTypes = {
  ref: PropTypes.node.isRequired,
};

export default useIsWithinScrollWidth;
