import { createLogic } from "../redux";

import { authActions } from "../ducks";
import * as UserService from "./userService";

export const getUserProfile = createLogic(
  async ({ identifiServicesClient, dispatch }) => {
    const response = await identifiServicesClient.get(`core/v1/UserProfile`);
    dispatch(authActions.setIDPUserProfile(response.data));
    const normalizedUserData = UserService.normalizeUserClientData(
      response.data.clients
    );
    dispatch(authActions.setNormalizedUserProfile(normalizedUserData));
  }
);
