import PropTypes from "prop-types";

import EhOptional from "./EhOptional";

export default function EhMaskedView(props) {
  const { value, wrapText, label, mask } = props;

  return (
    <EhOptional
      label={label}
      value={value}
      type="text"
      format={mask}
      wrapText={wrapText}
    />
  );
}
EhMaskedView.defaultProps = {
  wrapText: false,
};

EhMaskedView.propTypes = {
  /**
   * label of the field
   */
  label: PropTypes.string,
  /**
   * mask to be applied on the field
   */
  mask: PropTypes.string.isRequired,
  /**
   * value displayed for the field
   */
  value: PropTypes.any.isRequired,
  /**
   * The flag which determines if the text should wrap when too long.
   */
  wrapText: PropTypes.bool,
};
