import { Fragment, useState } from "react";
import PropTypes from "prop-types";
import { Typography } from "../../style";
import { isEmpty } from "lodash";
import styled from "styled-components";

const StyledDiv = styled.div`
  &&& {
    font-size: ${Typography.fontSizeMedium};
    font-weight: ${Typography.fontWeightRegular};

    a {
      font-size: ${Typography.fontSizeMedium};
      cursor: pointer;
    }
  }
`;

const getDescription = ({ isExpanded, truncateLength, description }) => {
  if (isExpanded) {
    return description;
  } else {
    const desc = description.slice(0, truncateLength);
    return typeof description === "string" ? desc.concat("... ") : desc;
  }
};

const EhShowMore = ({
  truncateLength = 200,
  description,
  showMoreLabel = "Show More",
  showLessLabel = " Show Less",
  parentWrapper = "div",
  expanded = false,
  onChange = () => {},
}) => {
  const [isExpanded, setIsExpanded] = useState(expanded);

  return (
    !isEmpty(description) && (
      <StyledDiv as={parentWrapper}>
        {truncateLength && description.length > truncateLength ? (
          <Fragment>
            {getDescription({ isExpanded, truncateLength, description })}
            <a
              onClick={(event) => {
                event.stopPropagation();
                setIsExpanded(!isExpanded);
                onChange(!isExpanded);
              }}
            >
              {isExpanded ? showLessLabel : showMoreLabel}
            </a>
          </Fragment>
        ) : (
          description
        )}
      </StyledDiv>
    )
  );
};

EhShowMore.propTypes = {
  truncateLength: PropTypes.number,
  description: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.element),
  ]),
  showMoreLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  showLessLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  parentWrapper: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  expanded: PropTypes.bool,
  onChange: PropTypes.func,
};

export default EhShowMore;
