const LOGGER_ENTRY_TYPES = {
  ERROR: "error",
  WARN: "warn",
  INFO: "info",
};

const LOGGER_ENTRY_SOURCES = {
  WINDOW_ERROR: "window.error",
  CONSOLE_ERROR: "console.error",
  CONSOLE_WARN: "console.warn",
  CONSOLE_LOG: "console.log",
  COMPONENT_DID_CATCH: "componentDidCatch",
  LOGGER_INFO: "logger.info",
  LOGGER_WARN: "logger.warn",
  LOGGER_ERROR: "logger.error",
};

const MONKEYPATCHED_CONSOLE_METHODS = ["log", "warn", "error"];

const _originalConsoleMethods = {};

// Monkey patch over the console.log/warn/error
// http://tobyho.com/2012/07/27/taking-over-console-log/
const monkeyPatchConsoleMethod = (method) => {
  const original = console[method];
  console[method] = function (message, ...objects) {
    const toLog = {
      message,
      objects,
    };

    switch (method) {
      case "log":
        Logger.info(toLog, LOGGER_ENTRY_SOURCES.CONSOLE_LOG);
        break;
      case "warn":
        Logger.warn(toLog, LOGGER_ENTRY_SOURCES.CONSOLE_WARN);
        break;
      case "error":
        Logger.error(toLog, LOGGER_ENTRY_SOURCES.CONSOLE_ERROR);
        break;
    }
    original.apply(console, arguments);
  };

  return original;
};

let _settings = null;

const Logger = {
  info: (entry, source = LOGGER_ENTRY_SOURCES.LOGGER_INFO) => {
    _throwIfNotInitialized();
    if (!_settings.disableInfoLogging)
      _signalEntry(LOGGER_ENTRY_TYPES.INFO, source, entry);

    if (_shouldRouteToConsole())
      _originalConsoleMethods[_generateFieldNameForStoreOriginalMethod("log")](
        entry
      );
  },

  warn: (entry, source = LOGGER_ENTRY_SOURCES.LOGGER_WARN) => {
    _throwIfNotInitialized();
    if (!_settings.disableWarnLogging)
      _signalEntry(LOGGER_ENTRY_TYPES.WARN, source, entry);

    if (_shouldRouteToConsole())
      _originalConsoleMethods[_generateFieldNameForStoreOriginalMethod("warn")](
        entry
      );
  },

  error: (entry, source = LOGGER_ENTRY_SOURCES.LOGGER_ERROR) => {
    _throwIfNotInitialized();
    if (!_settings.disableErrorLogging)
      _signalEntry(LOGGER_ENTRY_TYPES.ERROR, source, entry);

    if (_shouldRouteToConsole())
      _originalConsoleMethods[
        _generateFieldNameForStoreOriginalMethod("error")
      ](entry);
  },

  initialize: (settings) => {
    _settings = settings || {};

    MONKEYPATCHED_CONSOLE_METHODS.forEach((method) => {
      const originalMethod = monkeyPatchConsoleMethod(method);

      _originalConsoleMethods[
        _generateFieldNameForStoreOriginalMethod(method)
      ] = originalMethod;
    });

    window.addEventListener("error", _handleWindowError);
  },

  deinitialize: () => {
    _settings = null;

    MONKEYPATCHED_CONSOLE_METHODS.forEach((method) => {
      const originalMethoFieldName =
        _generateFieldNameForStoreOriginalMethod(method);

      const originalMethod = _originalConsoleMethods[originalMethoFieldName];

      if (originalMethod) {
        console[method] = originalMethod;
        delete _originalConsoleMethods[originalMethoFieldName];
      }
    });

    window.removeEventListener("error", _handleWindowError);
  },
};

function _shouldRouteToConsole() {
  return (
    (localStorage.getItem("__EhRouteLogsToConsole") || "").toLowerCase() ===
    "true"
  );
}

function _throwIfNotInitialized() {
  if (_settings == null)
    throw Error(
      "Logger has not been initialized, please call Logger.initialize(...)"
    );
}

function _handleWindowError(error) {
  _signalEntry(LOGGER_ENTRY_TYPES.ERROR, LOGGER_ENTRY_SOURCES.WINDOW_ERROR, {
    ...error,
    stackTrace: error.error ? error.error.stack : undefined,
  });
}

function _generateFieldNameForStoreOriginalMethod(method) {
  return `_original_${method}`;
}

function _signalEntry(entryType, entrySource, entry) {
  const wrappedEntry = {
    date: new Date(),
    entryType,
    entry: _fixErrorObjectForSerializationRecur(entry),
    entrySource,
  };

  if (_settings.onMessageReceived) {
    _settings.onMessageReceived(wrappedEntry);
  }
}

// https://stackoverflow.com/questions/18391212/is-it-not-possible-to-stringify-an-error-using-json-stringify
function _fixErrorObjectForSerializationRecur(obj) {
  if (obj instanceof Error) {
    const alt = {};

    Object.getOwnPropertyNames(obj).forEach(function (key) {
      alt[key] = obj[key];
    }, obj);

    return alt;
  }

  Object.getOwnPropertyNames(obj).forEach((prop) => {
    if (prop instanceof Error)
      obj[prop] = this._fixErrorObjectForSerializationRecur(prop);
  });

  return obj;
}

export default Logger;
export {
  Logger,
  LOGGER_ENTRY_TYPES,
  LOGGER_ENTRY_SOURCES,
  _fixErrorObjectForSerializationRecur,
};
