import { PureComponent } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { isEqual, isEmpty } from "lodash";

const INITIAL_STATE = {
  loading: false,
};

export default function withServiceFP(serviceLogic, selector) {
  return function withService(UnWrappedComponent) {
    class ServiceComponent extends PureComponent {
      static propTypes = {
        params: PropTypes.any,
        fetchedOptions: PropTypes.arrayOf(PropTypes.any),
        serviceLogic: PropTypes.func,
      };

      state = INITIAL_STATE;

      async componentDidMount() {
        if (!this.props.fetchedOptions || isEmpty(this.props.fetchedOptions)) {
          await this.fetchOptions(this.props.params);
        }
      }

      async componentDidUpdate(prevProps) {
        if (!isEqual(this.props.params, prevProps.params)) {
          await this.fetchOptions(this.props.params);
        }
      }

      async fetchOptions(params) {
        this.setState({ loading: true });
        await this.props.serviceLogic(params);
        this.setState({ loading: false });
      }

      render() {
        const { fetchedOptions, serviceLogic, params, ...componentProps } =
          this.props;
        const { loading } = this.state;

        return (
          <UnWrappedComponent
            options={fetchedOptions}
            {...componentProps}
            loading={loading}
          />
        );
      }
    }

    const mapStateToProps = (state, ownProps) => {
      return { fetchedOptions: selector(state, ownProps.params) };
    };

    return connect(mapStateToProps, { serviceLogic })(ServiceComponent);
  };
}
