import { Prompt } from "react-router";
import PropTypes from "prop-types";

const EhPrompt = ({ message, when }) => {
  return <Prompt message={message} when={when} />;
};

EhPrompt.propTypes = {
  message: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  when: PropTypes.bool,
};

export default EhPrompt;
