import { UserManager, WebStorageStateStore } from "oidc-client";
import { IDP_EVENTS, STORAGE_KEYS, COOKIES, APP_SETTINGS } from "../constants";
import { EventEmitter, Logger, JSUtils } from "../js-utils";
import ReturnToURLService from "./returnToURLService";
import Cookies from "js-cookie";
import DateHandler from "../js-utils/dateHandler";

class AuthenticationService {
  constructor(settings = {}) {
    this._settings = {
      authority: APP_SETTINGS.idpUrl,
      client_id: APP_SETTINGS.idpClientId,
      redirect_uri: `${window.location.origin}${
        window.location.pathname || ""
      }#/complete-authentication#`,
      response_type: "id_token token",
      scope: `openid profile ${APP_SETTINGS.idpRequiredScopes}`,
      filterProtocolClaims: true,
      loadUserInfo: false,
      automaticSilentRenew: false,
      silent_redirect_uri: `${window.location.origin}${
        window.location.pathname || ""
      }renew-authentication-token.html#`,
      silentRequestTimeout: 10000,
      userStore: new WebStorageStateStore({ store: window.localStorage }),
      default_home_page: `${window.location.origin}${
        window.location.pathname || ""
      }`,
      ssoData: Cookies.getJSON(COOKIES.SSO_DATA),
      shouldReturnToUrl: false,
      alwaysReturnToHome: false,
      ...settings,
    };

    this.isIdp = true;
    this.sessionManager = new UserManager(this._settings);
    this.registerUserManagerEvents();

    this.startAuthentication = this.startAuthentication.bind(this);
    this.getSession = this.getSession.bind(this);
    this.handleLogoutRedirect = this.handleLogoutRedirect.bind(this);
    this.silentIframe = this.silentIframe.bind(this);
    this.onSignedOut = this.onSignedOut.bind(this);
    this.validateReturnUrl = this.validateReturnUrl.bind(this);
    this.authenticate = this.authenticate.bind(this);
  }

  // Gets the access token from local storage
  async getSession() {
    this.session = await this.sessionManager.getUser();
    return this.session && !this.session.expired ? this.session : null;
  }

  async getSecondsSinceRenew() {
    await this.getSession();

    const currentTime = DateHandler.utcNow().valueOf() / 1000;
    const renewTime = this.session.expires_at - this._settings.sessionDuration;

    return currentTime - renewTime;
  }

  async startAuthentication() {
    Logger.info(
      "OIDC: Clear Stale State and Redirect to Authorization endpoint"
    );
    await this.sessionManager.clearStaleState();
    await this.sessionManager.signinRedirect({ state: { bar: 15 } });
  }

  async completeAuthentication() {
    this.session = await this.sessionManager.signinRedirectCallback();
    return this.session;
  }

  silentIframe(signoutRequest) {
    const iframe = document.createElement("iframe");
    iframe.src = signoutRequest.url;
    iframe.name = "frame";
    iframe.style.display = "none";
    document.body.appendChild(iframe);
    Logger.info("Silent signout");
    return frames["frame"].location.host;
  }

  // Uses the iframe to sign out silently saving an extra redirect
  async signout(hasTimedOut) {
    Logger.info("Signout Called by end-user");
    this._settings.shouldReturnToUrl = hasTimedOut;

    if (await this.getSession()) {
      this.silentIframe(
        await this.sessionManager.createSignoutRequest({
          id_token_hint: this.session.id_token,
        })
      );
    } else {
      this.onSignedOut();
    }
  }

  getUserManager() {
    return this.sessionManager;
  }

  getAuthorizationToken() {
    Logger.info("Return Authorization Token from Session");
    return this.session
      ? `${this.session.token_type} ${this.session.access_token}`
      : undefined;
  }

  validateReturnUrl(url) {
    return !/\/(logout|complete-authentication|no-access|forbidden)/.test(url);
  }

  authenticate(shouldReturnToUrl) {
    const returnUrl = window.location.href;

    if (
      (this._settings.shouldReturnToUrl || shouldReturnToUrl) &&
      !this._settings.alwaysReturnToHome &&
      this.validateReturnUrl(returnUrl)
    ) {
      Logger.info("Setting Session to Window Location");
      ReturnToURLService.sessionSet(STORAGE_KEYS.RETURN_TO_URL, returnUrl);
    } else {
      Logger.info("Setting Session to Default Home Page");
      ReturnToURLService.sessionSet(
        STORAGE_KEYS.RETURN_TO_URL,
        this._settings.default_home_page
      );
    }
    Logger.info(`OIDC: Authenticate`);
    this.startAuthentication();
  }

  async isAuthenticated() {
    const valid = await this.getSession();
    return valid;
  }

  // Gets called in every signout flow (manual/timeout), performs cleanup
  async onSignedOut() {
    EventEmitter.emit(IDP_EVENTS.USER_SIGNED_OUT);
    await JSUtils.safePromise(this._settings.onSessionEnd);
    window.onbeforeunload = null;
    await this.sessionManager.removeUser();
    await this.sessionManager.clearStaleState();
    Logger.info("Performing cleanup after signout");

    // Handles the redirect back to sign in
    this.handleLogoutRedirect();
  }

  signedOutEvent = async () => {
    Logger.info("OIDC: signed-out event.");
    await this.onSignedOut();
  };

  // Registers events with OIDC
  registerUserManagerEvents() {
    this.sessionManager.events.addUserLoaded((newSession) => {
      if (newSession) {
        this.session = newSession;
      }
    });

    // Gets called when the user (signs out/ times out) across all open tabs
    this.sessionManager.events.addUserSignedOut(this.signedOutEvent);

    this.sessionManager.events.addSilentRenewError(() => {
      Logger.info("silent renew error");
    });

    this.sessionManager.events.addAccessTokenExpiring(async () => {
      Logger.info("OIDC: Access Token Expiring Event");
      await this._settings.onAccessTokenExpiring();
    });
  }

  async renewAccessToken() {
    this.session = await this.sessionManager.signinSilent();
    this.session && !this.session.expired
      ? Logger.info(
          `renewal completed and expires at ${this.session.expires_at}`
        )
      : Logger.info("invalid session");
    return this.session;
  }

  handleLogoutRedirect() {
    const ssoData = Cookies.getJSON(COOKIES.SSO_DATA) || this._settings.ssoData;
    Cookies.remove(COOKIES.SSO_DATA, {
      path: "/",
      domain: APP_SETTINGS.ssoCookieDomainName || "myidentifi.com",
    });

    if (ssoData) {
      let skipSSORedirect = !ssoData.logoutUrl;

      if (APP_SETTINGS.ssoRedirectExclusions) {
        const exclusions = APP_SETTINGS.ssoRedirectExclusions.split(", ");

        skipSSORedirect = exclusions.some(
          (exclusion) =>
            exclusion &&
            ssoData.logoutUrl &&
            ssoData.logoutUrl.toLowerCase().includes(exclusion.toLowerCase())
        );
      }

      window.location.href = skipSSORedirect
        ? `${window.location.origin}/launcher/#/logout?sso=true`
        : ssoData.logoutUrl;
    } else {
      this.authenticate();
    }
  }
}

const bootstrap = (settings) => {
  Logger.info("Bootstrap Authentication service");
  return new AuthenticationService(settings);
};

export default { bootstrap };
