import { useState } from "react";
import { Controller } from "react-hook-form";
import styled from "styled-components";
import EhFlexBox from "../../../componentsStd/layout/EhFlexBox";
import { EhText, EhLabel } from "../../../components/typography";
import { EhFieldError } from "../../form";
import { Colors, Spacing, Typography } from "../../../style";
import is from "styled-is";
import { isArray, isFunction } from "lodash";
import PropTypes from "prop-types";
import { Form } from "semantic-ui-react";

const REQUIRED_MESSAGE = "This field is required";

const StyledLabel = styled(EhLabel)`
  ${(props) => {
    return (
      props.required &&
      `&:after {
        margin: 0 0 0 ${Spacing.spacingXxSmall};
        content: "(Required)";
        color: ${Colors.grey[70]};
        font-weight: ${Typography.fontWeightRegular};
      }`
    );
  }};
`;

const FieldLabelRow = styled(EhFlexBox)`
  margin-bottom: ${Spacing.spacingSmall};
`;
const FieldWrapper = styled(Form.Field)`
  &&&&&&&&&& {
    ${is("error")`
    label{
        color:${Colors.red[100]};
        &:after{
            color:${Colors.red[100]};   
        }
    }
   
  `}
    .ui.dropdown {
      ${is("error")`
    border-color:${Colors.red[100]};
     `}
    }
    .ui.input {
      width: 100%;
      input {
        ${is("error")`
                border-color:${Colors.red[100]};
              `}
      }
    }
  }
`;
const asControl = (Component) => (props) => {
  const EhControl = () => {
    const [loading, setLoading] = useState(false);
    const {
      name,
      editMode = true,
      control,
      fieldLabel,
      required,
      hideRequired,
      validate,
      wrapText = true,
      onChange: componentOnChange,
      onBlur: componentOnBlur,
      hideNotAvailable = false,
      ...remainingProps
    } = props;
    const validators = isArray(validate)
      ? validate
      : isFunction(validate)
      ? [validate]
      : [];
    const validationRules = {};

    validators.forEach((validator, index) => {
      const rule = async (v) => {
        setLoading(true);
        const error = await validator(v);
        setLoading(false);
        return error || true;
      };
      validationRules[index] = rule;
    });

    return (
      <Controller
        name={name}
        control={control}
        rules={{
          ...(required && { required: REQUIRED_MESSAGE }),
          validate: { ...validationRules },
        }}
        render={({ field, fieldState }) => {
          const onChange = (evtOrValue) => {
            field.onChange(evtOrValue);
            componentOnChange && componentOnChange(evtOrValue, field.name);
          };

          const onBlur = (evtOrValue) => {
            field.onBlur(evtOrValue);
            if (
              !(
                evtOrValue &&
                evtOrValue.nativeEvent &&
                (evtOrValue.target.name || evtOrValue.target.id)
              )
            ) {
              field.value = evtOrValue;
            }
            componentOnBlur && componentOnBlur(evtOrValue, field.name);
          };
          return (
            <>
              {editMode ? (
                <FieldWrapper error={fieldState.invalid}>
                  {fieldLabel && (
                    <FieldLabelRow justifyContent="space-between">
                      <StyledLabel required={required && !hideRequired}>
                        {fieldLabel}
                      </StyledLabel>
                    </FieldLabelRow>
                  )}
                  <Component
                    loading={loading}
                    {...field}
                    elementRef={field.ref}
                    onChange={onChange}
                    onBlur={onBlur}
                    {...remainingProps}
                  />
                  <EhFieldError fieldState={fieldState} dataAq={`err${name}`} />
                </FieldWrapper>
              ) : (
                <FieldWrapper>
                  {fieldLabel && <EhLabel>{fieldLabel}</EhLabel>}
                  {field.value === "" || field.value == null ? (
                    hideNotAvailable ? null : (
                      <EhText color="grey">Not Available</EhText>
                    )
                  ) : (
                    <Component
                      {...field}
                      {...remainingProps}
                      wrapText={wrapText}
                      editMode={false}
                    />
                  )}
                </FieldWrapper>
              )}
            </>
          );
        }}
      />
    );
  };
  EhControl.propTypes = {
    name: PropTypes.string.isRequired,
    control: PropTypes.func.isRequired,
    validate: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.func),
      PropTypes.func,
    ]),
    editMode: PropTypes.bool,
    fieldLabel: PropTypes.string,
    required: PropTypes.bool,
    hideRequired: PropTypes.bool,
    format: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    message: PropTypes.any,
    hideNotAvailable: PropTypes.bool,
    formLoading: PropTypes.bool,
    wrapText: PropTypes.bool,
    renderButton: PropTypes.func,
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    disabled: PropTypes.bool,
    noRightMargin: PropTypes.bool,
  };
  return <EhControl />;
};

export default asControl;
