const azureVar = (azureValue, defaultValue, castToType = type.string) =>
  castToType(/^__\w+__$/.test(azureValue) ? defaultValue : azureValue);

const type = {
  boolean: value => get(value, type.string(value).toLowerCase() === "true"),
  number: value => get(value, parseInt(value, 10)),
  string: value => get(value, `${value}`.trim())
};

const get = (value, returnValue) => (value == undefined ? value : returnValue); // eslint-disable-line eqeqeq

export { azureVar, type };
