import { forwardRef, PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import { Icon, Grid, Popup } from "semantic-ui-react";
import styled from "styled-components";
import { isEmpty } from "lodash";

import { filters } from "@evolent/roots";

import { Spacing } from "../style";
import {
  EhCheckboxGroupField,
  EhInputField,
  EhRadioGroupField,
  EhDateTimePickerField,
} from "./form/formFields";
import EhForm from "./form/EhForm";
import EhButton from "./EhButton";
import EhButtonGroup from "./EhButtonGroup";
import EhLabel from "./typography/EhLabel";

const StyledCheckboxGroupField = styled(EhCheckboxGroupField)`
  &&&& {
    margin-right: 0px;
  }
`;

const StyledColumn3 = styled(Grid.Column)`
  &&&& {
    padding: ${Spacing.spacingSmall} ${Spacing.spacingMedium};
  }
`;

const StyledIcon = styled(Icon)`
  padding-left: ${Spacing.spacingXxSmall};
`;

const textFilterTypes = {
  startsWith: "startsWith",
  contains: "contains",
  equals: "equals",
};

const textFilterOptions = [
  { label: "Starts with", value: textFilterTypes.startsWith },
  {
    label: "Contains",
    value: textFilterTypes.contains,
  },
  {
    label: "Equals",
    value: textFilterTypes.equals,
  },
];

const textFilterFuncs = {
  [textFilterTypes.startsWith]: filters.startsWith,
  [textFilterTypes.contains]: filters.contains,
  [textFilterTypes.equals]: filters.equals,
};
const renderWrapperDiv = (containerClassName) => {
  return forwardRef((props, ref) => {
    return (
      <div data-aq={`dv${containerClassName}`} className={containerClassName}>
        <div {...props} ref={ref} />
      </div>
    );
  });
};

export const filtersByType = {
  text(formValues, filterConfig) {
    const { filterText, textFilterType } = formValues[filterConfig.field];
    const filterTextValue = filterConfig?.isNumber
      ? filterText?.trim().replace(/\$|,/g, "")
      : filterText?.trim();
    return {
      field: filterConfig.field,
      value: filterText
        ? {
            textFilterType,
            filterText,
          }
        : {},
      filter: textFilterFuncs[textFilterType](
        filterConfig.field,
        filterTextValue,
        filterConfig.formatData
      ),
      header: filterConfig.header,
      renderValue: (value) => {
        return `${
          !filterConfig.params?.hideSearchTypes
            ? `${
                textFilterOptions.find(
                  (textFilter) => textFilter.value === value.textFilterType
                )?.label
              } `
            : ""
        }"${value.filterText}"`;
      },
      type: "text",
      params: filterConfig.params,
    };
  },
  checkbox(formValues, filterConfig) {
    const checkboxVal = formValues[filterConfig.field].filterOptions;
    return {
      field: filterConfig.field,
      value: !isEmpty(checkboxVal)
        ? {
            filterOptions: checkboxVal,
          }
        : {},
      filter: filters.isOneOf(
        filterConfig.field,
        checkboxVal.map((value) => value[filterConfig.params.keyField || "id"])
      ),
      header: filterConfig.header,
      name: filterConfig.value && filterConfig.value.name,
      renderValue: (value) => {
        return value.filterOptions?.map((item) => item.name).join(", ");
      },
      type: "checkbox",
      params: filterConfig.params,
    };
  },
  dateRange(formValues, filterConfig) {
    const { fromDate, toDate } = formValues[filterConfig.field];
    return {
      field: filterConfig.field,
      value:
        fromDate || toDate
          ? {
              fromDate,
              toDate,
            }
          : {},
      filter: filters.dateRange(filterConfig.field, fromDate, toDate),
      header: filterConfig.header,
      renderValue: (value) => {
        return `From ${value.fromDate || ""} to ${value.toDate || ""}`;
      },
      type: "dateRange",
      params: filterConfig.params,
    };
  },
};

class EhFilter extends PureComponent {
  static defaultProps = {
    size: "small",
  };

  static propTypes = {
    /**
     * CSS class name to apply to popup container in which filter content is rendered.  Needed when
     * using this component in legacy app so that it can be styled correctly.
     */
    containerClassName: PropTypes.string,

    /** The onFilterChange function to update the filters object. */
    onChange: PropTypes.func,

    filters: PropTypes.arrayOf(
      PropTypes.shape({
        /**
         * The field name of the prop on which to filter.
         */
        field: PropTypes.string.isRequired,
        /**
         * A more readable name for the filter that  will be displayed in the header of the filter.
         */
        header: PropTypes.string,

        /**
         * If the filter needs extra params, pass them here (e.g. checkbox filters need options).  Mostly varies by filter type.
         */
        params: PropTypes.object,

        /**
         * The type of filter that dictates what types of inputs are shown and what the value is.
         */
        type: PropTypes.oneOf(["checkbox", "dateRange", "text"]).isRequired,

        /**
         * The current or initial value of the filter if any.
         */
        value: PropTypes.object,
        /**
         * To check if the current value is number.
         */
        isNumber: PropTypes.bool,
      })
    ).isRequired,

    /**
     * The size to be displayed for filter Icon.
     */
    size: PropTypes.oneOf([
      "mini",
      "tiny",
      "small",
      "home",
      "large",
      "big",
      "huge",
      "massive",
    ]),

    /**
     * Flag to indicate whether the filter popup should hide when the user scrolls.  Default is true.
     */
    hideOnScroll: PropTypes.bool,

    /**
     * Style of the filter Popup. This is a prop passed along to Semantic UI React Popup.
     */
    basic: PropTypes.bool,

    /**
     * The element that will trigger the Filter Popup.
     */
    trigger: PropTypes.element,

    /**
     * Style prop to make the Popup wider.  This is passed along to Semantic UI React Popup
     */
    wide: PropTypes.oneOf(["very"]),
  };

  static defaultProps = {
    hideOnScroll: false,
    basic: false,
  };

  state = { open: false, fromDate: undefined, toDate: undefined };

  hideFilterForm = () => {
    this.setState({ open: false });
    this.setToFromDates.call();
  };
  showFilterForm = () => this.setState({ open: true });

  initialValuesByType = {
    text: {
      textFilterType: textFilterOptions[0].value,
      filterText: "",
    },
    checkbox: {
      filterOptions: [],
    },
    dateRange: {
      fromDate: "",
      toDate: "",
    },
  };

  getInitialValues() {
    return this.props.filters.reduce((result, filter) => {
      result[filter.field] =
        filter.value?.value || this.initialValuesByType[filter.type];
      return result;
    }, {});
  }

  emitFilter = async (formValues) => {
    const { filters: filterConfigs, onChange } = this.props;
    const newFilters = filterConfigs.reduce((filters, filterConfig) => {
      const newFilter = filtersByType[filterConfig.type](
        formValues,
        filterConfig
      );
      if (!isEmpty(newFilter.value)) {
        filters.push(newFilter);
      }

      return filters;
    }, []);

    if (!isEmpty(newFilters)) {
      await onChange(newFilters);
    }
  };

  setToFromDates = () => {
    const newFilter = this.props.filters[0];
    if (newFilter.type === "dateRange") {
      this.setState({
        fromDate: newFilter?.value?.value?.fromDate || undefined,
        toDate: newFilter?.value?.value?.toDate || undefined,
      });
    }
  };

  componentDidUpdate(prevProp) {
    if (prevProp.filters !== this.props.filters && this.props.filters.length) {
      this.setToFromDates.call();
    }
  }

  renderFilterFormByType = (filter) => {
    switch (filter.type) {
      case "text":
        return (
          <Fragment>
            {filter.header && (
              <Grid.Row>
                <Grid.Column>
                  <EhLabel>{filter.header}</EhLabel>
                </Grid.Column>
              </Grid.Row>
            )}
            {!filter.params?.hideSearchTypes && (
              <Grid.Row>
                <Grid.Column>
                  <EhRadioGroupField
                    fieldLabel=""
                    format="radioGroup"
                    hideRequired
                    name={`${filter.field}.textFilterType`}
                    options={textFilterOptions}
                    orientation="horizontal"
                    keyField="value"
                    required
                    select="value"
                    noRightMargin
                  />
                </Grid.Column>
              </Grid.Row>
            )}
            <Grid.Row>
              <Grid.Column>
                <EhInputField
                  name={`${filter.field}.filterText`}
                  placeholder="Enter filter parameter"
                  fluid
                  hideRequired
                  noRightMargin
                />
              </Grid.Column>
            </Grid.Row>
          </Fragment>
        );

      case "checkbox": {
        const name = `${filter.field}.filterOptions`;
        return (
          <Grid.Row>
            <Grid.Column>
              <StyledCheckboxGroupField
                display={filter.params.display || "name"}
                keyField={filter.params.keyField}
                fieldLabel={filter.header}
                height="max-content"
                name={name}
                options={filter.params.options}
                enableSelectAll={filter.params.enableSelectAll}
                noRightMargin
              />
            </Grid.Column>
          </Grid.Row>
        );
      }
      case "dateRange":
        return (
          <Grid.Row>
            <StyledColumn3 width={12}>
              <EhLabel>{filter.header}</EhLabel>
            </StyledColumn3>
            <StyledColumn3 width={12}>
              <EhDateTimePickerField
                name={`${filter.field}.fromDate`}
                data-aq="dtFromDate"
                fieldLabel="From:"
                maxDate={
                  this.state.toDate
                    ? new Date(this.state.toDate)
                    : filter.params?.maxDate
                }
                minDate={filter.params?.minDate}
                onChange={(value) => this.setState({ fromDate: value })}
                required
              />
            </StyledColumn3>
            <StyledColumn3 width={12}>
              <EhDateTimePickerField
                name={`${filter.field}.toDate`}
                fieldLabel="To:"
                data-aq="dtToDate"
                maxDate={filter.params?.maxDate}
                minDate={
                  this.state.fromDate
                    ? new Date(this.state.fromDate)
                    : filter.params?.minDate
                }
                onChange={(value) => this.setState({ toDate: value })}
                required
              />
            </StyledColumn3>
          </Grid.Row>
        );
    }
  };

  render() {
    const { basic, containerClassName, hideOnScroll, size, trigger, wide } =
      this.props;

    return (
      <Popup
        position="bottom center"
        basic={basic}
        hideOnScroll={hideOnScroll}
        as={containerClassName && renderWrapperDiv(containerClassName)}
        on="click"
        open={this.state.open}
        onClose={this.hideFilterForm}
        onOpen={this.showFilterForm}
        wide={wide}
        trigger={
          trigger || (
            <span>
              <StyledIcon
                data-aq="icnFilter"
                name="filter"
                size={size !== "home" ? size : "undefined"}
                onClick={this.showFilterForm}
              />
            </span>
          )
        }
      >
        <Popup.Content>
          <EhForm
            initialValues={this.getInitialValues()}
            onSave={this.emitFilter}
            onAfterSave={this.hideFilterForm}
          >
            {() => (
              <Grid data-aq="filterForm">
                {this.props.filters.map((filter, index) => (
                  <Fragment key={index}>
                    {this.renderFilterFormByType(filter, true)}
                  </Fragment>
                ))}
                <Grid.Row>
                  <Grid.Column textAlign="right">
                    <EhButtonGroup>
                      <EhButton
                        text="Filter"
                        floated="right"
                        primary
                        data-aq="btnFilter"
                        type="submit"
                      />
                      <EhButton
                        text="Cancel"
                        floated="right"
                        data-aq="btnCancel"
                        onClick={this.hideFilterForm}
                      />
                    </EhButtonGroup>
                  </Grid.Column>
                </Grid.Row>
              </Grid>
            )}
          </EhForm>
        </Popup.Content>
      </Popup>
    );
  }
}

export default EhFilter;
