import FileSaver from "file-saver";
import ContentDisposition from "content-disposition";

const fetchBlob = async (logic) => {
  const response = await logic();
  let file = new window.Blob([response.data]);
  file = setFileName(file, response);
  return file;
};

const setFileName = (file, response) => {
  const contentDisposition = response.headers["content-disposition"];
  if (contentDisposition) {
    const disposition = ContentDisposition.parse(contentDisposition);
    if (disposition.parameters && disposition.parameters.filename) {
      file.name = disposition.parameters.filename;
    }
  }
  return file;
};

const download = (logic, contentType) => {
  fetchBlob(logic, contentType).then((blob) => {
    FileSaver.saveAs(blob);
  });
};

export default download;
