import styled from "styled-components";
import PropTypes from "prop-types";
import { Component } from "react";
import { Colors, Spacing } from "../style";
import EhText from "./typography/EhText";
import is from "styled-is";
import { BADGE_TYPES } from "../constants";

const StyledText = styled(({ borderRadius, borderless, ...restProps }) => (
  <EhText {...restProps} />
))`
  display: inline-block;
  border: ${({ borderless }) => {
    return borderless ? null : `${Spacing.borderWidthThin} solid`;
  }};
  margin: ${Spacing.spacingXxSmall} ${Spacing.spacingXxSmall} !important;
  padding: ${Spacing.borderWidthThick} ${Spacing.spacingXSmall};
  color: ${Colors.grey[40]};
  border-radius: ${({ borderRadius }) =>
    borderRadius === "small" ? Spacing.borderRadiusSmall : "15px"};
  ${is("negative")`
    color: ${Colors.red[100]};
  `};
`;

const StyledBadge = styled(({ ...allProps }) => <EhText {...allProps} />)`
  position: relative;
  float: left;
  padding: ${Spacing.spacingXxSmall} ${Spacing.spacingXLarge};
  background: transparent;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background: ${Colors.aqua[125]};
    transform: skew(-15deg);
  }
`;

export default class EhBadge extends Component {
  static propTypes = {
    /** The flag which determines if the badge should be in the error state. */
    negative: PropTypes.bool,
    type: PropTypes.oneOf([BADGE_TYPES.CLIENT_BADGE]),
    borderRadius: PropTypes.string,
    ...EhText.propTypes,
  };

  static defaultProps = {
    color: "white",
  };

  render() {
    const { type, ...restProps } = this.props;
    if (type === BADGE_TYPES.CLIENT_BADGE) {
      return <StyledBadge color="white" size="medium" {...restProps} />;
    } else {
      return <StyledText {...restProps} />;
    }
  }
}
