import JSUtils from "./JSUtils";
class LocalStorageLogging {
  constructor(settings) {
    if (!settings) settings = {};

    const {
      localStorageKey,
      maxEntriesToStore,
      applicationName,
      userNameGetter,
    } = settings;

    this.userNameGetter = userNameGetter || (() => "N/A");
    this.applicationName = applicationName || "Application name not specified";
    this.localStorageKey = localStorageKey || "Identifi.ErrorLog";
    this.maxEntriesToStore = maxEntriesToStore || 25;
  }

  transformEntryForStorage(e) {
    return {
      date: e.date,
      application: this.applicationName,
      exceptionMessage: JSUtils.safeStringify(e.entry),
      exceptionType: `Severity:${e.entryType}  Intercepted from:${e.entrySource}`,
      stackTrace: e.entry.stackTrace,
      user: this.userNameGetter(),
    };
  }

  log(entry) {
    const transformedMessage = this.transformEntryForStorage(entry);

    const logEntries = this.getAllEntries();

    logEntries.unshift(transformedMessage);

    if (logEntries.length > this.maxEntriesToStore) {
      logEntries.pop();
    }

    localStorage.setItem(
      this.localStorageKey,
      JSUtils.safeStringify(logEntries)
    );
  }

  getAllEntries() {
    const existingEntries = localStorage.getItem(this.localStorageKey);

    if (!existingEntries) return [];

    return JSON.parse(existingEntries);
  }

  clearLogs() {
    localStorage.removeItem(this.localStorageKey);
  }
}

export default LocalStorageLogging;
