import { Component, createRef, Children } from "react";
import { Colors, Spacing, Typography } from "../../style";
import { Container, Grid, Menu } from "semantic-ui-react";
import EhMenu from "../EhMenu";
import EhButton from "../EhButton";
import { withRouter, Redirect } from "react-router-dom";
import queryString from "query-string";
import PropTypes from "prop-types";
import styled from "styled-components";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";
import EhBadge from "../EhBadge";

class EhSheetItem extends Component {
  static propTypes = {
    title: PropTypes.string, // eslint-disable-line react/no-unused-prop-types
    subItem: PropTypes.array, // eslint-disable-line react/no-unused-prop-types
    itemCount: PropTypes.number, // eslint-disable-line react/no-unused-prop-types
  };
}

const StyledFlexBox = styled(EhFlexBox)`
  margin-left: 34px;
`;

const Content = styled.div`
  background-color: ${Colors.white};
  position: relative;
  padding-top: 50px;
  min-height: 100vh;
`;

const Page = styled.div`
  background-color: white;
`;

const StyledButton = styled(EhButton)`
  &&&& {
    margin-left: ${Spacing.spacingLarge};
    min-width: 2em !important;
    height: 2.1em !important;
    font-size: ${Typography.fontSizeMedium};
  }
`;
const Header = styled.div`
  background-color: white;
  position: fixed;
  z-index: 1;
  width: 100%;
  border-bottom-width: ${Spacing.borderWidthThin};
  border-bottom-style: solid;
  border-color: ${Colors.grey[10]};
  padding: ${Spacing.spacingMedium} 0;
`;

const HeaderWrapper = styled.div`
  flex-grow: 1;
`;

const SheetLayoutGrid = styled(Grid)`
  min-height: 100vh;
`;

const StyledBadge = styled(EhBadge)`
  &&& {
    background-color: ${Colors.orange[100]};
    margin-bottom: 0px !important;
    margin-top: 2px !important;
    border: 0;
    padding: 2px 6px;
    vertical-align: top;
  }
`;

export class EhSheetLayout extends Component {
  constructor(props) {
    super(props);
    this.headerRef = createRef();
  }
  static propTypes = {
    header: PropTypes.node,
    children: PropTypes.oneOfType([
      PropTypes.node,
      PropTypes.arrayOf(PropTypes.node),
    ]),
    location: PropTypes.any,
    history: PropTypes.any,
    contentClass: PropTypes.string,
    headerClass: PropTypes.string,
    containerClass: PropTypes.string,
    size: PropTypes.string,
    useReplaceAction: PropTypes.bool,
  };

  static Item = EhSheetItem;

  static defaultProps = {
    useReplaceAction: false,
  };
  state = {
    headerHeight: 0,
  };

  componentDidUpdate() {
    this.getHeaderHeight();
  }

  getHeaderHeight = () => {
    if (this.headerRef.current) {
      const headerHeight = this.headerRef.current.clientHeight;
      headerHeight !== this.state.headerHeight &&
        this.setState({ headerHeight });
    }
  };

  getMenuWidth = (ref) => {
    if (ref) {
      const rect = ref.getBoundingClientRect();
      if (rect.width !== this.state.menuWidth) {
        this.setState({ menuWidth: rect.width });
      }
    }
  };

  pushOrReplace = (useReplaceAction, searchQuery, title, history) => {
    const searchObj = {
      search: queryString.stringify({
        ...searchQuery,
        activeTab: title,
      }),
    };
    useReplaceAction ? history.replace(searchObj) : history.push(searchObj);
  };

  render() {
    const {
      children,
      location,
      contentClass,
      headerClass,
      header,
      history,
      containerClass,
      size,
      useReplaceAction,
    } = this.props;
    const searchQuery = queryString.parse(location.search);
    let activeTab;
    let activeChild;
    const realChildren = children.filter
      ? children.filter((c) => !!c)
      : [children];
    if (realChildren.length) {
      activeTab = searchQuery.activeTab && searchQuery.activeTab.toLowerCase();
      activeChild = realChildren.find(
        (c) => c.props.title.toLowerCase() === activeTab
      );

      if (!activeChild) {
        const realSubItem = children
          .filter((c) => c?.props?.subItem)
          .map((r) => r.props.subItem);
        if (realSubItem.length) {
          realSubItem.find(
            (s) =>
              (activeChild = s.find(
                (c) => c.props.title.toLowerCase() === activeTab
              ))
          );
        }
        if (!activeChild) {
          return (
            <Redirect
              to={{
                pathname: location.pathname,
                search: queryString.stringify({
                  ...searchQuery,
                  activeTab: realChildren[0].props.title,
                }),
              }}
            />
          );
        }
      }
    } else {
      activeChild = children;
      activeTab = children.props.title.toLowerCase();
    }

    return (
      <Content className={contentClass}>
        <Page>
          <Header ref={this.headerRef} className={headerClass}>
            <Container>
              <StyledFlexBox>
                <HeaderWrapper>{header}</HeaderWrapper>
                <StyledButton text="&times;" onClick={() => history.goBack()} />
              </StyledFlexBox>
            </Container>
          </Header>

          <div style={{ paddingTop: this.state.headerHeight }}>
            <Container className={containerClass}>
              <SheetLayoutGrid celled="internally">
                <Grid.Row>
                  <Grid.Column width={2}>
                    <div ref={this.getMenuWidth} />
                    <div
                      style={{ width: this.state.menuWidth, position: "fixed" }}
                    >
                      <EhMenu vertical size={size}>
                        {Children.map(realChildren, (m) => {
                          const { title, subItem, itemCount } = m.props;
                          const isActive = activeTab === title.toLowerCase();
                          return subItem ? (
                            <Menu.Item
                              key={title}
                              name={title}
                              onClick={() => {}}
                              className="title-style"
                            >
                              <div
                                style={
                                  subItem.some(
                                    (c) =>
                                      activeTab === c.props.title.toLowerCase()
                                  )
                                    ? {
                                        borderLeftColor: Colors.grey[100],
                                        paddingBottom: 10,
                                        paddingLeft: 8,
                                        paddingTop: 8,
                                        borderLeftWidth: 3,
                                        borderLeftStyle: "solid",
                                        color: Colors.grey[100],
                                      }
                                    : {
                                        paddingBottom: 10,
                                        paddingLeft: 8,
                                        paddingTop: 8,
                                      }
                                }
                              >
                                {title}
                              </div>
                              <Menu.Menu>
                                {subItem.map((c) => {
                                  const { title } = c.props;
                                  return (
                                    <Menu.Item
                                      key={title}
                                      active={isActive}
                                      name={title}
                                      onClick={() => {
                                        if (children.length && !isActive) {
                                          this.pushOrReplace(
                                            useReplaceAction,
                                            searchQuery,
                                            title,
                                            history
                                          );
                                        }
                                      }}
                                    />
                                  );
                                })}
                              </Menu.Menu>
                            </Menu.Item>
                          ) : (
                            <Menu.Item
                              key={title}
                              active={isActive}
                              name={title}
                              onClick={() => {
                                if (children.length && !isActive) {
                                  this.pushOrReplace(
                                    useReplaceAction,
                                    searchQuery,
                                    title,
                                    history
                                  );
                                }
                              }}
                            >
                              {title}
                              {itemCount > 0 && (
                                <StyledBadge
                                  color="white"
                                  size="xsmall"
                                  fontWeight="bold"
                                  textAlign="center"
                                >
                                  {itemCount}
                                </StyledBadge>
                              )}
                            </Menu.Item>
                          );
                        })}
                      </EhMenu>
                    </div>
                  </Grid.Column>
                  <Grid.Column width={10}>
                    {activeChild.props.children}
                  </Grid.Column>
                </Grid.Row>
              </SheetLayoutGrid>
            </Container>
          </div>
        </Page>
      </Content>
    );
  }
}

export default withRouter(EhSheetLayout);
