import { connect } from "react-redux";
import { Popup, Grid, Icon } from "semantic-ui-react";
import styled from "styled-components";
import { EhButton } from "../../../components";
import { Colors, Typography, Spacing } from "../../../style/variables";
import AppTile from "./AppTile";
import PropTypes from "prop-types";
import { getModulesToShowSelector, APP_SETTINGS } from "@evolent/roots";

const LauncherButton = styled(EhButton)`
  background-color: ${Colors.grey[100]} !important;
`;
const StyledTriggerIcon = styled(Icon)`
  &&&&& {
    font-size: ${Typography.fontSizeLarge};
    margin-left: ${Spacing.spacingLarge};
  }
`;
const mapStateToProps = (state) => ({
  moduleTiles: getModulesToShowSelector(state),
});

const EhAppDrawerHorizontal = ({ moduleTiles, openInNewTab = false }) => {
  return (
    <Popup
      content={
        <Grid columns={3}>
          {moduleTiles?.length > 0 && (
            <Grid.Row>
              {moduleTiles?.map((tile) => {
                return (
                  <Grid.Column key={tile.name}>
                    <AppTile tile={tile} openInNewTab={openInNewTab} />
                  </Grid.Column>
                );
              })}
            </Grid.Row>
          )}
          <Grid.Row>
            <Grid.Column width={16}>
              <a data-aq="launcher-button" href={APP_SETTINGS.launcherUrl}>
                <LauncherButton
                  data-aq="btnLauncher"
                  fluid
                  text="Go To Launcher"
                  icon="home"
                  borderless
                />
              </a>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      }
      on="click"
      pinned
      position="bottom right"
      trigger={
        <a>
          <StyledTriggerIcon inverted name="th" />
          <Icon inverted name="caret down" />
        </a>
      }
    />
  );
};

EhAppDrawerHorizontal.propTypes = {
  moduleTiles: PropTypes.array,
  openInNewTab: PropTypes.bool,
};

export default connect(mapStateToProps)(EhAppDrawerHorizontal);
