import { forwardRef } from "react";
import { PropTypes } from "prop-types";
import { Popup } from "semantic-ui-react";

const EhPopup = forwardRef((props, ref) => {
  const { trigger, size, content, ...remProps } = props;
  const elementToTrigger = trigger(ref);
  const newContent = content || trigger();

  return (
    <Popup
      trigger={elementToTrigger}
      size={size}
      content={newContent}
      hoverable
      {...remProps}
      popperModifiers={{
        preventOverflow: {
          boundariesElement: "offsetParent",
        },
      }}
    />
  );
});

EhPopup.propTypes = {
  trigger: PropTypes.func.isRequired,
  size: PropTypes.string,
  content: PropTypes.element,
};

export default EhPopup;
