import { Component } from "react";
import { Colors, Spacing } from "../../style";
import { Grid } from "semantic-ui-react";
import { EhLabel } from "../typography";

import PropTypes from "prop-types";
import styled from "styled-components";
import { withRouter } from "react-router-dom";

import { HashLink } from "react-router-hash-link";

const Anchor = styled(HashLink)`
  display: block;
  margin-top: ${Spacing.spacingSmall};
  cursor: pointer;
  :hover {
    color: ${Colors.blue[100]};
  }
`;

const Fixed = styled.div`
  position: fixed;
`;

export class EhAnchorLinkLayout extends Component {
  static propTypes = {
    children: PropTypes.oneOfType([
      PropTypes.node,
      PropTypes.arrayOf(PropTypes.node),
    ]).isRequired,
    links: PropTypes.arrayOf(
      PropTypes.shape({
        title: PropTypes.string.isRequired,
        key: PropTypes.string,
      })
    ).isRequired,
    location: PropTypes.any,
  };

  scroll(el) {
    window.scroll(0, el.offsetTop + el.offsetHeight);
  }

  render() {
    return (
      <Grid>
        <Grid.Row>
          <Grid.Column width={9}>{this.props.children}</Grid.Column>
          <Grid.Column width={1} />
          <Grid.Column width={2}>
            <Fixed>
              <EhLabel>Jump to</EhLabel>
              {this.props.links.map((link) => (
                <Anchor
                  key={link.title}
                  to={{ ...this.props.location, hash: link.key || link.title }}
                  replace
                  scroll={this.scroll}
                >
                  {link.title}
                </Anchor>
              ))}
            </Fixed>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}

export default withRouter(EhAnchorLinkLayout);
