import { PureComponent, Fragment } from "react";
import { Progress, Icon } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled from "styled-components";
import bytes from "bytes";
import axios from "axios";
import Dropzone from "react-dropzone";
import { Colors, Spacing, Typography } from "../../style";
import EhButton from "../EhButton";
import { EhText } from "../typography";
import EhAlertModalMat from "../../componentsStd/layout/EhAlertModalMat";

const StyledDropzone = styled(Dropzone)`
  &[style] {
    height: 3rem;
    width: auto;
    border-style: dashed;
    border-color: ${(props) =>
      props.active ? Colors.blue[100] : Colors.indigo[25]};
    border-width: ${Spacing.borderWidthThin};
    border-radius: ${Spacing.borderRadiusLarge};
    padding: ${Spacing.spacingXSmall};
    display: flex;
    align-items: center;
    color: ${Colors.blue[100]};
    font-weight: ${Typography.fontWeightBold};
    font-size: ${Typography.fontSizeSmall};
    cursor: pointer;
  }
`;

const OnDragText = styled.div`
  flex-grow: 1;
  text-align: center;
`;

const UploadContainer = styled.div`
  height: 3rem;
  display: flex;
  align-items: center;
  padding-left: ${Spacing.spacingMedium};
  border-width: ${Spacing.borderWidthThin};
  border-radius: ${Spacing.borderRadiusLarge};
  border-color: ${Colors.indigo[25]};
  border-style: solid;
  word-break: break-all;
`;

const StrechDiv = styled.div`
  flex: 1;
  margin-right: ${Spacing.spacingMedium};
`;

const StyledProgress = styled(Progress)`
  &&& {
    margin: 0;
  }
`;

export default class EhFileUpload extends PureComponent {
  static propTypes = {
    /** The destination to upload the file. */
    uploadUrl: PropTypes.string,
    /** The Parameters to pass to axios. */
    uploadParams: PropTypes.object,
    /** The message which appears below the drag area. */
    acceptText: PropTypes.string,
    /** The file formats to accept. */
    accept: PropTypes.string,
    /** The maximum file size to accept. */
    maxSize: PropTypes.string,
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    value: PropTypes.object,
    /** The HTML input name. */
    name: PropTypes.string.isRequired,
    removeFromCache: PropTypes.bool,
    /** The functiom for inline file validation errors */
    renderError: PropTypes.func,
    showCustomisedFileUpload: PropTypes.bool,
    // Prop to make upload file button primary
    uploadButtonPrimary: PropTypes.bool,
  };

  static defaultProps = {
    maxSize: "50MB",
    accept:
      "application/pdf, application/msword, application/vnd.openxmlformats-officedocument.body.wordprocessingml.document, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, image/tiff, .tif, image/jpeg, image/png, audio/wav",
  };

  state = {};
  cancelRequest;

  modalOptions = [
    {
      text: "OK",
      onClick: () => this.closeModal(),
    },
  ];

  removeUpload = () => {
    this.setState({
      fileName: null,
      progress: null,
    });
    if (this.props.removeFromCache && this.props.value) {
      this.removeFromCache();
    }
    this.notifyChange();
  };

  notifyChange(val) {
    this.props.onChange && this.props.onChange(val);
  }
  removeFromCache() {
    axios.delete(`${this.props.uploadUrl}/${this.props.value}`, {
      params: this.props.uploadParams,
    });
  }

  showError(message) {
    this.setState({ uploadError: message });
  }

  closeModal() {
    this.setState({ uploadError: null });
  }

  componentWillUnmount() {
    this.cancelUpload();
  }

  cancelUpload = () => {
    this.cancelRequest && this.cancelRequest();
  };

  onUploadComplete = (response) => {
    this.setState({
      progress: null,
    });
    this.notifyChange(
      this.props.showCustomisedFileUpload ? response.data : response.data.id
    );
  };

  onUploadProgress = (progress) => {
    this.setState({
      progress: Math.floor((progress.loaded / progress.total) * 100),
    });
  };

  onUploadError = (error) => {
    this.setState({
      fileName: null,
      progress: null,
    });
    if (!axios.isCancel(error)) {
      const { response } = error;
      this.showError(response.data.error.message);
    }
  };

  onDropEnter = () => {
    this.setState({ showDropText: true });
  };

  onDropLeave = () => {
    this.setState({ showDropText: false });
  };

  doUpload = (acceptedFiles) => {
    this.setState({
      showDropText: false,
      uploadError: null,
    });

    const file = acceptedFiles[0];
    if (!acceptedFiles.length) {
      this.showError("File type not supported");
      this.props.onChange({ file });
      return;
    }

    if (this.props.maxSize && file.size > bytes.parse(this.props.maxSize)) {
      this.showError(`File size larger than ${this.props.maxSize}`);
      this.props.onChange({ file });
      return;
    }
    this.setState({
      fileName: file.name,
    });
    const data = new FormData();
    data.append("file", file, file.name);
    if (this.props.uploadUrl) {
      this.setState({
        progress: 1,
      });
      axios
        .post(this.props.uploadUrl, data, {
          onUploadProgress: this.onUploadProgress,
          withCredentials: true,
          params: this.props.uploadParams,
          cancelToken: new axios.CancelToken((c) => {
            this.cancelRequest = c;
          }),
        })
        .then(this.onUploadComplete, this.onUploadError);
    } else {
      this.props.onChange({ file });
    }
  };

  render() {
    const { progress, fileName } = this.state;
    if (!this.props.value || !fileName) {
      if (!progress) {
        return (
          <Fragment>
            <StyledDropzone
              accept={this.props.accept}
              name={this.props.name}
              multiple={false}
              onDrop={this.doUpload}
              onDragEnter={this.onDropEnter}
              onDragLeave={this.onDropLeave}
              disablePreview
              active={this.state.showDropText ? 1 : 0}
            >
              {this.state.showDropText ? (
                <OnDragText>Drop File Here</OnDragText>
              ) : (
                <Fragment>
                  <EhButton
                    icon="cloud upload"
                    text="Upload File"
                    primary={this.props.uploadButtonPrimary}
                  />
                  or Drag File Here
                </Fragment>
              )}
            </StyledDropzone>
            {this.props.acceptText ? (
              <EhText size="xsmall">{this.props.acceptText}</EhText>
            ) : null}
            {this.props.renderError ? (
              this.props.renderError({
                message: this.state.uploadError,
              })
            ) : (
              <EhAlertModalMat
                open={!!this.state.uploadError}
                heading="Oops, file upload failed"
                buttons={this.modalOptions}
                message={this.state.uploadError || null}
              />
            )}
          </Fragment>
        );
      } else {
        return (
          <UploadContainer>
            <StrechDiv>
              <StyledProgress percent={progress} />
            </StrechDiv>
            <div>{`${progress || 0}%`}</div>
            <EhButton onClick={this.cancelUpload} borderless negative>
              Cancel
            </EhButton>
          </UploadContainer>
        );
      }
    } else {
      return (
        !this.props.showCustomisedFileUpload && (
          <UploadContainer>
            <StrechDiv>
              <Icon name="file alternate" />
              {fileName}
            </StrechDiv>
            <EhButton
              data-aq="btnRemove"
              icon="trash alternate"
              borderless
              onClick={this.removeUpload}
            />
          </UploadContainer>
        )
      );
    }
  }
}
