import { useRef } from "react";
import PropTypes from "prop-types";
import EhText from "./typography/EhText";
import EhDate from "./EhDate";
import styled from "styled-components";
import { Spacing } from "../style";
import { useIsWithinScrollWidth } from "../hooks";
import EhPopup from "./EhPopup";

const StyledDiv = styled.div`
  display: flex;
  white-space: nowrap;

  && div {
    margin-top: 0px;

    &.date {
      overflow: hidden;
      text-overflow: ellipsis;
      margin-top: 0px;
      min-width: ${Spacing.spacingXLarge};
    }
  }
`;

function EhUserDateField(props) {
  const refDate = useRef(null);
  const refUserName = useRef(null);
  const shouldPopupDate = useIsWithinScrollWidth(refDate);
  const shouldPopupUserName = useIsWithinScrollWidth(refUserName);

  const { userName, dateValue, type, localTime } = props;

  const renderDate = (refProp) => (
    <EhDate
      className="date"
      ref={refProp}
      value={dateValue}
      type={type}
      localTime={localTime}
      data-aq="date"
    />
  );

  const renderUserName = (refProp) => (
    <EhText data-aq="username" ref={refProp} ellipsis showTitle={false}>
      {userName}
    </EhText>
  );

  return (
    <StyledDiv data-aq={props["data-aq"]}>
      {shouldPopupUserName ? (
        <EhPopup ref={refUserName} trigger={renderUserName} size="mini" />
      ) : (
        renderUserName(refUserName)
      )}
      <EhText>&nbsp; at &nbsp;</EhText>
      {shouldPopupDate ? (
        <EhPopup ref={refDate} trigger={renderDate} size="mini" />
      ) : (
        renderDate(refDate)
      )}
    </StyledDiv>
  );
}

EhUserDateField.propTypes = {
  /** The name of the user to display. */
  userName: PropTypes.string.isRequired,
  /** The date which will be formatted. */
  dateValue: PropTypes.string.isRequired,
  /** The type of date being passed. */
  type: PropTypes.oneOf(["date", "time", "datetime"]).isRequired,
  /** The flag which determines if local time should be used. */
  localTime: PropTypes.bool,
  "data-aq": PropTypes.string,
};

export default EhUserDateField;
