import { PureComponent } from "react";
import { TextArea } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled from "styled-components";

import { EhFormattedText } from "../typography";
import { Colors, Typography } from "../../style";

const StyledTextArea = styled(TextArea)`
  &&&& {
    resize: none;
    font-family: ${Typography.fontFamily};

    &:disabled {
      background-color: ${Colors.grey[5]};
      border-color: ${Colors.grey[25]};
      color: ${Colors.grey[25]};
    }

    &::placeholder {
      color: ${Colors.grey[40]} !important;
    }
  }
`;

class EhTextarea extends PureComponent {
  static propTypes = {
    error: PropTypes.bool,
    loading: PropTypes.bool,
    /** The maximum number of characters allowed. */
    maxLength: PropTypes.number,
    /** The hint text to appear when no text is input. */
    placeholder: PropTypes.string,
    renderButton: PropTypes.func,
    /** The flag which will set the component in the error state when attempting to move forward without completion. */
    required: PropTypes.bool,
    /** The number of rows to show at once without a scrollbar (height). */
    rows: PropTypes.number,
    /** The input text. */
    value: PropTypes.string,
  };

  static defaultProps = {
    value: "",
    rows: 3,
  };

  render() {
    const { error, loading, maxLength, renderButton, required, ...props } =
      this.props;
    return (
      <div>
        <StyledTextArea maxLength={maxLength} {...props} />
        {maxLength && (
          <EhFormattedText
            size="xsmall"
            textAlign="right"
            type="substitutor"
            value={`Remaining Characters: <<${
              maxLength - (this.props.value?.length || 0)
            }|number>>`}
          />
        )}
      </div>
    );
  }
}

export default EhTextarea;
