import styled from "styled-components";
import { Typography, LineHeight, Spacing, Colors } from "../../style";
import PropTypes from "prop-types";
import { Component } from "react";
import EhPopup from "../EhPopup";

const sizeStyle = {
  large: `
  font-size: ${Typography.fontSizeLarge};
  line-height: ${LineHeight.text.large};
`,
  medium: `
  font-size: ${Typography.fontSizeMedium};
  line-height: ${LineHeight.text.large};
`,
  "medium-condensed": `
  font-size: ${Typography.fontSizeMedium};
  line-height: ${LineHeight.text.small};
`,
  small: `
  font-size: ${Typography.fontSizeSmall};
  line-height: ${LineHeight.text.small};
`,
  xsmall: `
  font-size: ${Typography.fontSizeXSmall};
  line-height: ${LineHeight.text.small};
`,
};

const fontWeightStyle = {
  normal: Typography.fontWeightRegular,
  bold: Typography.fontWeightBold,
  black: Typography.fontWeightBlack,
};

/* eslint-disable */
const Div = ({
  wrapText,
  textAlign,
  size,
  color,
  fontWeight,
  fontStyle,
  ellipsis,
  popUpData,
  popUpHeader,
  shade,
  showTitle,
  textTransform,
  "data-aq": dataAq,
  children,
  className,
  onClick,
  title,
}) => (
  <div className={className} onClick={onClick} data-aq={dataAq} title={title}>
    {children}
  </div>
);
/* eslint-enable */

const wrapText = (props) =>
  props.wrapText ? `word-wrap: break-word;` : `white-space: nowrap;`;

const ellipsis = (props) =>
  props.ellipsis ? `overflow: hidden;text-overflow: ellipsis;` : null;

const wordBreak = (props) =>
  props.wordBreak ? `word-break: ${props.wordBreak}` : "";

const lineCount = (props) =>
  props.lineCount && props.ellipsis
    ? `white-space: inherit;
      -webkit-line-clamp: ${props.lineCount};
      display: -webkit-box;
      -webkit-box-orient: vertical;`
    : null;

const getColor = (props) => {
  const { color, shade } = props;
  let colorVal = "inherit";

  if (color) {
    if (shade) {
      colorVal = Colors[color][shade];
    } else if (color === "black" || color === "white") {
      colorVal = Colors[color];
    } else {
      colorVal = color === "grey" ? Colors.grey[40] : Colors[color][100];
    }
  }

  return colorVal;
};

const Text = styled(Div)`
  color: ${(props) => getColor(props)} !important;
  ${(props) => sizeStyle[props.size]};
  text-align: ${(props) => props.textAlign};
  font-weight: ${(props) => fontWeightStyle[props.fontWeight]};
  font-style: ${(props) => props.fontStyle};
  ${wrapText};
  ${ellipsis};
  ${lineCount};
  ${wordBreak};
  :not(:first-child) {
    margin-top: ${Spacing.spacingXxSmall};
  }
  display: ${(props) => props.display};
  text-transform: ${(props) => props.textTransform || "none"};
`;

const lineCountPropValidation = (props, propName, componentName) => {
  componentName = componentName || "ANONYMOUS";
  if (props[propName] && !props.ellipsis) {
    return new Error(
      `ellipsis prop should be passed if ${propName} prop is passed in ${componentName}`
    );
  }
  return null;
};

export default class EhText extends Component {
  static propTypes = {
    /** The size of the text. */
    size: PropTypes.oneOf([
      "large",
      "medium",
      "medium-condensed",
      "small",
      "xsmall",
    ]),
    /** The alignment of the text. */
    textAlign: PropTypes.oneOf(["left", "center", "right", "justify"]),
    /** The weight of the text. */
    fontWeight: PropTypes.oneOf(["normal", "bold", "black"]),
    /** The style of the text. */
    fontStyle: PropTypes.oneOf(["italic", "normal"]),
    /**  sets how the line breaks should appear. */
    wordBreak: PropTypes.oneOf([
      "normal",
      "break-all",
      "keep-all",
      "break-word",
    ]),
    /** The flag which determines if the text should wrap when too long. */
    wrapText: PropTypes.bool,
    /** The flag which determines if the text should display ellipsis when too long. */
    ellipsis: PropTypes.bool,
    /** The number of lines after which ellipsis should be displayed */
    lineCount: lineCountPropValidation,
    /** The color of the text. */
    color: PropTypes.string,
    /** The value of the text */
    children: PropTypes.any,
    /** The value of the text displaying on popUp */
    popUpData: PropTypes.any,
    /** The value of the text displaying on popUp header */
    popUpHeader: PropTypes.string,
    /** The flag which determines if the title on ellipsis should be shown or not */
    showTitle: PropTypes.bool,
    /** a number value indicating the shade of the color to use (available shades vary by color) */
    shade: PropTypes.number,
    /** display inline with another element */
    display: PropTypes.oneOf(["none", "inline", "block", "inline-block"]),
    /** The flag which determines if the text should wrap when too long. */
    textTransform: PropTypes.oneOf(["capitalize", "lowercase", "uppercase"]),
  };

  static defaultProps = {
    wrapText: true,
    showTitle: true,
  };

  getTitle = () => {
    const { ellipsis, showTitle, children } = this.props;
    return ellipsis && showTitle ? children : undefined;
  };

  render() {
    const { popUpData, popUpHeader } = this.props;
    const renderData = () => (
      <Text
        {...this.props}
        title={this.getTitle()}
        wrapText={!this.props.ellipsis && this.props.wrapText}
      />
    );
    const triggerMethod = () => <div>{renderData()}</div>;
    return popUpData ? (
      <EhPopup
        trigger={triggerMethod}
        size="small"
        content={popUpData}
        header={popUpHeader}
      />
    ) : (
      renderData()
    );
  }
}
