import PropTypes from "prop-types";
import styled from "styled-components";
import EhModalMat from "../componentsStd/layout/EhModalMat";
import EhButton from "./EhButton";
import EhButtonGroup from "./EhButtonGroup";
import EhDocument from "../componentsStd/EhDocument";

const StyledDiv = styled.div`
  min-height: 50vh;
`;

const DocumentViewer = ({
  closeModal,
  logic,
  title,
  showPrint = true,
  showDownload = true,
  showSearch = false,
  ...EhModalMatProps
}) => {
  const renderHeader = () => title;
  const renderFooter = () => (
    <EhButtonGroup>
      <EhButton onClick={closeModal} primary>
        Done
      </EhButton>
    </EhButtonGroup>
  );
  const wrappedCloseModal = () => {
    closeModal();
  };

  return (
    <EhModalMat
      renderHeader={renderHeader}
      renderFooter={renderFooter}
      {...EhModalMatProps}
      onClose={wrappedCloseModal}
    >
      <StyledDiv>
        <EhDocument
          logic={logic}
          showPrint={showPrint}
          showDownload={showDownload}
          showSearch={showSearch}
        />
      </StyledDiv>
    </EhModalMat>
  );
};

DocumentViewer.propTypes = {
  closeModal: PropTypes.func.isRequired,
  logic: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  showPrint: PropTypes.bool,
  showDownload: PropTypes.bool,
  showSearch: PropTypes.bool,
};
export default DocumentViewer;
