import PropTypes from "prop-types";
import { PureComponent, Fragment } from "react";
import styled from "styled-components";
import { Colors, Spacing, Typography, Elevation } from "../../style";
import { Checkbox, Icon } from "semantic-ui-react";

export const checkboxBorderColor = `
&&&& input:focus~label:before,input:hover~label:before{
  border-color:${Colors.blue[130]};
  box-shadow: 1px 2px 2px rgba(0,0,0,0.5);
}
  label{
    padding-left: ${Spacing.spacingXLarge};
    &::before {
      border-color: ${Colors.indigo[25]};
      width: ${Spacing.spacingLarge};
      height: ${Spacing.spacingLarge};
      border-radius: ${Spacing.borderRadiusLarge};
      box-shadow: ${Elevation.boxShadowXSmall};
    }
    &::after {
      color: ${Colors.grey[100]};
      width: ${Spacing.spacingLarge};
      height: ${Spacing.spacingLarge};
      padding: ${Spacing.spacingXxxSmall};
      font-size: ${Typography.fontSizeXSmall};
      line-height: 1;
    }
}

  &.checked label::before {
    border-color: ${Colors.indigo[50]};
  }

  
  label:focus::before,
  label:hover::before{
    border-color:${Colors.blue[130]};
    box-shadow: 1px 2px 2px rgba(0,0,0,0.5);
  }
`;

export const StyledCheckbox = styled(Checkbox)`
  &&&&&&&& {
    ${checkboxBorderColor};
  }
`;

class EhCheckbox extends PureComponent {
  // Removed importing PropTypes from Semantic Modal due to overhead it was causing for documentation.
  static propTypes = {
    /** The flag which determines if the component should be in the edit or display state. */
    editMode: PropTypes.bool,
    error: PropTypes.bool,
    loading: PropTypes.bool,
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    renderButton: PropTypes.func,
    /** The flag which determines the checked state. */
    value: PropTypes.bool,
    /** A function to override what renders as display, gets value as a parameter. */
    viewFormat: PropTypes.func,
    /** The text of the associated label element. */
    label: PropTypes.any,
    /** A checkbox can appear disabled and be unable to change states */
    disabled: PropTypes.bool,
    /** Whether or not checkbox is indeterminate. */
    indeterminate: PropTypes.bool,
    /** A checkbox can be read-only and unable to change states. */
    readOnly: PropTypes.bool,
  };

  static defaultProps = {
    editMode: true,
  };

  onChange = (e, { checked }) => {
    this.props.onChange && this.props.onChange(checked);
  };

  render() {
    const {
      editMode,
      error,
      loading,
      label,
      renderButton,
      value,
      viewFormat,
      ...remainingProps
    } = this.props;
    if (editMode) {
      return (
        <StyledCheckbox
          {...remainingProps}
          label={label}
          checked={value || false}
          onChange={this.onChange}
        />
      );
    } else {
      return (
        <Fragment>
          {viewFormat ? (
            viewFormat(value)
          ) : (
            <Fragment>
              {value ? <Icon name={"check"} /> : <Icon name={"ban"} />} {label}
            </Fragment>
          )}
        </Fragment>
      );
    }
  }
}

export default EhCheckbox;
