import {
  filter as _filter,
  flow,
  get,
  startsWith as _startsWith,
  toLower,
} from "lodash";
import DateHandler from "./dateHandler";

const startsWith = (field, value, formatData) => {
  return (item) => {
    const itemValue = get(item, field);
    return _startsWith(
      toLower(formatData ? formatData(itemValue) : itemValue),
      value.toString().toLowerCase()
    );
  };
};

const contains = (field, value, formatData) => {
  return (item) => {
    const itemValue = get(item, field);
    if (typeof value === "string") {
      return (
        toLower(formatData ? formatData(itemValue) : itemValue).indexOf(
          value.toLowerCase()
        ) !== -1
      );
    } else {
      return itemValue.indexOf(value) > -1;
    }
  };
};

const equals = (field, value, formatData) => {
  return (item) => {
    if (typeof value === "string") {
      const itemValue = get(item, field);
      return (
        toLower(formatData ? formatData(itemValue) : itemValue) ===
        value.toLocaleLowerCase()
      );
    } else {
      return get(item, field) === value;
    }
  };
};

const isOneOf = (field, values) => {
  return (item) => {
    return values.some((val) => get(item, field) === val);
  };
};

const dateRange = (field, beginDateStr, endDateStr) => {
  return (item) => {
    const fieldDate = DateHandler.getDate(get(item, field));
    if (beginDateStr && endDateStr) {
      const beginDate = DateHandler.getDate(beginDateStr);
      const endDate = DateHandler.getDate(endDateStr);
      return DateHandler.isBetween(fieldDate, beginDate, endDate);
    } else if (beginDateStr) {
      const beginDate = DateHandler.getDate(beginDateStr);
      return DateHandler.isSameOrAfter(fieldDate, beginDate);
    } else if (endDateStr) {
      const endDate = DateHandler.getDate(endDateStr);
      return DateHandler.isSameOrBefore(fieldDate, endDate);
    }
  };
};

export const filters = {
  contains,
  dateRange,
  equals,
  isOneOf,
  startsWith,
};

export default filters;

export const filter = (data, filters) => {
  const filterFuncs = filters.map((f) => (data) => _filter(data, f.filter));
  return flow(filterFuncs)(data);
};
