import EhAppDrawerSidebar from "./AppDrawerSidebar";
import EhAppDrawerPopup from "./AppDrawerPopup";
import PropTypes from "prop-types";

const EhAppDrawer = ({ type = "sidebar", ...remainingProps }) => {
  switch (type) {
    case "sidebar":
      return <EhAppDrawerSidebar {...remainingProps} />;

    case "popup":
      return <EhAppDrawerPopup {...remainingProps} />;
    default:
      return <EhAppDrawerSidebar {...remainingProps} />;
  }
};

EhAppDrawer.propTypes = {
  type: PropTypes.oneOf(["sidebar", "popup"]),
};

export default EhAppDrawer;
