import dayjs from "dayjs";
const utc = require("dayjs/plugin/utc");
const timezone = require("dayjs/plugin/timezone");
const advanced = require("dayjs/plugin/advancedFormat");
const isBetween = require("dayjs/plugin/isBetween");
const isSameOrBefore = require("dayjs/plugin/isSameOrBefore");
const isSameOrAfter = require("dayjs/plugin/isSameOrAfter");
const customParseFormat = require("dayjs/plugin/customParseFormat");
dayjs.extend(utc);
dayjs.extend(timezone);
dayjs.extend(advanced);
dayjs.extend(isBetween);
dayjs.extend(isSameOrBefore);
dayjs.extend(isSameOrAfter);
dayjs.extend(customParseFormat);
const INVALID_DATE = "Invalid Date";
const DATE_UNIT = {
  day: "day",
  week: "week",
  month: "month",
  year: "year",
  second: "second",
};

const today = (format) => {
  return dayjs().format(format || "YYYY-MM-DD");
};
const now = () => {
  return dayjs();
};
const utcNow = () => {
  return dayjs.utc();
};
const getDate = (dateString, format) => {
  return dayjs(dateString, format);
};
const getTime = (dateString) => {
  return dayjs(dateString).format("HH:mm:ss");
};
const getEndOfDay = (date) => {
  return date ? dayjs(date).endOf("day").format("YYYY-MM-DD HH:mm:ss") : date;
};
const getStartOfDay = (date) => {
  return date ? dayjs(date).startOf("day").format("YYYY-MM-DD HH:mm:ss") : date;
};
const formatDate = (dateValue, format) => {
  const dateFormat = format || "MM/DD/YYYY";
  if (!dateValue) return dateValue;
  const formattedDate = dayjs(dateValue).format(dateFormat);
  if (formattedDate === INVALID_DATE) {
    return dateValue;
  }
  return formattedDate;
};
const parseDate = (dateValue, format) => {
  if (!dateValue) {
    return dateValue;
  }
  return dayjs(dateValue, format).toDate();
};
const parseToServerDate = (dateValue) => {
  if (!dateValue) {
    return dateValue;
  }
  return dayjs.tz(dateValue, "America/New_York").toDate();
};
const toLocalTime = (dateValue, format) => {
  try {
    const date = String(dateValue);
    const localTime = dayjs.tz(date, "America/New_York");
    return localTime.tz(dayjs.tz.guess()).format(format);
  } catch {
    return dateValue;
  }
};
const toServerTime = (dateValue) => {
  return dayjs(dateValue).tz("America/New_York").format("YYYY-MM-DDTHH:mm:ss");
};
const add = (dateValue, number, unit) => {
  return dayjs(dateValue).add(number, unit).toDate();
};
const subtract = (dateValue, number, unit) => {
  return dayjs(dateValue).subtract(number, unit).toDate();
};
const checkTwoDatesAreValid = (date1, date2) => {
  const dayjsDate1 = dayjs(date1);
  const dayjsDate2 = dayjs(date2);
  return {
    isValid: dayjsDate1.isValid() && dayjsDate2.isValid(),
    dayjsDate1,
    dayjsDate2,
  };
};
const diff = (startDate, endDate, unit, isDecimal) => {
  const dayjsEndDate = dayjs(endDate);
  const dayjsStartDate = dayjs(startDate);
  if (dayjsEndDate.isValid() && dayjsStartDate.isValid()) {
    const difference = dayjsEndDate.diff(dayjsStartDate, unit, true);
    if (isDecimal) {
      return difference;
    } else {
      return Math.ceil(difference);
    }
  } else {
    return undefined;
  }
};
const getAge = (dobString) => {
  const dob = dayjs(dobString);
  const dayjsNow = dayjs();
  if (dob.isValid() && dob < dayjsNow) {
    return dayjsNow.diff(dob, DATE_UNIT.year);
  }
};
const isValid = (dateValue, format) => {
  if (format === undefined || !format || format.length === 0) {
    throw new Error("DateHandler: Date format missing.");
  }
  return dayjs(dateValue, format, true).isValid();
};
const isBefore = (date1, date2) => {
  const { isValid, dayjsDate1, dayjsDate2 } = checkTwoDatesAreValid(
    date1,
    date2
  );
  if (isValid) {
    return dayjsDate1.isBefore(dayjsDate2);
  }
};
const isDateSameOrBefore = (date1, date2) => {
  const { isValid, dayjsDate1, dayjsDate2 } = checkTwoDatesAreValid(
    date1,
    date2
  );
  if (isValid) {
    return dayjsDate1.isSameOrBefore(dayjsDate2, "day");
  }
};
const isAfter = (date1, date2) => {
  const { isValid, dayjsDate1, dayjsDate2 } = checkTwoDatesAreValid(
    date1,
    date2
  );
  if (isValid) {
    return dayjsDate1.isAfter(dayjsDate2);
  }
};

const isDateSameOrAfter = (date1, date2) => {
  const { isValid, dayjsDate1, dayjsDate2 } = checkTwoDatesAreValid(
    date1,
    date2
  );
  if (isValid) {
    return dayjsDate1.isSameOrAfter(dayjsDate2, "day");
  }
};
const isDateBetween = (date, fromDate, toDate) => {
  // for non-math-nerds, "[]" indicates inclusive in between
  return dayjs(date).isBetween(fromDate, toDate, "day", "[]");
};
const localizeComments = (comment, showTimezone) => {
  function defaultReplace(dateMatch) {
    return dayjs
      .tz(dateMatch, "M/D/YYYY h:mm:ss A", "America/New_York")
      .tz(dayjs.tz.guess())
      .format("MM/DD/YYYY h:mm A");
  }
  function timezoneReplace(dateMatch) {
    return dayjs
      .tz(dateMatch, "M/D/YYYY h:mm:ss A", "America/New_York")
      .tz(dayjs.tz.guess())
      .format("MM/DD/YYYY h:mm A z");
  }
  function findAndReplaceCommentDate(comment, replaceFunc, ignoreSeconds) {
    if (comment) {
      const timeRegex =
        "(1[0-2]|[0]?[0-9])\\/([1-3][0-9]|[0]?[0-9])\\/(2[0-9]{3})\\s(1[0-2]|[0-9]):([0-5][0-9])" +
        (ignoreSeconds ? "" : ":([0-5][0-9])") +
        "\\s[AP][M]";
      const commentRegex = "-\\s.+,\\s.+\\s" + timeRegex;
      return comment.replace(new RegExp(commentRegex, "g"), function (match) {
        return match.replace(new RegExp(timeRegex), replaceFunc);
      });
    } else {
      return "";
    }
  }
  const replaceFunc = showTimezone ? timezoneReplace : defaultReplace;
  return findAndReplaceCommentDate(comment, replaceFunc);
};
const getZoneName = () => {
  const timeZone = dayjs.tz.guess();
  const zoneName = dayjs().tz(timeZone).format("zzz");
  const abbrZoneName = zoneName.match(/[A-Z]-?\d?\d?:?\d?\d?/g).join("");
  return abbrZoneName;
};

const toLocalTimeFromUTC = (inputDate) => {
  if (!inputDate) return inputDate;
  if (DateHandler.formatDate(new Date(inputDate)) === "12/31/9999")
    return inputDate;
  if (DateHandler.formatDate(new Date(inputDate)) <= "01/01/1900")
    return inputDate;
  const utcDate = dayjs.utc(inputDate).toDate();
  const local = dayjs(utcDate).local().format("YYYY-MM-DD HH:mm:ss");
  if (local === DateHandler.INVALID_DATE) return inputDate;
  return local;
};

const DateHandler = {
  today,
  now,
  utcNow,
  getDate,
  getTime,
  formatDate,
  parseDate,
  parseToServerDate,
  toLocalTime,
  toServerTime,
  add,
  subtract,
  diff,
  getAge,
  isValid,
  isBefore,
  isSameOrBefore: isDateSameOrBefore,
  isAfter,
  isSameOrAfter: isDateSameOrAfter,
  isBetween: isDateBetween,
  localizeComments,
  getZoneName,
  getEndOfDay,
  getStartOfDay,
  DATE_UNIT,
  INVALID_DATE,
  toLocalTimeFromUTC,
};
export default DateHandler;
