import { PureComponent, Fragment } from "react";
import { escapeRegExp, filter, flatten, reduce, some, isString } from "lodash";
import styled from "styled-components";
import PropTypes from "prop-types";
import { defaultMemoize } from "reselect";
import { Grid, Icon, Loader } from "semantic-ui-react";

import { Spacing, Colors, Typography } from "../../style";
import EhCheckbox from "./EhCheckbox";
import EhInput from "./EhInput";
import { EhText, EhFormattedText } from "../typography";

export const CheckboxGroupContainer = styled.div`
  height: ${(props) => props.height};
  overflow: auto;
  margin-bottom: ${Spacing.spacingNone};
  padding: ${Spacing.spacingXxSmall} ${Spacing.spacingSmall};
`;

export const MultiselectContainer = styled.div`
  border: ${Spacing.borderWidthThin} solid
    ${(props) => (props.error ? Colors.red[100] : Colors.indigo[25])};
  border-radius: ${Spacing.borderRadiusLarge};
`;

export const MultiselectHeader = styled.div`
  padding: ${Spacing.spacingSmall};
  border-bottom: ${Spacing.borderWidthThin} solid
    ${(props) => (props.error ? Colors.red[100] : Colors.indigo[25])};
`;

export const GroupCheckboxContainer = styled.div`
  margin-bottom: ${Spacing.spacingXSmall};
`;

export const NestedCheckboxContainer = styled.div`
  padding-left: ${Spacing.spacingLarge};
`;

export const SearchInput = styled(EhInput)`
  &&&&& input {
    border-color: ${Colors.indigo[25]};
    font-size: ${Typography.fontSizeSmall};
    height: ${Spacing.spacingXLarge};
    padding: ${Spacing.spacingXxSmall} 1.375rem ${Spacing.spacingXxSmall}
      ${Spacing.spacingXSmall} !important;
    width: 10.25rem;
    &::placeholder {
      color: ${Colors.grey[40]};
    }
  }

  &&&&& input:focus {
    border-color: ${Colors.blue[100]};

    &::placeholder {
      color: ${Colors.grey[55]};
    }
  }
  &&&& > i.icon {
    height: ${Spacing.spacingLarge};
    margin: ${Spacing.spacingXxSmall};
    right: 0;
    top: 0;
    width: ${Spacing.spacingLarge};
  }
`;

export default class EhCheckboxGroup extends PureComponent {
  static propTypes = {
    /** The object used to display options. */
    options: PropTypes.arrayOf(PropTypes.any).isRequired,
    /** Used to determine which field to use as a display from options. */
    display: PropTypes.oneOfType([PropTypes.string, PropTypes.func]).isRequired,
    /** Select will only fire value with select field */
    select: PropTypes.string,
    /** Key indentifier for each option */
    keyField: PropTypes.string, // eslint-disable-line react/no-unused-prop-types
    // (linter gives false positive here because it is used via static method)
    /** The function which gets called when a change is detected. */
    onChange: PropTypes.func,
    /** The checkbox which is checked. */
    value: PropTypes.array,
    /** The flag which determines if the checkboxes should be rendered as nested. */
    nested: PropTypes.bool,
    /** The flag which determines if the search bar should be displayed. */
    enableSearch: PropTypes.bool,
    /** The flag which determines if the select all option appears above the component. */
    enableSelectAll: PropTypes.bool,
    /** The flag which determines if the selected count appears below the component. */
    showCount: PropTypes.bool,
    /** The height of the checkbox container in pixels. */
    height: PropTypes.string,
    /** The message to display when search has no results. */
    noResultsText: PropTypes.string,
    /** The flag which determines the loading state. */
    loading: PropTypes.bool,
    /** The flag which determines the error state. */
    error: PropTypes.bool,
    /** Placeholder will be displayed into search input */
    searchPlaceholder: PropTypes.string,
    /** Search will work on provided fields from options */
    searchFields: PropTypes.arrayOf(PropTypes.string),
    // The flag which will disable the checkboxes
    disabled: PropTypes.bool,
  };

  static defaultProps = {
    value: [],
    enableSelectAll: true,
    noResultsText: "No Results",
    height: "150px",
    searchPlaceholder: "Search",
  };

  static getDerivedStateFromProps(props) {
    let { options, nested, display, select, value, keyField } = props;
    keyField = select || keyField || display;

    const keyOptionMap = EhCheckboxGroup.getKeyOptionMap(
      options,
      nested,
      keyField
    );

    return {
      keyField,
      keyOptionMap,
      flatOptions: EhCheckboxGroup.getFlattenedOptions(options, nested),
      valueSet: EhCheckboxGroup.getValueSet(keyField, select, value),
    };
  }

  static getKeyOptionMap = defaultMemoize(function (options, nested, key) {
    const flattenOptions = EhCheckboxGroup.getFlattenedOptions(options, nested);
    const keyOptionMap = {};
    flattenOptions.forEach((item) => {
      keyOptionMap[item[key]] = item;
    });

    return keyOptionMap;
  });

  static getValueSet = defaultMemoize(function (key, select, value) {
    return new Set(value.map((v) => (select ? v : v[key])));
  });

  static getFilteredItems = defaultMemoize(
    (options, nested, display, query, searchFields) => {
      let filteredItems;
      const regexQuery = new RegExp(escapeRegExp(query), "i");
      const displayItem = (item) => {
        return isString(display) ? item[display] : display(item);
      };
      if (nested) {
        filteredItems = reduce(
          options,
          (result, group) => {
            if (
              some(group.options, (item) => regexQuery.test(displayItem(item)))
            ) {
              result.push({
                ...group,
                options: filter(group.options, (item) =>
                  regexQuery.test(displayItem(item))
                ),
              });
            }

            return result;
          },
          []
        );
      } else {
        filteredItems = options.filter((item) =>
          searchFields
            ? searchFields.some((field) => regexQuery.test(item[field]))
            : regexQuery.test(displayItem(item))
        );
      }

      return filteredItems;
    }
  );

  static getFlattenedOptions = defaultMemoize((options, nested) => {
    let flattenOptions = options;
    if (nested) {
      flattenOptions = flatten(options.map((group) => group.options));
    }

    return flattenOptions;
  });

  state = {
    query: "",
    searchIsFocused: false,
  };

  onChange = (value) => {
    this.props.onChange &&
      this.props.onChange(
        this.props.select ? value : value.map((v) => this.state.keyOptionMap[v])
      );
  };

  handleSelectAll = () => {
    const { flatOptions, keyField, valueSet } = this.state;
    const newValue = new Set(valueSet);

    if (newValue.size === 0) {
      flatOptions.forEach((option) => newValue.add(option[keyField]));
    } else {
      newValue.clear();
    }

    this.onChange(Array.from(newValue));
  };

  onSearchChange = (e) => {
    this.setState({ query: e.target.value });
  };

  onSearchFocus = () => {
    this.setState({ searchIsFocused: true });
  };

  onSearchBlur = () => {
    this.setState({ searchIsFocused: false });
  };

  onClearSearchClick = () => {
    this.setState({ query: "" });
  };

  checkOptionSelected = (option) =>
    this.state.valueSet.has(option[this.state.keyField]);

  groupCheckboxClicked = (e, { name }) => {
    const newValue = new Set(this.state.valueSet);
    const group = this.props.options.find((group) => group.name === name);
    const changeGroupOption = group.options.some(this.checkOptionSelected)
      ? (key) => newValue.delete(key)
      : (key) => newValue.add(key);
    group.options.forEach((option) =>
      changeGroupOption(option[this.state.keyField])
    );
    this.onChange(Array.from(newValue));
  };

  checkboxChanged(key, value) {
    const newValue = new Set(this.state.valueSet);
    if (value) {
      newValue.add(key);
    } else {
      newValue.delete(key);
    }
    this.onChange(Array.from(newValue));
  }

  render() {
    const {
      display,
      enableSearch,
      enableSelectAll,
      height,
      loading,
      options,
      nested,
      noResultsText,
      showCount,
      value,
      error,
      searchPlaceholder,
      searchFields,
      disabled,
    } = this.props;
    const { flatOptions, query, searchIsFocused, valueSet } = this.state;

    const filteredItems = EhCheckboxGroup.getFilteredItems(
      options,
      nested,
      display,
      query,
      searchFields
    );

    const showHeader = enableSearch || enableSelectAll;
    const searchIsActive = enableSearch && (searchIsFocused || query);

    return (
      <Fragment>
        <MultiselectContainer error={error}>
          {showHeader && (
            <MultiselectHeader error={error}>
              <Grid>
                {enableSelectAll && !searchIsActive && (
                  <Grid.Column
                    width={enableSearch ? 6 : 12}
                    verticalAlign="middle"
                  >
                    <EhCheckbox
                      label="Select All"
                      onClick={this.handleSelectAll}
                      indeterminate={
                        valueSet.size > 0 &&
                        valueSet.size !== flatOptions.length
                      }
                      value={
                        valueSet.size === flatOptions.length &&
                        flatOptions.length > 0
                      }
                      disabled={disabled}
                    />
                  </Grid.Column>
                )}
                {enableSearch && (
                  <Grid.Column width={searchIsActive ? 12 : 6}>
                    <SearchInput
                      value={query}
                      onChange={this.onSearchChange}
                      onFocus={this.onSearchFocus}
                      onBlur={this.onSearchBlur}
                      placeholder={searchPlaceholder}
                      fluid
                      icon={
                        query ? (
                          <Icon
                            name="close"
                            color="blue"
                            link
                            onClick={this.onClearSearchClick}
                          />
                        ) : (
                          <Icon name="filter" color="blue" />
                        )
                      }
                    />
                  </Grid.Column>
                )}
              </Grid>
            </MultiselectHeader>
          )}
          <CheckboxGroupContainer height={height}>
            {loading ? (
              <Loader active />
            ) : (
              <Fragment>
                {nested ? (
                  this.renderNested(filteredItems)
                ) : (
                  <div>
                    {filteredItems.map((checkbox) =>
                      this.renderCheckbox(checkbox)
                    )}
                  </div>
                )}
                {filteredItems.length === 0 && (
                  <EhText color="grey">{noResultsText}</EhText>
                )}
              </Fragment>
            )}
          </CheckboxGroupContainer>
        </MultiselectContainer>
        {showCount && (
          <EhFormattedText
            size="xsmall"
            textAlign="right"
            type="substitutor"
            value={`Selected items: <<${value ? value.length : 0}|number>>`}
          />
        )}
      </Fragment>
    );
  }

  renderCheckbox(checkbox) {
    const key = checkbox[this.state.keyField];
    const selected = this.state.valueSet.has(key);
    return (
      <div key={key}>
        <EhCheckbox
          name={key.toString()}
          data-aq={key.toString()}
          value={selected}
          label={
            isString(this.props.display)
              ? checkbox[this.props.display]
              : this.props.display(checkbox)
          }
          onChange={(value) => this.checkboxChanged(key, value)}
          disabled={this.props.disabled}
        />
      </div>
    );
  }

  renderNested(filteredItems) {
    return (
      <div>
        {filteredItems.map((group) => {
          const allOptionsAreSelected = group.options.every(
            this.checkOptionSelected
          );
          return (
            <GroupCheckboxContainer key={group.name}>
              {this.state.query ? (
                <EhText color="grey">{group.name}</EhText>
              ) : (
                <EhCheckbox
                  name={group.name}
                  label={group.name}
                  data-aq={group.name}
                  value={allOptionsAreSelected}
                  indeterminate={
                    group.options.some(this.checkOptionSelected) &&
                    !allOptionsAreSelected
                  }
                  onClick={this.groupCheckboxClicked}
                  disabled={this.props.disabled}
                />
              )}
              <NestedCheckboxContainer>
                {group.options.map((checkbox) => this.renderCheckbox(checkbox))}
              </NestedCheckboxContainer>
            </GroupCheckboxContainer>
          );
        })}
      </div>
    );
  }
}
