import { PureComponent, Fragment } from "react";
import { Grid } from "semantic-ui-react";
import PropTypes from "prop-types";

import { EhForm } from "../form";
import EhButton from "../EhButton";

import EhFormCancelButton from "../EhFormCancelButton";
import EhPanel from "./EhPanel";

export default class EhSection extends PureComponent {
  static propTypes = {
    /** The function that gets called on save. */
    onSave: PropTypes.func,
    /** The title element to be displayed up top. */
    heading: PropTypes.any.isRequired,
    /** The flag which determines if the component should start in the edit state. */
    initialEditMode: PropTypes.bool,
    /** The element to render in the body. */
    children: PropTypes.any,
  };

  state = {
    editMode: this.props.initialEditMode,
  };

  onEditClick = () => {
    this.setState({
      editMode: true,
    });
  };

  cancelEdit(form, newValues) {
    form.resetForm(newValues);
    this.setState({
      editMode: false,
    });
  }

  onSave = async (values, form) => {
    await this.props.onSave(values);
    this.cancelEdit(form, values);
  };

  renderForm = (form) => {
    return (
      <Fragment>
        {this.props.children instanceof Function
          ? this.props.children(form)
          : this.props.children}
        {this.state.editMode && (
          <Grid>
            <Grid.Column textAlign="right" width={12}>
              <EhFormCancelButton
                showWarning={form.dirty}
                onCancel={() => {
                  this.cancelEdit(form);
                }}
                disabled={form.isSubmitting}
              />
              <EhButton
                type="submit"
                text="Save"
                primary
                disabled={form.isSubmitting}
              />
            </Grid.Column>
          </Grid>
        )}
      </Fragment>
    );
  };

  render() {
    return (
      <EhPanel
        header={this.props.heading}
        buttons={[
          <EhButton borderless onClick={this.onEditClick}>
            Edit
          </EhButton>,
        ]}
      >
        <EhForm
          {...this.props}
          editMode={this.state.editMode}
          onSave={this.onSave}
        >
          {this.renderForm}
        </EhForm>
      </EhPanel>
    );
  }
}
