import { PureComponent } from "react";
import PropTypes from "prop-types";
import InputMask from "react-input-mask";
import EhOptional from "../EhOptional";

class EhMaskedInput extends PureComponent {
  static propTypes = {
    editMode: PropTypes.bool,
    error: PropTypes.bool,
    loading: PropTypes.bool,
    mask: PropTypes.string,
    onChange: PropTypes.func,
    onBlur: PropTypes.func,
    renderButton: PropTypes.func,
    required: PropTypes.bool,
    value: PropTypes.any,
    wrapText: PropTypes.bool,
  };
  static defaultProps = {
    value: "",
    editMode: true,
    wrapText: true,
  };

  getUnmaskedValue = (value) => value.replace(/\D/g, "");

  onChange = (e) => {
    const unmaskVal = this.getUnmaskedValue(e.target.value);
    this.props.onChange(unmaskVal);
  };
  onFocus = () => {
    this.text.setSelection(0, this.text?.value?.length);
  };
  componentDidUpdate(prevProps) {
    if (prevProps.mask !== this.props.mask) {
      this.text.setSelection(this.text.value.length, this.text?.value.length);
    }
  }

  render() {
    const {
      editMode,
      error,
      loading,
      renderButton,
      required,
      wrapText,
      ...props
    } = this.props;
    if (editMode) {
      return (
        <InputMask
          {...props}
          onChange={this.onChange}
          onFocus={this.onFocus}
          maskChar={null}
          ref={(input) => (this.text = input)}
        />
      );
    } else {
      return (
        <EhOptional
          type="phone"
          format={this.props.mask}
          value={this.props.value}
          wrapText={this.props.wrapText}
        />
      );
    }
  }
}
export default EhMaskedInput;
