import { Fragment } from "react";
import styled from "styled-components";
import { Container } from "semantic-ui-react";
import PropTypes from "prop-types";
import { Spacing } from "./../style";
import Footer from "./Footer";
import { connect } from "react-redux";
import { getTermsAndConditionLogic } from "../js-utils/logic";

const Content = styled.div`
  position: relative;
  padding-top: 50px;
  margin-bottom: ${Spacing.spacingSmall};
  min-height: calc(100vh - 56px);
`;

const PageContainer = styled(Container)`
  padding-top: ${Spacing.spacingXLarge};
  padding-bottom: ${Spacing.spacingXLarge};
`;

const EhPage = ({ children, getTermsAndConditionLogic, className }) => {
  return (
    <Fragment>
      <Content className={className}>
        <PageContainer>{children}</PageContainer>
      </Content>
      <Footer getTermsLogic={getTermsAndConditionLogic} />
    </Fragment>
  );
};

EhPage.propTypes = {
  children: PropTypes.any,
  getTermsAndConditionLogic: PropTypes.func,
  className: PropTypes.string,
};

export default connect(null, { getTermsAndConditionLogic })(EhPage);
