import styled, { css } from "styled-components";
import PropTypes from "prop-types";
import { Spacing, Colors, Elevation } from "../../../style";
import { APP_SETTINGS } from "@evolent/roots";

const StyledAnchor = styled.a`
  margin: ${Spacing.spacingMedium} 0;
  cursor: pointer;
  display: flex;
`;

const StyeldIcon = styled.div`
min-width: 3rem;
min-height: 3rem;
border-radius: ${Spacing.borderRadiusLarge};
margin-right: ${Spacing.spacingLarge};
background-position: center;
background-repeat: no-repeat;
box-shadow:${Elevation.boxShadowMedium};
${(props) => css`
  background-color: ${(props) => {
    const { shade, color } = props.tile?.reactApp;
    return shade ? Colors[color][shade] : Colors[color];
  }};
  background-image: url(${props.tile.reactApp?.iconUrl});
  background-size: ${props.tile.reactApp?.iconUrl?.endsWith(".svg")
    ? "80% 80%"
    : "80%"};
`}
}
`;

const StyledText = styled.div`
  min-height: 3rem;
  background-size: cover;
  ${({ textImgUrl, textImgWidth }) => css`
    background-image: url(${textImgUrl});
    width: ${textImgWidth || `7.5625rem`};
  `}
`;

const AppTile = ({ tile, openInNewTab = false }) => {
  const { textImgUrl, textImgWidth } = tile.reactApp;
  let appUrl = "";

  if (tile?.isSameDomain) {
    const baseUrl = APP_SETTINGS.launcherUrl
      .toLowerCase()
      .replace("/launcher/", "");
    const urlString = `${baseUrl}${tile?.url?.substring(
      tile.url.indexOf("/")
    )}`;
    appUrl = urlString;
  } else {
    appUrl = tile?.url;
  }

  return (
    <StyledAnchor href={appUrl} target={openInNewTab ? "_blank" : "_self"}>
      <StyeldIcon tile={tile} />
      <StyledText
        textImgUrl={textImgUrl}
        textImgWidth={textImgWidth}
        data-aq={`tile${tile?.name}`}
      />
    </StyledAnchor>
  );
};

AppTile.propTypes = {
  tile: PropTypes.object,
  openInNewTab: PropTypes.bool,
};

export default AppTile;
