import { useState, useRef, useEffect } from "react";
import styled, { keyframes } from "styled-components";
import PropTypes from "prop-types";
import { EhFlexBox } from "../layout";
import { EhTextMat } from "../typography";
import { Spacing, Colors, LineHeight } from "../../style";
import { EhButtonMat } from "../EhButtonMat";

const initialAnimation = keyframes`
 0% { height: 0rem;  }
 30% { height: 7rem; }
 40% { height: 14rem;}
 100% { height: 20rem; }
`;

const StyledDiv = styled.div`
  position: sticky;
  bottom: 0;
  width: 100%;
  background: white;
  line-height: ${LineHeight.heading.large};
  animation-name: ${initialAnimation};
  animation-duration: 0.2s;
  height: ${(props) => (props.maximized ? "calc(100vh - 8.5rem)" : "20rem")};
  overflow: auto;
  box-shadow: ${Spacing.spacingNone} -${Spacing.borderWidthThin} ${Spacing.spacingLarge}
    ${Spacing.borderRadiusSmall} rgb(0 0 0 / 39%);
  border-top: ${Spacing.borderWidthThin} solid ${Colors.grey[40]};
  z-index: 22;
  padding: ${Spacing.spacingNone} ${Spacing.spacingXxLarge}
    ${Spacing.spacingMedium};
  transition: all 0.2s ease-out;
  &:after {
    content: "";
    display: block;
    padding-bottom: ${Spacing.spacingMedium};
  }
`;

const IconsContainer = styled.div`
  display: inline;
`;

const MinMaxButton = styled(EhButtonMat)`
  &&&&& {
    background-color: inherit !important;
    svg {
      transition: transform 0.2s;
    }
    & :hover,
    :focus-within {
      svg {
        transform: scale(1.3);
      }
    }
  }
`;

const CloseButton = styled(MinMaxButton)`
  &&&&&& svg {
    color: ${Colors.grey[40]};
  }
`;

const StyledChildContainer = styled.div`
  padding-top: ${Spacing.spacingMedium};
`;

const StyledFlexBox = styled(EhFlexBox)`
  position: sticky;
  top: 0;
  background-color: ${Colors.white};
  margin-top: ${Spacing.spacingMedium};
  z-index: 22;
`;

export const EhSplitScreen = ({
  title,
  children,
  onClose,
  autoFocus = true,
  open = true,
  openMaximized = false,
}) => {
  const [maximized, setMaximized] = useState(openMaximized);
  const buttonRef = useRef();
  useEffect(() => {
    open && autoFocus && buttonRef.current.focus();
  }, [open]);

  return (
    <StyledDiv maximized={maximized} hidden={!open}>
      <StyledFlexBox justifyContent="space-between">
        <EhTextMat size="large" display="inline" fontWeight="black">
          {title}
        </EhTextMat>
        <IconsContainer>
          <MinMaxButton
            dataAq="minMaxBtn"
            borderless
            icon={!maximized ? "expand" : "compress"}
            elementRef={buttonRef}
            onClick={() => {
              if (!maximized) {
                setMaximized(true);
              } else {
                setMaximized(false);
              }
            }}
          />
          <CloseButton
            onClick={onClose}
            dataAq="btnClose"
            borderless
            icon="close"
            iconColor="grey"
          />
        </IconsContainer>
      </StyledFlexBox>
      <StyledChildContainer>{children}</StyledChildContainer>
    </StyledDiv>
  );
};

EhSplitScreen.propTypes = {
  onClose: PropTypes.func,
  children: PropTypes.node,
  title: PropTypes.string,
  autoFocus: PropTypes.bool,
  open: PropTypes.bool,
  openMaximized: PropTypes.bool,
};
