import { Component } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import PropTypes from "prop-types";
import {
  EmitApiResponse,
  localStorageUtil,
  getUserProfileSelector,
  NOTIFICATION_DATA,
  APP_SETTINGS,
} from "@evolent/roots";
import { EhMessage, EhText } from "../components";
import createHttpClient from "../js-utils/HttpClient";

const { notificationRefreshInterval, eventName, notificationUrl } =
  NOTIFICATION_DATA;

const StyledEhMessage = styled(EhMessage)`
  z-index: 100000;
  top: 5vh;
  right: 0;
  left: 0;
  opacity: 0.9;
  width: 95% !important;
  margin-left: 3.5rem !important;
  position: fixed !important;
  padding-top: 0px !important;
  padding-bottom: 0px !important;
  &&&& .content {
    width: 90% !important;
    font-family: sans-serif;
    div {
      margin-top: 0 !important;
    }
  }
  &&&& .info.circle.icon {
    font-size: 1.3rem !important;
  }
`;

export class EhNotificationBanner extends Component {
  state = { count: 0, data: [] };
  onDismiss(id) {
    this.setState(({ count }) => ({ count: count + 1 }));
    localStorageUtil.updateEntry(this.localStoragekey, id);
  }

  listener = (data) => data && this.setState({ data, count: 0 });

  subscribeToAPI() {
    const { appId, userGUID } = this.props;
    const idpClient = createHttpClient(APP_SETTINGS.idpUrl);
    const NOTIFICATION_API = `${notificationUrl}${appId}`;
    this.localStoragekey = `${userGUID}$${appId}`;
    const emitApiResponse = new EmitApiResponse(
      NOTIFICATION_API,
      notificationRefreshInterval,
      this.listener,
      eventName,
      this.localStoragekey
    );
    return emitApiResponse.subscribe(idpClient);
  }
  componentDidMount() {
    this.unsubscribe = this.subscribeToAPI();
  }
  componentWillUnmount() {
    this.unsubscribe();
  }
  render() {
    const { data, count } = this.state;
    const { className } = this.props;
    return data[count] ? (
      <StyledEhMessage
        info
        onDismiss={() => {
          this.onDismiss(data[count].notificationId);
        }}
        className={className}
        header="System Notification"
        size="small"
        data-aq="eh-notification-banner"
        content={
          <EhText wrapText color="black" data-aq="eh-notification-content">
            {data[count].notificationText}
          </EhText>
        }
      />
    ) : null;
  }
}

EhNotificationBanner.propTypes = {
  /**
   * Id of an application
   */
  appId: PropTypes.number.isRequired,
  /**
   * userGUID of logged In User
   */
  userGUID: PropTypes.string.isRequired,
  className: PropTypes.string,
};

const mapStateToProps = (state) => ({
  userGUID: getUserProfileSelector(state)?.userGUID,
});

export default connect(mapStateToProps)(EhNotificationBanner);
