import { Component, Fragment } from "react";
import PropTypes from "prop-types";
import { ProfileContext, profileState } from "./hoc/ProfileContext";
import { ACTION_HANDLERS } from "./hoc/editing";
export default class EhProfile extends Component {
  static propTypes = {
    /** You can add any number of child component to the EhProfile Component.
     * EhProfileSection must be descendent of EhProfile component
     */
    children: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.node),
      PropTypes.node,
    ]),
  };

  actionDispatcher = (action) => {
    const handler = ACTION_HANDLERS[action.type];

    this.setState((state) => ({
      editing: handler ? handler(state.editing, action) : state.editing,
    }));
  };

  state = {
    ...profileState,
    actionDispatcher: this.actionDispatcher,
  };

  render() {
    return (
      <Fragment>
        <ProfileContext.Provider value={this.state}>
          {this.props.children}
        </ProfileContext.Provider>
      </Fragment>
    );
  }
}
