import { Component } from "react";
import PropTypes from "prop-types";
import { debounce } from "lodash";

import { StyledDropdown } from "./EhDropdown";

const INITIAL_STATE = {
  loading: false,
  results: [],
};

export default class EhTypeAheadDropdown extends Component {
  static propTypes = {
    clearable: PropTypes.bool,
    /** The flag which determines if the component should be in the disabled state. */
    disabled: PropTypes.bool,
    display: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    keyField: PropTypes.string,
    /** The most results to display when typing. */
    maxResults: PropTypes.number,
    onBlur: PropTypes.func,
    onChange: PropTypes.func,
    placeholder: PropTypes.string,
    select: PropTypes.string,
    service: PropTypes.func,
    /** The currently selected option. */
    value: PropTypes.any,
  };

  state = {
    isFocused: false,
    loading: false,
    results: this.props.value ? [this.props.value] : [],
    searchQuery: "",
  };

  static getDerivedStateFromProps(props) {
    return {
      keyField: props.select || props.keyField || props.display,
    };
  }

  performSearch = debounce(async () => {
    const { service } = this.props;
    const { searchQuery } = this.state;
    if (searchQuery && searchQuery.length) {
      try {
        const results = await service(searchQuery);
        this.setState({
          results,
        });
      } finally {
        this.setState({ loading: false });
      }
    } else {
      this.setState((prevState) => ({
        ...prevState,
        ...INITIAL_STATE,
      }));
    }
  }, 300);

  handleSearchChange = (event, { searchQuery }) => {
    this.setState({ searchQuery, loading: true, isFocused: true });
    this.performSearch();
  };

  getOptions = (results) => {
    return results.map((option) => {
      return {
        value: option[this.state.keyField],
        text:
          this.props.display instanceof Function
            ? this.props.display(option)
            : option[this.props.display],
      };
    });
  };

  handleChange = (evt, { value }) => {
    const { keyField, onChange, select } = this.props;
    const val = select
      ? value
      : this.state.results.find((option) => option[keyField] === value);
    onChange(val || "");

    if (!val) {
      this.setState({ searchQuery: "", results: [] });
    }

    this.setState({ isFocused: false });
  };

  handleFocus = (event) => {
    event.target.select();
    this.setState({ isFocused: true });
  };

  handleBlur = (evt) => {
    this.props.onBlur(evt);
    this.setState({ isFocused: false });
  };

  handleKeyDown = (e) => {
    if (e.key === "Enter") {
      this.handleBlur(e);
    }
  };

  render() {
    const { clearable, disabled, keyField, maxResults, placeholder, value } =
      this.props;
    const { isFocused, loading, results, searchQuery } = this.state;

    const options =
      maxResults && results.length > maxResults
        ? this.getOptions(results.slice(0, maxResults))
        : this.getOptions(results);

    return (
      <StyledDropdown
        data-aq="ddTypeAhead"
        fluid
        search={(options) => options}
        selection
        value={value && (keyField ? value[keyField] : value)}
        placeholder={placeholder}
        options={options}
        clearable={clearable}
        loading={loading}
        onSearchChange={this.handleSearchChange}
        onChange={this.handleChange}
        onFocus={this.handleFocus}
        onBlur={this.handleBlur}
        disabled={disabled}
        open={!!(isFocused && !loading && (searchQuery || value))}
        icon="search"
        selectOnNavigation={false}
        onKeyDown={this.handleKeyDown}
      />
    );
  }
}
