import { Viewer } from "@react-pdf-viewer/core";
import { printPlugin } from "@react-pdf-viewer/print";
import { searchPlugin } from "@react-pdf-viewer/search";
import { pageNavigationPlugin } from "@react-pdf-viewer/page-navigation";
import { getFilePlugin } from "@react-pdf-viewer/get-file";
import { zoomPlugin } from "@react-pdf-viewer/zoom";
import "@react-pdf-viewer/core/lib/styles/index.css";
import "@react-pdf-viewer/search/lib/styles/index.css";
import PropTypes from "prop-types";
import styled, { createGlobalStyle } from "styled-components";
import EhFlexBox from "./layout/EhFlexBox";
import { Colors, Spacing } from "../style";

const GlobalStyle = createGlobalStyle`
    @media print {
      .rpv-print__body-printing #root {
          display: none;
      }
      .rpv-print__body-printing .MuiDialog-root {
        display: none !important;
      }
      .rpv-print__zone {
          display: block;
      }
    }
  `;
const StyledDiv = styled.div`
  border: ${Spacing.borderWidthThin} solid ${Colors.blue[25]};
`;
const StyledPDFViewerHeader = styled(EhFlexBox)`
  background-color: ${Colors.blue[25]};
  border-bottom: ${Spacing.borderWidthThin} solid ${Colors.blue[25]};
  padding: ${Spacing.spacingXSmall} ${Spacing.spacingMedium};
`;

const StyledPDFViewer = styled(EhFlexBox)`
  flex: 1;
  overflow: hidden;
`;
const StyledPaddingDiv = styled(EhFlexBox)`
  padding: ${Spacing.spacingXSmall} ${Spacing.spacingMedium};
`;
const StyledPDFDiv = styled.div`
  height: 45rem;
`;

const EhPDFViewerMat = ({
  searchedWord,
  pageNumber,
  pdfurl,
  showSearch,
  showPrint,
  showDownload,
}) => {
  const searchPluginInstance = searchPlugin({
    keyword: [searchedWord],
  });
  const pageNavigationPluginInstance = pageNavigationPlugin();
  const zoomPluginInstance = zoomPlugin();
  const printPluginInstance = printPlugin();
  const getFilePluginInstance = getFilePlugin();

  const {
    CurrentPageInput,
    GoToFirstPageButton,
    GoToLastPageButton,
    GoToNextPageButton,
    GoToPreviousPage,
  } = pageNavigationPluginInstance;
  const { ZoomInButton, ZoomOutButton, ZoomPopover } = zoomPluginInstance;
  const pageIndex = pageNumber ? pageNumber - 1 : 0;
  const { setTargetPages } = searchPluginInstance;
  const { ShowSearchPopoverButton } = searchPluginInstance;
  const { PrintButton } = printPluginInstance;
  const { DownloadButton } = getFilePluginInstance;

  // Only search in the  pages
  setTargetPages((pageFilter) => pageFilter.pageIndex === pageIndex);

  return (
    <>
      <GlobalStyle />
      <StyledDiv>
        <StyledPDFViewerHeader alignItems="center">
          {showSearch ? <ShowSearchPopoverButton /> : ""}
          <StyledPaddingDiv>
            <GoToFirstPageButton />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <GoToPreviousPage />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <CurrentPageInput />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <GoToNextPageButton />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <GoToLastPageButton />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <ZoomInButton />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <ZoomPopover />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            <ZoomOutButton />
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            {showPrint ? <PrintButton /> : ""}
          </StyledPaddingDiv>
          <StyledPaddingDiv>
            {showDownload ? <DownloadButton /> : ""}
          </StyledPaddingDiv>
        </StyledPDFViewerHeader>
        <StyledPDFViewer />
        <StyledPDFDiv>
          <Viewer
            fileUrl={pdfurl}
            initialPage={pageIndex}
            plugins={[
              searchPluginInstance,
              pageNavigationPluginInstance,
              zoomPluginInstance,
              printPluginInstance,
              getFilePluginInstance,
            ]}
            defaultScale={1.2}
          />
        </StyledPDFDiv>
      </StyledDiv>
    </>
  );
};

EhPDFViewerMat.defaultProps = {
  searchedWord: "",
  pageNumber: 1,
};

EhPDFViewerMat.propTypes = {
  searchedWord: PropTypes.string,
  pageNumber: PropTypes.number,
  pdfurl: PropTypes.string,
  showSearch: PropTypes.bool,
  showPrint: PropTypes.bool,
  showDownload: PropTypes.bool,
};
export default EhPDFViewerMat;
