import { PureComponent } from "react";
import { Grid, Segment } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled from "styled-components";

import { Colors, Spacing } from "../../style";

const LayoutHeaderSegment = styled(Segment)`
  &&&& {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: ${Colors.indigo[100]};
    z-index: 100;
    padding: 0 ${Spacing.spacingMedium};
    margin: 0;
  }
`;

const StyledGrid = styled(Grid)`
  &&&&& {
    margin: 0 -1rem;
    height: auto;
    .row {
      padding-top: 0;
      padding-bottom: 0;
    }
  }
`;

export default class EhLayoutHeader extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    appControls: PropTypes.any,
    logo: PropTypes.any,
    platformControls: PropTypes.any,
  };

  render() {
    const { appControls, logo, platformControls, className } = this.props;
    return (
      <LayoutHeaderSegment className={className}>
        <StyledGrid>
          <Grid.Row>
            <Grid.Column width={2} verticalAlign="middle">
              {logo}
            </Grid.Column>
            <Grid.Column width={6} verticalAlign="middle" textAlign="right">
              {appControls}
            </Grid.Column>
            <Grid.Column width={4} verticalAlign="middle" textAlign="right">
              {platformControls}
            </Grid.Column>
          </Grid.Row>
        </StyledGrid>
      </LayoutHeaderSegment>
    );
  }
}
