import { Fragment, useState, useEffect } from "react";
import PropTypes from "prop-types";
import {
  Backdrop,
  CircularProgress,
  Dialog,
  DialogContent,
  Typography as MatTypography,
} from "@mui/material";
import { Colors, Spacing, LineHeight, Typography } from "../../style";
import EhButton from "../../components/EhButton";
import styled from "styled-components";

const commonContentStyle = `  
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
z-index: 2500 !important;
& .MuiPaper-root {
  padding: ${Spacing.spacingXLarge} ${Spacing.spacingLarge} 3rem;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
  border: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
}`;
const Modal = styled(Dialog)`
  ${commonContentStyle}
  & .MuiBackdrop-root {
    background-color: ${Colors.backdropColor};
    transition: none;
  }
`;

const StyledDialogContent = styled(DialogContent)`
  ${commonContentStyle}
`;

const ModalContent = styled.div`
  margin-bottom: ${Spacing.spacingXxLarge};
  color: ${Colors.grey[100]};
  font-size: ${Typography.fontSizeSmall};
`;

const ModalButtonContainer = styled.div`
  margin-right: ${Spacing.spacingXSmall};
`;

const Header = styled(MatTypography)`
  font-size: ${Typography.fontSizeLarge};
  font-weight: ${Typography.fontWeightBold};
  font-family: ${Typography.fontFamily};
  line-height: ${LineHeight.heading.normal};
  margin-bottom: ${Spacing.spacingLarge};
`;

const StyledSpan = styled.span`
  margin-right: ${Spacing.spacingSmall};
  color: ${({ iconColor }) => iconColor};
  font-size: ${Typography.fontSizeXLarge};
`;

const StyledBackdrop = styled(Backdrop)`
  background-color: ${Colors.backdropColor};
  transition: none;
`;

export const EhAlertModalMat = ({
  iconName = "exclamation-triangle",
  iconColor = Colors.yellow[110],
  heading,
  buttons,
  message,
  onClose,
  closeOnDimmerClick = false,
  maxWidth = "md",
  ...remainingProps
}) => {
  const [loading, setLoading] = useState(false);
  const [hasMounted, setHasMounted] = useState(true);

  useEffect(() => {
    setHasMounted(true);
    return () => setHasMounted(false);
  }, []);

  const wrapOnClick = (onClick, afterClick) => {
    return async (event) => {
      setLoading(true);
      await onClick(event);
      hasMounted && setLoading(false);
      if (afterClick) {
        afterClick(event);
      }
    };
  };

  const renderButton = ({ onClick, afterClick, ...config }) => {
    return (
      <EhButton
        size="medium"
        key={config.text}
        onClick={wrapOnClick(onClick, afterClick)}
        {...config}
      />
    );
  };

  return (
    <Fragment>
      <Modal
        open
        maxWidth={maxWidth}
        onClose={(event, reason) => {
          if (reason !== "backdropClick" || closeOnDimmerClick) {
            onClose && onClose(event);
          }
        }}
        BackdropProps={{
          timeout: 0,
        }}
        BackdropComponent={Backdrop}
        {...remainingProps}
      >
        <StyledDialogContent>
          {loading ? (
            <StyledBackdrop open>
              <CircularProgress />
            </StyledBackdrop>
          ) : (
            <Fragment>
              <Header variant="h6">
                {iconName && (
                  <StyledSpan iconColor={iconColor}>
                    <i className={`fa fa-${iconName}`} />
                  </StyledSpan>
                )}
                {heading}
              </Header>
              <ModalContent>{message}</ModalContent>
              <ModalButtonContainer>
                {buttons && buttons.map(renderButton)}
              </ModalButtonContainer>
            </Fragment>
          )}
        </StyledDialogContent>
      </Modal>
    </Fragment>
  );
};

EhAlertModalMat.propTypes = {
  /** The heading text displayed at the top in bold. */
  heading: PropTypes.string,
  /** The name of the icon to display besides the heading. */
  iconName: PropTypes.string,
  /** The color of the icon displayed next to the icon. */
  iconColor: PropTypes.string,
  /** The buttons to be rendered at the bottom of the modal. */
  buttons: PropTypes.arrayOf(
    PropTypes.shape({
      ...EhButton.propTypes,
      afterClick: PropTypes.func,
      text: PropTypes.string.isRequired,
    }).isRequired
  ),
  /** The message to display between the header and the buttons. */
  message: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  maxWidth: PropTypes.oneOf(["lg", "md", "sm", "xl", "xs", false]),
  closeOnDimmerClick: PropTypes.bool,
  onClose: PropTypes.func,
  onClick: PropTypes.func,
  afterClick: PropTypes.func,
};

export default EhAlertModalMat;
