import { Component, Fragment } from "react";
import { PropTypes } from "prop-types";
import { Table } from "semantic-ui-react";
import styled from "styled-components";
import { isArray, isFunction, orderBy as _orderBy } from "lodash";

import { Colors, Spacing } from "../style";
import { EhText, EhLabel } from "./typography";
import EhButton from "./EhButton";

const List = styled(Table)`
  &&&& {
    margin-top: ${Spacing.spacingLarge};
    border-bottom: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
  }
`;

const ListHeader = styled(Table.HeaderCell)`
  &&&& {
    border-bottom: ${Spacing.borderWidthThin} solid ${Colors.indigo[25]};
  }
`;

const ListCell = styled(Table.Cell)`
  &&&& {
    border-top: none;
  }
`;

const SummaryText = styled(EhText)`
  &&&& {
    margin-top: ${Spacing.spacingSmall};
  }
`;

export default class EhList extends Component {
  static propTypes = {
    /** Determines how to render columns. */
    columns: PropTypes.arrayOf(
      PropTypes.shape({
        header: PropTypes.string,
        name: PropTypes.string,
        render: PropTypes.func,
        width: PropTypes.number,
      })
    ),
    /** The data corresponding to specified columns. */
    data: PropTypes.any,
    /** The number of elements to display initially. */
    initialLength: PropTypes.number,
    /** The field from data to use as a row key. */
    keyField: PropTypes.string,
    /** The label which appears above the list. */
    label: PropTypes.string,
    /** The name to use when displaying count. */
    entityName: PropTypes.string,
    /** The name to use from columns to order by. */
    orderBy: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.arrayOf(PropTypes.string),
      PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
      PropTypes.func,
    ]),
  };

  state = {
    expanded: false,
  };

  sortData() {
    const { data, orderBy } = this.props;
    let sortedData;
    if (isFunction(orderBy)) {
      sortedData = orderBy(data);
    } else {
      let sorts, orders;
      if (isArray(orderBy)) {
        if (isArray(orderBy[0])) {
          sorts = orderBy[0];
          orders = orderBy[1];
        } else {
          sorts = orderBy;
        }
      } else {
        sorts = [orderBy];
      }
      sortedData = _orderBy(data, sorts, orders);
    }

    return sortedData;
  }

  renderHeader() {
    return (
      <Table.Header>
        <Table.Row>
          {this.props.columns.map((column) => (
            <ListHeader key={column.name} width={column.width}>
              <EhText size="xsmall" fontWeight="bold" color="grey">
                {column.header}
              </EhText>
            </ListHeader>
          ))}
        </Table.Row>
      </Table.Header>
    );
  }

  renderRows() {
    const { data, initialLength, keyField, orderBy } = this.props;
    const endIndex = this.listIsExpanded() ? undefined : initialLength;
    const tableData = orderBy ? this.sortData() : data;
    return (
      <Table.Body>
        {tableData.slice(0, endIndex).map((row) => (
          <Table.Row key={row[keyField]}>
            {this.props.columns.map((column, index) => {
              const content = column.render
                ? column.render(row)
                : row[column.name];
              return (
                <ListCell key={index} width={column.width}>
                  {content}
                </ListCell>
              );
            })}
          </Table.Row>
        ))}
      </Table.Body>
    );
  }

  showButton() {
    return !!(
      this.props.initialLength &&
      this.props.initialLength < this.props.data.length
    );
  }

  showButtonClick = () => {
    this.setState((state) => ({ expanded: !state.expanded }));
  };

  listIsExpanded() {
    return !this.props.initialLength || this.state.expanded;
  }

  render() {
    return (
      <div>
        <EhLabel>{this.props.label}</EhLabel>
        {this.props.data && this.props.data.length ? (
          <Fragment>
            <SummaryText>{`${this.props.data.length} ${this.props.entityName}s`}</SummaryText>
            <List basic="very">
              {this.renderHeader()}
              {this.renderRows()}
            </List>
            {this.showButton() && (
              <EhButton
                onClick={this.showButtonClick}
                text={this.state.expanded ? "Show less" : "Show more"}
                borderless
              />
            )}
          </Fragment>
        ) : (
          <EhText color="grey">Not Available</EhText>
        )}
      </div>
    );
  }
}
