import { STORAGE_KEYS } from "../constants";

const processRedirect = async (onBeforeExternalRedirect) => {
  const returnToUrlExternal = getValue()(STORAGE_KEYS.RETURN_TO_URL_EXTERNAL);
  const returnToUrl = getValue()(STORAGE_KEYS.RETURN_TO_URL);

  if (returnToUrlExternal) {
    await onBeforeExternalRedirect();
    window.location.href = returnToUrlExternal;
  } else if (returnToUrl) {
    window.location.href = returnToUrl;
  } else {
    window.location.href = window.location.pathname;
  }
};

const getValue =
  (storage = window.sessionStorage) =>
  (key) => {
    const value = storage.getItem(key);
    storage.removeItem(key);
    return value;
  };

const setValue =
  (storage = window.sessionStorage) =>
  (key, value) => {
    storage.setItem(key, value);
  };

export default {
  processRedirect,
  sessionGet: getValue(),
  sessionSet: setValue(),
  localGet: getValue(window.localStorage),
  localSet: setValue(window.localStorage),
};
