import axios from "axios";
import { APP_SETTINGS } from "../constants";
import Session from "./session";

export const handleContactSupport = async (userEmail) => {
  const isEvoUser = userEmail?.toLowerCase()?.endsWith("@evolenthealth.com");
  if (isEvoUser) {
    window.open(APP_SETTINGS.evolentJiraLoginServices, "_blank");
  } else {
    const samlToken = await getJiraSamlToken();
    goToJiraLogic(samlToken?.data);
  }
};
const getJiraSamlToken = async () => {
  try {
    const response = await axios.get(
      `${APP_SETTINGS.idpUrl}sso/jira/outbound`,
      {
        headers: {
          Authorization: Session.getAuthorizationToken(),
        },
      }
    );
    return response?.data;
  } catch (e) {
    console.log(e);
  }
};

export const goToJiraLogic = (token) => {
  const base64EncodedSAMLToken = btoa(token);

  /**
   * Replicating Form POST submission with redirection by dynamically
   * generating a form and then removing it after submitting it for authentication
   */
  const jiraSAMLResponseForm = document.createElement("form");
  jiraSAMLResponseForm.setAttribute("method", "post");
  jiraSAMLResponseForm.setAttribute(
    "action",
    APP_SETTINGS.identifiJiraLoginServices
  );

  // Opens in New Window everytime
  jiraSAMLResponseForm.setAttribute("target", "_blank");

  const hiddenSAMLTokenField = document.createElement("input");
  hiddenSAMLTokenField.setAttribute("type", "hidden");
  hiddenSAMLTokenField.setAttribute("name", "SAMLResponse");
  hiddenSAMLTokenField.setAttribute("value", base64EncodedSAMLToken);

  jiraSAMLResponseForm.appendChild(hiddenSAMLTokenField);
  document.body.appendChild(jiraSAMLResponseForm);

  jiraSAMLResponseForm.submit();

  document.body.removeChild(jiraSAMLResponseForm);
};
