import { useEffect } from "react";
import { AppInsightsService } from "@evolent/roots";
import { Route } from "react-router-dom";
import PropTypes from "prop-types";

export default function EhRoute(props) {
  useEffect(() => {
    const pageViewTitle = window.__env?.pageViewNamePrefix
      ? `${window.__env.pageViewNamePrefix}: ${props.title}`
      : props.title;
    AppInsightsService.logPageView({ name: pageViewTitle });
    document.title = `Identifi - ${props.title}`;
  }, [props.title]);

  return <Route {...props} />;
}
EhRoute.propTypes = {
  title: PropTypes.string,
};
