import { PureComponent } from "react";
import { debounce } from "lodash";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Dropdown } from "semantic-ui-react";
import classNames from "classnames";

import { Colors, Elevation, LineHeight, Spacing, Typography } from "../style";
import EhButton from "./EhButton";

const SearchBarContainer = styled.div`
  display: flex;
`;

const SearchButton = styled(EhButton)`
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
  height: auto !important;
`;

const FOOTER_VALUE = -1;
const INITIAL_STATE = {
  loading: false,
  results: [],
  showFooter: false,
};

const SearchDropdown = styled(Dropdown)`
  &&&&&&&& {
    border-bottom: ${Spacing.borderWidthThin} solid ${Colors.indigo[50]};
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: ${Spacing.borderRadiusLarge} !important;
    padding: ${Spacing.spacingXSmall} ${Spacing.spacingSmall}
      ${Spacing.spacingXSmall} ${Spacing.medium};
    line-height: ${LineHeight.text.normal};
    font-size: ${Typography.fontSizeMedium};

    &:hover {
      background-color: ${Colors.white};
    }

    .dropdown.icon {
      padding-top: calc((((1.5 * 15px) + 12px) / 2)) !important;
      padding-bottom: calc((((1.5 * 15px) + 12px) / 2)) !important;
      font-size: ${Typography.fontSizeMedium};
    }

    .search {
      padding: ${Spacing.spacingXSmall} ${Spacing.spacingSmall}
        ${Spacing.spacingXSmall} ${Spacing.medium};
      line-height: ${LineHeight.text.normal};
      font-size: ${Typography.fontSizeMedium};

      &:focus {
        box-shadow: ${Elevation.boxShadowInsetMedium} !important;
      }
    }

    &.active {
      border-bottom-color: ${Colors.blue[100]};

      .menu {
        border-top-left-radius: ${Spacing.borderRadiusLarge};
        border-top-right-radius: ${Spacing.borderRadiusLarge};
        border-style: none;
        margin-top: ${Spacing.spacingSmall};
        max-height: 24rem;
        width: calc(100% + 4rem);
        left: -1rem;
        box-shadow: ${Elevation.boxShadowMedium};

        .item {
          font-size: ${Typography.fontSizeMedium} !important;
          font-weight: ${Typography.fontWeightRegular};
          padding: 1rem !important;
          border-style: none;

          &.selected {
            border-left: none;
            background-color: ${Colors.grey[10]};
          }

          &.disabled {
            color: ${Colors.grey[40]};

            &.selected,
            &:hover {
              background-color: ${Colors.white};
            }
          }
        }
      }
    }

    &.large {
      padding: ${Spacing.spacingMedium} ${Spacing.spacingLarge};

      &.active {
        .menu {
          width: calc(100% + 5rem);
        }
      }

      .dropdown.icon {
        padding-top: calc((((1.5 * 18px) + 24px) / 2)) !important;
        padding-bottom: calc((((1.5 * 18px) + 24px) / 2)) !important;
        font-size: ${Typography.fontSizeLarge};
      }

      .search {
        line-height: ${LineHeight.text.large};
        font-size: ${Typography.fontSizeLarge};
      }
    }
  }
`;

class EhSearchBar extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    service: PropTypes.func.isRequired,
    renderRow: PropTypes.func,
    /** The function to run when an option gets selected. Gets the value as a parameter. */
    onSelectRow: PropTypes.func.isRequired,
    onSearchButtonClick: PropTypes.func,
    maxResults: PropTypes.number,
    renderFooter: PropTypes.func,
    /** The function to run when the footer is selected. Gets the search query as a parameter. */
    onSelectFooter: PropTypes.func,
    /** The unique identifier for each row. */
    keyField: PropTypes.string,
    /** The field which displays in dropdown; it also serves as fall back for keyField. */
    display: PropTypes.string,
    /** The dropdown will return select field instead whole option object if present. */
    select: PropTypes.string,
    /** The propery in options list which will be used to disable the perticular option */
    disabledProp: PropTypes.string,
    /** The propery which displays description on rightside */
    description: PropTypes.string,
    /** What text to display in the searchbox when no search is applied. */
    placeholder: PropTypes.string,
    /** The element to render as a header. */
    renderHeader: PropTypes.any,
    /** The default search to be applied. */
    defaultSearchQuery: PropTypes.string,
    /** The size of the searchbox. */
    size: PropTypes.oneOf(["large", "medium"]),
    /** The flag which determines if footer should always be displayed. */
    alwaysShowFooter: PropTypes.bool,
    /** The message to display when the search has no results. */
    noResultsMessage: PropTypes.string,
    /** The minimum search query length. */
    searchLength: PropTypes.number,
  };

  static defaultProps = {
    keyField: "id",
    display: "text",
    disabledProp: "disabled",
    searchLength: 1,
    description: "",
  };

  state = {
    ...INITIAL_STATE,
    searchQuery: this.props.defaultSearchQuery || "",
    isFocused: false,
  };

  static getDerivedStateFromProps(props) {
    return {
      keyField: props.select || props.keyField,
    };
  }

  performSearch = debounce(async () => {
    const { maxResults, service, searchLength } = this.props;
    const { searchQuery } = this.state;
    if (searchQuery?.length >= searchLength) {
      try {
        const data = await service(searchQuery);
        const results =
          maxResults && data.length > maxResults
            ? this.getOptions(data.slice(0, maxResults))
            : this.getOptions(data);
        this.setState({
          showFooter: this.showFooter(data),
          results,
        });
        this.setState({ searchLengthMessage: undefined });
      } catch (e) {
        console.log(e);
      } finally {
        this.setState({ loading: false });
      }
    } else {
      this.setState({
        ...INITIAL_STATE,
        searchLengthMessage: `Type at least ${searchLength} characters to search`,
      });
    }
  }, 300);

  handleSearchChange = (event, { searchQuery }) => {
    this.setState({ searchQuery, loading: true, isFocused: true });
    this.performSearch();
  };

  handleChange = (event, { value, options }) => {
    const { onSelectRow, onSelectFooter } = this.props;

    if (value === FOOTER_VALUE) {
      onSelectFooter && onSelectFooter(this.state.searchQuery);
    } else {
      const val = this.props.select
        ? value
        : options.find((option) => option[this.state.keyField] === value);
      onSelectRow(val);
    }

    this.setState({ isFocused: false });
  };

  handleSearchButtonClick = () => {
    if (this.state.searchQuery) {
      this.setState({ isFocused: false });
      this.props.onSearchButtonClick &&
        this.props.onSearchButtonClick(this.state.searchQuery);
    }
  };

  handleFocus = (event) => {
    event.target.select();
    this.setState({ isFocused: true });
    const { searchQuery, results } = this.state;
    if (searchQuery && !results.length) {
      this.setState({ loading: true });
      this.performSearch();
    }
  };

  handleBlur = () => {
    this.setState({ isFocused: false });
  };

  showFooter(data) {
    const { renderFooter, maxResults, alwaysShowFooter } = this.props;
    return !!(
      renderFooter &&
      (alwaysShowFooter || (maxResults && data.length > maxResults))
    );
  }

  getOptions(data) {
    const { keyField } = this.state;
    const { display, disabledProp, renderRow, description } = this.props;
    return data.map((item) => {
      return {
        ...item,
        description: item[description],
        key: item[keyField],
        text: item[display],
        value: item[keyField],
        content: renderRow && renderRow(item),
        disabled: item[disabledProp],
      };
    });
  }

  getFooterOption() {
    return {
      key: -1,
      text: "See All Results",
      value: FOOTER_VALUE,
      content:
        this.props.renderFooter &&
        this.props.renderFooter(this.state.searchQuery),
    };
  }

  render() {
    const {
      results,
      loading,
      showFooter,
      searchQuery,
      isFocused,
      searchLengthMessage,
    } = this.state;
    const { placeholder, renderHeader, noResultsMessage } = this.props;

    const options = showFooter
      ? [...results.slice(0, this.props.maxResults), this.getFooterOption()]
      : [...results];

    return (
      <SearchBarContainer className={this.props.className}>
        <SearchDropdown
          placeholder={placeholder}
          fluid
          search={(options) => options}
          selection
          options={options}
          onSearchChange={this.handleSearchChange}
          loading={loading}
          selectOnNavigation={false}
          icon={loading ? "dropdown" : false}
          selectOnBlur={false}
          onChange={this.handleChange}
          onFocus={this.handleFocus}
          header={results.length && renderHeader ? renderHeader() : null}
          searchQuery={searchQuery}
          open={!!(searchQuery && isFocused && !loading)}
          onBlur={this.handleBlur}
          noResultsMessage={searchLengthMessage || noResultsMessage}
          className={classNames(this.props.size)}
          value={searchQuery}
        />
        <SearchButton
          icon="search"
          primary
          size={this.props.size}
          onClick={this.handleSearchButtonClick}
        />
      </SearchBarContainer>
    );
  }
}

export default EhSearchBar;
