import { Component } from "react";
import PropTypes from "prop-types";
import Menu from "semantic-ui-react/dist/es/collections/Menu/Menu";
import styled from "styled-components";
import is, { isNot, match } from "styled-is";
import { Colors, Spacing, Typography, LineHeight } from "../style";

const SemanticMenu = (props) => {
  const { size, ...otherProps } = props; // eslint-disable-line react/prop-types
  return <Menu {...otherProps} />;
};

const largeStyle = () => match("size", "large")`
  font-size: ${Typography.fontSizeLarge};
  line-height: ${LineHeight.heading.normal};
  ${(props) =>
    props.vertical
      ? `padding: ${Spacing.spacingMedium};
      padding-left: 1.3125rem;`
      : `padding: ${Spacing.spacingMedium};
      padding-bottom: calc(${Spacing.spacingMedium} - ${Spacing.borderWidthThick});`}
`;

const mediumStyle = () => match("size", "medium")`
  font-size: ${Typography.fontSizeMedium};
  line-height: ${LineHeight.heading.normal};
  ${(props) =>
    props.vertical
      ? `padding: ${Spacing.spacingSmall};
      padding-left: 0.8125rem;`
      : `padding: ${Spacing.spacingSmall};
      padding-bottom: calc(${Spacing.spacingSmall} - ${Spacing.borderWidthThick});`}
`;

const smallStyle = () => match("size", "small")`
  font-size: ${Typography.fontSizeSmall};
  ${(props) =>
    props.vertical
      ? `padding: ${Spacing.spacingXxSmall};
      padding-left: 0.5625rem;`
      : `padding: ${Spacing.spacingXxSmall};
      padding-bottom: calc(${Spacing.spacingXxSmall} - ${Spacing.borderWidthThick});`}
`;

export { largeStyle, mediumStyle, smallStyle };

const StyledMenu = styled(SemanticMenu)`
  &&&&& {
    border-width: 0;

    ${is("vertical")`
      border-right-style: none;
    `};

    .item {
      :hover {
        background-color: ${Colors.blue[25]};
        border-right-width: 0;
      }

      ${is("vertical")`
        margin-bottom : ${Spacing.spacingXxSmall};
        border-right-style: none;
        border-left-width: ${Spacing.borderWidthThick};
        border-left-style: solid;
        border-color: transparent;
        &.active {
          border-left-color: ${Colors.grey[100]};
          cursor:default;
        }
      `};

      ${isNot("vertical")`
        border-bottom-width: ${Spacing.borderWidthThick};
        margin-right: ${Spacing.spacingXSmall}
      `};

      ${largeStyle()}
      ${mediumStyle()}
      ${smallStyle()}
    }
  }
`;

class EhMenu extends Component {
  static propTypes = {
    /** The size of the menu text. */
    size: PropTypes.oneOf(["small", "medium", "large"]),
    /** The flag which determines which orientation to display the menu in. */
    vertical: PropTypes.bool,
  };

  static defaultProps = {
    size: "medium",
  };

  render() {
    return <StyledMenu secondary pointing {...this.props} />;
  }
}

EhMenu.Item = Menu.Item;

export default EhMenu;
