import { PureComponent, Fragment } from "react";
import { Card } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Spacing, Colors, Typography } from "../../style";
import EhShowMore from "../../componentsStd/layout/EhShowMore";

const StyledCard = styled(Card)`
  &&&& {
    border: ${Spacing.borderWidthThin} solid ${Colors.blue[25]};
    box-shadow: none;
    color: ${Colors.grey[100]};

    .header-actions {
      position: absolute;
      top: ${Spacing.spacingLarge};
      right: ${Spacing.spacingLarge};
    }

    :hover {
      border-color: ${(props) => !props.disabled && Colors.blue[100]};
      cursor: ${({ cursor }) => cursor};
      transform: none;
    }
    div.card-header {
      font-weight: ${Typography.fontWeightBold};
      font-size: ${Typography.fontSizeMedium};
      padding-bottom: ${Spacing.spacingXxSmall};
    }
    div.card-meta {
      ${(props) =>
        props.description ? `padding-bottom: ${Spacing.spacingSmall};` : null}
      font-size: ${Typography.fontSizeSmall};
      font-weight: ${Typography.fontWeightRegular};
    }
    background-color: ${(props) => props.disabled && Colors.grey[5]};
  }
`;
class EhCard extends PureComponent {
  static propTypes = {
    /** A card can have a header section */
    header: PropTypes.any,
    /** */
    headerActions: PropTypes.any,
    /** A card can have a meta section */
    meta: PropTypes.any,
    /** A card can have a description text */
    description: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    /** Length can be provided to truncate the description and display show more button */
    truncateLength: PropTypes.number,
    /** A card can take up the width of its container. */
    fluid: PropTypes.bool,
    /** A card can have disabled style */
    disabled: PropTypes.bool,
    /** Cursor style on hover of card */
    cursor: PropTypes.oneOf(["default", "pointer"]),
  };

  static defaultProps = {
    cursor: "default",
  };

  render() {
    const {
      header,
      headerActions,
      meta,
      description,
      truncateLength,
      ...remainingProps
    } = this.props;
    return (
      <StyledCard
        header={
          <Fragment>
            {header && (
              <div className="card-header" data-aq="care-gaps-header">
                {header}
              </div>
            )}
            <div className="header-actions">{headerActions}</div>
          </Fragment>
        }
        meta={meta && <div className="card-meta">{meta}</div>}
        description={
          typeof description === "string" ? (
            <EhShowMore
              description={description}
              truncateLength={truncateLength}
            />
          ) : (
            description
          )
        }
        {...remainingProps}
      />
    );
  }
}

export default EhCard;
