import { showModal } from "../componentsStd/EhGlobalModal";
import { EhAlertModalMat } from "../componentsStd";

export const UNSAVED_MESSAGE =
  "You will lose any unsaved changes if you leave this page.";

export const showUnsavedChangeWarning = (callback) => {
  const closeModal = showModal(
    <EhAlertModalMat
      message={UNSAVED_MESSAGE}
      heading="Are you sure?"
      buttons={[
        {
          text: "No, I'm still working",
          onClick: () => {
            closeModal();
            callback(false); // eslint-disable-line
          },
          "data-aq": "keep-changes-btn",
        },
        {
          text: "Yes, discard my changes",
          negative: true,
          onClick: () => {
            closeModal();
            callback(true); // eslint-disable-line
          },
          "data-aq": "discard-changes-btn",
        },
      ]}
    />
  );
};
