import { Fragment } from "react";
import PropTypes from "prop-types";
import { isNil } from "lodash";
import EhLabel from "./typography/EhLabel";
import EhFormattedText from "./typography/EhFormattedText";
import EhLoader from "./layout/EhLoader";

const isEmpty = (value) => isNil(value) || value.toString().trim() === "";

const isTextAvailable = (value, alternateText) =>
  !(isEmpty(value) && isEmpty(alternateText));

export default function EhOptional(props) {
  const {
    loading,
    label,
    labelColor,
    value,
    alternateText,
    viewFormat,
    type,
    format,
    color,
    ...ehTextProps
  } = props;

  const propsToPass = {
    value: isEmpty(value) ? alternateText || "Not Available" : value,
    type: isEmpty(value) ? "text" : type,
    format: isEmpty(value) ? undefined : format,
    color: isTextAvailable(value, alternateText) ? color || "black" : "grey",
  };
  return (
    <Fragment>
      {loading ? (
        <EhLoader active />
      ) : (
        <Fragment>
          {label && (
            <Fragment>
              <EhLabel color={labelColor}>{label}</EhLabel>
              <br />
            </Fragment>
          )}

          {viewFormat ? (
            viewFormat(propsToPass.value)
          ) : (
            <EhFormattedText {...ehTextProps} {...propsToPass} />
          )}
        </Fragment>
      )}
    </Fragment>
  );
}

EhOptional.propTypes = {
  /**
   * label of the field
   */
  label: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  /**
   * color of the label
   */
  labelColor: PropTypes.string,
  /**
   * color of the value
   */
  color: PropTypes.string,
  /**
   * value for the field
   */
  value: PropTypes.any,
  /**
   * alternateText will be shown if value is not provided
   */
  alternateText: PropTypes.string,
  /**
   * This is renderProps. We can render any other component inside this component
   */
  viewFormat: PropTypes.func,
  /**
   * loding prop we will use to load Loader
   */
  loading: PropTypes.bool,
  /** The type of formatting. See EhFormattedText for more details */
  type: PropTypes.oneOf([
    "number",
    "text",
    "phone",
    "percent",
    "localDateTime",
    "utcDateTime",
    "substitutor",
    "zip",
    "pluralize",
    "ssn",
  ]),
  /** The type of formatting. See EhFormattedText for more details */
  format: PropTypes.any,
};
