import { memo, Fragment } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Spacing } from "../style";
import EhFlexBox from "../componentsStd/layout/EhFlexBox";
import EhText from "./typography/EhText";

const StyledColumnSeparater = styled((props) => <EhText {...props} />)`
  &&&& {
    margin: ${Spacing.spacingXxxSmall} ${Spacing.spacingSmall} 0
      ${Spacing.spacingSmall};
    line-height: 1;
    font-size: inherit;
  }
`;
const StyledFlexBox = styled(EhFlexBox)`
  &&& {
    line-height: 1;
    margin-top: ${Spacing.spacingXxxSmall};
  }
`;

export const EhDelimiter = (props) => {
  const {
    data,
    delimiter,
    size,
    color,
    shade,
    renderDelimiter = (
      <StyledColumnSeparater
        data-aq="delimiter-symbol"
        size={size}
        color={color}
        shade={shade}
      >
        {delimiter}
      </StyledColumnSeparater>
    ),
  } = props;

  return (
    <>
      {data?.map(
        (dataObj, index) =>
          dataObj?.element && (
            <Fragment key={index}>
              {index > 0 && renderDelimiter}
              {dataObj.noFragment ? (
                dataObj.element
              ) : (
                <StyledFlexBox alignItems="center">
                  {dataObj.element}
                </StyledFlexBox>
              )}
            </Fragment>
          )
      )}
    </>
  );
};

EhDelimiter.defaultProps = {
  size: "small",
  delimiter: "|",
  color: "indigo",
  shade: 25,
};

EhDelimiter.propTypes = {
  /* Array of elements with thier size */
  data: PropTypes.arrayOf(
    PropTypes.shape({
      element: PropTypes.any.isRequired,
      // noFragment if enabled will not use flexbox as parent wrapper
      noFragment: PropTypes.bool,
    })
  ).isRequired,
  /* delimiter to be applied */
  delimiter: PropTypes.oneOf(["|", "$", "-"]),
  /* size of delimiter */
  size: PropTypes.oneOf([
    "xlarge",
    "xxlarge",
    "xxxlarge",
    "large",
    "medium",
    "small",
    "xsmall",
    "xxsmall",
    "xxxSmall",
  ]),
  /* this parameter use to a custom delimiter element */
  renderDelimiter: PropTypes.element,
  /* color of delimiter */
  color: PropTypes.string,
  /* shade of delimiter */
  shade: PropTypes.number,
};

export default memo(EhDelimiter);
