import { Fragment, useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Grid, Pagination } from "semantic-ui-react";
import { EhLabel } from "./typography";
import { Spacing } from "../style";
import styled from "styled-components";
import { isEmpty } from "lodash";

const StyledDiv = styled.div`
  padding: ${Spacing.spacingSmall} ${Spacing.spacingSmall};
  text-align: center;
`;

function EhPagination(props) {
  const [startItem, setStartItem] = useState(0);
  const [endItem, setEndItem] = useState(null);

  useEffect(() => {
    setEndItem(Math.min(props.children.length, props.listSize));
  }, [props.children]);

  const updatePage = (e, data) => {
    const startItem = (data.activePage - 1) * props.listSize;
    const endItem = Math.min(startItem + props.listSize, props.children.length);
    setStartItem(startItem);
    setEndItem(endItem);
  };

  return (
    <Fragment>
      {!isEmpty(props.children) ? (
        <Fragment>
          <Grid.Row>
            <Grid.Column>
              <EhLabel size="small">
                {`Showing ${
                  props.children.length ? startItem + 1 : 0
                }-${endItem} of ${props.children.length}`}
              </EhLabel>
            </Grid.Column>
          </Grid.Row>
          {props.children.slice(startItem, endItem)}
          <StyledDiv>
            <Pagination
              defaultActivePage={1}
              onPageChange={updatePage}
              siblingRange={1}
              totalPages={Math.ceil(props.children.length / props.listSize)}
            />
          </StyledDiv>
        </Fragment>
      ) : props.noChildrenMessage ? (
        props.noChildrenMessage()
      ) : (
        ""
      )}
    </Fragment>
  );
}

EhPagination.propTypes = {
  children: PropTypes.any.isRequired,
  listSize: PropTypes.number.isRequired,
  noChildrenMessage: PropTypes.func,
};

export default EhPagination;
