import { PureComponent, Fragment } from "react";
import {
  find,
  findIndex,
  get,
  isEmpty,
  isEqual,
  isFunction,
  isString,
  times,
} from "lodash";
import PropTypes from "prop-types";
import styled from "styled-components";
import { EhText } from "../typography";
import EhFilter from "../EhFilter";
import EhLoadingBar from "../../componentsStd/EhLoadingBar";
import { EhTable } from "./EhTable";
import { Spacing, Colors } from "../../style";
import { Icon } from "semantic-ui-react";

const StyledFilterTrigger = styled.span`
  cursor: pointer;
`;
const StyledEhText = styled(EhText)`
  &&&& {
    cursor: pointer;
  }
`;

const StyledTableContainer = styled.div`
  overflow: auto;
  border-width: ${Spacing.borderWidthThin};
  border-radius: ${Spacing.borderRadiusLarge};
  border-color: ${Colors.indigo[25]};
  border-style: solid;
  width: 100%;
  height: 100%;
`;

const StyledRow = styled(EhTable.Row)`
  &&& {
    vertical-align: ${(props) =>
      props.verticalAlign ? "baseline" : "inherit"};
  }
`;

const StyledExpandedRow = styled(EhTable.Cell)`
  &&&&& {
    padding: 0;
  }
`;

export default class EhDataTable extends PureComponent {
  static propTypes = {
    /**
     * Column definitions
     * (header): table header text
     * (field): field name of object, it will be used for sorting and displaying data
     * (width): adjust column width
     * (renderData): data will use this render function instead of default rendering if defined
     * (nosort): disable sorting on this column
     * (sortDescending): perferred decending sorting on this column
     * (orderBy): The iteratees to sort by.
     * (filter): filter to be applied as text/checkbox
     */
    columns: PropTypes.arrayOf(
      PropTypes.shape({
        header: PropTypes.node.isRequired,
        field: PropTypes.string,
        type: PropTypes.string,
        width: PropTypes.number,
        // the below prop will keep the content inside the column in single line and does not break it while resizing
        singleLine: PropTypes.bool,
        // the below prop type will disable the normal column responsiveness
        fixedWidth: PropTypes.string,
        renderData: PropTypes.func,
        filters: PropTypes.arrayOf(
          PropTypes.shape({
            /**
             * The field name of the prop on which to filter.
             */
            field: PropTypes.string,
            /**
             * A more readable name for the filter that  will be displayed in the header of the filter.
             */
            header: PropTypes.string,

            /**
             * If the filter needs extra params, pass them here (e.g. checkbox filters need options).  Mostly varies by filter type.
             */
            params: PropTypes.object,

            /**
             * The type of filter that dictates what types of inputs are shown and what the value is.
             */
            type: PropTypes.oneOf(["checkbox", "dateRange", "text"]),

            /**
             * The current or initial value of the filter if any.
             */
            value: PropTypes.object,

            wide: PropTypes.oneOf(["very"]),
            /**
             * To check if the current value is number.
             */
            isNumber: PropTypes.bool,
          })
        ),
      })
    ).isRequired,
    /** The information used to populate table data. */
    data: PropTypes.arrayOf(PropTypes.any),
    filterClassName: PropTypes.string,
    filters: PropTypes.array,
    /** The footer which apears at the end of the table. */
    footer: PropTypes.oneOfType([PropTypes.element, PropTypes.func]),
    /** Indicates which field from data should be used as a key for each row. */
    keyField: PropTypes.oneOfType([PropTypes.func, PropTypes.string])
      .isRequired,
    /** The flag which determines if the table is in the loading state. */
    loading: PropTypes.bool,
    onFilterChange: PropTypes.func,
    /** The message displayed when no data is passed. */
    noResultsMessage: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.element,
    ]),
    onSortChange: PropTypes.func,
    /** The flag which determines if the table is sortable. */
    rowProps: PropTypes.object,
    sortable: PropTypes.bool,
    name: PropTypes.string,
    sortColumn: PropTypes.string,
    sortDescending: PropTypes.bool,
    fixed: PropTypes.bool,
    stickyArea: PropTypes.shape({
      headers: PropTypes.bool,
      columns: PropTypes.shape({
        position: PropTypes.oneOf(["left", "right", "both"]),
        leftCount: PropTypes.number,
        rightCount: PropTypes.number,
      }),
    }),
    striped: PropTypes.bool,
    elementRef: PropTypes.any,
    verticalAlign: PropTypes.bool,
    shouldHideExpandedRow: PropTypes.func,
    /** Expanded row component which is accepting single row data as prop */
    renderExpandedRow: PropTypes.oneOfType([
      PropTypes.node,
      PropTypes.elementType,
    ]),
    /** Keep rows expanded  */
    defaultExpandedRows: PropTypes.array,
    /** A callback when clicked on expand arrow */
    onExpandedArrowClick: PropTypes.func,
    /** Returns a key using row Data */
    getExpandedRowKey: PropTypes.func,
  };

  static defaultProps = {
    getExpandedRowKey: () => {},
    noResultsMessage: "No Results",
    onExpandedArrowClick: () => {},
    shouldHideExpandedRow: () => false,
  };

  state = {
    sortColumn: this.props.sortColumn || "",
    sortDescending: this.props.sortDescending,
    expandedRows: [],
  };

  checkExpandedRow = (data, index) => {
    const { getExpandedRowKey } = this.props;
    return getExpandedRowKey(data) || index;
  };

  expandRowIcon = [
    {
      field: "expandedRow",
      header: "",
      nosort: true,
      fixedWidth: "1.5rem",
      renderData: (d, index) => {
        const { shouldHideExpandedRow } = this.props;
        const key = this.checkExpandedRow(d, index);
        const flag = this.state.expandedRows.includes(key);
        return (
          !shouldHideExpandedRow(d, index) && (
            <StyledEhText
              wrapText={false}
              data-aq="collapseArrow"
              color="blue"
              shade={100}
              onClick={() => this.handleExpandArrowClick(key)}
            >
              <Icon name={`caret ${flag ? "down" : "right"}`} />
            </StyledEhText>
          )
        );
      },
    },
  ];

  handleExpandArrowClick = (key) => {
    const { onExpandedArrowClick } = this.props;
    const expandedRows = [...this.state.expandedRows];

    const idx = expandedRows.indexOf(key);
    idx === -1 ? expandedRows.push(key) : expandedRows.splice(idx, 1);

    this.setState({ expandedRows });
    onExpandedArrowClick(expandedRows);
  };

  getExpandedRows = () => {
    const { defaultExpandedRows } = this.props;
    if (!isEmpty(defaultExpandedRows)) {
      const expandedRows = [...defaultExpandedRows];
      this.setState({ expandedRows });
    }
  };

  componentDidMount() {
    this.getExpandedRows();
  }

  componentDidUpdate(prevProps) {
    const { defaultExpandedRows } = this.props;
    if (
      !isEqual(defaultExpandedRows, prevProps.defaultExpandedRows) &&
      !isEqual(defaultExpandedRows, this.state.expandedRows)
    ) {
      this.getExpandedRows();
    }
  }

  filterChanged = (filters, filter) => {
    const filterIndex = findIndex(filters, { field: filter.field });
    let newFilters;
    if (filterIndex !== -1) {
      newFilters = [
        ...filters.slice(0, filterIndex),
        filter,
        ...filters.slice(filterIndex + 1),
      ];
    } else {
      newFilters = [...filters, filter];
    }
    return newFilters;
  };

  filtersChanged = (newFilter) => {
    const { filters, onFilterChange } = this.props;
    const newFilterList = newFilter.reduce(this.filterChanged, filters);
    onFilterChange(newFilterList);
  };

  async onSortChange(column) {
    if (this.state.sortColumn === column.field) {
      await this.setState((state) => {
        return { sortDescending: !state.sortDescending };
      });
    } else {
      await this.setState({
        sortColumn: column.field,
        sortDescending: false,
      });
    }
    this.props.onSortChange({
      field: this.state.sortColumn,
      sortDescending: this.state.sortDescending,
      type: column.type,
    });
  }

  renderHeader() {
    const { columns, filterClassName, filters, sortable, renderExpandedRow } =
      this.props;
    const { sortColumn, sortDescending } = this.state;
    const columnsExtended = [
      ...(renderExpandedRow ? this.expandRowIcon : []),
      ...columns,
    ];
    return (
      <EhTable.Header>
        <EhTable.Row>
          {columnsExtended.map((column, index) => {
            const sorted =
              sortColumn === column.field
                ? sortDescending
                  ? "descending"
                  : "ascending"
                : null;
            let headerContent;
            if (column.filters) {
              column.filters = column.filters.map((filterConfig) => {
                return {
                  ...filterConfig,
                  value: find(filters, { field: filterConfig.field }),
                };
              });
              headerContent = (
                <EhText
                  size="medium-condensed"
                  data-aq={`column${index + 1}`}
                  fontWeight="bold"
                  color={"blue"}
                  shade={100}
                  display="inline"
                >
                  {column.header}
                </EhText>
              );
            } else {
              headerContent = (
                <EhText
                  display="inline"
                  data-aq={`column${index + 1}`}
                  size="medium-condensed"
                  fontWeight="bold"
                >
                  {column.header}
                </EhText>
              );
            }

            const filterTrigger = (
              <StyledFilterTrigger>{headerContent}</StyledFilterTrigger>
            );

            const HeaderCell = EhTable.HeaderCell;
            return (
              <HeaderCell
                fixedwidth={column.fixedWidth}
                width={column.width}
                key={column.field}
                sorted={sorted}
                onClick={(e) =>
                  sortable &&
                  !column.nosort &&
                  e.target.tagName === "TH" &&
                  this.onSortChange(column)
                }
                className={column.nosort ? "nosort" : ""}
              >
                {column.filters ? (
                  <EhFilter
                    filters={column.filters}
                    onChange={this.filtersChanged}
                    containerClassName={filterClassName}
                    trigger={filterTrigger}
                    basic
                    wide={column.filters.find((filter) => !!filter.wide)?.wide}
                  />
                ) : (
                  headerContent
                )}
              </HeaderCell>
            );
          })}
        </EhTable.Row>
      </EhTable.Header>
    );
  }

  renderBody() {
    const {
      columns,
      data,
      keyField,
      loading,
      noResultsMessage,
      rowProps,
      verticalAlign,
      renderExpandedRow,
    } = this.props;
    const columnsExtended = [
      ...(renderExpandedRow ? this.expandRowIcon : []),
      ...columns,
    ];

    let rows;
    if (loading) {
      rows = times(10, (num) => {
        return (
          <EhTable.Row key={num}>
            {times(columnsExtended.length, (cellNum) => (
              <EhTable.Cell key={cellNum}>
                <EhLoadingBar />
              </EhTable.Cell>
            ))}
          </EhTable.Row>
        );
      });
    } else if (data && data.length) {
      rows = data.map((record, index) => {
        const key = isFunction(keyField) ? keyField(record) : record[keyField];
        const recordRowProps = rowProps && rowProps[key];
        return (
          <Fragment key={key}>
            <StyledRow
              key={key}
              {...recordRowProps}
              verticalAlign={verticalAlign}
            >
              {columnsExtended.map((column) => {
                return (
                  <EhTable.Cell
                    singleLine={column.singleLine}
                    key={column.field}
                  >
                    {column.renderData ? (
                      column.renderData(record, index)
                    ) : (
                      <EhText size="small">{get(record, column.field)}</EhText>
                    )}
                  </EhTable.Cell>
                );
              })}
            </StyledRow>
            {renderExpandedRow &&
            this.state.expandedRows.includes(
              this.checkExpandedRow(record, index)
            ) ? (
              <StyledRow>
                <StyledExpandedRow colSpan={columnsExtended.length}>
                  {renderExpandedRow(record)}
                </StyledExpandedRow>
              </StyledRow>
            ) : null}
          </Fragment>
        );
      });
    } else {
      rows = (
        <EhTable.Row>
          <EhTable.Cell colSpan={columnsExtended.length}>
            {isString(noResultsMessage) ? (
              <EhText color="grey">{noResultsMessage}</EhText>
            ) : (
              noResultsMessage
            )}
          </EhTable.Cell>
        </EhTable.Row>
      );
    }

    return <EhTable.Body>{rows}</EhTable.Body>;
  }

  renderFooter() {
    const { footer } = this.props;
    return (
      <EhTable.Footer fullWidth>
        {isFunction(footer) ? (
          footer(this.props)
        ) : (
          <EhTable.Row>
            <EhTable.HeaderCell colSpan={100}>{footer}</EhTable.HeaderCell>
          </EhTable.Row>
        )}
      </EhTable.Footer>
    );
  }

  render() {
    const {
      footer,
      sortable,
      fixed,
      name,
      stickyArea,
      columns,
      elementRef,
      striped,
      renderExpandedRow,
    } = this.props;
    return (
      <StyledTableContainer ref={elementRef}>
        <EhTable
          name={name}
          sortable={sortable}
          fixed={fixed}
          data-aq={`tbl-${name}`}
          columnsdata={[
            ...(renderExpandedRow ? this.expandRowIcon : []),
            ...columns,
          ]}
          stickyarea={stickyArea}
          striped={striped}
        >
          {this.renderHeader()}
          {this.renderBody()}
          {footer && this.renderFooter()}
        </EhTable>
      </StyledTableContainer>
    );
  }
}
