import { Fragment, Component } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { EhAlertModalMat } from "../componentsStd";
import { Colors } from "../style";

const TimerSpan = styled.span`
  text-align: center;
  color: ${Colors.red[100]};
  font-weight: bold;
  margin-top: 26px;
  display: block;
  font-size: 20px;
`;

export default class TimeoutModal extends Component {
  static propTypes = {
    open: PropTypes.bool,
    timeLeft: PropTypes.number,
    onSignout: PropTypes.func,
    onExtend: PropTypes.func,
    closeModal: PropTypes.func,
  };

  calculateTimeLeft(timeLeft) {
    return (
      Math.floor(timeLeft / 60) + ":" + ("" + (timeLeft % 60)).padStart(2, "0")
    );
  }

  render() {
    return (
      <EhAlertModalMat
        open={this.props.open}
        message={
          <Fragment>
            <span>Your session will expire soon due to inactivity.</span>
            <TimerSpan>{this.calculateTimeLeft(this.props.timeLeft)}</TimerSpan>
          </Fragment>
        }
        heading="Are you still there?"
        buttons={[
          {
            text: "Sign out",
            onClick: async () => {
              this.props.closeModal();
              await this.props.onSignout();
            },
          },
          {
            text: "I'm still working",
            primary: true,
            onClick: async (event) => {
              event.stopPropagation();
              await this.props.onExtend();
              this.props.closeModal();
            },
          },
        ]}
      />
    );
  }
}
