import PropTypes from "prop-types";
import { EhFormattedText } from "../components/typography";

const currentPageNumber = (pageNumber, pageSize, totalCount) => {
  if (pageSize === 1) {
    return `${pageNumber}`;
  } else {
    return `<<${pageSize * pageNumber - pageSize + 1}|number>> - <<${Math.min(
      pageSize * pageNumber,
      totalCount
    )}|number>>`;
  }
};

const EhPaginationLabel = ({
  initialText = "Showing",
  pageNumber = 1,
  pageSize,
  totalCount,
  endingText = "",
  ...ehTextProps
}) => (
  <EhFormattedText
    size="medium"
    color="black"
    data-aq="pagination-information-text"
    type="substitutor"
    value={`${initialText} ${currentPageNumber(
      pageNumber,
      pageSize,
      totalCount
    )} of <<${totalCount}|number>> ${endingText}`}
    {...ehTextProps}
  />
);
EhPaginationLabel.propTypes = {
  pageNumber: PropTypes.number.isRequired,
  pageSize: PropTypes.number.isRequired,
  totalCount: PropTypes.number.isRequired,
  initialText: PropTypes.string,
  endingText: PropTypes.string,
};

export default EhPaginationLabel;
