import { useEffect } from "react";
import {
  withAITracking,
  ReactPlugin,
} from "@microsoft/applicationinsights-react-js";

export const EhReactPlugin = new ReactPlugin();
let renderStartTime;

export const updateMetrics = (componentName, duration) => {
  if (window.renderMetrics) {
    let metrics = window.renderMetrics[componentName];
    if (!metrics) {
      metrics = {
        avg: 0,
        count: 0,
        min: 0,
        max: 0,
        m2: 0,
      };
    }
    // track total number of renders
    metrics.count++;
    // update mean, and squared distance from mean
    const delta = duration - metrics.avg;
    metrics.avg += delta / metrics.count;
    const delta2 = duration - metrics.avg;
    metrics.m2 += delta * delta2;
    // update shortest digest time, longest digest time
    metrics.min =
      metrics.min === 0 ? duration : Math.min(metrics.min, duration);
    metrics.max = Math.max(metrics.max, duration);
    window.renderMetrics[componentName] = metrics;
  }
};

const EhWithTracking = (Component, componentName, className) => {
  const TrackedComponent = (props) => {
    useEffect(() => {
      // post-render
      const duration = Date.now() - renderStartTime;
      updateMetrics(componentName, duration);
    });
    // pre-render
    renderStartTime = Date.now();
    return <Component {...props} />;
  };
  return withAITracking(
    EhReactPlugin,
    TrackedComponent,
    componentName,
    className
  );
};

export default EhWithTracking;
