import { useState } from "react";
import Backdrop from "@mui/material/Backdrop";
import SpeedDial from "@mui/material/SpeedDial";
import SpeedDialAction from "@mui/material/SpeedDialAction";
import SpeedDialIcon from "@mui/material/SpeedDialIcon";
import PropTypes from "prop-types";
import styled from "styled-components";
import is, { match } from "styled-is";
import { EhIcon } from "../componentsStd";
import { Colors, Typography, Spacing } from "../style/variables";

const StyledBackDrop = styled(Backdrop)`
  background-color: ${Colors.backdropColor};
  z-index: 1;
`;

const StyledSpeedDial = styled(SpeedDial)`
  z-index: 3;
  position: absolute;

  .MuiSpeedDial-fab {
    background-color: ${({ backgroundColor }) => Colors[backgroundColor][125]};
    ${is("disableSpeedDial")`
      background-color: ${Colors.grey[40]};
    `}
    color: ${Colors.white};
    ${match("size", "large")` 
    width: 2.8125rem;
    height: 2.8125rem;`}

    ${match("size", "medium")` 
    width: ${Spacing.spacingXxLarge};
    height: ${Spacing.spacingXxLarge};
    min-height: ${Spacing.spacingXxLarge};`}
  }

  & .MuiSpeedDialAction-staticTooltipLabel {
    background-color: ${Colors.grey[80]};
    color: ${Colors.white};
    font-family: ${Typography.fontFamily};
    font-size: ${Typography.fontSizeStandard};
    height: 1.875rem;
  }
`;

const StyledSpeedDialAction = styled(SpeedDialAction)`
  background-color: ${({ backgroundColor }) => Colors[backgroundColor][100]};
  color: ${Colors.white};
  ${match("size", "large")` 
  width: 2.5rem;
  height: 2.5rem;`}

  ${match("size", "medium")` 
  width:${Spacing.spacingXxLarge};
  height: ${Spacing.spacingXxLarge}; 
  min-height: ${Spacing.spacingXxLarge};`}

  &:hover {
    background-color: ${({ backgroundColor }) => Colors[backgroundColor][75]};
  }
`;

export const EhSpeedDialMat = ({
  actions,
  direction,
  name,
  onOpen,
  onClose,
  showDescription,
  disabled,
  backgroundColor,
  size,
  mode,
}) => {
  const isClickMode = mode === "click";
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    if (!isClickMode) {
      setOpen(false);
      onClose && onClose();
    }
  };
  const handleOpen = () => {
    if (!isClickMode) {
      setOpen(true);
      onOpen && onOpen();
    }
  };

  const handleSpeedDialClick = () => {
    if (isClickMode) {
      if (open) {
        setOpen(false);
        onClose && onClose();
      } else {
        setOpen(true);
        onOpen && onOpen();
      }
    }
  };
  return (
    <>
      <StyledBackDrop open={open} onClick={handleSpeedDialClick} />
      <StyledSpeedDial
        ariaLabel={name}
        icon={<SpeedDialIcon />}
        component
        backgroundColor={backgroundColor}
        onClose={handleClose}
        onOpen={handleOpen}
        direction={direction}
        open={open}
        disableSpeedDial={disabled}
        FabProps={{ disabled: disabled }}
        size={size}
        onClick={handleSpeedDialClick}
      >
        {actions.map(({ name, icon, iconStyle = "solid", onClick }) => (
          <StyledSpeedDialAction
            size={size}
            backgroundColor={backgroundColor}
            key={name}
            tooltipTitle={name}
            tooltipOpen={showDescription && ["up", "down"].includes(direction)}
            icon={
              <EhIcon
                iconStyle={iconStyle}
                iconSize={size === "large" ? "lg" : "md"}
                iconName={icon}
                iconColor="inherit"
              />
            }
            onClick={() => {
              onClick && onClick();
              handleClose();
            }}
          />
        ))}
      </StyledSpeedDial>
    </>
  );
};

EhSpeedDialMat.propTypes = {
  /** direction in which speed dial actions should open */
  direction: PropTypes.oneOf(["down", "up", "left", "right"]),
  /** options that should be displayed when speed dial is open */
  actions: PropTypes.arrayOf(
    PropTypes.shape({
      /** name of font-awesome 6 icon to be passed to EhIcon. */
      icon: PropTypes.string,
      /** iconStyle needed for EhIcon component. */
      iconStyle: PropTypes.oneOf(["solid", "regular"]),
      /** Label to display if tooltip is needed */
      label: PropTypes.string,
      /** callback that will be called on click of speed dial option. */
      onClick: PropTypes.func,
    })
  ),
  /** prop for speed dial size */
  size: PropTypes.oneOf(["medium", "large"]),
  /** this is needed by mui to render speed dial and it is required */
  name: PropTypes.string.isRequired,
  /** callback function that will be called when speed dial opens */
  onOpen: PropTypes.func,
  /** callback function that will be called when speed dial closes */
  onClose: PropTypes.func,
  /** Flag that determines if tooltop should be shown  */
  showDescription: PropTypes.bool,
  /** flag to disable speed dial */
  disabled: PropTypes.bool,
  /** prop that decides color of speed dial */
  backgroundColor: PropTypes.oneOf(["green", "blue"]),
  /** this prop decides when should speed dial open (hover or click) */
  mode: PropTypes.oneOf(["hover", "click"]),
};

EhSpeedDialMat.defaultProps = {
  direction: "down",
  showDescription: true,
  disabled: false,
  backgroundColor: "green",
  size: "large",
  mode: "hover",
};
