import { Header } from "semantic-ui-react";
import styled from "styled-components";
import { Typography, LineHeight } from "../../style";
import PropTypes from "prop-types";
import { Component } from "react";

const wrapText = (props) =>
  props.wrapText ? `word-wrap: break-word;` : `white-space: nowrap;`;

const ellipsis = (props) =>
  props.ellipsis ? `overflow: hidden;text-overflow: ellipsis;` : null;

const HeaderSmall = styled(Header)`
  &&&& {
    font-weight: ${Typography.fontWeightBold};
    line-height: ${LineHeight.heading.small};
    ${wrapText};
    ${ellipsis};

    .sub.header {
      font-size: ${Typography.fontSizeSmall};
    }
  }
`;
const HeaderMedium = styled(Header)`
  &&&& {
    font-weight: ${Typography.fontWeightBlack};
    line-height: ${LineHeight.heading.normal};
    ${wrapText};
    ${ellipsis};
  }
`;

export default class EhHeader extends Component {
  static propTypes = {
    /** The size of the header. */
    size: PropTypes.oneOf(["medium", "large"]),
    /** The flag which determines if the text should wrap when too long. */
    wrapText: PropTypes.bool,
    /** The flag which determines if the text should display ellipsis when too long. */
    ellipsis: PropTypes.bool,
  };

  render() {
    const { size, ...props } = this.props;
    let as, Element;
    switch (size) {
      case "large":
        as = "h3";
        Element = HeaderMedium;
        break;
      default:
        as = "h4";
        Element = HeaderSmall;
    }
    return <Element forwardedAs={as} {...props} />;
  }
}
