import { useState } from "react";
import PropTypes from "prop-types";
import is from "styled-is";
import { useFieldArray, Controller } from "react-hook-form";
import {
  EhText,
  EhTable,
  EhInput,
  EhDropdown,
  EhButton,
  EhCheckboxGroup,
  EhDateTimePicker,
} from "../../components";
import EhFlexBox from "../../componentsStd/layout/EhFlexBox";
import { Spacing, Colors } from "../../style";
import styled from "styled-components";
import { isEmpty, isString } from "lodash";

const StyledTableContainer = styled.div`
  overflow: ${(props) => (props.overflowauto ? "auto" : "none")};
  border-width: ${Spacing.borderWidthThin};
  border-radius: ${Spacing.borderRadiusLarge};
  border-color: ${Colors.indigo[25]};
  border-style: solid;
  width: 100%;
  height: 100%;
  &&&&&&&& .ui.input input {
    width: 100%;
  }
`;

const InputContainer = styled.div`
  &&&&&&&&&& .ui.input input {
    ${is("invalid")`
      border-color:${Colors.red[100]};
    `}
  }
`;

const DeleteButton = styled(EhButton)`
  &&&&& {
    background-color: inherit !important;
  }
`;
export const ErrorComponent = ({ fieldState, dataAq, errorProps }) => (
  <>
    {fieldState?.invalid ? (
      <EhText size="small" color="red" data-aq={`err${dataAq}`} {...errorProps}>
        {fieldState.error.message}
      </EhText>
    ) : null}
  </>
);

ErrorComponent.propTypes = {
  fieldState: PropTypes.shape({
    invalid: PropTypes.bool,
    error: PropTypes.shape({
      type: PropTypes.string,
      message: PropTypes.string,
    }),
  }),
  dataAq: PropTypes.string,
  errorProps: PropTypes.object,
};

const EhEditTable = ({
  form,
  name,
  title,
  showEditButton = true,
  showUndoButton = false,
  showButtonInfo = false,
  newRowProps: { dataToAdd, position = "start" } = {},
  striped,
  stickyArea,
  canAddNewRow = true,
  isTableEditMode = false,
  showSaveButton,
  onSave,
  onEdit,
  onUndo,
  disableSaveButton = false,
  disableUndoButton = true,
  saveButtonLoader = false,
  editButtonLoader = false,
  thresholdCount,
  columns,
  elementRef,
  fixed,
  noResultsMessage = "No Results",
  contentAlign,
  keyField,
  canDeleteRow = true,
  undoButtonLoader = false,
}) => {
  const [editMode, setEditMode] = useState(isTableEditMode);
  const newData = {
    ...dataToAdd,
    inputProps: { disabled: false, editMode: true },
  };
  const { control } = form;
  const fieldArray = useFieldArray({
    control,
    name: name,
  });
  const { fields, append, prepend, remove } = fieldArray;

  const getField = ({
    name,
    key,
    control,
    fieldProps,
    rules,
    type = "text",
    additionalProps,
    additionalData,
    errorProps,
  }) => {
    switch (type) {
      case "text":
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState }) => {
              return (
                <InputContainer invalid={fieldState.invalid} key={key}>
                  <EhInput
                    {...field}
                    elementRef={field.ref}
                    editMode={editMode}
                    type="text"
                    data-aq={`txt${name}`}
                    name={name}
                    {...fieldProps}
                    {...additionalProps}
                  />
                  <ErrorComponent
                    fieldState={fieldState}
                    dataAq={`err${name}`}
                    errorProps={errorProps}
                  />
                </InputContainer>
              );
            }}
            rules={rules}
          />
        );
      case "number":
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState }) => (
              <InputContainer invalid={fieldState.invalid} key={key}>
                <EhInput
                  {...field}
                  elementRef={field.ref}
                  editMode={editMode}
                  type="number"
                  data-aq={`txt${name}`}
                  name={name}
                  {...fieldProps}
                  {...additionalProps}
                />
                <ErrorComponent
                  fieldState={fieldState}
                  dataAq={`err${name}`}
                  errorProps={errorProps}
                />
              </InputContainer>
            )}
            rules={rules}
          />
        );
      case "dropdown":
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState }) => (
              <InputContainer invalid={fieldState.invalid} key={key}>
                <EhDropdown
                  {...field}
                  elementRef={field.ref}
                  editMode={editMode}
                  name={name}
                  data-aq={`dd${name}`}
                  {...fieldProps}
                  {...additionalProps}
                />
                <ErrorComponent
                  fieldState={fieldState}
                  dataAq={`err${name}`}
                  errorProps={errorProps}
                />
              </InputContainer>
            )}
            rules={rules}
          />
        );
      case "checkboxgroup":
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState }) => (
              <InputContainer invalid={fieldState.invalid} key={key}>
                <EhCheckboxGroup
                  {...field}
                  elementRef={field.ref}
                  editMode={editMode}
                  name={name}
                  data-aq={`cg${name}`}
                  {...fieldProps}
                  {...additionalProps}
                />
                <ErrorComponent
                  fieldState={fieldState}
                  dataAq={`err${name}`}
                  errorProps={errorProps}
                />
              </InputContainer>
            )}
            rules={rules}
          />
        );
      case "date":
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState }) => (
              <InputContainer invalid={fieldState.invalid} key={key}>
                <EhDateTimePicker
                  {...field}
                  elementRef={field.ref}
                  editMode={editMode}
                  name={name}
                  data-aq={`dt${name}`}
                  {...fieldProps}
                  {...additionalProps}
                />
                <ErrorComponent
                  fieldState={fieldState}
                  dataAq={`err${name}`}
                  errorProps={errorProps}
                />
              </InputContainer>
            )}
            rules={rules}
          />
        );
      case "custom":
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState, formState }) => (
              <InputContainer invalid={fieldState.invalid} key={key}>
                {fieldProps.renderField({
                  editMode: editMode,
                  name,
                  "data-aq": `cus${name}`,
                  field,
                  fieldState,
                  formState,
                  fieldArray,
                  ...fieldProps,
                  ...additionalProps,
                })}
                <ErrorComponent
                  fieldState={fieldState}
                  dataAq={`err${name}`}
                  errorProps={errorProps}
                />
                {additionalData &&
                  additionalData(additionalProps.index, formState)}
              </InputContainer>
            )}
            rules={rules}
          />
        );

      default:
        return (
          <Controller
            control={control}
            name={name}
            render={({ field, fieldState }) => (
              <InputContainer invalid={fieldState.invalid} key={key}>
                <EhInput
                  {...field}
                  elementRef={field.ref}
                  editMode={editMode}
                  type="text"
                  data-aq={`txt${name}`}
                  name={name}
                  {...fieldProps}
                  {...additionalProps}
                />
                <ErrorComponent fieldState={fieldState} dataAq={`err${name}`} />
              </InputContainer>
            )}
            rules={rules}
          />
        );
    }
  };

  getField.propTypes = {
    name: PropTypes.string,
    key: PropTypes.string,
    control: PropTypes.any,
    rules: PropTypes.object,
    fieldProps: PropTypes.object,
    type: PropTypes.string,
    additionalProps: PropTypes.object,
    additionalData: PropTypes.func,
    errorProps: PropTypes.object,
  };

  const renderHeader = () => {
    return (
      <EhTable.Header>
        <EhTable.Row>
          {columns.map((columnData, index) => (
            <EhTable.HeaderCell
              fixedwidth={columnData.fixedWidth}
              width={columnData.width}
              key={index}
              collapsed={columnData.collapsed}
            >
              <EhText
                size="medium-condensed"
                fontWeight="bold"
                display="inline"
              >
                {columnData.header}
              </EhText>
            </EhTable.HeaderCell>
          ))}
          {editMode && canDeleteRow && (
            <EhTable.HeaderCell fixedwidth={Spacing.spacingLarge} />
          )}
        </EhTable.Row>
      </EhTable.Header>
    );
  };

  const renderBody = () => {
    return (
      <EhTable.Body>
        {fields.length === 0 && (
          <EhTable.Row>
            <EhTable.Cell colSpan={columns.length + 1}>
              {isString(noResultsMessage) ? (
                <EhText data-aq="noResultsMsg" color="grey">
                  {noResultsMessage}
                </EhText>
              ) : (
                noResultsMessage
              )}
            </EhTable.Cell>
          </EhTable.Row>
        )}
        {fields.map((item, rowIndex) => {
          const rowKey = keyField ? item[`${keyField}`] : rowIndex;
          return (
            <EhTable.Row key={rowKey} verticalAlign={contentAlign}>
              {columns.map((columnData, columnIndex) => {
                const {
                  fieldKey,
                  type,
                  fieldProps,
                  singleLine = false,
                  rules,
                  additionalData,
                  errorProps,
                } = columnData;
                return (
                  <EhTable.Cell
                    singleLine={singleLine}
                    key={columnIndex}
                    collapsed={columnData.collapsed}
                  >
                    {getField({
                      name: `${name}.${rowIndex}.${fieldKey}`,
                      key: rowKey,
                      control,
                      fieldProps,
                      type,
                      additionalProps: !isEmpty(item.inputProps)
                        ? { index: rowIndex, ...item.inputProps }
                        : { index: rowIndex },
                      rules,
                      fieldArray,
                      additionalData,
                      errorProps,
                    })}
                  </EhTable.Cell>
                );
              })}
              {canDeleteRow && (
                <EhTable.Cell>
                  {(editMode || !isEmpty(item.inputProps)) && (
                    <DeleteButton
                      borderless
                      data-aq="lnkRemove"
                      negative
                      icon="close"
                      onClick={() => {
                        remove(rowIndex);
                      }}
                    />
                  )}
                </EhTable.Cell>
              )}
            </EhTable.Row>
          );
        })}
      </EhTable.Body>
    );
  };

  const addNewRowButton = () => (
    <EhText popUpData={showButtonInfo && "Add New Row"}>
      <EhButton
        size="large"
        borderless
        icon="add circle"
        data-aq="btnAdd"
        disabled={fields.length === thresholdCount}
        onClick={() => {
          if (position === "start") {
            prepend(newData);
          } else {
            append(newData);
          }
        }}
      />
    </EhText>
  );

  const saveButton = () => (
    <EhText popUpData={showButtonInfo && "Save"}>
      <EhButton
        size="large"
        borderless
        icon="save"
        data-aq="btnSave"
        loading={saveButtonLoader}
        onClick={async () => {
          setEditMode(false);
          onSave && (await onSave());
        }}
        disabled={!editMode || disableSaveButton}
      />
    </EhText>
  );

  const editButton = () => (
    <EhText popUpData={showButtonInfo && "Edit"}>
      <EhButton
        size="large"
        borderless
        icon="pencil"
        data-aq="btnEdit"
        loading={editButtonLoader}
        onClick={async () => {
          setEditMode(true);
          onEdit && (await onEdit());
        }}
        disabled={editMode}
      />
    </EhText>
  );

  const undoButton = () => (
    <EhText popUpData={showButtonInfo && "Undo"}>
      <EhButton
        size="large"
        borderless
        icon="undo"
        data-aq="btnUndo"
        loading={undoButtonLoader}
        onClick={async () => {
          onUndo && onUndo();
        }}
        disabled={disableUndoButton}
      />
    </EhText>
  );
  return (
    <>
      <EhFlexBox justifyContent="space-between" alignItems="center">
        <EhFlexBox justifyContent="flex-start">{title}</EhFlexBox>
        <EhFlexBox justifyContent="flex-end" alignItems="baseline">
          {showUndoButton && undoButton()}
          {canAddNewRow && addNewRowButton()}
          {showSaveButton && editMode && saveButton()}
          {showEditButton && !editMode && editButton()}
        </EhFlexBox>
      </EhFlexBox>
      <StyledTableContainer
        overflowauto={!isEmpty(stickyArea)}
        ref={elementRef}
      >
        <EhTable
          name={name}
          fixed={fixed}
          data-aq={`tbl-${name}`}
          columnsdata={columns}
          stickyarea={stickyArea}
          striped={striped}
        >
          {renderHeader()}
          {renderBody()}
        </EhTable>
      </StyledTableContainer>
    </>
  );
};

EhEditTable.propTypes = {
  // react-hook-form library's form
  form: PropTypes.object.isrequired,
  // name of the editable table
  name: PropTypes.string.isRequired,
  // field that will be unique for each row
  keyField: PropTypes.string,
  // title of table
  title: PropTypes.string,
  // this flag determines if we need to show edit button
  showEditButton: PropTypes.bool,
  // this flag determines if we need to show undo button
  showUndoButton: PropTypes.bool,
  // This flag will display button info tooltip on hover
  showButtonInfo: PropTypes.bool,
  newRowProps: PropTypes.shape({
    // data that should be added when new row is clicked
    dataToAdd: PropTypes.object,
    // it determines where the new row should be added
    position: PropTypes.oneOf(["start", "end"]),
  }),
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      // heading for column
      header: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
      // key for that column
      fieldKey: PropTypes.string,
      width: PropTypes.number,
      fixedWidth: PropTypes.string,
      singleLine: PropTypes.bool,
      type: PropTypes.oneOf([
        "text",
        "number",
        "dropdown",
        "checkboxgroup",
        "custom",
        "date",
      ]),
      // other input  related props
      fieldProps: PropTypes.object,
      // validations for that column
      rules: PropTypes.object,
    })
  ).isRequired,
  striped: PropTypes.bool,
  stickyArea: PropTypes.shape({
    headers: PropTypes.bool,
    columns: PropTypes.shape({
      position: PropTypes.oneOf(["left", "right", "both"]),
      leftCount: PropTypes.number,
      rightCount: PropTypes.number,
    }),
  }),
  // this flag show/hide add new row button
  canAddNewRow: PropTypes.bool,
  isTableEditMode: PropTypes.bool,
  // this flag show/hides save button
  showSaveButton: PropTypes.bool,
  // func to be executed on click of save button
  onSave: PropTypes.func,
  // func to be executed on click of edit button
  onEdit: PropTypes.func,
  // func to be executed on click of undo button
  onUndo: PropTypes.func,
  // this flag disables save button
  disableSaveButton: PropTypes.bool,
  // this flag disables undo button
  disableUndoButton: PropTypes.bool,
  // this flag shows loader on edit/save button
  saveButtonLoader: PropTypes.bool,
  // max count of rows in table
  thresholdCount: PropTypes.number,
  // element ref for table container . can be used to scroll the table
  elementRef: PropTypes.any,

  editButtonLoader: PropTypes.bool,

  fixed: PropTypes.bool,
  // message to show if no results are present
  noResultsMessage: PropTypes.string,
  // alignment of content in cell
  contentAlign: PropTypes.oneOf(["top", "middle", "bottom"]),
  // this flag shows delete button for each row
  canDeleteRow: PropTypes.bool,
  // this flag shows loader on undo button
  undoButtonLoader: PropTypes.bool,
};

export default EhEditTable;
