const START_EDITING = "START_EDITING";
const STOP_EDITING = "STOP_EDITING";

export const ACTION_HANDLERS = {
  [START_EDITING]: startEditingHandler,
  [STOP_EDITING]: stopEditingHandler,
};

function startEditingHandler(state, action) {
  const namespace = action.namespace || "global";
  if (!state[namespace]) {
    return {
      ...state,
      [namespace]: action.key,
    };
  }
  console.warn(
    `Failed to acquire edit status, current key is ${state[namespace]}`
  );
  return state;
}

function stopEditingHandler(state, action) {
  const namespace = action.namespace || "global";
  if (!action.key || state[namespace] === action.key) {
    return {
      ...state,
      [namespace]: undefined,
    };
  }
  return state;
}

export function startEditing(key) {
  return {
    type: START_EDITING,
    key,
  };
}

export function stopEditing(key) {
  return {
    type: STOP_EDITING,
    key,
  };
}
