import { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import { Grid } from "semantic-ui-react";
import { get, map, filter, isEmpty } from "lodash";
import EhAdder from "./EhAdder";
import {
  EhInputField,
  EhDropdownField,
  EhDateTimePickerField,
} from "./formFields";
import { EhLabel } from "../typography";
import { EhTable, EhOptional, EhDate } from "../index";

import {
  createSortedArray,
  mapJsonToPropsInputField,
  mapJsonToPropsDropDownField,
  mapJsonToPropsDatePickerField,
  mapJsonToPropsEhAdder,
  mapJsonToPropsGrouped,
  matchDataFormatForSingle,
  matchDataFormatForGrouped,
  matchDataFormatForRepeatable,
} from "../../util/JSONSchemaHandler";

class EhDynamicField extends PureComponent {
  static propTypes = {
    /** This prop will have json schema which will be used to render components dynamically */
    json: PropTypes.object,
    /** Default layout will be considered as 'rows',
     *  so that every component from json schema will be rendered on new line */
    layout: PropTypes.string,
    /** This is the read-only props, it will have the values to be displyed for the components */
    values: PropTypes.any,
    /** This prop tells to load read-only mode components */
    editMode: PropTypes.bool,
    /** This is the form in which all the fields are getting rendered */
    form: PropTypes.any,
  };

  static defaultProps = {
    layout: "rows",
    editMode: true,
  };

  /** This method takes the json schema for text field and then map its attributes to match with the props of EhInputField component
   *  then render it. */
  getInputField(json) {
    const props = mapJsonToPropsInputField(json);
    const { editMode, form, values } = this.props;
    const { name, fieldLabel } = props;
    matchDataFormatForSingle(name, form, values);
    return editMode ? (
      <EhInputField {...props} />
    ) : (
      <EhOptional label={fieldLabel} value={get(values, name)} />
    );
  }

  /** This method takes the json schema for date field and then map its attributes to match with the props of EhDateTimePicker component
   *  then render it. */
  getDateField(json) {
    const props = mapJsonToPropsDatePickerField(json);
    const { editMode, form, values } = this.props;
    const { name, fieldLabel } = props;
    matchDataFormatForSingle(name, form, values);
    return editMode ? (
      <EhDateTimePickerField {...props} />
    ) : (
      <Fragment>
        <EhLabel>{fieldLabel}</EhLabel>
        <EhDate localTime type={json.fieldType} value={get(values, name)} />
      </Fragment>
    );
  }

  /** This method takes the json schema for singleSelect field and then map its attributes to match with the props of EhDropdownField component
   *  then render it. */
  getDropDownField(json) {
    const props = mapJsonToPropsDropDownField(json);
    const { editMode, form, values } = this.props;
    const { name, fieldLabel, options, display, select } = props;
    const dropdownValue = get(values, name);
    const value = filter(options, (obj) => obj[select] === dropdownValue);
    matchDataFormatForSingle(name, form, values);
    return editMode ? (
      <EhDropdownField {...props} />
    ) : (
      <EhOptional label={fieldLabel} value={get(value, `0.${display}`)} />
    );
  }

  /** This method takes the json schema for grouped field and then map its attributes
   *  so that we can render componentd dynamically in a group with specific layout(default to 'rows') */
  getGroupedField(json) {
    const { form, values } = this.props;
    const groupedFieldJson = mapJsonToPropsGrouped(json);
    const { name } = groupedFieldJson;
    matchDataFormatForGrouped(name, form, values);
    return (
      <Fragment>
        <EhLabel>{groupedFieldJson.fieldLabel}</EhLabel>
        <EhTable>
          <EhTable.Body>
            {this.renderLayoutWithComponents(
              groupedFieldJson.layout,
              groupedFieldJson.groupedFields
            )}
          </EhTable.Body>
        </EhTable>
      </Fragment>
    );
  }

  /** This method takes the json schema for repeater field and then create the props for EhAdder which will render component inside it and modify name attribute for the component */
  getEhAdder(json) {
    const props = mapJsonToPropsEhAdder(json);
    const { editMode, form, values } = this.props;
    const { fieldLabel, name, addButtonText, required } = props;
    matchDataFormatForRepeatable(name, form, values);
    const ehAdderValues = get(values, name);
    if (form && editMode && required) {
      const val = form.values[name];
      (!val || isEmpty(val)) && (form.values[name] = [{}]);
    }
    return (
      <Fragment>
        <EhLabel>{fieldLabel}</EhLabel>
        <Grid>
          {editMode ? (
            <EhAdder
              addButtonText={addButtonText}
              name={name}
              renderAddButton={(button) => button}
              editMode={editMode}
              hideAddButton={!editMode}
              required={required}
            >
              {(item, index, array, deleteButton) => {
                return this.getEhAdderContents(
                  props,
                  item,
                  index,
                  array,
                  deleteButton
                );
              }}
            </EhAdder>
          ) : (
            map(ehAdderValues, (item, index) =>
              this.getEhAdderContents(props, item, index)
            )
          )}
        </Grid>
      </Fragment>
    );
  }

  getEhAdderContents = (props, item, index, array, deleteButton) => {
    const { component, name, canDeleteSavedRecords } = props;
    return (
      <Grid.Row key={index}>
        <Grid.Column width={6}>
          {this.renderDynamicFields({
            ...component,
            name: `${name}[${index}].ehField`,
          })}
        </Grid.Column>
        <Grid.Column width={5} />
        <Grid.Column width={1}>
          {canDeleteSavedRecords
            ? deleteButton
            : get(this.props.values, `${name}.${index}`) === undefined &&
              deleteButton}
        </Grid.Column>
      </Grid.Row>
    );
  };
  /** This method takes json schema as an input and then get the sorted array of json schema object to render the components */
  renderComponents(json) {
    const sortedJson = createSortedArray(json);
    return sortedJson.map((obj) => {
      if (obj.isRepeatable) {
        return this.getEhAdder(obj);
      }
      return this.renderDynamicFields(obj);
    });
  }

  /** Based on the field type, it will render the specific component */
  renderDynamicFields(obj) {
    switch (obj.fieldType) {
      case "text":
        return this.getInputField(obj);
      case "date":
        return this.getDateField(obj);
      case "singleSelect":
        return this.getDropDownField(obj);
      case "grouped":
        return this.getGroupedField(obj);
      default:
        return null;
    }
  }

  /** This will arrange the components in a rows( each component in new row ) */
  renderRowsLayout(components) {
    return components.map((component, index) => (
      <EhTable.Row key={index}>
        <EhTable.Cell>{component}</EhTable.Cell>
      </EhTable.Row>
    ));
  }

  /** This method renders the component with selected layout */
  renderLayoutWithComponents(layout, json) {
    const components = this.renderComponents(json);
    switch (layout) {
      case "rows":
        return this.renderRowsLayout(components);
      default:
        return null;
    }
  }

  render() {
    const { layout, json } = this.props;
    return (
      <EhTable>
        <EhTable.Body>
          {this.renderLayoutWithComponents(layout, json)}
        </EhTable.Body>
      </EhTable>
    );
  }
}

export default EhDynamicField;
