import { reduce, partial } from "lodash";
// ------------------------------------
// Constants
// ------------------------------------
const OPEN_TOAST = "OPEN_TOAST";
const RESET_TOAST = "RESET_TOAST";

// ------------------------------------
// Reducer
// ------------------------------------

const ACTION_HANDLERS = {
  [OPEN_TOAST]: openToast,
  [RESET_TOAST]: resetToast,
};
const initialState = {
  notifyData: null,
  notifyStatus: "TOAST_CLOSED",
};
export default function createNotifyReducer(state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type];

  return handler ? handler(state, action) : state;
}

function openToast(state, action) {
  return {
    ...state,
    notifyData: { ...action.data },
    notifyStatus: "TOAST_OPENED",
  };
}

function resetToast() {
  return initialState;
}
// ------------------------------------
// Action Creators
// ------------------------------------
export const actions = {
  resetToastAction,
  ...reduce(
    ["success", "warning", "wait", "error", "info"],
    (action, method) => {
      action[method] = partial(openToastAction, method);
      return action;
    },
    {}
  ),
};

function openToastAction(type, text) {
  const data = {
    type,
    text,
  };
  return {
    type: OPEN_TOAST,
    data,
  };
}

function resetToastAction() {
  return { type: RESET_TOAST };
}
