import { Fragment, Component } from "react";
import { withRouter } from "react-router";
import PropTypes from "prop-types";
import { Loader } from "semantic-ui-react";
import { EventEmitter, AUTH_EVENTS, Session, IDP_EVENTS } from "@evolent/roots";
import TimeoutModal from "../componentsStd/TimeoutModal";

export class SecureSession extends Component {
  constructor(settings) {
    super();

    Session.addSettings({ ...settings });

    this.state = {
      session: null,
      modalOpen: false,
      signingOut: false,
      countdownInterval: null,
      countdownTime: 0,
    };

    EventEmitter.addListener(AUTH_EVENTS.NOT_AUTHENTICATED, async () => {
      await Session.end();
    });

    EventEmitter.addListener(AUTH_EVENTS.NOT_AUTHORIZED, () => {
      this.props.history.push("/forbidden");
    });

    EventEmitter.addListener(IDP_EVENTS.USER_SIGNED_OUT, () => {
      this.setState({ signingOut: true });
    });

    this.closeModal = this.closeModal.bind(this);
  }

  static propTypes = {
    children: PropTypes.any,
    hasAccess: PropTypes.bool,
    history: PropTypes.shape({
      push: PropTypes.func,
    }),
  };

  async componentDidMount() {
    const session = await Session.start();
    this.setState({ session });

    EventEmitter.addListener(
      IDP_EVENTS.SESSION_WARNING,
      this.handleModalEvent.bind(this)
    );
  }

  async componentWillUnmount() {
    await Session.destroy();
  }

  closeModal() {
    clearInterval(this.state.countdownInterval);
    this.setState({ modalOpen: false });
  }

  handleModalEvent(data) {
    if (!data) {
      this.closeModal();
      return;
    }

    let countdownInterval;
    this.setState(
      {
        countdownTime: data.timeoutWarningTimeLeft,
        onSignout: data.onSignout,
        onExtend: data.onExtend,
      },
      () => {
        countdownInterval = setInterval(() => {
          if (this.state.countdownTime < 1) {
            this.setState({ signingOut: true });
            clearInterval(this.state.countdownInterval);
            return;
          }

          if (this.state.countdownTime % 10 === 0) {
            this.setState({
              countdownTime: Session.getTimeoutTimeLeft(),
            });
          }

          this.setState((state) => ({
            countdownTime: state.countdownTime - 1,
          }));
        }, 1000);
      }
    );

    this.setState({ countdownInterval, modalOpen: true });
  }

  render() {
    const {
      session,
      signingOut,
      modalOpen,
      onSignout,
      onExtend,
      countdownTime,
    } = this.state;
    const { hasAccess, children } = this.props;
    return (
      <div>
        {session && !signingOut && hasAccess ? (
          <Fragment>
            {modalOpen && (
              <TimeoutModal
                open={modalOpen}
                onSignout={async () => {
                  this.setState({ signingOut: true });
                  await onSignout();
                }}
                onExtend={onExtend}
                closeModal={this.closeModal}
                timeLeft={countdownTime}
              />
            )}
            {children}
          </Fragment>
        ) : (
          <Loader active content={signingOut ? "Signing Out" : "Loading"} />
        )}
      </div>
    );
  }
}

export default withRouter(SecureSession);
