import { Component, Fragment } from "react";
import PropTypes from "prop-types";
import { Dropdown, Icon, Divider } from "semantic-ui-react";
import styled from "styled-components";
import { Colors, Spacing, Typography, Elevation } from "../../style";

const StyledDivider = styled(Divider)`
  &&&&& {
    margin: ${Spacing.spacingSmall} 0 ${Spacing.spacingLarge};
  }
`;

const StyledTriggerIcon = styled(Icon)`
  &&&&& {
    font-size: ${Typography.fontSizeLarge};
  }
`;

const StyledMenuIcon = styled(Icon)`
  &&&&& {
    margin: 0 ${Spacing.spacingXxSmall} 0 0 !important;
  }
`;

const StyledDropdown = styled(Dropdown)`
  &&&&&&&&&&&& {
    color: ${Colors.white};
    padding: ${Spacing.spacingSmall} ${Spacing.spacingMedium};
    border-bottom: ${Spacing.borderWidthThick} solid ${Colors.indigo[100]};
    background-color: ${Colors.indigo[100]};

    :focus {
      background-color: ${Colors.midnight};
      border-bottom: ${Spacing.borderWidthThick} solid ${Colors.blue[100]};
      box-shadow: ${Elevation.boxShadowInsetSmall};
    }

    .menu {
      margin-top: ${Spacing.spacingXSmall};
      border-radius: ${Spacing.borderRadiusSmall};
      padding: ${Spacing.spacingSmall} 0 ${Spacing.spacingLarge};

      .item {
        color: ${Colors.blue[100]};
        font-weight: ${Typography.fontWeightRegular};
        padding: 0 ${Spacing.spacingLarge} ${Spacing.spacingSmall} !important;
      }

      .item:last-child {
        padding: 0 ${Spacing.spacingLarge} !important;
      }
    }

    .menu:after {
      right: 1.8em !important;
    }

    .header {
      padding: 0;
      margin: 0;
      font-weight: ${Typography.fontWeightBold};
      font-size: ${Typography.fontSizeMedium};
      text-transform: capitalize;
    }

    .icon {
      margin: 0 ${Spacing.spacingXxxSmall};
    }
  }
`;

const PaddedDiv = styled.div`
  &&&&& {
    padding: 0 ${Spacing.spacingLarge};
  }
`;

export default class EhHeaderMenu extends Component {
  static propTypes = {
    /** The icon displayed on the header menu button. */
    icon: PropTypes.string.isRequired,
    /** The header for the menu. */
    header: PropTypes.any,
    /** The items to render below the header. */
    items: PropTypes.array.isRequired,
  };

  renderItem = (config, index) => {
    return (
      <Dropdown.Item
        key={index}
        name={config.name}
        onClick={async () => {
          await config.onClick();
        }}
        data-aq={`mnItem${config.name}`}
      >
        {config.display}
      </Dropdown.Item>
    );
  };

  render() {
    const { icon, header, items } = this.props;

    return (
      <StyledDropdown
        trigger={
          <span>
            <StyledTriggerIcon name={icon} data-aq={`mnIcon-${icon}`} />
          </span>
        }
        direction="left"
        pointing="top"
      >
        <Dropdown.Menu>
          {header && (
            <Dropdown.Header>
              <Fragment>
                <PaddedDiv>
                  <StyledMenuIcon
                    color="black"
                    name={icon}
                    data-aq={`mnheader-${header}`}
                  />
                  {header}
                </PaddedDiv>
                <StyledDivider />
              </Fragment>
            </Dropdown.Header>
          )}
          {items && items.map(this.renderItem)}
        </Dropdown.Menu>
      </StyledDropdown>
    );
  }
}
