import { map, sortBy, get, set, forEach, isArray, isObject } from "lodash";

/** This method takes 'object of objects' as input and output 'array of objects' based on 'order' attribute */
const createSortedArray = (json) =>
  sortBy(
    map(json, (obj, name) => ({ name, ...obj })),
    "order"
  );

/** This method maps the properties of json to the props of EhInputField */
const mapJsonToPropsInputField = (json) => {
  const { name } = json;
  return {
    name,
    fieldLabel: get(json, "props.fieldLabel", ""),
    "data-aq": get(json, "props.data-aq", ""),
  };
};

/** This method maps the properties of json to the props of EhDropdownField */
const mapJsonToPropsDropDownField = (json) => {
  const { name } = json;
  const select = "value";
  const display = "label";
  const fluid = false;
  return {
    name,
    fieldLabel: get(json, "props.fieldLabel", ""),
    "data-aq": get(json, "props.data-aq", ""),
    fluid,
    options: get(json, "props.options", ""),
    select,
    display,
  };
};

/** This method maps the properties of json to the props of EhDateTimePickerField */
const mapJsonToPropsDatePickerField = (json) => {
  const { name } = json;
  return {
    name,
    fieldLabel: get(json, "props.fieldLabel", ""),
    "data-aq": get(json, "props.data-aq", ""),
  };
};

/** This method create the json object which will be used to render the EhAdder
 *  Also, it will add new object 'component' which will get rendered inside the EhAdder component
 */
const mapJsonToPropsEhAdder = (json) => {
  const { name, fieldType, ...otherProps } = json;
  const component = {
    ...otherProps,
    fieldType,
    name,
    props: {
      ...otherProps.props,
      fieldLabel: "",
    },
  };
  return {
    name,
    addButtonText: "Add ID",
    canDeleteSavedRecords: false,
    required: true,
    fieldLabel: get(json, "props.fieldLabel", ""),
    component,
  };
};

/** This method sets the layout for the group, if not defined
 *  It will modify the name of every field based upon the name of a group
 */
const mapJsonToPropsGrouped = (json) => {
  const { name } = json;
  const layout = json.layout || "rows";
  const groupedFields = createSortedArray(json.groupedFields);
  forEach(groupedFields, (jsonObj) => {
    jsonObj.name = `${name}.${jsonObj.name}`;
  });
  return {
    name,
    fieldLabel: get(json, "props.fieldLabel", ""),
    groupedFields,
    layout,
  };
};

/** This method will map the saved data to the form data  */
const mapDbDataToFormData = (data) => {
  const mappedData = {};
  if (!isObject(data)) {
    return data;
  }
  forEach(data, (value, key) => {
    let mappedValue = value;
    if (isArray(value)) {
      mappedValue = map(value, (val) => ({
        ehField: mapDbDataToFormData(val),
      }));
    } else if (isObject(value)) {
      mappedValue = mapDbDataToFormData(value);
    }
    mappedData[key] = mappedValue;
  });
  return mappedData;
};

/** This method will map the form data to the db data  */
const mapFormDataToDbData = (data) => {
  const mappedData = {};
  if (!isObject(data)) {
    return data;
  }
  forEach(data, (value, key) => {
    let mappedValue = value;
    if (isArray(value)) {
      mappedValue = map(value, (val) => mapFormDataToDbData(val.ehField));
    } else if (isObject(value)) {
      mappedValue = mapFormDataToDbData(value);
    }
    mappedData[key] = mappedValue;
  });
  return mappedData;
};

/** This method will match the input data with the single field data format */
const matchDataFormatForSingle = (name, form, values) => {
  if (form && typeof get(form.values, `${name}`) === "object") {
    set(form.values, `${name}`);
  }
  if (typeof get(values, `${name}`) === "object") {
    set(values, `${name}`);
  }
};

/** This method will match the input data with the grouped field data format */
const matchDataFormatForGrouped = (name, form, values) => {
  const formVal = get(form, `values[${name}]`);
  const val = get(values, `${name}`);
  if (form && (isArray(formVal) || typeof formVal === "string")) {
    set(form.values, `${name}`, {});
  }
  if (isArray(val) || typeof val === "string") {
    set(values, `${name}`, {});
  }
};

/** This method will match the input data with the repeatable field data format */
const matchDataFormatForRepeatable = (name, form, values) => {
  const formVal = get(form, `values[${name}]`);
  const val = get(values, `${name}`);
  if (
    form &&
    ((isObject(formVal) && !isArray(formVal)) || typeof formVal === "string")
  ) {
    set(form.values, `${name}`, [{}]);
  }
  if ((isObject(val) && !isArray(val)) || typeof val === "string") {
    set(values, `${name}`, [{}]);
  }
};

export {
  createSortedArray,
  mapJsonToPropsInputField,
  mapJsonToPropsDropDownField,
  mapJsonToPropsDatePickerField,
  mapJsonToPropsEhAdder,
  mapJsonToPropsGrouped,
  mapDbDataToFormData,
  mapFormDataToDbData,
  matchDataFormatForSingle,
  matchDataFormatForGrouped,
  matchDataFormatForRepeatable,
};
