import { PureComponent } from "react";
import { conformToMask } from "react-text-mask";
import PropTypes from "prop-types";

export const presetMasks = {
  phone: [
    "(",
    /[1-9]/,
    /\d/,
    /\d/,
    ")",
    " ",
    /\d/,
    /\d/,
    /\d/,
    "-",
    /\d/,
    /\d/,
    /\d/,
    /\d/,
  ],
  phoneExt: ["x", /\d/, /\d/, /\d/, /\d/, /\d/],
};

const removeNonDigits = (value) => value.replace(/\D/g, "");

const unmaskers = {
  phone: removeNonDigits,
  phoneExt: removeNonDigits,
};

export default function withMaskFP(mask) {
  return function withMask(WrappedComponent) {
    class EhMaskedInput extends PureComponent {
      static propTypes = {
        loading: PropTypes.any,
        onBlur: PropTypes.func,
        onChange: PropTypes.func,
        required: PropTypes.bool,
        value: PropTypes.string,
      };

      static applyMask = (mask, value) =>
        conformToMask(value, presetMasks[mask], {
          showMask: true,
          guide: false,
        }).conformedValue;

      onChange = (evt, { value }) => {
        const maskedValue = EhMaskedInput.applyMask(mask, value);
        this.props.onChange(unmaskers[mask](maskedValue));
      };

      render() {
        return (
          <WrappedComponent
            {...this.props}
            value={EhMaskedInput.applyMask(mask, this.props.value)}
            onChange={this.onChange}
          />
        );
      }
    }

    return EhMaskedInput;
  };
}
