import PropTypes from "prop-types";
import InputMask from "react-input-mask";

import { DateHandler } from "@evolent/roots";
import asField from "./hoc/asField";

const DateInput = ({ required, ...props }) => {
  return <InputMask mask="99/99/9999" maskChar={null} {...props} />;
};

DateInput.propTypes = {
  onChange: PropTypes.func,
  required: PropTypes.bool,
};

const DateInputField = asField(DateInput);
const EhDateInput = ({
  name,
  fieldLabel,
  value,
  required,
  maxDate,
  minDate,
  ...props
}) => {
  const validateDate = (value) => {
    let validationMessage = null;
    if (DateHandler.isValid(value, "MM/DD/YYYY")) {
      const date = DateHandler.getDate(value, "MM/DD/YYYY");
      minDate &&
        DateHandler.isBefore(date, minDate) &&
        (validationMessage = `Date cannot be before ${minDate}.`);
      maxDate &&
        DateHandler.isAfter(date, maxDate) &&
        (validationMessage = `Date cannot be after ${maxDate}.`);
    } else {
      validationMessage = "Please enter a valid date";
    }
    return validationMessage;
  };

  return (
    <DateInputField
      fieldLabel={fieldLabel}
      name={name}
      value={value}
      validate={validateDate}
      required={required}
      {...props}
    />
  );
};

EhDateInput.propTypes = {
  name: PropTypes.string.isRequired,
  fieldLabel: PropTypes.string.isRequired,
  value: PropTypes.any,
  required: PropTypes.bool,
  maxDate: PropTypes.string,
  minDate: PropTypes.string,
};

export default EhDateInput;
