import { isNil } from "lodash";

const minValue = (minAmount, message) => (inputValue) => {
  if (isNil(minAmount)) {
    return null;
  }
  const errorMsg =
    message || `Amount must be atleast greater than ${minAmount}`;
  return inputValue < minAmount ? errorMsg : null;
};

const maxValue = (maxAmount, message) => (inputValue) => {
  if (isNil(maxAmount)) {
    return null;
  }
  const errorMsg = message || `Amount must not be greater than ${maxAmount}`;
  return inputValue > maxAmount ? errorMsg : null;
};

const isAmountBetween = (minAmount, maxAmount) => (inputValue) => {
  if (isNil(minAmount && maxAmount)) {
    return null;
  }
  return inputValue < minAmount || inputValue > maxAmount
    ? `Amount must be between ${minAmount} and ${maxAmount}`
    : null;
};

export { minValue, maxValue, isAmountBetween };
