import { PureComponent } from "react";
import PropTypes from "prop-types";
import EhText from "./typography/EhText";
import EhOptional from "./EhOptional";

// 2018-10-26T11:12:08.277
const typeFormats = {
  date: "MM/DD/YYYY",
  time: "hh:mm A",
  datetime: "MM/DD/YYYY hh:mm A",
  datetimeWithSeconds: "MM/DD/YYYY hh:mm:ss A",
};

export default class EhDate extends PureComponent {
  static propTypes = {
    ...EhText.propTypes,
    /** The incoming date to be formatted. */
    value: PropTypes.string,
    /** The type of formatting to apply on the date. */
    type: PropTypes.oneOf(["date", "time", "datetime", "datetimeWithSeconds"]),
    /** The flag which determines if localtime should be used. */
    localTime: PropTypes.bool,
  };

  render() {
    const { localTime, type, value, ...ehTextProps } = this.props;
    return (
      <EhOptional
        type={localTime ? "localDateTime" : "utcDateTime"}
        format={typeFormats[type]}
        value={value}
        {...ehTextProps}
      />
    );
  }
}
