import { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Icon } from "semantic-ui-react";
import EhFlexBox from "../../../componentsStd/layout/EhFlexBox";
import { Spacing, Colors, Typography, LineHeight } from "../../../style";
import { APP_SETTINGS, getModulesToShowSelector } from "@evolent/roots";
import EhIdentifiText from "../../../componentsStd/EhIdentifiText";
import { AppTile } from "./index";

const TransitionProperties = css`
  transition-duration: 0.15s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0s;
`;

const StyledFlex = styled(EhFlexBox)`
  overflow: hidden;
  background-color: #fff;
  height: 100vh;
  width: 0;
  transition-property: width;
  ${() => TransitionProperties}
  ${(props) => props.showAppDrawer && "width: 18.4237rem;"}
`;

const StyledAppMenu = styled(EhFlexBox)`
  && {
    padding: ${Spacing.spacingXLarge} 0 ${Spacing.spacingXLarge}
      ${Spacing.spacingXLarge};
    width: 100%;
  }
`;

const StyledOverlay = styled(EhFlexBox)`
  && {
    background-color: ${Colors.black};
    height: 100vh;
    position: absolute;
    width: 0;
    top: 0;
    opacity: 0;
    transition-property: opacity;
    z-index: -1;
    ${() => TransitionProperties}
    ${(props) =>
      props.showAppDrawer &&
      css`
        opacity: 0.25;
      `}
    ${(props) =>
      props.showOverlay &&
      css`
        width: 100vw;
      `}
  }
`;

const StyledContentSection = styled(EhFlexBox)`
  height: calc(100% - 2.5rem);
`;
const StyledTopSection = styled(EhFlexBox)`
  height: 100%;
  overflow: auto;
  margin: ${Spacing.spacingXLarge} 0;
  padding-right: ${Spacing.spacingLarge};
`;

const StyledAnchor = styled.a`
  font-size: ${Typography.fontSizeSmall};
  line-height: ${LineHeight.text.small};
  border-radius: ${Spacing.borderRadiusLarge};
  height: ${Spacing.spacingXxLarge};
  padding: ${Spacing.spacingSmall} ${Spacing.spacingMedium};
  margin-right: ${Spacing.spacingSmall};
  font-weight: ${Typography.fontWeightBold};
  &:hover {
    background-color: ${Colors.grey[5]};
    color: ${Colors.blue[100]};
  }
`;

const mapStateToProps = (state) => ({
  moduleTiles: getModulesToShowSelector(state),
});

const EhAppDrawerSidebar = ({
  showAppDrawer,
  setShowAppDrawer,
  moduleTiles,
  openInNewTab = false,
}) => {
  const [showOverlay, setShowOverlay] = useState(showAppDrawer);
  useEffect(() => {
    if (showAppDrawer === false) {
      setTimeout(() => {
        setShowOverlay(false);
      }, 150);
    } else {
      setShowOverlay(true);
    }
  }, [showAppDrawer]);
  return (
    <StyledFlex showAppDrawer={showAppDrawer} data-aq="app-drawer">
      <StyledAppMenu flexDirection="column" data-aq="app-menu">
        <EhIdentifiText />
        <StyledContentSection
          flexDirection="column"
          justifyContent="space-between"
        >
          <StyledTopSection flexDirection="column">
            {moduleTiles.map((tile) => (
              <AppTile
                tile={tile}
                key={tile.name}
                data-aq={tile.name}
                openInNewTab={openInNewTab}
              />
            ))}
          </StyledTopSection>
          <StyledAnchor
            data-aq="launcher-button"
            href={APP_SETTINGS.launcherUrl}
          >
            <Icon name="home" />
            Go To Launcher
          </StyledAnchor>
        </StyledContentSection>
      </StyledAppMenu>
      <StyledOverlay
        data-aq="drawer-overlay"
        showOverlay={showOverlay}
        showAppDrawer={showAppDrawer}
        onClick={() => setShowAppDrawer(false)}
      />
    </StyledFlex>
  );
};

EhAppDrawerSidebar.propTypes = {
  showAppDrawer: PropTypes.bool,
  setShowAppDrawer: PropTypes.func,
  moduleTiles: PropTypes.array,
  openInNewTab: PropTypes.bool,
};

export default connect(mapStateToProps)(EhAppDrawerSidebar);
