import { Component } from "react";
import styled from "styled-components";
import { Loader } from "semantic-ui-react";
import PropTypes from "prop-types";
import { Colors } from "../../style";

const StyledLoader = styled(Loader)`
  &&&& {
    width: 100% !important;
    height: 100% !important;
    background-color: #38313142;
    padding-top: 50vh !important;
    transform: translate(-50%, -50%);
    color: ${Colors.black};
    :after {
      top: 48%;
      left: 50%;
      border-color: ${Colors.grey[55]} transparent transparent;
    }
    :before {
      top: 48%;
      left: 50%;
    }
  }
`;
export default class EhLoader extends Component {
  static propTypes = {
    loaderContent: PropTypes.string,
  };

  render() {
    const { loaderContent } = this.props;
    return <StyledLoader active content={loaderContent} />;
  }
}
