import StringMask from "string-mask";
import DateHandler from "./dateHandler";

const formatDate = (value, format = `MM/DD/YYYY`, localTime) =>
  localTime
    ? DateHandler.toLocalTime(value, format)
    : DateHandler.formatDate(value, format);

const formatNumber = (value, format, locale = "en-US") =>
  isNaN(parseFloat(value))
    ? ""
    : new Intl.NumberFormat(locale, format).format(value);

const formatPercent = (value) => `${parseInt(value)}%`;

const mask = (maskPattern, value) => {
  return new StringMask(maskPattern).apply(value);
};

const maskText = (value, format) => (format ? mask(format, value) : value);

const maskPhone = (value, format = "(999) 999-9999") => maskText(value, format);

const maskSsn = (value) => maskText(value, "999-99-9999");

const formatZip = (value) => {
  if (isNaN(parseInt(value))) {
    return NaN;
  }
  return value?.replace(/(\d{5})/gi, "$1-").replace(/-$/gi, "");
};

const pluralizeText = (
  value,
  { singular = "Count", plural = `${singular}s`, showCount = true, zeroText }
) => {
  try {
    const parsedValue = parseInt(value);
    let text = parsedValue === 1 ? singular : plural;
    showCount && (text = `${parsedValue} ${text}`);
    if (parsedValue === 0 && zeroText) {
      return zeroText;
    }
    return text;
  } catch (error) {
    return NaN;
  }
};

const getFormattedValue = (value, type, format, locale) => {
  switch (type) {
    case "number":
      return formatNumber(value, format, locale);
    case "percent":
      return formatPercent(value);
    case "text":
      return format ? maskText(value, format) : value;
    case "phone":
      return maskPhone(value, format);
    case "localDateTime":
      return formatDate(value, format, true);
    case "utcDateTime":
      return formatDate(value, format, false);
    case "substitutor":
      return substitutor(value);
    case "zip":
      return formatZip(value);
    case "ssn":
      return maskSsn(value);
    case "pluralize":
      return pluralizeText(value, format || {});
    default:
      return value;
  }
};

const substitutor = (() => {
  const regEx = /<<([^<<]*?)>>/g;

  const checkForSubstitutors = (str) => regEx.test(str);

  const getSubstitueValue = (regexMatch, placeholder) => {
    const [value, type, format, locale] = placeholder.split("|");
    let parseFormat;
    try {
      parseFormat = JSON.parse(format);
    } catch (error) {
      parseFormat = format?.trim();
    }
    return getFormattedValue(
      value?.trim(),
      type?.trim(),
      parseFormat,
      locale?.trim()
    );
  };

  return (input) => {
    while (checkForSubstitutors(input)) {
      input = input.replace(regEx, getSubstitueValue);
    }
    return input;
  };
})();

const Formatters = {
  formatDate,
  formatNumber,
  formatPercent,
  maskText,
  maskPhone,
  getFormattedValue,
  substitutor,
  formatZip,
  pluralizeText,
  maskSsn,
};

export default Formatters;
