import { APP_SETTINGS } from "../constants";
import axios from "axios";
import Session from "../authentication/session";

export const goToReports = async (data) => {
  try {
    const { evt, src, reportName, share } = data || {};
    const reportsTab = reportName ? `reportsTab${reportName}` : "reportsTab";
    window.open("", reportsTab).document.write("<h3>Loading...</h3>");

    let reportURL = `${APP_SETTINGS.identifiServices}core/reports/dashboard?applicationId=${APP_SETTINGS.moduleId}`;

    if (reportName) {
      reportURL = `${reportURL}&reportName=${reportName}`;
    }

    const response = await axios.get(reportURL, {
      headers: {
        Authorization: Session.getAuthorizationToken(),
      },
    });

    const { reportsDashboardUrl, reportsDashboardParams } = response?.data;
    const { pg, messageID, usrSmgr } = reportsDashboardParams;

    // Create hidden form
    const reportsRedirectForm = document.createElement("form");
    reportsRedirectForm.setAttribute("method", "post");
    reportsRedirectForm.setAttribute("action", `${reportsDashboardUrl}`);
    // Redirect to new window
    reportsRedirectForm.setAttribute("target", reportsTab);

    // Add the fields to our forum to be used in the POST call
    if (evt && src) {
      addFieldToForm(reportsRedirectForm, evt, varToString({ evt }));
      addFieldToForm(reportsRedirectForm, src, varToString({ src }));
      share &&
        addFieldToForm(reportsRedirectForm, share, varToString({ share }));
    } else {
      addFieldToForm(reportsRedirectForm, pg, varToString({ pg }));
    }
    addFieldToForm(reportsRedirectForm, messageID, varToString({ messageID }));
    addFieldToForm(reportsRedirectForm, usrSmgr, varToString({ usrSmgr }));

    // Submit the form and remove it
    document.body.appendChild(reportsRedirectForm);
    reportsRedirectForm.submit();
    document.body.removeChild(reportsRedirectForm);
  } catch (e) {
    console.log(e);
  }
};

// Add a field to the form with a type, name, and value
const addFieldToForm = (form, value, valueName) => {
  const fieldForForm = document.createElement("input");
  fieldForForm.setAttribute("type", "hidden");
  fieldForForm.setAttribute("name", valueName);
  fieldForForm.setAttribute("value", value);
  form.appendChild(fieldForForm);
};

// Convert JSON name field to string
const varToString = (variable) => Object.keys(variable)[0];
