import { Component, Fragment } from "react";
import { Modal, Segment } from "semantic-ui-react";
import PropTypes from "prop-types";
import styled, { css } from "styled-components";
import EhLoader from "./EhLoader";
import { LineHeight, Typography, Spacing, Colors } from "../../style";

const StyledModal = styled(Modal)`
  &&&& {
    line-height: ${LineHeight.text.normal};
    .close {
      font-size: ${Typography.fontSizeMedium};
      padding: ${Spacing.spacingNone};
      margin: ${Spacing.spacingXxSmall} ${Spacing.spacingSmall};
      top: ${Spacing.spacingSmall};
      right: ${Spacing.spacingSmall};
      color: ${Colors.grey[55]};
      height: auto;
      width: auto;
    }
  }
  &&&& > .header {
    padding: ${Spacing.spacingMedium} ${Spacing.spacingLarge};
    font-size: ${Typography.fontSizeLarge};
    font-weight: ${Typography.fontWeightBlack};
    margin-bottom: 0;
  }
  &&&& > .actions {
    padding: ${Spacing.spacingLarge};
    text-align: right;
    background-color: ${Colors.white};
  }
`;

const EhModalHeader = styled(Modal.Header)``;

const EhModalContent = styled(({ noContentPadding, ...rest }) => (
  <Segment {...rest} />
))`
  &&& {
    ${({ noContentPadding }) =>
      css`
        margin: 0;
        padding: ${noContentPadding
          ? Spacing.spacingNone
          : Spacing.spacingLarge};
      `}
  }
`;

const EhModalFooter = styled(Modal.Actions)``;

export default class EhModal extends Component {
  // Removed importing PropTypes from Semantic Modal due to overhead it was causing for documentation.
  static propTypes = {
    /** The elements to be loaded in the modal. */
    children: PropTypes.any,
    /** The flag which determines if the loader should be visible. */
    loading: PropTypes.bool,
    /** The text to display under the loader. */
    loaderContent: PropTypes.string,
    /** The function which renders the header of the modal. */
    renderHeader: PropTypes.func,
    /** The function which renders the footer of the modal. */
    renderFooter: PropTypes.func,
    /** Whether or not the Modal should close when the dimmer is clicked. */
    closeOnDimmerClick: PropTypes.bool,
    /** A modal can reduce its complexity */
    basic: PropTypes.bool,
    /** A Modal can appear in a dimmer. */
    dimmer: PropTypes.oneOf([true, "inverted", "blurring"]),
    /** Preset available sizes for the modal. */
    size: PropTypes.oneOf(["mini", "tiny", "small", "large", "fullscreen"]),
    /** Shorthand for the close icon. Closes the modal on click. */
    closeIcon: PropTypes.oneOfType([PropTypes.node, PropTypes.bool]),
    /** Action onClick handler when using shorthand actions.
     * @param {SyntheticEvent} event - React's original SyntheticEvent.
     * @param {object} data - All props.
     */
    onActionClick: PropTypes.func,
    /** Called when a close event happens.
     * @param {SyntheticEvent} event - React's original SyntheticEvent.
     * @param {object} data - All props.
     */
    onClose: PropTypes.func,
    /** Called when an open event happens.
     * @param {SyntheticEvent} event - React's original SyntheticEvent.
     * @param {object} data - All props.
     */
    onOpen: PropTypes.func,
    /** Called when the portal is mounted on the DOM.
     * @param {null} @param {object} data - All props.
     */
    onMount: PropTypes.func,
    /** Called when the portal is unmounted from the DOM.
     * @param {null}
     * @param {object} data - All props.
     */
    onUnmount: PropTypes.func,
    /** The flag which determines if the EhModal content padding should be required or not. */
    noContentPadding: PropTypes.bool,
  };

  static defaultProps = {
    size: "fullscreen",
    closeIcon: false,
    closeOnDimmerClick: false,
  };

  static Header = EhModalHeader;
  static Footer = EhModalFooter;
  static Content = EhModalContent;

  handleHeader = (renderHeader) => {
    return <EhModalHeader>{renderHeader()}</EhModalHeader>;
  };

  handleFooter = (renderFooter) => {
    return <EhModalFooter>{renderFooter()}</EhModalFooter>;
  };

  renderSlottedModal = () => {
    const { children, renderFooter, renderHeader, noContentPadding } =
      this.props;
    return (
      <Fragment>
        {renderHeader && this.handleHeader(renderHeader)}
        <EhModalContent basic noContentPadding={noContentPadding}>
          {children}
        </EhModalContent>
        {renderFooter && this.handleFooter(renderFooter)}
      </Fragment>
    );
  };

  render() {
    const {
      children,
      renderHeader,
      renderFooter,
      loading,
      loaderContent,
      ...modalProps
    } = this.props;
    return (
      <StyledModal open {...modalProps}>
        {loading && <EhLoader active loaderContent={loaderContent} />}
        {renderHeader || renderFooter ? this.renderSlottedModal() : children}
      </StyledModal>
    );
  }
}
