import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Chart } from "react-chartjs-2";
import ChartDataLabels from "chartjs-plugin-datalabels";
import styled from "styled-components";
import PropTypes from "prop-types";
import { Colors, Spacing } from "../../style/variables";

const StyledDiv = styled.div`
  ${(props) => ` height: ${props.height};
width: ${props.width};`}
  padding: ${Spacing.spacingLarge};
`;

ChartJS.register(ArcElement, Tooltip, Legend, ChartDataLabels);

const EhChart = ({
  legendData,
  chartData,
  type,
  zeroCountMessage,
  height,
  width,
}) => {
  const { dataElements, labelColor } = chartData;
  const {
    legendPosition,
    onClick = null,
    boxWidth,
    legendPadding,
  } = legendData;
  const labels = dataElements.map((item) => item.label);
  const counts = dataElements.map((item) => item.count);
  const totalCount = counts.reduce((acc, item) => acc + item);
  const percentageToDisplay = dataElements.map((item) =>
    (item.count / totalCount) * 100 < 4 && (item.count / totalCount) * 100 > 0
      ? 4
      : (item.count / totalCount) * 100
  );
  const colors = [
    Colors.indigo[100],
    Colors.orange[10],
    Colors.blue[30],
    Colors.grey[30],
  ];
  const options = {
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: legendPosition,
        onClick: onClick,
        labels: {
          boxWidth: boxWidth,
          padding: legendPadding,
        },
      },
      tooltip: {
        backgroundColor: Colors.white,
        bodyColor: Colors.black,
        callbacks: {
          label: (context) => {
            const percentage = (counts[context.dataIndex] / totalCount) * 100;
            const displayPercentage =
              percentage < 1 ? "1%" : `${Math.floor(percentage)}%`;
            return `${context.label}: ${
              counts[context.dataIndex]
            } (${displayPercentage})`;
          },
        },
      },
      datalabels: {
        color: labelColor,
        formatter: (value, context) => {
          const percentage = (counts[context.dataIndex] / totalCount) * 100;
          if (percentage === 0) {
            return null;
          } else if (percentage < 1 && percentage > 0) {
            return "1%";
          } else {
            return `${Math.floor(percentage)}%`;
          }
        },
        align: "start",
        anchor: "end",
      },
    },
  };
  const data = {
    labels: labels,
    datasets: [
      {
        data: percentageToDisplay,
        backgroundColor: colors,
        borderWidth: 0,
      },
    ],
  };

  return (
    <StyledDiv height={height} width={width}>
      {totalCount ? (
        <Chart type={type} data={data} options={options} />
      ) : (
        <div>{zeroCountMessage}</div>
      )}
    </StyledDiv>
  );
};
EhChart.propTypes = {
  /* type of the chart ex: pie,line,doughnut...etc */
  type: PropTypes.oneOf(["pie"]),
  /* chartData to dispay the chart having labels,colors,counts,labelColor */
  chartData: PropTypes.shape({
    dataElements: PropTypes.arrayOf(
      PropTypes.shape({
        labels: PropTypes.string,
        counts: PropTypes.number,
      })
    ),
    labelColor: PropTypes.string,
  }),
  /* legendData to display legends having position of legends,boxWidth of legends,
  function to click legends, legendPadding for spacing */
  legendData: PropTypes.shape({
    legendPosition: PropTypes.oneOf(["right", "left", "top", "bottom"]),
    onClick: PropTypes.func,
    boxWidth: PropTypes.number,
    legendPadding: PropTypes.number,
  }),
  /* To display the message if count is zero , it has both option to display a string or a node */
  zeroCountMessage: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  /* Change the height of the chart */
  height: PropTypes.string,
  /* Change the height of the chart */
  width: PropTypes.string,
};
export default EhChart;
