import PropTypes from "prop-types";
import { Formatters } from "@evolent/roots";
import EhText from "./EhText";

const EhFormattedText = ({
  locale = "en-US",
  value,
  type = "text",
  format,
  ...ehTextProps
}) => (
  <EhText {...ehTextProps}>
    {Formatters.getFormattedValue(value, type, format, locale)}
  </EhText>
);

EhFormattedText.propTypes = {
  /** Locale for the formatting */
  locale: PropTypes.string,
  /** Value to be shown */
  value: PropTypes.oneOfType([
    PropTypes.string.isRequired,
    PropTypes.number.isRequired,
    PropTypes.element.isRequired,
  ]),
  /** The type of formatting */
  type: PropTypes.oneOf([
    "number",
    "text",
    "phone",
    "percent",
    "localDateTime",
    "utcDateTime",
    "substitutor",
    "zip",
    "pluralize",
    "ssn",
  ]),
  /** Formatting to be done on value
   * For Date: Provide any format with MM, DD, YYYY, hh, mm, A, a
   * For Number: Provide any options as a format from https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/NumberFormat
   * For Number: If no format is provided, number will be displayed as "123,456,789" for 123456789
   */
  format: PropTypes.any,
};

export default EhFormattedText;
