import { Component, Children, useState } from "react";
import PropTypes from "prop-types";
import warning from "warning";
import { EhButton } from "../../components";
import { Tabs, Tab as MaterialTab, Box } from "@mui/material";
import { Spacing, Colors, Typography } from "../../style";
import { LineHeight } from "../../style/variables";
import { isNil } from "@evolent/roots";
import styled from "styled-components";

const Container = styled(Box)`
  display: ${({ orientation }) =>
    orientation === "vertical" ? "inline-flex" : "block"};
`;

const TabContainer = styled(Box)`
  display: ${({ orientation }) =>
    orientation === "vertical" ? "inline-flex" : "flex"};
  justify-content: ${({ orientation }) =>
    orientation === "vertical" ? "flex-start" : "space-between"};
  border-bottom: ${({ orientation }) =>
    orientation === "horizontal"
      ? `${Spacing.borderWidthThin} solid rgba(34,36,38,.15)`
      : "none"};
  flex-direction: ${({ orientation }) =>
    orientation === "horizontal" ? "row" : "column"};
  align-items: flex-end;
  margin-bottom: ${Spacing.spacingSmall};
  && .MuiTabs-root {
    min-height: 0;
  }
  && .MuiTabs-indicator {
    background-color: inherit;
  }
`;

const getSizeStyle = ({ size, orientation }) => {
  let sizeStyle = "";
  switch (size) {
    case "small":
      sizeStyle = `
        font-size: ${Typography.fontSizeSmall};
        line-height: ${LineHeight.heading.small};
        padding:0.25rem;
        ${() => orientation === "vertical" && "padding-left: 0.5625rem;"}
        ${() => orientation === "horizontal" && "padding-bottom: 0.0625rem;"}
      `;
      break;
    case "medium":
      sizeStyle = `
      font-size: ${Typography.fontSizeMedium};
      line-height: ${LineHeight.heading.normal};
      padding:0.5rem;
      ${() => orientation === "vertical" && "padding-left: 0.8125rem;"}
      ${() => orientation === "horizontal" && "padding-bottom: 0.3125rem;"}
      `;
      break;

    case "large":
      sizeStyle = `
      font-size: ${Typography.fontSizeLarge};
      line-height: ${LineHeight.heading.normal};
      padding:0.75rem;
      ${() => (orientation === "vertical" ? "padding-left: 1.3125rem;" : "")}
      ${() =>
        orientation === "horizontal" ? "padding-bottom: 0.5625rem;" : ""}
      `;
      break;
  }

  return sizeStyle;
};

const StyledMaterialTab = styled(MaterialTab)`
  font-weight: ${({ isActive }) =>
    isActive ? Typography.fontWeightBold : Typography.fontWeightRegular};
  color: ${Colors.blue[100]};
  opacity: 1;
  text-transform: none;
  min-width: 0;
  min-height: 0;
  max-width: 100%;
  ${(props) => getSizeStyle(props)}
  &:hover: {
    background-color: ${Colors.blue[25]};
  }
  &.Mui-selected {
    color: ${Colors.grey[100]};
    border-bottom: ${({ orientation }) =>
      orientation === "horizontal"
        ? `${Spacing.borderWidthThick} solid ${Colors.grey[100]}`
        : "none"};
    border-left: ${({ orientation }) =>
      orientation === "vertical"
        ? `${Spacing.borderWidthThick} solid ${Colors.grey[100]}`
        : "none"};
  }
  &.MuiTab-root {
    align-items: ${({ orientation }) =>
      orientation === "vertical" ? "start" : "center"};
  }

  &.MuiButtonBase-root:hover {
    background-color: ${Colors.blue[25]};
  }
`;

const TabPanel = (props) => {
  const { children, value, index, hideInactiveTabs, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      className="scrollable-auto-tabpanel"
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index || hideInactiveTabs ? children : null}
    </div>
  );
};

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
  hideInactiveTabs: PropTypes.bool,
};

class EhTabMat extends Component {
  static propTypes = {
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.node]), // eslint-disable-line react/no-unused-prop-types
    disabled: PropTypes.bool, // eslint-disable-line react/no-unused-prop-types
  };
}

const EhTabsMat = ({
  addButton,
  size = "medium",
  activeIndex = 0,
  onChange,
  children,
  orientation = "horizontal",
  hideInactiveTabs = false,
  ...tabProps
}) => {
  const [tabIndex, setTabIndex] = useState(activeIndex);

  onChange && tabIndex !== activeIndex && setTabIndex(activeIndex);

  const handleChange = (event, newValue) => {
    onChange &&
      onChange(event, {
        activeIndex: newValue,
        children,
      });
    setTabIndex(newValue);
  };

  warning(
    Children.toArray(children).every(
      (child) => child.type.name === "EhTabMat" || isNil(child)
    ),
    "EhTabsMat can only have Tab as children"
  );

  return (
    <Container orientation={orientation} className={`container`}>
      <TabContainer orientation={orientation} className={`tabContainer`}>
        <Tabs
          orientation={orientation}
          value={tabIndex}
          onChange={handleChange}
          {...tabProps}
        >
          {Children.map(children, (child, index) => {
            return (
              !isNil(child) && (
                <StyledMaterialTab
                  isActive={index === tabIndex}
                  size={size}
                  orientation={orientation}
                  key={index}
                  value={index}
                  label={child.props.label}
                  disabled={child.props.disabled}
                />
              )
            );
          })}
        </Tabs>
        {addButton ? (
          <EhButton
            key="ehtabs-button"
            className="ehtabs-button"
            {...addButton}
            data-aq={`btn-${addButton.text}`}
          />
        ) : null}
      </TabContainer>
      {Children.map(children, (child, index) => {
        return (
          !isNil(child) && (
            <TabPanel
              hideInactiveTabs={hideInactiveTabs}
              value={tabIndex}
              key={index}
              index={index}
            >
              {child.props.children}
            </TabPanel>
          )
        );
      })}
    </Container>
  );
};

EhTabsMat.Tab = EhTabMat;

EhTabsMat.propTypes = {
  children: PropTypes.array,
  addButton: PropTypes.shape(EhButton.propTypes),
  onChange: PropTypes.func,
  activeIndex: PropTypes.number,
  orientation: PropTypes.oneOf(["horizontal", "vertical"]),
  size: PropTypes.oneOf(["small", "medium", "large"]),
  // this flag will determine if it should unmount the non selected tabs or just hide it. By default this flag will be false
  hideInactiveTabs: PropTypes.bool,
};

export default EhTabsMat;
